<template>
    <div class="ui-main-module">
        <div class="operation-box">
            <div>
                <el-button size="small" type="text" @click="handleDownloadModule">下载模板</el-button>
                <el-button type="text" @click="handleExportListInfo" size="small" icon="icon-daochu icon_blue">导出
                </el-button>
                <ComUpload label="批量导入" :url="importUrl" @success="handleUploadSuccess"
                           :upData="upData" :multiple="true"></ComUpload>
                <el-button type="danger" size="small" @click="delData">删除</el-button>

            </div>
            <div>

            </div>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :other-height="['operation-box']"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">

            <template slot="operate" slot-scope="scope">
                <el-table-column
                    width="200px"
                    fixed="right"
                    label="操作">
                    <template slot-scope="scope">
                        <el-button @click="delItem(scope.row.id)" type="text" size="small">
                            删除
                        </el-button>
                        <el-button type="text" @click="editItem(scope.row)" size="small">编辑</el-button>
                        <el-button type="text" @click="viewLog(scope.row.id)" size="small">日志</el-button>
                    </template>
                </el-table-column>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog title="编辑" class="ui-layout_edit-dialog" :visible.sync="dialogFormVisible" width="500px">
            <el-form :model="editform" label-width="100px" size="small">
                <el-form-item :label="item" v-for="(item,index) in tableKey" :key="index">
                    <el-input v-if="!(tableProps[index]==='reject_to_dpt'|| tableProps[index]==='setting_introduce')"
                              v-model="editform[tableProps[index]]"
                              :disabled="tableProps[index]==='id'||tableProps[index]==='create_time'||tableProps[index]==='create_user'"></el-input>
                    <el-select v-if="tableProps[index]==='reject_to_dpt'"
                               v-model="editform.reject_to_dpt"
                               placeholder="请选择">
                        <el-option
                            v-for="(label, value) in departmentOption"
                            :key="value"
                            :label="label"
                            :value="value"></el-option>
                    </el-select>
                    <el-input
                        style="width: 66%"
                        placeholder="请输入"
                        type="textarea"
                        :rows="4"
                        resize="none"
                        v-if="tableProps[index]==='setting_introduce'"
                        v-model="editform.setting_introduce"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
                <el-button type="primary" @click="saveEditInfo" size="small">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="查看日志" class="ui-layout_edit-dialog" :visible.sync="dialogTableVisible" top="5vh">
            <ComTableInfo
                :tableKey="logKey"
                :tableProps="logProps"
                :tableData="logData"
                :tableWidth="logWidth">
                <template slot="remark" slot-scope="scope">
                    <p v-for="(item,index) in scope.row.remark" :key="index">
                        {{item}}
                    </p>
                </template>
            </ComTableInfo>
        </el-dialog>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import * as downloadApi from '@/api/download_api';
import {
    mapGetters
} from 'vuex';
import {
    getRejectedListInfo,
    delRejected,
    changeRejectedInfo,
    exportRejectedInfo,
    importRejectedInfo
} from '@/api/copy_department/copy_edit_module/rejected_info_maintain';
import { getLogList } from '@/api/common';

export default {
    data () {
        return {

            // 搜索缩进
            showSearch: true,

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 上传列表
            saveFileList: [],

            //编辑弹窗
            dialogFormVisible: false,
            editform: {},

            //部门参数
            departmentOption: config.departmentInfo,

            //日志弹窗
            dialogTableVisible: false,
            logData: [],
            logKey: [],
            logProps: [],
            logWidth: ['200px', '120px', '120px'],

            //导入的接口
            importUrl: '/rejection/rejection/import_reject_config',

            upData: {}

        };
    },
    computed: {
        ...mapGetters([
            'loginInfo',
        ]),
        downloadUrl () {
            return downloadApi['DOWNLOAD_REJECTED_MAINTAIN'];
        }
    },
    methods: {
        // 下载导入模板
        handleDownloadModule () {
            window.open(this.downloadUrl + '?reject_from_dpt=3', '_self');
        },
        handleUploadSuccess (data) {
            if (data) {
                this.handleQueryInfo();
            }
        },

        //查看日志
        viewLog (id) {
            this.dialogTableVisible = true;
            let obj = {
                mod_type: 1,
                id
            };
            getLogList(obj).then(({data}) => {
                if (data.status === 1) {
                    this.logData = data.data.value;
                    this.logData.map((item) => {
                        return item.remark = item.remark.split('|');
                    });
                    this.logProps = Object.keys(data.data.key);
                    this.logKey = Object.values(data.data.key);
                } else {
                    this.logData = [];
                }
            });
        },
        //保存修改
        saveEditInfo () {

            let newData = {...this.editform};
            delete newData.create_time;

            //转换部门
            let arr = Object.values(config.departmentInfo);
            let idx = arr.findIndex((item) => {
                return newData.reject_to_dpt === item;
            });
            if (idx >= 0) {
                newData.reject_to_dpt = idx + 1;
            }
            let obj = {
                modify_user: this.loginInfo.user_name,
                user_id: this.loginInfo.uid,
                ...newData
            };
            changeRejectedInfo(obj).then(({data}) => {
                if (data.errorMess) {
                    this.$message({
                        message: data.errorMess,
                        type: 'warning'
                    });
                    return;
                }
                this.dialogFormVisible = false;

                this.$message({
                    message: '修改成功!',
                    type: 'success'
                });
                this.handleQueryInfo();
            });

        },

        //编辑弹窗
        editItem (obj) {
            this.dialogFormVisible = true;
            this.editform = Object.assign({}, obj);
        },

        //勾选的id
        selectId () {
            let idArr = [];
            this.saveCheckInfo.forEach((obj) => {
                idArr.push(obj.id);
            });
            return idArr;
        },

        //删除多个
        delData () {
            let idArr = this.selectId();
            if (!idArr.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: idArr.join(','),
                    user_id: this.loginInfo.uid,
                    modify_user: this.loginInfo.user_name
                };
                delRejected(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        //删除单个
        delItem (id) {
            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: id,
                    user_id: this.loginInfo.uid,
                    modify_user: this.loginInfo.user_name
                };
                delRejected(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        // 导出明细信息
        handleExportListInfo () {
            let obj = {
                reject_from_dpt: 3,
                id: this.selectId().join(',')
            };
            exportRejectedInfo(obj);

        },

        // 列表信息查询
        handleQueryInfo (params = {}) {
            let obj = {
                reject_from_dpt: 3,
                ...params
            };

            getRejectedListInfo(obj).then(({data}) => {
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            });
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            });
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            //数据渲染
            this.tableData = data.data.value;
            this.tableProps = Object.keys(data.data.key);
            this.tableKey = Object.values(data.data.key);

            this.tableWidth = ['60', '120', '700', '120', '80', '140', '120', '120', '150'];

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        }
    },
    created () {
        this.handleQueryInfo();
        this.upData = {
            user_id: this.loginInfo.staff_code || '12534',
            reject_from_dpt: 3
        };
    },
};
</script>

<style lang="less" scoped>
/deep/ .el-input {
    /*width: 100%;*/
}

@primary-color: #1299d8;
.operation-box {
    display: flex;
    justify-content: space-between;
    background: #FFFFFF;
    padding: 5px 10px;

    .el-button:nth-child(1) {
        color: #3388ff;
        border-bottom: 1px #3388ff solid;
        border-radius: 0;
        font-weight: normal;
        padding-bottom: 5px;
        padding-left: 0;
        padding-right: 0;
    }

    /deep/ .el-button:nth-child(2) {
        margin-right: 10px;

        i {
            font-size: 14px;
            margin-right: 5px;
        }
    }

    > div {
        display: flex;
        align-items: center;

        .upload-demo {
            margin: 0 10px;
        }
    }
}

</style>



