/**  =========================> *|`| % 品控扫码关联 % |`|*<======================== **/

import * as types from '../../../mutation-type';

const config = {
    state: {
        sampleScanListInfo: {}
    },
    mutations: {
        [types.SAVE_SAMPLE_SAMPLE_LIST_INFO] (state, saveInfo) {
			console.log('mkLog: saveInfo', saveInfo);
            state.sampleScanListInfo = saveInfo;
        },
        [types.DELETE_SAMPLE_LIST_INFO](state){
            state.sampleScanListInfo = {};
        }
    },
    actions: {

        // 保存扫码列表
        SaveSampleScanListInfo ({commit}, queryData = {}) {

            commit(types['SAVE_SAMPLE_SAMPLE_LIST_INFO'], queryData);
        },
        DeleteSampleScanListInfo ({commit}) {
            commit(type['DELETE_SAMPLE_LIST_INFO']);
        }
    }
};

export default config;




