/**  --->>>>   |^ TQE审核记录^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// TQE审核记录
export function getAuditRecordInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.excelType) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }

        if (data.excelType === 1) {

            //导出当前页
            window.open('/sample_control/sample_check_record/export_current_page' + str, '_self');
        } else {

            //导出全部
            window.open('/sample_control/sample_check_record/export_all_page' + str, '_self');
        }
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/sample_control/sample_check_record/spu_check_record', data);
    }
}



