
/**  --->>>>   |^ 样品跟单记录^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取样品跟单记录信息
export function getSampleTrackRecordInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 4,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/create_supplier' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/sample/sample_order_record/select_sample_order_record_list', data);
    }
}


