<template>
    <div @click="handleClick" class="ui-table__img">
        <div class="ui-image--static" v-show="src && !isComplete" :style="{width, height}"></div>
        <img v-show="false"  :src="src" alt="" ref="myImg">
        <div v-show="isComplete" :style="{width, height, background: `url(${src}) no-repeat center/contain`}"></div>
    </div>
</template>

<script>
    export default {
        name: "LoadingImg",
        props: {
            src: {
                type: String,
                default: ''
            },
            width: {
                type: String,
                default: '70px'
            },
            height: {
                type: String,
                default: '70px'
            }
        },
        data() {
            return {
                isComplete: false
            }
        },
        methods: {
            handleClick(e) {
                this.$emit('click', e)
            }
        },
        mounted() {
            const vm = this;
            this.$nextTick(() => {

                this.$refs.myImg.addEventListener('load', function() {
                    vm.isComplete = true;
                    // console.log(vm.$refs.myImg.clientHeight);
                    // console.log(vm.$refs.myImg.clientWidth);
                    console.log(window.getComputedStyle(vm.$refs.myImg, null).height);
                });

                this.$refs.myImg.addEventListener('error', function() {
                    // vm.isComplete = true;
                    this.src = require('../assets/image/img_error.png');
                })
            })
        }
    }
</script>

<style scoped>
    .ui-table__img {
        background: #eeeeee;
        border: 1px solid #e6e6e6;
        display: inline-block;
    }

    .ui-image--static {
        background: url("../assets/image/img_loading.gif") no-repeat center/contain;
    }
</style>


