            //     `from_time` datetime DEFAULT NULL COMMENT '公司经营开始时间',
            //     `type` tinyint(1) NOT NULL DEFAULT '2' COMMENT '法人类型（1 个人 2 公司）',
            //     `category_score` int(11) NOT NULL DEFAULT '0' COMMENT '行业分数',
            //     `company_id` int(11) NOT NULL DEFAULT '0' COMMENT '天眼查公司id',
            //     `reg_number` varchar(150) NOT NULL DEFAULT '' COMMENT '注册号',
            //     `percentile_score` int(11) NOT NULL DEFAULT '0' COMMENT '企业评分',
            //     `reg_capital` varchar(30) NOT NULL DEFAULT '' COMMENT '注册资金',
            //     `reg_institute` varchar(50) NOT NULL DEFAULT '' COMMENT '登记机关',
            //     `name` varchar(50) NOT NULL DEFAULT '' COMMENT '公司名',
            //     `reg_location` varchar(100) NOT NULL DEFAULT '' COMMENT '注册地址',
            //     `industry` varchar(50) NOT NULL DEFAULT '' COMMENT '所属行业',
            //     `approved_time` datetime DEFAULT NULL COMMENT '核准时间',
            //     `logo` varchar(255) NOT NULL DEFAULT '' COMMENT '公司logo',
            //     `org_approved_institute` varchar(30) NOT NULL DEFAULT '' COMMENT '核准机构',
            //     `tax_number` varchar(30) NOT NULL DEFAULT '' COMMENT '纳税人识别号',
            //     `business_scope` text COMMENT '经营范围',
            //     `property3` varchar(30) NOT NULL DEFAULT '' COMMENT '英文名',
