<style scoped lang="less">
@border-color: #e6e6e6;
.ui-query {
    .ui-title {
        text-align: left;
        margin-bottom: 8px;

        span {
            color: #333333;
            font-size: 12px;
            padding-left: 5px;
            border-left: 2px solid #333333;
        }
    }

    table {
        width: 100%;
        border-collapse: collapse;

        td {
            border: 1px solid @border-color;
            text-align: left;
            padding: 10px 0 10px 10px;
            font-size: 12px;

            &:nth-child(odd) {
                background: #f8f8f8;
                width: 130px;
                font-weight: bold;
            }

            &:nth-child(even) {
                width: 170px;
            }
        }
    }
}
</style>



