<template>
    <el-dialog :visible="show" title="物流信息" width="1000px" top="10vh" @close="handleClose">
        <div class="ui-logistics">
            <div class="ui-logistics--top clearFix">
                <div class="ui-logistics--content">
                    <ul>
                        <li>
                            <span>物流编号：{{detail.track_number}}</span>
                        </li>
                        <li>
                            <span>物流公司：{{detail.carrier_name}}</span>
                        </li>
                        <li>
                            <span>运单号码：{{detail.receive_no}}</span>
                        </li>
                        <li>
                            <span>发货时间：{{detail.delivered_time}}</span>
                        </li>
                    </ul>
                </div>
                <div class="ui-logistics--detail">
                    <ul>
                       <li v-for="item in detail.logistics_info"><span>{{item.acceptTime}}</span> <span>{{item.remark}}</span></li>
                    </ul>
                </div>
            </div>
            <div class="ui-logistics--bottom">
                <div class="ui-bottom--title">
                    <span>物流含如下物品</span>
                </div>
                <ul>
                    <li class="clearFix" v-for="item in detail.sku_list">
                        <div class="ui-bottom--item clearFix">
                            <div class="ui-media__img">
                                <img :src="JSON.parse(item.link)[0]" alt="">
                            </div>
                            <div class="ui-media--info">
                                <p v-ellipsis="2">素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描素描</p>
                                <span>{{item.attribute_value_cn}}</span>
                            </div>
                        </div>
                        <div class="ui-bottom--item">
                            <span>单位重量：{{item.weight}}/件</span>
                        </div>
                        <div class="ui-bottom--item">
                            <span>数量：{{item.purchase_qty}}件</span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </el-dialog>
</template>

<script>
    export default {
        props: {
            show: {
                type: Boolean,
                default: false
            },
            detail: {
                type: Object,
                default: {}
            }
        },
        name: "LogisticsInfo",
        methods: {
            handleClose() {
                this.$emit('update:show', false)
            }
        }
    };
</script>

<style scoped lang="less">
    @import (reference) '../../../../assets/less/index';
    .ui-logistics {
        text-align: left;
        .ui-logistics--top {
            min-height: 180px;
            position: relative;
            border: 1px solid @base-bdr-color;
            & > div {
                float: left;
            }
        }

        .ui-logistics--content {
            padding: 20px;
            width: 250px;
            box-sizing: border-box;
            border-right: 1px solid @base-bdr-color;
            position: absolute;
            height: 100%;

            li {
                margin-bottom: 5px;
                font-size: 13px;
                color: #666666;
            }
        }

        .ui-logistics--detail {
            padding: 20px;
            margin-left: 250px;
            /*box-sizing: b;*/

            ul {
                border-left: 1px solid @base-bdr-color;
                padding-left: 15px;

                li {
                    color: #666666;
                    font-size: 13px;
                    margin-bottom: 15px;
                    position: relative;
                    &::before {
                        content: '';
                        display: block;
                        width: 5px;
                        height: 5px;
                        box-shadow: 0 0 0 2px #fff;
                        background: @base-bdr-color;
                        border-radius: 50%;
                        position: absolute;
                        top: 6px;
                        left: -18px;
                    }

                    &.ui-active::before {
                        box-shadow: 0 0 10px 4px #F57842;
                        background: #F57842;
                    }

                    span {
                        display: inline-block;
                        vertical-align: text-top;
                        &:first-child {
                            width: 20%;
                        }

                        &:last-child {
                            width: 78%;
                        }
                    }
                }
            }
        }

        .ui-logistics--bottom {
            margin-top: 10px;
            border: 1px solid @base-bdr-color;

            .ui-bottom--title {
                padding-top: 10px;
                padding-left: 20px;
                span {
                    border-left: 2px solid #333333;
                    padding-left: 5px;
                    font-size: 13px;
                }
            }

            ul li {
                height: 95px;
                padding: 10px 20px;

                &:not(:last-child) {
                    border-bottom: 1px solid @base-bdr-color;
                }
            }

            .ui-bottom--item {
                font-size: 12px;
                float: left;
                &:first-child {
                    width: 310px;
                    margin-right: 100px;
                }

                &:nth-child(2) {
                    width: 140px;
                    margin-right: 100px;
                }

            }

            .ui-media__img {
                width: 70px;
                height: 70px;
                float: left;
                margin-right: 10px;
                img {
                    width: 100%;
                    height: 100%;
                }
            }

            .ui-media--info {
                /*float: left;*/
                p {
                    line-height: 1.3em;
                    font-size: 12px;
                }
                span {
                    color: @base-txtColor-three;
                    font-size: 12px;
                }
            }
        }
    }
</style>


