<template>
    <transition name="bounce">
        <div class="ui-dialog_page" v-show="show" @keydown.esc="handleClosePage">
            <div class="ui-head">
                <span>品控审核弹窗</span>
                <span class="el-icon-close" @click="handleClosePage"></span>
            </div>
            <div class="ui-left_nav">
                <ul>
                    <li
                        v-for="(item, index) in operationList"
                        :key="index"
                        :class="{ 'ui-active': activePage === index && 'ui-active' || submitSkuList.indexOf(item.label) > -1,'ui-reject':rejectSkuList.indexOf(item.label) > -1}"
                        @click="handleToggleEditArea(index, item)">
                        <span>{{ item.label }}</span>
                    </li>
                </ul>
            </div>
            <SKUPage v-for="(item, index) in operationList" :key="index" :row="row" :sku="item.label"
                     v-if="activePage === index" @submit="handleSaveSkuSubmitState"
                     @reject="handleSaveSkuRejectState"></SKUPage>
        </div>
    </transition>
</template>

<script>
import { getSKURepairInfo } from '@/api/quality_control_department/sample_quality_control/sample_inspect_detail';
import SKUPage from './SKUPage';

export default {
    name: 'EditPage',
    components: {
        SKUPage
    },
    props: {
        show: false,
        row: {
            type: Object,
            default () {
                return {};
            }
        }
    },
    data () {
        return {
            sku: '',
            activePage: -1,
            submitSkuList: [],
            rejectSkuList: [],
            editPageData: {}
        };
    },
    computed: {
        operationList () {
            let skuList = [];
            let spu = '';
            try {
                this.row.sku.split(',').forEach(value => {
                    skuList.push({
                        value: value,
                        label: value
                    });
                });
                // spu = this.row.spu[0];
            } catch (e) {
                console.log(this.row);
            }
            return [
                ...skuList
            ];
        },
    },
    methods: {
        handleClosePage () {
            this.$emit('update:show', false);
        },

        // 切换编辑区域
        handleToggleEditArea (index, item) {
            this.sku = item.value;
            this.handleGetRepairInfo({sku: item.value, spu: this.row.spu[0]}, item.value, index);
        },

        // 获取补录信息
        handleGetRepairInfo (query = {}, editKey, editInx) {
            let obj = {
                is_reject: this.row.reject_type,
                ...query
            };
            getSKURepairInfo(obj).then(({data}) => {
                this.$set(this.editPageData, editKey, data.data);
                console.log(this.editPageData, 563);
                this.activePage = editInx;
            });
        },

        // 保存SKU提交状态
        handleSaveSkuSubmitState (val) {
            if (this.submitSkuList.indexOf(val) === -1) {
                this.submitSkuList.push(val);
                if ((this.operationList.length) === (this.submitSkuList.length + this.rejectSkuList.length)) {
                    this.handleClosePage();
                }
            }
        },

        //保存sku 驳回状态
        handleSaveSkuRejectState (val) {
            if (this.rejectSkuList.indexOf(val) === -1) {
                this.rejectSkuList.push(val);
                if ((this.operationList.length) === (this.submitSkuList.length + this.rejectSkuList.length)) {
                    this.handleClosePage();
                }
            }
        }
    },
    watch: {
        show (val) {
            if (val) {
                this.handleGetRepairInfo({
                    sku: this.row.sku.split(',')[0],
                    spu: this.row.spu[0]
                }, this.row.sku.split(',')[0], 0);
                // this.handleGetRepairInfo({sku: '263264190000711', spu: '2632641900007'}, '263264190000711', 0);
            } else {
                this.activePage = -1;
            }
        }
    }
};
</script>

<style scoped lang="less">

@border-color: #e6e6e6;
@active-color: #5b8eff;
.ui-dialog_page {
    width: 100%;
    height: 100%;
    background: #FFFFFF;
    top: 0;
    left: 0;
    position: absolute;
    z-index: 1;
    text-align: left;

    .ui-head {
        background: #f3f3f3;
        height: 45px;
        line-height: 45px;
        padding: 0 20px;
        font-size: 16px;
        position: relative;
        z-index: 2;

        .el-icon-close {
            float: right;
            font-size: 22px;
            cursor: pointer;
            margin-top: 10px;
        }
    }

    .ui-left_nav {
        width: 140px;
        height: 100%;
        position: absolute;
        left: 0;
        border-right: 1px solid @border-color;
        border-top: 1px solid @border-color;
        z-index: 1;

        ul > li {
            height: 40px;
            border-bottom: 1px solid @border-color;
            line-height: 40px;
            font-size: 14px;
            padding-left: 20px;
            cursor: pointer;

            &.ui-active {
                background: @active-color;
                color: #FFFFFF;
            }

            &.ui-reject {
                background: #fd996b;
                color: #FFFFFF;
            }
        }
    }

    .ui-left_nav {
        top: 0;
        padding-top: 45px;
        box-sizing: border-box;
    }

    /deep/ .el-checkbox {
        margin-right: 20px;
        margin-left: 0;
    }
}

/deep/ .el-collapse {
    border: 0 none;
    overflow: hidden;
}

/deep/ .el-collapse-item {
    .el-collapse-item__header {
        background: #f3f3f3;
        height: 30px;
        line-height: 30px;
        text-align: left;
        padding-left: 10px;
        color: #5a8dfd;

        .el-collapse-item__arrow {
            line-height: 30px;
        }
    }

    .el-collapse-item__wrap {
        border: 0 none;
    }

    .el-collapse-item__content {
        padding: 10px;
        box-sizing: border-box;
    }
}

.bounce-enter-active {
    animation: bounce-in .5s;
}

.bounce-leave-active {
    animation: bounce-in .5s reverse;
}

@keyframes bounce-in {
    0% {
        transform: scale(0) translate(-1000px, -1000px) rotateX(360deg);
    }
    100% {
        transform: scale(1) translate(0, 0) rotateX(0deg);
    }
}
</style>



