/**  =========================> *|`| % 样品小组管理 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getSampleGroupInfo,
} from '../../../../api/sample_track_manage/sample_track_module/sample_group_manage'

const config = {
    state: {
        sampleGroupInfo: {},
    },
    mutations: {
        [types.SAVE_SAMPLE_GROUP_INFO] (state, saveInfo) {
            state.sampleGroupInfo = saveInfo
        }
    },
    actions: {
        // 获取样品小组管理信息
        GetSampleGroupInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getSampleGroupInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SAMPLE_GROUP_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




