/**  =========================> *|`| % 合格供应商列表 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getSupplierQualifiedListInfo,
} from '../../../../api/dev_department/supplier_development/supplier_qualified_list';

const config = {
    state: {
        supplierQualifiedInfo: {}
    },
    mutations: {
        [types.SAVE_QUALIFIED_SUPPLIER_INFO](state, saveInfo) {
            state.supplierQualifiedInfo = saveInfo;
        }
    },
    actions: {
        // 获取合格供应商列表信息
        GetSupplierQualifiedListInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSupplierQualifiedListInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_QUALIFIED_SUPPLIER_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




