        // 保存或提交信息
        handleSaveOrSubmitInfo (type, editData = this.editData) {

            // let saveFileData;
            // if (type === '3') {
            //     saveFileData = editData;
            // } else {
            //     saveFileData = this.saveFileData;
            // }

            //详情列表数据重组
            let qc_detail_list = [];

            if (this.editData.sku_qc_report_info.qc_detail_list) {

                this.editData.sku_qc_report_info.qc_detail_list.forEach((item) => {
                    qc_detail_list.push(item.val);
                });
            }
            console.log(qc_detail_list, 156);
            //异常记录数据重组
            let sku_exception_record = [];
            if (this.editData.exceptionRecord) {
                this.editData.exceptionRecord.forEach((item) => {
                    sku_exception_record.push({
                        'record_type': item.recordType,
                        'abnormity_description': item.description,
                        'solution': item.solution,
                        'image': item.file
                    });
                });
            }

            let obj = {
                spu: '10101800005',
                sku: '1010180000512',
                qc_detail_list,
                sku_exception_record,
            };
            if (type === '3') {
                obj = Object.assign(obj, {
                    buffer_pack_type: editData['sku_pack_request_info.buffer_pack_type'],
                    shipment_pack_type: editData['sku_pack_request_info.shipment_pack_type'],
                    qc_grade: editData['sku_qc_report_info.qc_grade'],
                    qc_sample_scheme: editData['sku_qc_report_info.qc_sample_scheme'],
                    qc_remark: editData['sku_qc_report_info.qc_remark'],
                });

            } else {
                obj = Object.assign(obj, {
                    sample_pack_type: editData.sku_pack_request_info.sample_pack_type,
                    sample_pack_weight: editData.sku_pack_request_info.sample_pack_weight,
                    sample_pack_size: editData.sku_pack_request_info.sample_pack_size,
                    buffer_pack_type: editData.sku_pack_request_info.buffer_pack_type,
                    buffer_pack_model: editData.sku_pack_request_info.buffer_pack_model,
                    buffer_pack_request: editData.sku_pack_request_info.buffer_pack_request,
                    shipment_pack_type: editData.sku_pack_request_info.shipment_pack_type,
                    shipment_pack_model: editData.sku_pack_request_info.shipment_pack_model,
                    shipment_pack_request: editData.sku_pack_request_info.shipment_pack_request,
                    add_pack_price: editData.sku_pack_request_info.add_pack_price,
                    estimate_shipment_weight: editData.sku_pack_request_info.estimate_shipment_weight,
                    estimate_shipment_size: editData.sku_pack_request_info.estimate_shipment_size,
                    qc_grade: editData.sku_qc_report_info.qc_grade,
                    qc_sample_scheme: editData.sku_qc_report_info.qc_sample_scheme,
                    qc_remark: editData.sku_qc_report_info.qc_remark,
                });
            }
            console.log(obj, 'obj');

            let formData = new FormData();

            for (let key in obj) {
                if (obj[key] || obj[key] === 0) {

                    //数组单独遍历出来
                    if (Array.isArray(obj[key])) {
                        obj[key].forEach((item, i) => {

                            if (typeof item === 'string') {
                                formData.append(`${key}[${i}]`, item);
                            } else {
                                //如果是对象格式的
                                let k = Object.keys(item);
                                let value = Object.values(item);
                                k.forEach((o, idx) => {
                                    formData.append(`${key}[${i}][${o}]`, value[idx]);
                                });
                            }
                        });
                    } else {
                        formData.append(key, obj[key] || '');
                    }
                }
            }

            switch (type) {
                case '3':
                    // 同步
                    // formData.append('sync_sku', this.checkSku);
                    this.checkSku.forEach((v, i) => {
                        formData.append(`sync_sku[${i}]`, v);
                    });
                    syncSkuInfo(formData).then(({data}) => {
                    });
                    break;

                case '1':
                    // 保存
                    submitSaveSKURepairInfo(formData).then(({data}) => {
                        console.log(data, 'save');
                        if (data.status) {
                            this.$message.success('操作成功');
                        } else {
                            this.$message.error(data.errorMess);
                        }
                    });
                    break;

                case '2':
                    //审核
                    auditSkuInfo(formData).then(({data}) => {
                        console.log(data, 'audit');
                        this.handleOperationCallBack(data);
                    });
                    break;

                case '4':
                    //驳回
                    for (let i of obj.sku_exception_record) {
                        if (!i.record_type) {
                            this.$message.warning('退样类型不能为空！');
                            return;
                        } else if (!i.abnormity_description) {
                            this.$message.warning('产品异常描述不能为空！');
                            return;
                        }
                    }
                    rejectSkuInfo(formData).then(({data}) => {
                        console.log(data, 'reject');
                        if (data.status) {
                            this.$message.success('操作成功');
                        } else {
                            this.$message.error(data.errorMess);
                        }

                    });

            }

            // let isBigSubmit = type === '2' && this.$parent.submitSkuList.indexOf(this.sku) === -1 &&
            //     (this.$parent.submitSkuList.length === this.skuList.length - 1);
            //
            // if (type === '3') {
            //     // formData.append('sync_sku', this.checkSku);
            //
            //     ['1010180000511'].forEach((v, i) => {
            //         formData.append(`sync_sku[${i}]`, v);
            //     });
            // } else if (isBigSubmit) {
            //     // let obj = {
            //     //     sku: this.skuList,
            //     //     spu: this.row.spu[0]
            //     // };
            //     // formData.append('sku', this.sku);
            //     // formData.append('big_status', JSON.stringify(obj));
            // } else {
            //     // formData.append('sku', this.sku);
            // }
            //
            // if (type === '3') {
            //     syncSkuInfo(formData).then(({data}) => {
            //     });
            // } else {
            //
            // }
        },

        // 关联SKU
        handleRelevanceSkuChange (value) {
            let checkedCount = value.length;
            this.checkAllSku = checkedCount === this.skuList.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.skuList.length;
        },

