                            // } else {
                            //     keyArr.push(this.syncCheckArea[index][i]);
                            // }
                            keyArr.push(this.syncCheckArea[index][i]);
                        }
                    });
                }
            });
            let obj = {...fileObj};
            keyArr.forEach(key => {
                let arr = key.split('.');
                if (arr.length > 1) {
                    obj[key] = this.editData[arr[0]][arr[1]];
                } else {
                    obj[key] = this.editData[arr[0]];
                }
            });

            this.handleSaveOrSubmitInfo('3', obj);
        },

