        handleInitData () {

            console.log(this.$parent.editPageData, 888);
            // let data = this.$parent.editPageData[this.sku];
            let data = this.$parent.editPageData;
            console.log(this.sku, 'sku');

            // let data = this.$parent.editPageData['263264190000711'];

            this.editData = Object.assign(this.editData, data);

            console.log(this.editData, 999);

            //初始化数据

            //检测清单列表处理
            this.editData.sku_test_list.test_list.forEach((item) => {
                let arr = item.value.split(/\d['、']/);
                if (arr[0].length === 0) {
                    arr.splice(0, 1);
                }
                arr.forEach((item) => {
                    this.optionList.testList.push({value: item});
                });
            });
            console.log(this.editData.sku_qc_report_info, 1212);

            //检测清单 选择的列表
            let qcDetailList = this.editData.sku_qc_report_info.qc_detail_list;
            this.editData.sku_qc_report_info.qc_detail_list = [];

            qcDetailList.forEach((item) => {
                this.editData.sku_qc_report_info.qc_detail_list.push({val: item});
            });

            this.optionList.attrCheckList = data.sku_attr_info.attribute;

            //处理异常记录
            this.optionList.exceptionList = data.exception_list;

            this.exceptionDefault = {
                recordList: this.optionList.exceptionList,
                recordType: '',
                description: '',
                solution: '',
                image: ''
            };

            //如果是无数据情况,初始一条数据
            if (this.editData.sku_exception_record.length === 0) {
                this.editData.exceptionRecord.push(Object.assign({}, this.exceptionDefault));
            } else {
                this.editData.sku_exception_record.forEach((item) => {

                    let record = this.optionList.exceptionList.find((exception) => {
                        return exception.id === item.record_type;
                    });
                    let recordObj = {
                        recordList: this.optionList.exceptionList,
                        recordType: record && record.name,
                        description: item.abnormity_description,
                        solution: item.solution,
                        image: item.image !== 'undefined' ? item.image : '',
                    };
                    console.log(recordObj, 112233);
                    //路径转文件格式
                    if (item.image && item.image !== 'undefined') {
                        let fileName = /\/[0-9A-Za-z._-]+$/.exec(item.image) || ['未知图片.jpeg'];
                        imageUrlChangeFile(`${this.firstUrl}/${item.image}`, fileName[0].replace('/', ''), (file) => {
                            recordObj.file = file;
                        });
                    }
                    this.editData.exceptionRecord.push(recordObj);

                });
            }

            const attrCheckFn = (eKey, iKey) => {
                if (data.sku_attr_info.sku_attr_select[iKey]) {
                    this.editData[eKey] = data.sku_attr_info.sku_attr_select[iKey].split(',');
                }
            };

            attrCheckFn('publishAttr', 'publish');
            attrCheckFn('orderAttr', 'order_attribute');
            attrCheckFn('basisAttr', 'basic');

            attrCheckFn('specialAttr', 'special_attr');
            attrCheckFn('batteryAttr', 'battery_attr');

            attrCheckFn('isBanAttr', 'prohibited');
            attrCheckFn('modelAttr', 'shape_attribute');
            attrCheckFn('overseas', 'overseas_attribute');
            attrCheckFn('certificateType', 'certificate_type');
            attrCheckFn('instructions', 'instructions');

            data.attach_info && data.attach_info.forEach((value, index) => {
                let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                imageUrlChangeFile(`${this.firstUrl}/${value}`, fileName[0].replace('/', ''), (file) => {
                    this.saveUrlFile.appendixDatum.splice(index, 1, file);
                    this.saveFileData.appendixDatum.push({
                        url: value
                    });
                });
            });

            // 商品开发图片 develop_image
            data.attach_info && data.attach_info.forEach((value, index) => {
                let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                imageUrlChangeFile(`${this.firstUrl}/${value}`, fileName[0].replace('/', ''), (file) => {
                    this.saveUrlFile.devImage.splice(index, 1, file);
                    this.saveFileData.devImage.push({
                        url: value
                    });
                });
            });

            // data.file && data.file.forEach((value, index) => {
            //     let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
            //     imageUrlChangeFile(`${this.firstUrl}/${value}`, fileName[0].replace('.', ''), (file) => {
            //         this.$set(this.saveUrlFile.appendixDatum, index, file);
            //         this.saveFileData.appendixDatum.push({
            //             url: value
            //         });
            //         console.log(this.saveFileData.appendixDatum);
            //     });
            // });
        },

        // 操作回调
        handleOperationCallBack (data) {
            if (data.status) {
                this.$emit('submit', this.sku);
                this.$message.success('操作成功');
            } else {
                this.$message.error(data.errorMess);
            }
        }
    },
    watch: {
        sku (val) {
            this.handleInitData();
        },

    },
};
</script>

