<template>
    <div class="ui-main-module">
        <div class="ui-fn-module">
            <el-button type="text" @click="handleSort(-2)" size="small" icon="icon-shangsheng icon_blue">上升</el-button>
            <el-button type="text" @click="handleSort(2)" size="small" icon="icon-xiajiang icon_orange">下降</el-button>
            <el-button type="text" @click="handleExportListInfo" size="small" icon="icon-daochu icon_blue">导出
            </el-button>

            <ComUpload label="批量导入" btnType="text" :url="importUrl" @success="handleUploadSuccess"
                       :upData="upData" :multiple="true" class="ui-upload_btn">
                <template slot="icon">
                    <i class="icon-piliangdaoru icon_purple"></i>
                </template>
            </ComUpload>

            <el-button size="small" type="text" @click="handleDownloadModule" icon="icon-xiajiang icon-main">下载模板
            </el-button>
            <el-button type="text" size="small" @click="addData" icon="icon-xinzeng1 icon_blue">新增</el-button>
            <el-button type="text" size="small" @click="delData" icon="icon-shanchu1 icon_pink">删除</el-button>
            <el-button type="text" class="danger-border" size="small" @click="goBack"
                       icon="icon-fanhui icon_blue">返回
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :tableMinWidth="tableMinWidth"
            :showSearch="showSearch"
            :otherHeight="['ui-main-tab']"
            @checked="handleSaveCheckInfo">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.id)"
                              :class="moduleType === item.id && 'ui-active'">{{item.title}}{{item.level}}<i
                            class="ui-tab_line"></i></span>
                    </li>
                </el-badge>
            </div>
            <template slot="rqstd" slot-scope="scope">
                <!--<el-tooltip placement="left">-->
                <!--<div class="el-tooltip-content" slot="content">{{scope.row.rqstd}}</div>-->
                <!--<div class="el-tooltip-left-more">{{scope.row.rqstd}}</div>-->
                <!--</el-tooltip>-->
            </template>
            <template slot="operate" slot-scope="scope">
                <el-table-column
                    fixed="right"
                    label="操作">
                    <template slot-scope="scope">
                        <el-button @click="delItem(scope.row.id)" type="text" size="small">
                            删除
                        </el-button>
                        <el-button type="text" @click="editItem(scope.row)" size="small">编辑</el-button>
                        <el-button type="text" @click="viewLog(scope.row.id)" size="small">日志</el-button>
                    </template>
                </el-table-column>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog title="编辑" class="ui-layout_edit-dialog" :visible.sync="dialogFormVisible" width="500px">
            <el-form :model="editform" label-width="80px">
                <el-form-item :label="item" v-for="(item,index) in tableKey" :key="index">
                    <el-input v-model="editform[tableProps[index]]" size="small"
                              v-if="tableProps[index]!=='rqstd'"
                              :disabled="tableProps[index]==='id'||tableProps[index]==='parent_id'||tableProps[index]==='create_time'||tableProps[index]==='create_user'"></el-input>
                    <el-input type="textarea" v-model="editform.rqstd" v-if="tableProps[index]==='rqstd'" :rows="5"
                              resize="none"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
                <el-button type="primary" @click="saveEditInfo" size="small">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog
            width="640px"
            top="5vh"
            title="新增"
            class="ui-edit_layout_big"
            :visible.sync="dialogAddVisible"
            :before-close="cancelAddInfo">

            <el-form :model="addForm" label-width="100px">
                <el-form-item :label="item" v-for="(item,index) in tableKey" :key="index"
                              v-show="!(tableProps[index]==='id'||tableProps[index]==='create_time'||tableProps[index]==='create_user')">
                    <el-input v-show="!(tableProps[index]==='parent_id')"
                              v-model="addForm[tableProps[index]]"
                              size="small"></el-input>

                    <el-select v-if="tableProps[index]==='parent_id'"
                               v-model="addForm.parent_id"
                               placeholder="请选择">
                        <el-option
                            v-for="(item, index) in moduleTypeList"
                            :key="index"
                            :label="item.title+item.level"
                            :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddVisible = false" size="small">取 消</el-button>
                <el-button type="primary" @click="saveAddInfo" size="small">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="查看日志" class="ui-layout_edit-dialog" :visible.sync="dialogTableVisible" top="5vh">
            <ComTableInfo
                :tableKey="logKey"
                :tableProps="logProps"
                :tableData="logData"
                :tableWidth="logWidth">
                <template slot="remark" slot-scope="scope">
                    <p v-for="item in scope.row.remark" :key="item">
                        {{item}}
                    </p>
                </template>
            </ComTableInfo>
        </el-dialog>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import * as downloadApi from '@/api/download_api';
import { mapGetters } from 'vuex';
import {
    getTestListInfo,
    exportTestListInfo,
    delTestList,
    changeTestList,
    sortEdit,
    addTestList
} from '@/api/quality_control_department/sample_quality_control/data_maintain/test_list_maintain';
import { getLogList } from '@/api/common';
import { getBasicCheckListInfo } from '@/api/quality_control_department/sample_quality_control/basic_data_maintain';
import TemplateDefault from '../../../../../components/TemplateDefault';

//处理父级id数据
function getParentName (id) {
    let obj = this.moduleTypeList.find((item) => {
        return id == item.id;
    });
    if (obj) {
        let newStr = obj.title;
        if (obj.level) {
            newStr + obj.level;
        }
        return newStr;
    }
    return '-';
}

export default {
    components: {TemplateDefault},
    props: {
        queryParams: 0
    },
    data () {
        return {

            // 搜索缩进
            showSearch: true,

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableMinWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 上传列表
            saveFileList: [],

            //编辑弹窗
            dialogFormVisible: false,
            editform: {},

            //部门参数
            departmentOption: config.departmentInfo,

            //日志弹窗
            dialogTableVisible: false,
            logData: [],
            logKey: [],
            logProps: [],
            logWidth: ['200px', '120px', '120px'],

            //导入的接口
            importUrl: '/sample_config/qc_config/import_config ',

            upData: {},

            //列表tab选项
            moduleTypeList: [],

            moduleType: this.queryParams,

            //新增弹窗
            dialogAddVisible: false,
            addForm: {},

            isShowLookImage: false,
            lookImageUrl: ''

        };
    },
    computed: {
        ...mapGetters([
            'loginInfo',
            'pageEditState'
        ]),
        downloadUrl () {
            return downloadApi['DOWNLOAD_TEST_LIST_MAINTAIN'];
        }
    },
    methods: {
        cancelAddInfo () {},
        //新增
        saveAddInfo () {
            let obj = {
                ...this.addForm
            };
            addTestList(obj).then(({data}) => {
                if (data.errorMess) {
                    this.$message({
                        message: data.errorMess,
                        type: 'warning'
                    });
                    return;
                }
                this.dialogAddVisible = false;

                this.$message({
                    message: '添加成功!',
                    type: 'success'
                });
                this.addForm = {};
                this.handleQueryInfo();
            });

        },

        //新增
        addData () {
            this.dialogAddVisible = true;
        },

        handleSort (val) {
            let idArr = this.selectId();
            if (!idArr.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            let obj = {
                parent_id: this.moduleType,
                order: val,
                id: idArr.join(',')
            };
            sortEdit(obj).then(() => {
                this.handleQueryInfo();
            });

        },
        // 切换tab列表
        handleToggleTableInfo (val) {
            this.moduleType = val;
            this.handleQueryInfo();
        },

        // 下载导入模板
        handleDownloadModule () {
            window.open(this.downloadUrl, '_self');
        },

        //上传文件成功
        handleUploadSuccess (data) {
            if (data) {
                this.getBasicCheckList();
            }
        },

        //查看日志
        viewLog (id) {
            this.dialogTableVisible = true;
            let obj = {
                mod_type: 4,
                id
            };
            getLogList(obj).then(({data}) => {
                if (data.status === 1) {
                    this.logData = data.data.value;
                    this.logData.map((item) => {
                        return item.remark = item.remark.split('|');
                    });
                    this.logProps = Object.keys(data.data.key);
                    this.logKey = Object.values(data.data.key);
                } else {
                    this.logData = [];
                }
            });
        },

        //保存修改
        saveEditInfo () {

            let newData = {...this.editform};
            delete newData.create_time;
            delete newData.parent_id;

            let obj = {
                ...newData
            };
            changeTestList(obj).then(({data}) => {
                if (data.errorMess) {
                    this.$message({
                        message: data.errorMess,
                        type: 'warning'
                    });
                    return;
                }
                this.dialogFormVisible = false;

                this.$message({
                    message: '修改成功!',
                    type: 'success'
                });
                this.handleQueryInfo();
            });

        },

        //编辑弹窗
        editItem (obj) {
            this.dialogFormVisible = true;
            this.editform = Object.assign({}, obj);
        },

        //勾选的id
        selectId () {
            let idArr = [];
            this.saveCheckInfo.forEach((obj) => {
                idArr.push(obj.id);
            });
            return idArr;
        },

        //删除多个
        delData () {
            let idArr = this.selectId();
            if (!idArr.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: idArr.join(',')
                };
                delTestList(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        //删除单个
        delItem (id) {
            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: id
                };
                delTestList(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        // 导出明细信息
        handleExportListInfo () {
            let obj = {
                parent_id: this.moduleType,
                id: this.selectId().join(',')
            };
            exportTestListInfo(obj);

        },

        // 列表信息查询
        handleQueryInfo (params = {}) {
            let obj = {
                parent_id: this.moduleType,
                ...params
            };

            getTestListInfo(obj).then(({data}) => {
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            });
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            });
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //查询回调
        handleQueryInfoCallBack (data) {
            console.log('TCL: handleQueryInfoCallBack -> data', data);
            let _this = this;

            if (data.data) {
                //数据渲染
                this.tableData = data.data.value;
                this.tableData.map((item) => {

                    //处理所属父级的名称
                    let id = Reflect.apply(getParentName, _this, [item.parent_id]);
                    return item.parent_id = id;
                });

                // COMMENT: 2019.3.27 应需求去除所属父级
                const keyMaps = {...data.data.key};
                delete keyMaps['parent_id'];
                this.tableProps = Object.keys(keyMaps);
                this.tableKey = Object.values(keyMaps);
                this.tableMinWidth = this.tableProps.map(item => {
                    // id type_name rqstd create_time create_user
                    if (item == 'id') return '40';
                    if (item == 'rqstd') return '500';
                    if (item == 'type_name') return '100';
                    if (item == 'create_time') return '70';
                    return '';
                });

                //渲染分页
                this.pageData.currentList = data.page.limit * 1;
                this.pageData.currentPage = data.page.offset * 1;
                this.pageData.currentTotal = data.page.total * 1;

            } else {

                this.tableData = [];
            }

            // this.tableWidth = ['', '', '', '', '220px', '220px'];

        },

        // 获取标题类型列表
        getBasicCheckList () {
            this.moduleTypeList = [{
                title: '全部',
                id: 0,
                level: ''
            }];
            getBasicCheckListInfo().then(({data}) => {
                data.data.list.forEach((item) => {
                    let {type_name: title, id} = item;
                    let level = '';
                    if (Array.isArray(title)) {
                        level = `(${title[1]})`;
                        title = title[0];
                    }
                    this.moduleTypeList.push({
                        title,
                        level,
                        id
                    });

                });
                this.handleQueryInfo();
            });

        },
        goBack () {
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'basic_data_router',
                value: null
            });
            this.$router.go(-1);
        }
    },
    created () {

        this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
            key: 'basic_data_router',
            value: this.$route.fullPath
        });
        this.moduleType = this.queryParams;
        this.getBasicCheckList();

        this.upData = {
            user_id: this.loginInfo.staff_code || '12534'
        };

    },
};
</script>

<style lang="less" scoped>
/deep/ .el-input {
    width: 100%;
}

@primary-color: #1299d8;


.operation-box {
    display: flex;
    justify-content: space-between;

    > div {
        display: flex;
        align-items: center;

        .upload-demo {
            margin: 0 10px;
        }
    }

    .danger-border {
        background: #f5f5f5;
        color: #FF3333;
        border-color: #FF3333;
    }

    .load-text {
        color: @primary-color;
        text-decoration: underline;
        font-size: 13px;
        font-weight: bold;
    }
}

.ui-main-tab {
    overflow: auto;
}

/deep/ .ui-upload_btn {
    .el-button {
        padding: 0;
    }
}
</style>



