
/**  --->>>>   |^ 供应商禁用审核^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取供应商禁用审核信息
export function getSupplierDisabledAuditInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 2,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/create_supplier' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/supplier/supplier/supplier_search', data);
    }
}

// 修改状态
export function changeSupplierState(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/supplier/supplier/handle_sup_status', data);
}


