/**  =========================> *|`| % 开发异常审核 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getDevAnomalyAuditInfo,
} from '../../../../api/dev_department/product_development/dev_anomaly_audit';

const config = {
    state: {
        devAnomalyAuditInfo: {}
    },
    mutations: {
        [types.SAVE_DEV_ANOMALY_AUDIT_INFO](state, saveInfo) {
            state.devAnomalyAuditInfo = saveInfo;
        }
    },
    actions: {
        // 获取开发异常审核信息
        GetDevAnomalyAuditInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getDevAnomalyAuditInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_DEV_ANOMALY_AUDIT_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




