<template>
    <div class="ui-content_area clearFix">
        <el-form ref="editData" :model="editData" :rules="editRule" size="mini">
            <div class="ui-content_area--left">
                <el-collapse v-model="editShowArea">
                    <div class="ui-content_area--item">
                        <el-collapse-item title="丨商品基础资料" name="1">
                            <ComTableInfo
                                    :isSetHeight="false"
                                    :tableKey="tableKey"
                                    :tableData="tableData"
                                    :tableProps="tableProps"
                                    :tableWidth="tableWidth">
                                <template slot="spu" slot-scope="scope">
                                    <div><span>{{scope.row.spu[0]}}</span></div>
                                </template>
                                <template slot="good_name" slot-scope="scope">
                                    <div>
                                        <p><span>{{scope.row.good_name[0]}}</span></p>
                                        <p><span>{{scope.row.good_name[1]}}</span></p>
                                    </div>
                                </template>
                                <template slot="category_path" slot-scope="scope">
                                    <div>
                                        <p v-for="item of scope.row.category_path"><span>{{item}}</span></p>
                                    </div>
                                </template>
                                <template slot="sample_type" slot-scope="scope">
                                    <div>
                                        <p v-for="item of scope.row.sample_type"><span>{{item}}</span></p>
                                    </div>
                                </template>
                                <template slot="supplier_datum" slot-scope="scope">
                                    <div>
                                        <p><span>{{scope.row.good_info[1]}}</span></p>
                                        <p><span>{{scope.row.good_info[2]}}</span></p>
                                        <el-popover
                                                placement="bottom"
                                                title="采样链接"
                                                width="200"
                                                trigger="click">
                                            <li v-for="item in scope.row.link_reference">
                                                <a :href="item" target="_blank">{{item}}</a>
                                            </li>
                                            <el-button slot="reference" type="text"><i class="icon-caiyanglianjie"></i></el-button>
                                        </el-popover>
                                        <el-popover
                                                placement="bottom"
                                                title="参考链接"
                                                width="200"
                                                trigger="click">
                                            <li v-for="item in scope.row.link_reference">
                                                <a :href="item" target="_blank">{{item}}</a>
                                            </li>
                                            <el-button slot="reference" type="text"><i class="icon-cankaolianjie"></i></el-button>
                                        </el-popover>
                                    </div>
                                </template>
                            </ComTableInfo>
                            <ul class="ui-content_area--form clearFix">
                                <li class="ui-layout">
                                    <el-form-item label="节日属性" label-width="65px">
                                        <el-radio-group :disabled="isPreview" v-model="editData.festivalAttr">
                                            <el-radio label="0">非</el-radio>
                                            <el-radio label="1">圣诞节</el-radio>
                                            <el-radio label="2">万圣节</el-radio>
                                        </el-radio-group>
                                    </el-form-item>
                                </li>
                                <li class="ui-layout">
                                    <el-form-item label="季节属性" label-width="65px">
                                        <el-radio-group :disabled="isPreview" v-model="editData.seasonAttr">
                                            <el-radio label="0">非</el-radio>
                                            <el-radio label="1">春季</el-radio>
                                            <el-radio label="2">夏季</el-radio>
                                            <el-radio label="3">秋季</el-radio>
                                            <el-radio label="4">冬季</el-radio>
                                            <el-radio label="5">雨季</el-radio>
                                            <el-radio label="6">旱季</el-radio>
                                        </el-radio-group>
                                    </el-form-item>
                                </li>
                                <li class="ui-layout ui-ope_area">
                                    <span>参考链接</span>
                                    <el-form-item>
                                        <div v-for="(item, index) in editData.referenceLink" :class="!isPreview && 'ui-ope_area--item'">
                                            <el-input :disabled="isPreview" v-model="editData.referenceLink[index]" placeholder="请输入"></el-input>
                                            <div class="ui-ope_area--btn" v-show="!isPreview">
                                                <span class="icon-shanchu" @click="handleDeleteInput('referenceLink', index, 2)"></span>
                                                <span class="icon-zengjia1" @click="handleAddInput('referenceLink', index + 1)"></span>
                                            </div>
                                        </div>
                                    </el-form-item>
                                </li>
                                <li class="ui-layout">
                                    <span>样品备注</span>
                                    <el-form-item>
                                        <el-input :disabled="isPreview" v-model="editData.sampleRemarks" type="textarea" resize="none" placeholder="请输入"></el-input>
                                    </el-form-item>
                                </li>
                                <li class="ui-layout">
                                    <span>图片要求</span>
                                    <el-form-item>
                                        <el-input :disabled="isPreview" v-model="editData.imgDemand" type="textarea" resize="none" placeholder="请输入"></el-input>
                                    </el-form-item>
                                </li>
                            </ul>
                        </el-collapse-item>
                    </div>
                    <div class="ui-content_area--item">
                        <el-collapse-item title="丨开发卖点图" name="2">
                            <div class="ui-com_edit__upload">
                                <ul class="clearFix">
                                    <li v-for="(item, index) in saveFileData.devSellingImg" :key="index">
                                        <div class="ui-com_edit__upload--item">
                                            <div class="ui-com_edit__upload--item__img">
                                                <img :src="item.url" alt="">
                                            </div>
                                            <div class="ui-com_edit__upload--item--mask">
                                                <span v-show="!isPreview" class="el-icon-delete" @click="handleDeleteFile(index, 'devSellingImg', 'devSellingImgName')"></span>
                                                <span class="el-icon-zoom-in" @click="handleLookImage(item.url)"></span>
                                            </div>
                                            <!--<span>{{item.name.replace(/.[a-zA-Z]+$/, '')}}</span>-->
                                        </div>
                                        <el-input :disabled="isPreview" size="small" placeholder="中文名"
                                                  v-model="saveFileData.devSellingImgName[index]"></el-input>
                                    </li>
                                    <li v-if="!isPreview">
                                        <div class="ui-com_edit__upload--last_btn">
                                            <el-upload
                                                    ref="devSellingImg"
                                                    action=""
                                                    multiple
                                                    :show-file-list="false"
                                                    :auto-upload="false"
                                                    :on-change="(file, fileList) => { handleSaveFileList('devSellingImg', fileList) }">
                                                <span class="icon-tupianshangchuan"></span>
                                            </el-upload>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-collapse-item>
                    </div>
                    <div class="ui-content_area--btn" v-if="!isPreview && !isComplete">
                        <el-button size="small" @click="handleSubmitInfo('1')">保存</el-button>
                        <el-button type="primary" size="small" @click="handleSubmitInfo('2')">提交</el-button>
                    </div>
                </el-collapse>
            </div>
            <div class="ui-content_area--right">
                <el-collapse v-model="editShowArea">
                    <div class="ui-content_area--item">
                        <el-collapse-item title="丨SPU文案资料" name="3">
                            <ul class="ui-content_area--form clearFix">
                                <li class="ui-layout ui-ope_area ui-ope_area--three">
                                    <span>关键词</span>
                                    <el-form-item>
                                        <div v-for="(item, index) in editData.keyword" :class="!isPreview && 'ui-ope_area--item'">
                                            <el-input :disabled="isPreview" v-model="editData.keyword[index][0]" placeholder="请输入"></el-input>
                                            <el-input :disabled="isPreview" v-model="editData.keyword[index][1]" placeholder="请输入"></el-input>
                                            <el-input :disabled="isPreview" v-model="editData.keyword[index][2]" placeholder="请输入"></el-input>
                                            <div class="ui-ope_area--btn" v-show="!isPreview">
                                                <span class="icon-shanchu" @click="handleDeleteInput('keyword', index)"></span>
                                                <span class="icon-zengjia1" @click="handleAddInput('keyword', index + 1)"></span>
                                            </div>
                                        </div>
                                    </el-form-item>
                                </li>
                                <li class="ui-layout ui-ope_area">
                                    <span>主要卖点</span>
                                    <el-form-item>
                                        <div v-for="(item, index) in editData.sellingPoint" :class="!isPreview && 'ui-ope_area--item'">
                                            <el-input :disabled="isPreview" v-model="editData.sellingPoint[index]" placeholder="请输入"></el-input>
                                            <div class="ui-ope_area--btn" v-show="!isPreview">
                                                <span class="icon-shanchu" @click="handleDeleteInput('sellingPoint', index)"></span>
                                                <span class="icon-zengjia1" @click="handleAddInput('sellingPoint', index + 1)"></span>
                                            </div>
                                        </div>
                                    </el-form-item>
                                </li>
                                <li class="ui-layout">
                                    <span>商品描述</span>
                                    <el-form-item>
                                        <el-input :disabled="isPreview"
                                                  v-model="editData.describe"
                                                  type="textarea"
                                                  placeholder="请输入"
                                                  resize="none"></el-input>
                                    </el-form-item>
                                </li>
                                <li class="ui-layout">
                                    <span>商品参数</span>
                                    <el-form-item>
                                        <el-input :disabled="isPreview"
                                                  v-model="editData.params"
                                                  type="textarea"
                                                  placeholder="请输入"
                                                  resize="none"></el-input>
                                    </el-form-item>
                                </li>
                                <li class="ui-layout">
                                    <span>注意事项</span>
                                    <el-form-item>
                                        <el-input :disabled="isPreview"
                                                  v-model="editData.takeCare"
                                                  type="textarea"
                                                  placeholder="请输入"
                                                  resize="none"></el-input>
                                    </el-form-item>
                                </li>
                                <li class="ui-layout ui-ope_area">
                                    <span>包装清单</span>
                                    <el-form-item>
                                        <div :class="!isPreview && 'ui-ope_area--item'">
                                            <el-input :disabled="isPreview" v-model="editData.packList" placeholder="请输入"></el-input>
                                            <div class="ui-ope_area--btn" v-show="!isPreview">
                                                <el-button type="text" @click="handleClickRelevance">关联</el-button>
                                            </div>
                                        </div>
                                        <div v-for="(item, index) in confirmSyncInfo">
                                            <el-input :disabled="isPreview" v-model="editData.otherSku[index][item]">
                                                <template slot="prepend">关联SKU {{item}}</template>
                                            </el-input>
                                        </div>
                                    </el-form-item>
                                </li>
                            </ul>
                        </el-collapse-item>
                    </div>
                    <div class="ui-content_area--item">
                        <el-collapse-item title="丨商品开发图片" name="4">
                            <div class="ui-com_edit__upload">
                                <ul class="clearFix">
                                    <li v-for="(item, index) in saveFileData.productDevImage" :key="index">
                                        <div class="ui-com_edit__upload--item">
                                            <div class="ui-com_edit__upload--item__img">
                                                <img :src="item.url" v-if="item.raw.type.indexOf('image') > -1">
                                                <img src="../../../../../assets/image/wenjian.png" v-else>
                                            </div>
                                            <div class="ui-com_edit__upload--item--mask">
                                                <span v-show="!isPreview" class="el-icon-delete" @click="handleDeleteFile(index, 'productDevImage')"></span>
                                                <span class="el-icon-zoom-in" @click="handleLookImage(item.url)"></span>
                                                <span class="el-icon-back" v-if="index > 0" @click="handleMoveFile('_last', index)"></span>
                                                <span class="el-icon-back el-icon-back--right" v-if="index < saveFileData.productDevImage.length - 1" @click="handleMoveFile('_next', index)"></span>
                                            </div>
                                            <!--<span>{{item.name.replace(/.[a-zA-Z]+$/, '')}}</span>-->
                                        </div>
                                    </li>
                                    <li v-if="!isPreview">
                                        <div class="ui-com_edit__upload--last_btn">
                                            <el-upload
                                                    :disabled="isPreview"
                                                    ref="productDevImage"
                                                    action=""
                                                    multiple
                                                    :show-file-list="false"
                                                    :auto-upload="false"
                                                    :on-change="(file, fileList) => { handleSaveFileList('productDevImage', fileList) }">
                                                <span class="icon-tupianshangchuan"></span>
                                            </el-upload>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-collapse-item>
                    </div>
                    <div class="ui-content_area--btn" v-if="!isPreview && !isComplete">
                        <el-button size="small" @click="handleSubmitInfo('1')">保存</el-button>
                        <el-button type="primary" size="small" @click="handleSubmitInfo('2')">提交</el-button>
                    </div>
                </el-collapse>
            </div>
        </el-form>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <el-dialog append-to-body :visible.sync="isSyncInfo" width="600px" top="10vh" title="选择关联的SKU">
            <div class="ui-edit_check">
                <el-checkbox-group v-model="syncInfoList">
                    <el-checkbox v-for="(item, index) in skuRelevanceList" :key="index" :label="item.value">{{item.value}} {{item.label}}</el-checkbox>
                </el-checkbox-group>
            </div>
            <div slot="footer">
                <el-button type="primary" size="small" @click="handleConfirmRelevance">确认</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {
        submitSPURepairInfo
    } from '@/api/dev_department/product_development/product_datum_create';
    import CONFIG from '@/assets/js/config';
    import { imageUrlChangeFile } from '@/services'
    export default {
        name: "SPUPage",
        props: ['row', 'type'],
        data() {
            return {
                testCheck: [],

                editShowArea: ['1', '2', '3', '4', '5', '6'],

                tableKey: ['商品SPU', '商品名称', '产品目录', '样品类型', '供应商资料'],
                tableProps: ['spu', 'good_name', 'category_path', 'sample_type', 'supplier_datum'],
                tableWidth: ['120', '200', '', '', '250'],

                editData: {
                    referenceLink: ['', ''],
                    keyword: [[]],
                    sellingPoint: ['', '', ''],
                    packList: '',
                    festivalAttr: '',
                    seasonAttr: '',
                    sampleRemarks: '',
                    imgDemand: '',
                    describe: '',
                    params: '',
                    takeCare: '',
                    otherSku: []
                },
                editRule: {},

                // 保存文件列表
                saveFileData: {
                    productDevImage: [],
                    devSellingImg: [],
                    devSellingImgName: []
                },

                saveUrlFile: {
                    devSellingImg: [],
                    productDevImage: []
                },

                // 预览图片
                lookImageUrl: '',
                isShowLookImage: false,

                // 同步
                isSyncInfo: false,
                confirmSyncInfo: [],
                syncInfoList: []
            }
        },
        computed: {
            isPreview() {
                return this.type === 'preview';
            },
            isComplete() {
                return this.$parent.isComplete
            },
            firstUrl() {
                return CONFIG.PRODUCTION_PATH
            },
            tableData() {
                if (JSON.stringify(this.row) === '{}') {
                    return []
                } else {
                    return [{
                        ...this.row
                    }]
                }
            },
            skuRelevanceList() {
                let list = [];
                try {
                    let skuList = this.row.sku.split(',');
                    this.row.sku_name.split(',').forEach((value, inx) => {
                        list.push({
                            label: value,
                            value: skuList[inx]
                        });
                        if (!this.editData.otherSku[0]) {
                            this.$set(this.editData.otherSku, inx, {
                                [skuList[inx]]: ''
                            });
                        }
                    });
                } catch (e) {
                    console.log(this.row);
                }
                return list
            }
        },
        created() {
            this.row.sku.split(',').forEach((value, inx) => {
                this.$set(this.editData.otherSku, inx, {
                    [value]: ''
                });
            })
            this.handleInitData();
        },
        methods: {

            // 保存文件列表
            handleSaveFileList(type, fileList) {
                this.saveFileData[type] = [...this.saveFileData[type], ...fileList];
                this.$refs[type].clearFiles();
            },

            // 预览图片
            handleLookImage(url) {
                this.isShowLookImage = true;
                this.lookImageUrl = url;
            },

            // 移动文件
            handleMoveFile(type, inx) {
                if (type === '_last') {
                    let lastFile = this.saveFileData.productDevImage[inx - 1];
                    this.$set(this.saveFileData.productDevImage, [inx - 1], this.saveFileData.productDevImage[inx]);
                    this.$set(this.saveFileData.productDevImage, [inx], lastFile);
                } else {
                    let nextFile = this.saveFileData.productDevImage[inx + 1];
                    this.$set(this.saveFileData.productDevImage, [inx + 1], this.saveFileData.productDevImage[inx]);
                    this.$set(this.saveFileData.productDevImage, [inx], nextFile);
                }
            },

            // 删除文件
            handleDeleteFile(inx, key, oKey) {
                this.saveFileData[key].splice(inx, 1);
                oKey && this.saveFileData[oKey].splice(inx, 1);
                this.saveUrlFile[key].splice(inx, 1);
            },

            // 添加input
            handleAddInput(key, index) {
                this.editData[key].splice(index, 0, key === 'keyword' ? [] : '');
            },

            // 删除input
            handleDeleteInput(key, index, length = 1) {
                if (this.editData[key].length === length) return;
                this.editData[key].splice(index, 1);
            },

            // 点击关联
            handleClickRelevance() {
                this.syncInfoList = this.confirmSyncInfo;
                this.isSyncInfo = true;
            },

            // 确认关联
            handleConfirmRelevance() {
                this.confirmSyncInfo = this.syncInfoList;
                this.isSyncInfo = false;
            },

            // 提交
            handleSubmitInfo(type) {

                let keywordArr = [];
                this.editData.keyword.forEach(value => {
                    keywordArr = [...keywordArr, ...value];
                });

                let otherSku = [];
                this.editData.otherSku.forEach(value => {
                    for (let key in value) {
                        if (!value[key]) continue;
                        otherSku.push({
                            sku: key,
                            packList: value[key]
                        })
                    }
                });

                let obj = {
                    spu: this.row.spu[0],
                    for_holiday: this.editData.festivalAttr,
                    feature_val: this.editData.seasonAttr,
                    link: this.editData.referenceLink,
                    buy_sample_note: this.editData.sampleRemarks,
                    image_remark: this.editData.imgDemand,
                    text_value_one: keywordArr,
                    text_value_two: this.editData.sellingPoint,
                    goods_desc: this.editData.describe,
                    goods_params: this.editData.params,
                    notice_remark: this.editData.takeCare,
                    pack_list: this.editData.packList,
                    otherSku: JSON.stringify(otherSku),
                    sku: this.row.sku,
                    status: type
                };

                if (this.row.reject_type === '1') {
                    obj.is_reject = '1';
                }

                if (this.confirmSyncInfo.length) {
                    obj.sku_list = this.confirmSyncInfo;
                }

                let formData = new FormData();
                for (let [key, value] of Object.entries(obj)) {
                    if (value) {
                        formData.append(key, value);
                    }
                }

                let [myfileIndex, myfileLink] = [0, []];
                this.saveFileData.productDevImage.forEach((value, index) => {
                    if (value.raw && !value.raw.isLink) {
                        formData.append(`myfile[${myfileIndex}]`, value.raw);
                        myfileIndex++;
                    } else {
                        myfileLink.push(value.url);
                    }
                });
                formData.append(`myfile_list`, JSON.stringify(myfileLink));

                let [fileIndex, fileLink] = [0, []];
                this.saveFileData.devSellingImg.forEach((value, index) => {
                    formData.append(`photo_name[${index}]`, this.saveFileData.devSellingImgName[index] || '');
                    if (value.raw) {
                        formData.append(`file[${fileIndex}]`, value.raw);
                        fileIndex++;
                    } else {
                        // 暂时不使用链接转文件功能
                        // formData.append(`file[${index}]`, this.saveUrlFile.devSellingImg[index]);
                        // formData.append(`photo_name[${index}]`, this.saveFileData.devSellingImgName[index] || '');
                        fileLink.push(value.url);
                    }
                });
                formData.append(`file_list`, JSON.stringify(fileLink));

                submitSPURepairInfo(formData).then(({ data }) => {
                    this.handleOperationCallBack(data, type);
                })
            },

            // 字段校验
            handleProofKey() {

            },

            // 初始化数据
            handleInitData() {
                let data = this.$parent.editPageData[this.row.spu[0]];
                if (data) {
                    this.editData.sampleRemarks = data.buy_sample_note;
                    this.editData.seasonAttr = data.feature_val;
                    this.editData.festivalAttr = data.for_holiday;
                    this.editData.describe = data.goods_desc;
                    this.editData.params = data.goods_params;
                    this.editData.imgDemand = data.image_remark;
                    this.editData.takeCare = data.notice_remark;
                    this.editData.packList = data.pack_list;

                    data.otherSku && JSON.parse(data.otherSku).forEach((value, index) => {
                        this.confirmSyncInfo.splice(index, 1, value.sku);
                        this.$set(this.editData.otherSku, index, {
                            [value.sku]: value.packList
                        });
                    });

                    Array.isArray(data.file) && data.file.forEach((value, index) => {
                        this.saveFileData.devSellingImgName[index] = data.photo_name[index];

                        this.saveFileData.devSellingImg.push({
                            url: value
                        });

                        // let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                        // imageUrlChangeFile(value, fileName[0].replace('.', ''), (file) => {
                        //     this.saveUrlFile.devSellingImg.splice(index, 1, file);
                        // });
                    });

                    Array.isArray(data.myfile) && data.myfile.forEach((value, index) => {
                        let fileName = /.[A-Za-z]+$/.exec(value);
                        let fileType = '';
                        if (fileName && CONFIG.RULE_CONFIG.image.indexOf(fileName[0].toLowerCase()) > -1) {
                            fileType = 'image';
                        } else {
                            fileType = 'file';
                        }
                        this.saveFileData.productDevImage.push({
                            url: value,
                            raw: {
                                type: fileType,
                                isLink: true
                            }
                        });
                        // let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                        // imageUrlChangeFile(value, fileName[0].replace('.', ''), (file) => {
                        //     this.saveUrlFile.productDevImage.splice(index, 1, file);
                        //     this.saveFileData.productDevImage.push({
                        //         url: value
                        //     });
                        // });
                    });

                    let text_value_one = data.text_value_one && data.text_value_one.split(',');
                    Array.isArray(text_value_one) && text_value_one.forEach((value, index) => {
                        let keyInx = (index + 1) / 3 - 1;
                        if (Array.isArray(this.editData.keyword[Math.ceil(keyInx)])) {
                            this.$set(this.editData.keyword[Math.ceil(keyInx)], index % 3, value);
                        } else {
                            this.$set(this.editData.keyword, Math.ceil(keyInx), []);
                            this.$set(this.editData.keyword[Math.ceil(keyInx)], index % 3, value);
                        }
                    });

                    const singleFn = (eKey, iKey) => {
                        if (!data[iKey]) return;
                        let arr = data[iKey].split(',');
                        Array.isArray(arr) && arr.forEach((value, index) => {
                            this.editData[eKey].splice(index, 1, value);
                        });
                    };

                    singleFn('referenceLink', 'link');
                    singleFn('sellingPoint', 'text_value_two');
                    // singleFn('packList', 'pack_list');
                }
            },

            // 操作回调
            handleOperationCallBack(data, type) {
                if (data.status) {
                    this.$message.success('操作成功');
                    // this.handleInitData();
                    if (type === '2') {
                        this.$emit('submit')
                    }
                } else {
                    this.$message.error(data.errorMess || '操作失败');
                }
            }
        }
    }
</script>

<style scoped lang="less">

    @border-color: #e6e6e6;
    @active-color: #5b8eff;

    @upload-height: 130px;

    /deep/.el-form-item__label {
        font-size: 12px;
        color: #333333;
    }
    /deep/.el-radio__label {
        font-size: 12px;
        padding-left: 5px;
    }

    .ui-content_area {
        height: 100%;
        padding: 45px 150px 0;
        position: absolute;
        top: 0;
        width: 100%;
        box-sizing: border-box;

        .el-form {
            height: 100%;
        }

        .ui-content_area--item {
            &:not(:first-child) {
                margin-top: 20px;
            }

            ul.ui-content_area--list > li {
                line-height: 40px;
                height: 40px;
                position: relative;
            }

            .ui-label {
                padding-left: 10px;
                height: 40px;
                line-height: 40px;
                width: 100px;
                font-size: 14px;
                color: #666666;
                position: absolute;
                top: 0;
                left: 0;
            }

            .ui-value {
                margin-left: 110px;
            }
        }

        .ui-content_area--left {
            float: left;
            width: 55%;
            padding-top: 10px;
            border-right: 1px solid @border-color;
            box-sizing: border-box;
            padding-right: 10px;
            height: 100%;
            overflow: auto;
        }
        .ui-content_area--right {
            float: right;
            width: 45%;
            padding-top: 10px;
            padding-left: 10px;
            box-sizing: border-box;
            max-height: 100%;
            overflow: auto;
        }

        .ui-content_area--form {
            /*margin-top: 10px;*/

            /deep/ .el-form-item {
                margin-bottom: 10px;
            }

            li {
                float: left;
                width: 40%;

                &:nth-child(2n) {
                    float: right;
                }

                &.ui-layout {
                    & > span {
                        font-size: 12px;
                        color: #333333;
                        padding-left: 4px;
                    }
                    width: 100%;
                }

                &.ui-ope_area {
                    position: relative;
                    .el-input {
                        width: 100%;
                        /*display: block;*/
                    }

                    .ui-ope_area--btn {
                        position: absolute;
                        right: 0;
                        top: 0;
                        width: 60px;

                        span {
                            cursor: pointer;
                        }
                    }

                    .ui-ope_area--item {
                        position: relative;
                        margin-bottom: 5px;
                        display: flex;
                        padding-right: 70px;
                        justify-content: space-between;

                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }

                &.ui-ope_area--three .ui-ope_area--item {
                    .el-input {
                        width: 100%;
                        margin-right: 5px;

                        &:nth-child(3) {
                            margin-right: 0;
                        }
                    }
                }
            }
        }

        .ui-content_area--btn {
            text-align: center;
            padding: 20px 0;
            .el-button {
                margin: 0 28px;
            }
        }
    }

    .ui-com_edit__upload {
        .el-input {
            margin-top: 5px;
            width: 100%;
        }
        .ui-com_edit__upload--title {
            font-size: 14px;
            font-weight: bold;
            text-align: left;
            display: block;
        }
        li {
            float: left;
            width: 130px;
            margin-bottom: 10px;
            /*height: 185px;*/
            margin-right: 20px;
            text-align: center;
            position: relative;

            .ui-com_edit__upload--item > span {
                color: #666;
                font-size: 14px;
                position: absolute;
                bottom: 10px;
                left: 0;
                display: block;
                width: 100%;
            }

            .ui-com_edit__upload--item__img {
                width: 100%;
                height: @upload-height;

                img {
                    width: 100%;
                    height: 100%;
                }
            }

            .ui-com_edit__upload--last_btn {
                border: 1px solid #EAEEED;
                width: 100%;
                height: @upload-height;

                /deep/ .el-upload {
                    display: block;
                    height: @upload-height;
                    line-height: @upload-height;
                    background: #F5F9F8;

                    span {
                        color: @border-color;
                    }

                    .icon-tupianshangchuan {
                        font-size: 40px;
                        margin: 0;
                    }
                }
            }

            .ui-com_edit__upload--item--mask {

                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: @upload-height;
                background: rgba(0, 0, 0, .3);
                text-align: center;
                line-height: @upload-height;

                & > div {
                    display: inline-block;
                }

                span {
                    margin: 0 5px;
                    cursor: pointer;
                    color: #cccccc;
                    font-size: 20px;

                    &:hover {
                        color: #FFFFFF;
                    }
                }
            }

            &:hover {
                .ui-com_edit__upload--item--mask {
                    display: block;
                }
            }
        }
    }

    .ui-edit_check {
        /deep/ .el-checkbox {
            margin: 0;
            width: 100%;
        }
    }

    .el-icon-back {
        top: 10px;
        left: 10px;
        position: absolute;
    }

    .el-icon-back--right {
        right: 10px;
        left: unset;
        transform: rotateZ(180deg);
    }
</style>


