<template>
    <TemplateDefault color="#fff">
        <div class="ui-product_classify">
            <div class="ui-product_classify--top">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item v-for="item in topToggle" :key="item.sid">
                        <span class="ui-product_classify--top__span" @click="handleTopToggle(item)">{{item.sname}}</span>
                    </el-breadcrumb-item>
                </el-breadcrumb>
            </div>
            <div class="ui-product_classify--content">
                <ul class="clearFix">
                    <li v-for="(list, index) in treeData" :key="index">
                        <div class="ui-product_classify--item">
                            <ul v-if="!list[treeProps.layout]">
                                <li class="ui-product_classify--item--title">
                                    <span>{{!index ? '产品分类目录' : list[treeProps.label]}}</span>
                                    <div class="ui-product_classify--item--ope">
                                        <span class="icon-chaxun" @click="handleTopOperation('query', index)"></span>
                                        <span class="icon-zengjia1" @click="handleTopOperation('add', index)"></span>
                                    </div>
                                </li>
                                <li class="ui-product_classify--item__input" v-if="treeIsOpe[index]">
                                    <el-input clearable :ref="`input${index}`" v-model="treeIsOpeVal[index]" @keyup.enter.native="handleOperateItem(index, treeIsOpe[index])" @input="handleSearchItem(index, treeIsOpe[index])">
                                        <i v-if="treeIsOpe[index] === 'query'" slot="prefix" class="el-input__icon el-icon-search"></i>
                                        <i v-show="treeIsOpe[index] === 'add'" slot="suffix" class="el-input__icon el-icon-back"></i>
                                    </el-input>
                                    <span v-show="treeIsOpe[index] === 'add'" class="ui-product_classify--tip">回车保存</span>
                                </li>
                                <li
                                        v-for="(item, itemIndex) in list[treeProps.children]"
                                        :class="autoValidateActive(item, index) ? 'ui-active' : ''"
                                        :key="itemIndex"
                                        @click="handleClickItem(item, index)">
                                    <span v-show="operateId !== item[treeProps.id]">{{item[treeProps.label]}}</span>
                                    <el-input :ref="`input${index}${itemIndex}`" v-show="operateId === item[treeProps.id]" v-model="editValue" @blur="operateId = ''" @keyup.enter.native="handleSubmit(item, index, itemIndex)">
                                        <i slot="suffix" class="el-input__icon el-icon-back"></i>
                                    </el-input>
                                    <span class="ui-product_classify--tip" v-show="operateId === item[treeProps.id]">回车保存</span>
                                    <div class="ui-product_classify--item--ope" v-show="operateId !== item[treeProps.id]">
                                        <!--<span class="icon-lianjie"></span>-->
                                        <span class="icon-bianji" @click.stop="handleEditItem(item, index, itemIndex)"></span>
                                        <span class="icon-zengjia1" v-if="index !== 3" @click.stop="handleListAddItem(item, index, itemIndex)"></span>
                                        <!--<span class="icon-shanchu" @click.stop="handleDeleteItem(item, index, itemIndex)"></span>-->
                                    </div>
                                </li>
                            </ul>
                            <ul v-else v-for="(item, itemIndex) in list[treeProps.children]">
                                <li class="ui-product_classify--item--title">
                                    <span>{{item[treeProps.label]}}</span>
                                    <div class="ui-product_classify--item--ope">
                                        <span class="icon-chaxun" @click="handleTopOperation('query', index, itemIndex)"></span>
                                        <span class="icon-zengjia1" @click="handleTopOperation('add', index, itemIndex)"></span>
                                    </div>
                                </li>
                                <li class="ui-product_classify--item__input" v-if="treeIsOpe[`${index}-${itemIndex}`]">
                                    <el-input clearable
                                              :ref="`input${index}-${itemIndex}`"
                                              v-model="treeIsOpeVal[`${index}-${itemIndex}`]"
                                              @keyup.enter.native="handleOperateItem(index, treeIsOpe[`${index}-${itemIndex}`], itemIndex)"
                                              @change="handleSearchItem(index, treeIsOpe[`${index}-${itemIndex}`], itemIndex)">
                                        <i v-if="treeIsOpe[`${index}-${itemIndex}`] === 'query'" slot="prefix" class="el-input__icon el-icon-search"></i>
                                        <i v-show="treeIsOpe[`${index}-${itemIndex}`] === 'add'" slot="suffix" class="el-input__icon el-icon-back"></i>
                                    </el-input>
                                    <span v-show="treeIsOpe[`${index}-${itemIndex}`] === 'add'" class="ui-product_classify--tip">回车保存</span>
                                </li>
                                <template v-for="(fourItem, fourIndex) in item[treeProps.children]">
                                    <li
                                            :class="autoValidateActive(fourItem, index) ? 'ui-active' : ''"
                                            :key="fourIndex"
                                            @click="handleClickItem(fourItem, index)">
                                        <span v-show="operateId !== fourItem[treeProps.id]">{{fourItem[treeProps.label]}}</span>
                                        <el-input :ref="`fourInput${index}${fourIndex}${fourItem[treeProps.id]}`" v-show="operateId === fourItem[treeProps.id]" v-model="editValue" @blur="operateId = ''" @keyup.enter.native="handleSubmit(fourItem, index, fourIndex)">
                                            <i slot="suffix" class="el-input__icon el-icon-back"></i>
                                        </el-input>
                                        <span class="ui-product_classify--tip" v-show="operateId === fourItem[treeProps.id]">回车保存</span>
                                        <div class="ui-product_classify--item--ope" v-show="operateId !== fourItem[treeProps.id]">
                                            <span class="icon-lianjie"></span>
                                            <span class="icon-bianji" @click.stop="handleEditItem(fourItem, index, fourIndex, fourItem[treeProps.id])"></span>
                                            <span class="icon-shanchu" @click.stop="handleDeleteItem(fourItem, index, fourIndex)"></span>
                                        </div>
                                    </li>
                                </template>
                            </ul>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </TemplateDefault>
</template>

<script>
    import { mapGetters } from 'vuex';
    import {
        getProductCatalogueManageInfo,
        addProductCatalogueInfo,
        editProductCatalogueInfo,
        deleteProductCatalogueInfo
    } from '../../../../api/dev_department/system_manage/product_catalogue_manage'
    export default {
        name: "ProductCatalogueManage",
        data() {
            return {
                topToggle: [
                    {
                        sname: '全部分类',
                        sid: '0'
                    }
                ],
                topToggleObj: {
                    time: '',
                    value: []
                },
                treeIsOpe: [],
                treeData: [],
                treeFilterData: [],
                treeIsOpeVal: [],
                treeProps: {
                    label: 'sname',
                    id: 'sid',
                    children: 'child',
                    layout: 'layout'
                },
                editValue: '',
                operateId: ''
            }
        },
        computed: {
            ...mapGetters(['loginInfo'])
        },
        methods: {
            getProductCatalogueManageInfo,

            // 面包屑切换
            handleTopToggle(item) {
                this.handleClickItem(item, item.index);
            },

            /**
             * 激活输入框
             * index: 横向索引
             * colInx: 纵向索引
             * */
            AutoFocus(index, colInx) {
                let inputName = '';
                if (colInx !== undefined) {
                    inputName = `input${index}-${colInx}`;
                } else {
                    inputName = `input${index}`;
                }

                this.$nextTick().then(() => {
                    this.$refs[inputName] && this.$refs[inputName][0].focus && this.$refs[inputName][0].focus();
                })
            },

            // 新增
            handleTopOperation(type, index, itemIndex) {
                if (type === 'query') {
                    this.treeFilterData = JSON.parse(JSON.stringify(this.treeData));
                }

                if (itemIndex !== undefined) {
                    index = `${index}-${itemIndex}`;
                }

                if (type !== this.treeIsOpe[index]) {
                    this.treeIsOpeVal[index] = '';
                    this.$set(this.treeIsOpe, index, type);
                }

                this.AutoFocus(index, itemIndex);
            },

            // 点击列表新增
            handleListAddItem(item, index, inx) {

                this.$set(this.treeIsOpe, index + 1, '');
                this.$set(this.treeIsOpeVal, index + 1, '');

                // 清空
                this.treeData.splice(index + 1, this.treeData.length - 1);

                let [{ id, children, label }] = [this.treeProps];

                let obj = {
                    sid: item[id]
                };

                this.getProductCatalogueManageInfo(obj).then(({ data }) => {

                    let treeData = {};
                    if (!data.data || !data.data.length) {
                        treeData = {
                            [id]: item[id],
                            [label]: item[label],
                            [children]: []
                        }
                    } else {
                        treeData = data.data[0][children] && data.data[0][children].length ? data.data[0] : {
                            ...data.data[0],
                            [children]: []
                        };
                    }

                    this.$set(this.treeIsOpe, index + 1, 'add');
                    this.$set(this.treeData, index + 1, treeData);

                    this.AutoFocus(index + 1);
                });
            },

            // 新增子级
            handleOperateItem(index, type, fourIndex) {
                if (type === 'query') return;

                let addVal = '';

                if (fourIndex === undefined) {
                    if (!this.treeIsOpeVal[index]) {
                        this.$message.warning('请输入内容！');
                        return;
                    }
                    addVal = this.treeIsOpeVal[index];
                } else {
                    if (!this.treeIsOpeVal[`${index}-${fourIndex}`]) {
                        this.$message.warning('请输入内容！');
                        return;
                    }
                    addVal = this.treeIsOpeVal[`${index}-${fourIndex}`];
                }

                let [{ id, label, children }] = [this.treeProps];

                let obj = {
                    parent_id: fourIndex !== undefined ? (this.treeData[index][children][fourIndex][id] || '0') : (this.treeData[index][id] || '0'),
                    category_name: addVal,
                    category_level: index + 1,
                    create_user: this.loginInfo.uid || 'ynmautest'
                };

                addProductCatalogueInfo(obj).then(({ data }) => {
                    if (fourIndex !== undefined) {
                        if (this.treeData[index][children][fourIndex]) {
                            this.treeData[index][children][fourIndex][children].push({
                                [label]: addVal,
                                [id]: data.data
                            });
                        } else {
                            this.treeData[index][children][fourIndex] = {
                                [children]: {
                                    [label]: addVal,
                                    [id]: data.data
                                }
                            };
                        }
                    } else {
                        this.treeData[index][children].push({
                            [label]: addVal,
                            [id]: data.data
                        });
                    }

                    this.handleOperationCallBack(index, fourIndex);
                });
            },
            
            // 编辑Item
            handleEditItem(item, index, inx, elId) {
                let [{ id, label }] = [this.treeProps];
                this.operateId = item[id];
                this.editValue = item[label];
                this.$nextTick().then(() => {
                    if (elId) {
                        this.$refs[`fourInput${index}${inx}${elId}`][0].focus();
                    } else {
                        this.$refs[`input${index}${inx}`][0].focus();
                    }
                });
            },

            // 保存编辑
            handleSubmit(item, index, inx) {
                let [{ id, label, children }] = [this.treeProps];
                let obj = {
                    category_name: this.editValue,
                    category_level: index + 1,
                    sid: item[id]
                };
                editProductCatalogueInfo(obj).then(({ data }) => {
                    if (data.status) {
                        this.treeData[index][children][inx][label] = this.editValue;
                        this.operateId = '';
                        this.editValue = '';
                        this.$message.success('操作成功');
                    } else {
                        this.$message.error(data.errorMess || '操作失败')
                    }
                });
            },

            // 删除子级
            handleDeleteItem(item, index, inx) {
                let [{ children, id }] = [this.treeProps];
                let vm = this;
                let delFn = new Promise((resolve, reject) => {
                    vm.$confirm('确认删除？', '提示', {
                        type: 'warning'
                    }).then(() => {
                        resolve(true);
                    }).catch(() => {
                        reject(false);
                    })
                });

                delFn.then(res => {
                    let obj = {
                        category_level: index + 1,
                        sid: item[id]
                    };
                    deleteProductCatalogueInfo(obj).then(({ data }) => {
                        if (data.status) {
                            this.treeData[index][children].splice(inx, 1);
                            this.$message.success('操作成功');
                        } else {
                            this.$message.error(data.errorMess || '操作失败');
                        }
                    });
                });
            },

            // 搜索
            handleSearchItem(index, type, itemIndex) {
                if (type === 'add') return;
                let [{ label, children }] = [this.treeProps];
                let filterData = [...this.treeFilterData[index][children]];

                if (itemIndex !== undefined) {
                    let indexKey = `${index}-${itemIndex}`;
                    if (!this.treeIsOpeVal[`${index}-${itemIndex}`]) {
                        this.treeData[index][children][itemIndex][children] = [...filterData];
                        return;
                    }
                    this.treeData[index][children][itemIndex][children] = filterData.filter(item => item[label].indexOf(this.treeIsOpeVal[indexKey]) > -1);
                } else {
                    if (!this.treeIsOpeVal[index]) {
                        this.treeData[index][children] = [...filterData];
                        return;
                    }
                    this.treeData[index][children] = filterData.filter(item => item[label].indexOf(this.treeIsOpeVal[index]) > -1);
                    this.treeData.splice(index + 1, this.treeData.length - (index + 1));
                }
            },

            // 点击li
            handleClickItem(item, index) {

                let [{ id, children, layout }] = [this.treeProps];

                // 最多4级
                if (index > 2) return;

                // 点击第一级清空3、4级
                if (!index) {
                    this.treeData.splice(1, this.treeData.length - 1);
                }


                // 面包屑
                if (item[id] === '0') {
                    this.topToggle.splice(1, this.topToggle.length - 1);
                } else {
                    this.topToggle.splice(index + 1, this.topToggle.length - 1);
                    this.$set(this.topToggle, index + 1, {
                        ...item,
                        index
                    });
                }

                let obj = {
                    sid: item[id]
                };

                this.$set(this.treeIsOpe, index + 1, '');
                this.$set(this.treeIsOpeVal, index + 1, '');

                this.getProductCatalogueManageInfo(obj).then(({ data }) => {

                    if (!data.data || !data.data.length) {
                        this.$message.warning('无数据！');
                        return;
                    }

                    let treeData = data.data[0][children] && data.data[0][children].length ? data.data[0] : {
                        ...data.data[0],
                        [children]: []
                    };
                    this.$set(this.treeData, index + 1, treeData);

                    // 点击二级带出3、4级
                    if (index === 1){

                        // 组装4级内容
                        let fourLvData = [];
                        this.$set(this.treeData, index + 2, {
                            ...treeData,
                            [layout]: true
                        });
                    }
                });
            },

            // 判断是否激活
            autoValidateActive(item, index) {
                if (index === 3 || !this.treeData[index + 1]) return false;
                let [{ id }] = [this.treeProps];
                return item[id] === this.treeData[index + 1][id];
            },

            // 操作成功回调
            handleOperationCallBack(index, inx) {
                if (inx === undefined) {
                    this.treeIsOpeVal[index] = '';
                } else {
                    delete this.treeIsOpeVal[`${index}-${inx}`];
                }

                this.$message.success('操作成功');
            }
        },
        created() {
            this.getProductCatalogueManageInfo().then(({ data }) => {
                let [{ children, label }] = [this.treeProps];
                const initData = [
                    {
                        [label]: '全部分类',
                        [children]: [...data.data]
                    }
                ];
                this.treeData = [...initData];
            });
        }
    }
</script>

<style scoped lang="less">
    @border-color: #cccccc;

    .ui-product_classify {
        padding: 20px;
    }

    .ui-product_classify--top {

        margin-bottom: 10px;

        .ui-product_classify--top__span {
            cursor: pointer;
        }
    }
    
    .ui-product_classify--content {

        text-align: left;
        
        & > ul > li {
            float: left;
            width: 300px;
            position: relative;

            &:not(:first-child) {
                .ui-product_classify--item {
                    border-left: 0 none;
                }
            }
        }

        .ui-product_classify--tip {
            color: #cccccc;
            margin-left: 10px;
        }

        .ui-product_classify--item {

            & > ul > li {
                padding-left: 10px;
                height: 40px;
                /*box-sizing: border-box;*/
                border: 1px solid @border-color;
                border-bottom: 0 none;
                line-height: 40px;
                font-size: 14px;
                cursor: pointer;

                &:last-child {
                    border-bottom: 1px solid @border-color;
                }
                
                .ui-product_classify--item--ope {
                    display: none;
                }
                
                &:hover {
                    .ui-product_classify--item--ope {
                        display: block;
                    }
                }
            }

            & > ul > li.ui-active {
                background: #cee6ff;
                color: #3c4d9e;
            }

            .ui-product_classify--item--title {
                background: #f8f8f8;

                span {
                    font-size: 15px;
                    font-weight: bold;
                }
                
                .ui-product_classify--item--ope {
                    display: block;
                }
            }

            .ui-product_classify--item--ope {
                float: right;

                span {
                    cursor: pointer;
                }
            }

            /deep/ .el-input__inner {
                border: 0 none;
                border-radius: 0;
                height: 39px;
                /*padding-left: 0;*/
                float: left;
            }

            /deep/ .el-input {
                float: left;
                width: 200px;
            }
        }
    }

    .fn-border_span (@n, @i: 1) when (@i <= @n) {
        @px: -1px;
        .ui-product_classify--content > ul > li {
            &:nth-child(@{i}n) {
                left: @i * @px;
            }
        }
        .fn-border_span(@n, (@i + 1));
    }

    .fn-border_span(4);
</style>


