/**  =========================> *|`| % 样品跟单管理 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getSampleTrackManageInfo,
} from '../../../../api/sample_track_manage/sample_track_module/sample_track_manage';

const config = {
    state: {
        sampleTrackManageInfo: {}
    },
    mutations: {
        [types.SAVE_SAMPLE_TRACK_MANAGE_INFO](state, saveInfo) {
            state.sampleTrackManageInfo = saveInfo;
        }
    },
    actions: {
        // 获取样品跟单管理信息
        GetSampleTrackManageInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSampleTrackManageInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SAMPLE_TRACK_MANAGE_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




