/**  =========================> *|`| % 品控小组管理 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getQualityControlGroupInfo,
} from '../../../../api/quality_control_department/sample_quality_control/quality_control_group'

const config = {
    state: {
        qualityControlGroupInfo: {},
    },
    mutations: {
        [types.SAVE_QUALITY_CONTROL_GROUP_INFO] (state, saveInfo) {
            state.qualityControlGroupInfo = saveInfo
        }
    },
    actions: {
        // 获取品控小组管理信息
        GetQualityControlGroupInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getQualityControlGroupInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_QUALITY_CONTROL_GROUP_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




