/**  =========================> *|`| % 取消开发列表 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getCancelDevListInfo,
} from '../../../../api/dev_department/product_development/cancel_dev_list';

const config = {
    state: {
        cancelDevListInfo: {}
    },
    mutations: {
        [types.SAVE_CANCEL_DEV_LIST_INFO](state, saveInfo) {
            state.cancelDevListInfo = saveInfo;
        }
    },
    actions: {
        // 获取取消开发列表信息
        GetCancelDevListInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getCancelDevListInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_CANCEL_DEV_LIST_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




