/**  =========================> *|`| % 供应商禁用审核 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getSupplierDisabledAuditInfo,
} from '../../../../api/dev_department/supplier_development/supplier_disabled_audit';

const config = {
    state: {
        supplierDisAuditInfo: {}
    },
    mutations: {
        [types.SAVE_SUPPLIER_DIS_AUDIT_INFO](state, saveInfo) {
            state.supplierDisAuditInfo = saveInfo;
        }
    },
    actions: {
        // 获取供应商禁用审核信息
        GetSupplierDisabledAuditInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSupplierDisabledAuditInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SUPPLIER_DIS_AUDIT_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




