                                    :value="value"
                                ></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核状态">
                            <el-select size="small" v-model="queryData.auditState">
                                <el-option
                                    v-for="(value, label) in optionList.statusList"
                                    :key="value"
                                    :label="label"
                                    :value="value"
                                ></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作用时">
                            <el-input size="small" v-model="queryData.operateUseTime"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员">
                            <el-input size="small" v-model="queryData.operateUser"></el-input>
                        </SearchRequirement>
                    </li>
                    <transition
                        name="slide-fade"
                        @after-enter="animation()"
                        @after-leave="animation()"
                        @before-enter="animation(1)"
                    >
                        <div v-show="filterShow">
                            <li>
                                <SearchRequirement
                                    label="操作时间"
                                    template="daterange"
                                    v-model="queryData.operateTime"
                                ></SearchRequirement>
                            </li>
                        </div>
                    </transition>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button
                        size="small"
                        type="primary"
                        @click="handleQueryInfo({limit: pageData.currentList})"
                        icon="icon-chaxun"
                    >查询
                    </el-button>
                    <el-button size="small" type="text" @click="handleClickFilter" v-show="!filterShow">
                        高级筛选
                        <i class="el-icon-caret-bottom"></i>
                    </el-button>
                    <el-button size="small" type="text" @click="handleClickFilter" v-show="filterShow">
                        基本筛选
                        <i class="el-icon-caret-top"></i>
                    </el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
              <span class="ui-top_i" @click="showSearch = !showSearch">
                <ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon>
              </span>
        </div>
        <ComTableInfo
            isCheck
            ref="myTable"
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">

            <template slot="spu" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.spu[0]}}</p>
                    <p>{{scope.row.spu[1]}}</p>
                    <div class="ui-table--supplier_code--flag">
                        <span>试卖</span>
                    </div>
                    <div class="ui-table--icon_btn">
                        <i
                            :class="`icon-bianji${scope.row.status_code === '1' || scope.row.status_code === '4' ? '' : ' ui-disable'}`"
                            @click.stop="handleClickEdit(scope.row)"
                        ></i>
                    </div>
                </div>
            </template>
            <template slot="img" slot-scope="scope">
                <div class="ui-table__img">
                    <img :src="scope.row.img" alt @click.stop="handlePreviewImage(scope.row.img)">
                </div>
            </template>
            <template slot="good_name" slot-scope="scope">
                <div>
                    <p>
                        <span>{{scope.row.good_name[0]}}</span>
                    </p>
                    <p>
                        <span>{{scope.row.good_name[1]}}</span>
                    </p>
                </div>
            </template>
            <template slot="category_path" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.category_path" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
            </template>
            <template slot="sample_type" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.sample_type" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
            </template>
            <template slot="good_info" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.good_info" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
            </template>
            <template slot="product_status" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.product_status" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
            </template>
            <template slot="opera_log" slot-scope="scope">
                <div v-for="(item, index) in scope.row.opera_log" v-if="scope.row.opera_log.length<3" :key="index">
                    {{item}}
                </div>
                <el-popover v-if="scope.row.opera_log.length>3"
                            style="cursor: pointer;"
                            placement="left"
                            trigger="hover">
                    <div v-for="(item, index) in scope.row.opera_log"
                         :key="index">{{item}}
                    </div>
                    <div slot="reference">
                        <div v-for="(item, index) in scope.row.opera_log" v-if="index<3" :key="index">{{item}}
                        </div>
                        <div v-if="scope.row.opera_log.length>3">...</div>
                    </div>
                </el-popover>
            </template>
            <template slot="opera_time" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.opera_time" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal"
            ></el-pagination>
        </div>

        <ComDialogRemarks
            :visible.sync="isShowAssign"
            title="指派任务"
            type="select"
            formLabel="指派任务"
            :option="optionList.workerList"
            @confirm="handleConfirmAssign"
        ></ComDialogRemarks>
        <ComDialogRemarks
            :visible.sync="isShowReject"
            title="驳回"
            :showRemarks="true"
            @confirm="handleConfirmReject"
        ></ComDialogRemarks>

        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <CopyEditPage :show.sync="isShowEdit" :spu="editSpu"></CopyEditPage>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import CopyEditPage from './CopyEditPage';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage
} from '@/services/index';
import { mapActions, mapGetters } from 'vuex';
import { getCityInfo } from '@/api/login';
import {
    saveEditInfo,
    // 指派人员
    assignSpu,
    // 重新指派人员
    reAssignSpu
} from '@/api/copy_department/copy_edit_module/copy_edit_list';

export default {
    data () {
        return {
            // 指派类别
            assignType: 1, // 1 指派， 2 重新指派；
            //高级筛选
            filterShow: false,

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                moduleType: '1', //1 待分配  2 待编辑  3 已编辑
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: []
            },

            optionList: {
                productList: [],
                functionInfoList: {},
                statusList: {},
                moduleTypeList: [
                    {
                        label: '待分配',
                        value: '1'
                    },
                    {
                        label: '待编辑',
                        value: '2'
                    }
                ],
                workerList: {
                    '1': '张三',
                    '2': '李四'
                }
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0
            },

            // 编辑
            isEditShow: false,
            formLabelWidth: '110px',
            saveCheckId: '',

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 上传列表
            saveFileList: [],

            //文案小组和商品分类
            classSearch: [
                {
                    title: '全部',
                    number: 400
                },
                {
                    title: '手机周边',
                    number: 35
                },
                {
                    title: '全部',
                    number: 350
                },
                {
                    title: '服饰珠宝',
                    number: 50000
                },
                {
                    title: '服饰珠宝',
                    number: 5
                },
                {
                    title: '服饰珠宝',
                    number: 5012121
                },
                {
                    title: '服饰珠宝',
                    number: 50121212
                },
                {
                    title: '服饰珠宝',
                    number: 5012121
                },
                {
                    title: '服饰珠宝',
                    number: 501212
                },
                {
                    title: '服饰珠宝',
                    number: 501212
                },
                {
                    title: '服饰珠宝',
                    number: 501213213
                }
            ],

            //指派任务弹窗
            isShowAssign: false,

            //驳回弹窗
            isShowReject: false,

            isShowEdit: false,
            editSpu: '',
            languageList: {}
        };
    },
    components: {
        CopyEditPage
    },
    computed: {
        ...mapGetters(['queryParamsInfo', 'copyEditRecordInfo'])
    },
    methods: {
        ...mapActions(['GetCopyEditRecordInfo']),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,

        handleClickTest () {
            this.isShowEdit = true;
        },
        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {
            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['copy_edit_record']);
            }

            let obj = {
                spu: this.queryData.productSPU, //SPU
                spu_name: this.queryData.productName, //SPU名称
                supplier_code: this.queryData.supplierCode, //供应商代码
                supplier_name: this.queryData.supplierName, //供应商名称
                category_id: this.queryData.productList, //分类
                function: this.queryData.functionInfo, //职能
                audit_status: this.queryData.auditState, //审核状态
                operator: this.queryData.workUser, //操作人员
                operation_use_time: '', //操作用时
                operation_time_start: this.queryData.operateTime[0], //操作开始
                operation_time_end: this.queryData.operateTime[1], //操作结束
                query_type: 3 //1 待分配  2 待编辑  3 已编辑
            };

            this.GetCopyEditRecordInfo(obj)
                .then(({data}) => {
                    console.log(data, 99);

                    this.handleQueryInfoCallBack(data);
                })
                .catch(err => {
                    console.log(err);
                });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                moduleType: this.queryData.moduleType,
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: []
            };
        },

        //高级筛选
        handleClickFilter () {
            this.filterShow = !this.filterShow;
        },

        animation (isHide) {
            if (isHide) {
                console.log(document.querySelector('body'));
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo(
                {
                    limit: val,
                    offset: 1
                },
                true
            );
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo(
                {
                    limit: this.pageData.currentList,
                    offset: val
                },
                true
            );
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.moduleType = val;
            this.handleQueryInfo();
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo(
                {
                    isExcel: 1
                },
                true
            );
        },

        // 保存上传文件
        handleSaveFileList (file, fileList) {
            this.saveFileList = [...fileList];
        },

        // 选择发货地址
        handleSelectDeliver (val) {
            let obj = {
                province_code: val[0]
            };
            for (let item of this.optionList.deliverAddressList) {
                if (item.value === val[0] && item.children.length) return;
            }
            getCityInfo(obj).then(({data}) => {
                this.optionList.deliverAddressList.map(value => {
                    if (value.value === val[0]) {
                        value.children = [...data.data];
                    }
                });
            });
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = [
                'http://pic10.nipic.com/20101016/2531170_233023137000_2.jpg'
            ];
        },

        // 批量创建
        handleSupplierBatchCreate () {
            goToNewPage(
                this,
                {
                    pageMain: '/supplier_create_list',
                    pageUrl: '/batch_create_supplier',
                    pageName: '批量创建'
                },
                '_new'
            );
        },

        // 点击编辑
        handleClickEdit (row) {
            this.isShowEdit = true;
            this.editSpu = row.spu[0];
        },

        // 关闭编辑
        handleCloseEdit () {
            for (let key in this.editData) {
                if (Array.isArray(this.editData[key])) {
                    this.editData[key] = [];
                } else {
                    this.editData[key] = '';
                }
            }
            this.$refs.editData.resetFields();
        },

        // 保存编辑信息
        handleSaveEditInfo () {
            let obj = {
                supplier_id: this.saveCheckId,
                credit_code: this.editData.comCode,
                supplier_code: this.editData.supplierName,
                supplier_name: this.editData.supplierName,
                settle_method: this.editData.settlementType,
                tax_point: this.editData.point,
                type: this.editData.supplierType,
                devlop_user: this.editData.devUser,
                status: this.editData.state,
                // modify_time: this.editData.aaaa,
                web_link: this.editData.supplierLink,
                contacts: this.editData.contacts,
                mobile: this.editData.contactsPhone,
                phone: this.editData.phoneNumber,
                wechat: this.editData.wxNumber,
                qq: this.editData.qqNumber,
                email: this.editData.email,
                shipping_province: this.editData.pAndCity[0],
                shipping_city: this.editData.pAndCity[1],
                shipping_address: this.editData.area,
                pay_method: this.editData.paymentType,
                shop_name: this.editData.shopName,
                receive_name: this.editData.gatheringName,
                receive_account: this.editData.gatheringAccount,
                receive_mobile: this.editData.gatheringPhone,
                receive_id_number: this.editData.gatheringId,
                receive_bank: this.editData.bankName
                // bl_url: this.editData.aaaa,
                // tca_url: this.editData.aaaa,
                // gtc_url: this.editData.aaaa,
                // b_r_rul: this.editData.aaaa,
                // reg_address: this.editData.aaaa,
                // reg_time: this.editData.aaaa,
            };

            saveEditInfo(obj).then(({data}) => {
                console.log(data);
            });
        },
        // 格式化用戶列表數據
        formatMemberUser (originSource) {
            let targetSource = {};
            for (let key in originSource) {
                targetSource = {
                    ...targetSource,
                    [key]: originSource[key]['user_name']
                };
            }
            return targetSource;
        },
        //查询回调
        handleQueryInfoCallBack (data) {
            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'copy_edit_record',
                value: JSON.stringify(this.queryData)
            });

            if ('select_list' in data) {
                // 渲染下拉
                this.optionList.productList = [...data.select_list.category_list];
                this.optionList.functionInfoList = data.select_list.function;
                this.optionList.statusList = data.select_list.status;
                this.optionList.workerList = this.formatMemberUser(data.member_user);

                this.languageList = data.select_list.language_list;

                //数据渲染
                this.tableData = data.data.value;
                this.tableData.map((item) => {
                    return item.opera_log.reverse();
                });
                this.tableData.map((item) => {
                    return item.opera_log.reverse();
                });
                this.tableKey = data.data.key;
                // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
                this.tableProps = [
                    'index',
                    'spu',
                    'photo_path',
                    'good_name',
                    'category_path',
                    'sample_type',
                    'good_info',
                    'product_status',
                    'opera_log',
                    'opera_time',
                    'remarks'
                ];
                this.tableWidth = [
                    '60',
                    '150',
                    '120',
                    'good_name',
                    'category_path',
                    'sample_type',
                    'good_info',
                    '100',
                    '200',
                    '100',
                    'remarks'
                ];

                //渲染分页
                this.pageData.currentList = data.page.limit * 1;
                this.pageData.currentPage = data.page.offset * 1;
                this.pageData.currentTotal = data.page.total * 1;
            }
        },

        //是否勾选
        checkedStatus (obj) {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this[obj] = true;
        },

        //指派任务
        handleClickAssign () {
            this.checkedStatus('isShowAssign');
            this.assignType = 1;
        },
        // 重新指派任务
        handleClickReAssign () {
            this.checkedStatus('isShowAssign');
            this.assignType = 2;
        },
        //勾选的id
        selectId () {
            return this.saveCheckInfo.map(item => item.spu[0]);
        },

        //确认指派
        handleConfirmAssign (val) {
            const {check: assigned_user} = val;
            const spu_list = this.selectId();

            if (!assigned_user) {
                return this.$message.warning('请选择指派人员!');
            }
            if (this.assignType === 1) {
                // 指派人员
                assignSpu({assigned_user, spu_list}).then(({data}) => {
                    if (data.status !== 1) {
                        return this.$message.error(data.errorMess);
                    }
                    this.$message.success('指派成功！');
                });
            } else if (this.assignType === 2) {
                // 重新指派人员
                reAssignSpu({assigned_user, spu_list}).then(({data}) => {
                    if (data.status !== 1) {
                        return this.$message.error(data.errorMess);
                    }
                    this.$message.success('指派成功！');
                });
            }
        },

        // 驳回
        handleClickReject () {
            this.checkedStatus('isShowReject');
        },

        // 确认驳回
        handleConfirmReject (val) {
            console.log(val);
        },

        // 提交
        handleClickSubmit () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this.$confirm('确认提交？', '提示', {
                type: 'warning'
            }).then(() => {});
        },

        //小组分类
        searchGroup (value) {
            console.log(value);
        }
    },
    created () {
        this.thisVm = this;

        try {
            if (JSON.stringify(this.copyEditRecordInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['copy_edit_record']);
                this.handleQueryInfoCallBack(this.copyEditRecordInfo);
            }
        } catch (e) {
            console.log(e);
        }
    }
};
</script>

