        cursor: pointer;
        font-size: 14px;
        color: #6facff;
    }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: @upload-height;
        }

        .ui-com_edit__upload--last_btn {
            border: 1px solid #e6e6e6;
            width: 100%;
            height: @upload-height;

            /deep/ .el-upload {
                display: block;
                height: @upload-height;
                line-height: @upload-height;
                background: #F5F9F8;

                span {
                    color: @border-color;
                    font-size: 34px;
                    margin-right: 0;
                    position: relative;
                    top: -8px
                }

                p {
                    position: absolute;
                    left: 33px;
                    top: 20px;
                    font-size: 12px;
                    color: #999999;
                }
            }
        }

        .ui-com_edit__upload--item--mask {
            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: @upload-height;
            background: rgba(0, 0, 0, 0.3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #ffffff;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}

.ui-table__img img {
    max-width: 70px;
    max-height: 90px;
}
</style>



