<template>
    <div class="ui-scan_page ui-main-module">
        <div class="ui-head">
            <ul class="ui-head-ope_item">
                <!--<li>-->
                <!--<el-input size="small" placeholder="请输入工号" @blur="handleUserInfo"-->
                <!--v-model="queryData.user_id"></el-input>-->
                <!--<el-input size="small" placeholder="带出部门" v-model="queryData.department"></el-input>-->
                <!--<el-input size="small" placeholder="带出姓名" v-model="queryData.handle_type">-->
                <!--<el-button slot="append">退出</el-button>-->
                <!--</el-input>-->
                <!--</li>-->
                <li class="ui-head-ope_item--layout">
                    <el-input size="small" autofocus @keyup.enter.native="handleScanInfo" placeholder="商品台车/精品SPU/试卖SPU"
                              v-model="queryData.code">
                    </el-input>
                </li>
            </ul>
        </div>
        <div class="ui-fn-module">
            <el-button type="text" size="small" @click="handleDelScan">
                <i class="icon-shanchusaoma icon-danger"></i>删除扫码
            </el-button>
            <el-button type="text" size="small" @click="handleSendScan">
                <i class="icon-tijiaoshenhe icon-main"></i>提交送样
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            @selectd="handleSelectAbout"
            :otherHeight="['ui-fn-module','ui-head']"
            @checked="handleSaveCheckInfo">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.value)" :class="queryData.list_type === item.value && 'ui-active'">{{item.label}}</span>
                    </li>
                </el-badge>
            </div>

            <template slot="create_user" slot-scope="scope">
                <p>开发部：{{scope.row.create_user[0]}}</p>
                <p>品控部：{{scope.row.create_user[1]}}</p>
            </template>

            <template slot="create_time" slot-scope="scope">
                <p>开发部：{{scope.row.create_time[0]}}</p>
                <p>品控部：{{scope.row.create_time[1]}}</p>
            </template>

        </ComTableInfo>

        <el-dialog title="查看台车明细" :visible.sync="dialogTableVisible">
            <ComTableInfo
                :tableKey="detailKey"
                :tableProps="detailProps"
                :tableData="detailData"
                :tableWidth="detailWidth">
                <template slot="num" slot-scope="scope">
                    {{scope.row.num.num}}
                </template>
                <template slot="pro_spu" slot-scope="scope">
                    {{scope.row.pro_spu.spu}}
                </template>
                <template slot="pro_name" slot-scope="scope">
                    {{scope.row.pro_name.product_name}}
                </template>

                <template slot="pro_category" slot-scope="scope">
                    {{scope.row.pro_category.one_category_name}}
                </template>

                <template slot="pro_amount" slot-scope="scope">
                    {{scope.row.pro_amount.sku_count}}
                </template>

                <template slot="bind_sku" slot-scope="scope">
                    {{scope.row.bind_sku.sku}}
                </template>
            </ComTableInfo>
        </el-dialog>
    </div>
</template>

<script>

import {
    mapGetters,
    mapActions
} from 'vuex';
import {
    getSampleListInfo,
    getSampleScanInfo,
    delSampleScan,
    sendSampleScan,
    getDetail,
    // 提交送样
    submitSampleCode,
} from '@/api/quality_control_department/sample_quality_control/sample_scan';
import {
    makeLog,
    isValidData,
} from '@/services';

export default {
    name: 'SampleScan',
    data () {
        return {
            thisVm: {},
            dialogTableVisible: false,
            queryData: {
                user_id: '',
                department: 2,
                handle_type: 1,
                list_type: '1'
            },

            moduleTypeList: [
                {
                    label: '台车&箱子',
                    value: '1'
                },
                {
                    label: '台车&SPU',
                    value: '2'
                },
                {
                    label: 'SPU',
                    value: '3'
                }
            ],

            tableKey: ['编号', '商品台车', '商品箱号', '商品目录', '商品数量', '送样人', '送样时间', '备注说明'],
            tableData: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //查看台车明细
            detailData: [],
            detailKey: [],
            detailWidth: [],
            detailProps: [],
            cacheList: {
                'trolleyBoxData': [],
                'trolleySpuData': [],
                'spuData': []
            }

        };
    },
    computed: {
        ...mapGetters(['queryParamsInfo', 'sampleScanListInfo'])
    },
    methods: {
        ...mapActions(['SaveSampleScanListInfo', 'DeleteSampleScanListInfo']),
        //显示明细
        showDetail (id) {
            if (!id) {
                return;
            }

            this.dialogTableVisible = true;
            let obj = {
                box_id: id,
                ...this.queryData
            };
            getDetail(obj).then(({data}) => {

                this.detailData = data.data.value;
                this.detailProps = Object.keys(data.data.key);
                this.detailKey = Object.values(data.data.key);
            });
        },

        //通过工号查询用户信息
        handleUserInfo () {
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'sample_scan',
                value: JSON.stringify(this.queryData)
            });
        },

        //选中时 同一台车的需要全部选中
        handleSelectAbout (obj) {
            this.tableData.forEach((item, index) => {
                if (parseInt(this.queryData.list_type, 10) === 1 && obj.data.pro_trolley.trolley_code) {
                    if (obj.data.pro_trolley.trolley_code === item.pro_trolley.trolley_code && obj.data.num.num !== item.num.num) {
                        obj.refObj.toggleRowSelection(this.tableData[index]);
                    }
                } else if (obj.data.trolley_num.trolley_code) {
                    if (obj.data.trolley_num.trolley_code === item.trolley_num.trolley_code && obj.data.num.num !== item.num.num) {
                        obj.refObj.toggleRowSelection(this.tableData[index]);
                    }
                }
            });
        },
 
        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //勾选的对象
        selectId () {
            // let idArr = [];
            // this.saveCheckInfo.forEach((obj) => {
            //     idArr.push(obj);
            // });
            // return idArr;
            return [...this.saveCheckInfo];
        },

        //删除扫码
        handleDelScan (checkList = []) {
            if(!this.saveCheckInfo.length && checkList.length === 0) return this.$message.warning('请勾选数据！');

            checkList = checkList.length > 0 ? checkList : this.saveCheckInfo;
            let restArr = [...this.tableData];
            checkList.forEach(check => {
                const num = check.num;
                restArr = restArr.filter(item => item.num !== num);
            })
            this.handleQueryInfoCallBack(restArr);
        },

        //提交送样
        handleSendScan () {
            if (!this.saveCheckInfo.length) return this.$message.warning('请勾选数据！');
            // this.queryData.list_type 操作类型 1、台车&箱子  2、台车&spu  3、spu

            const saveCheckInfoBackup = [...this.saveCheckInfo];
            this.$confirm('确认提交？', '提示', {
                type: 'warning'
            }).then(() => {
                const handle_type = parseInt(this.queryData.list_type, 10);
                const submit_data = this.getSumbitData(this.saveCheckInfo);
                submitSampleCode({handle_type, submit_data})
                    .then(({data}) => {
                        if(data.status === 1){
                            // 删除提交的项目
                            this.handleDelScan(saveCheckInfoBackup);
                            return this.$message.success(data.errorMess || '提交成功！');
                        } 
                        return this.$message.warning(data.errorMess || '提交失败！');
                    });
            });
        },

        // 获取选中数据的submit数据
        getSumbitData (dataList) {
            const type = parseInt(this.queryData.list_type, 10);
            // 获取对象数组中对应key值对象的辅助函数
            const formataData = (data, keys) => {
                keys = Array.isArray(keys) ? [...keys] : keys.split(',');
                return data.map(item => ({
                    ...keys.reduce((pre, cur) => ({
                        ...pre, [cur]: item[cur]
                    }), {})
                }));
            };

            switch (type) {
                case 1: // 台车&箱子
                    return formataData(dataList, 'trolley_id,box_id');
                case 2: // 台车&spu
                    return formataData(dataList, 'trolley_id,spu');
                case 3: // spu
                    return formataData(dataList, 'spu');
                default:
                    return null;
            }
        },

        // 列表信息查询
        handleQueryInfo () {
            switch (this.queryData.list_type) {
                case '1' :
                    this.handleQueryInfoCallBack(this.cacheList.trolleyBoxData);
                    break;
                case '2' :
                    this.handleQueryInfoCallBack(this.cacheList.trolleySpuData);
                    break;
                case '3' :
                    this.handleQueryInfoCallBack(this.cacheList.spuData);
            }
        },

        //扫码送样
        handleScanInfo () {
            let obj = {
                handle_type: this.queryData.list_type,
                code: this.queryData.code,
            };
            getSampleScanInfo(obj).then(({data}) => {

                if (data.status !== 1) {
                    this.$message({
                        message: data.errMsg,
                        type: 'warning'
                    });
                    return;
                }
                this.queryData.code = '';

               
                // 渲染扫码列表
                this.handleQueryInfoCallBack(data.list);
            });
        },

        //查询回调&&并缓存数据
        handleQueryInfoCallBack (data) {
            const type = this.queryData.list_type;
            if (isValidData(data)) {
                //数据渲染
                const tabData = [...data];

                for (let i in tabData) {
                    let n = parseInt(i, 10) + 1;
                    tabData[i] = Object.assign({num: n}, tabData[i]);
                }

                this.tableData = tabData;

                this.tableProps = Object.keys(tabData[0]);

            } else {
                this.tableData = [];
            }

            //添加缓存&渲染头部信息
            switch (type) {
                case '1' : // 台车&箱子
                    this.tableKey = ['编号', '商品台车', '商品箱号', '商品目录', '商品数量', '送样人', '送样时间', '备注说明'];
                    this.cacheList.trolleyBoxData = data;
                    this.tableWidth[4] = '90px';
                    this.tableWidth[5] = '200px';
                    this.tableWidth[7] = '300px';
                    break;
                case '2' : // 台车&SPU
                    this.tableKey = ['编号', '商品台车', '商品SPU', '商品名称', '商品目录', '送样人', '送样时间', '关联SKU', '样品数量', '备注说明'];
                    this.cacheList.trolleySpuData = data;
                    break;
                case '3' : // SPU
                    this.tableKey = ['编号', '商品SPU', '商品名称', '商品目录', '样品数量', '送样时间', '送样人', '关联SKU', '备注说明'];
                    this.cacheList.spuData = data;
                    break;
                default:
                    break;
            }


            this.tableWidth[0] = '90px';
            if (type !== '1') {
                this.tableWidth[5] = '90px';
                this.tableWidth[6] = '200px';
                this.tableWidth[9] = '300px';
            }
            
            // 保存扫码列表
            this.SaveSampleScanListInfo(this.cacheList);
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.list_type = val;
            this.handleQueryInfo();
        },
    },
    created () {
        if (isValidData(this.sampleScanListInfo)) {
            this.cacheList = this.sampleScanListInfo;
            this.handleQueryInfo();
        } else {
            let obj = {
                handle_type: this.queryData.list_type,
                is_frist: 1,
                code: ''
            };

            getSampleScanInfo(obj).then(({data}) => {
                console.log(data, '8888');
            });

        }
        this.detailWidth[0] = '90px';
        this.detailWidth[4] = '90px';

        if (this.queryParamsInfo['sample_scan']) {
            this.queryData = JSON.parse(this.queryParamsInfo['sample_scan']);
        }
    },
    beforeDestory () {
        // 删除缓存
        // this.DeleteSampleScanListInfo();

    },
};
</script>

<style scoped lang="less">
@border-color: #e6e6e6;
.ui-scan_page {
    text-align: left;

    .ui-head {
        padding: 20px;
        background: #FFFFFF;
        border: 1px solid @border-color;
    }

    .ui-head-ope_item {
        /deep/ .el-input {
            width: 200px;
        }

        li:not(:last-child) {
            margin-bottom: 10px;
        }

        .ui-head-ope_item--layout {
            .el-input {
                width: 609px;
            }
        }
    }
}
</style>



