/**  =========================> *|`| % 文案小组管理 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getCopyEditGroupInfo,
} from '../../../../api/copy_department/copy_edit_module/copy_group_manage'

const config = {
    state: {
        copyEditGroupInfo: {},
    },
    mutations: {
        [types.SAVE_COPY_GROUP_MANAGE_INFO] (state, saveInfo) {
            state.copyEditGroupInfo = saveInfo
        }
    },
    actions: {
        // 获取文案小组管理信息
        GetCopyEditGroupInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getCopyEditGroupInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_COPY_GROUP_MANAGE_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




