<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品目录" template="cascader"
                                           :options="optionList.productListList"
                                           :props="{ value: 'id', children: 'son_id', label: 'category_name' }"
                                           v-model="queryData.productList"></SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="作业组员">
                            <el-select size="small" v-model="queryData.workUser" clearable>
                                <el-option v-for="(item, index) in optionList.workUserList" :label="item" :key="index" :value="item"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="职能">
                            <el-select size="small" v-model="queryData.functionInfo">
                                <el-option
                                        v-for="(value, label) in optionList.functionInfoList"
                                        :key="value"
                                        :label="label"
                                        :value="value">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核状态">
                            <el-select :disabled="!queryData.functionInfo" size="small" v-model="queryData.auditState">
                                <el-option
                                        v-for="(value, label) in optionList.statusList"
                                        :key="value"
                                        :label="label"
                                        :value="value">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作用时">
                            <el-select :disabled="!queryData.functionInfo" size="small" v-model="queryData.operateUseType"
                                       @change="handleTimeRange(queryData.operateUseType)">
                                <el-option v-for="(label,value) in optionList.timeTypeList"
                                           :label="label"
                                           :value="value"
                                           :key="label">

                                </el-option>
                            </el-select>
                            <el-slider
                                    :disabled="!queryData.functionInfo"
                                    v-if="queryData.operateUseType"
                                    v-model="queryData.operateUseTime"
                                    range
                                    :min="1"
                                    :max="queryData.operateUseMax">
                            </el-slider>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员">
                            <el-select :disabled="!queryData.functionInfo" size="small" v-model="queryData.operateUser" clearable>
                                <el-option v-for="(item, index) in optionList.operationUserList" :label="item" :key="index" :value="item"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <transition name="slide-fade" @after-enter="animation()" @after-leave="animation()" @before-enter="animation(1)">
                        <div v-show="FilterShow">
                            <li>
                                <SearchRequirement label="操作时间" template="daterange" v-model="queryData.operateTime"></SearchRequirement>
                            </li>
                        </div>
                    </transition>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})" icon="icon-chaxun">查询</el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="!FilterShow">高级筛选<i class="el-icon-caret-bottom"></i></el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="FilterShow">基本筛选<i class="el-icon-caret-top"></i></el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <!--<el-button-->
            <!--v-if="item.method && item.permit(thisVm)"-->
            <!--v-for="item in buttonList"-->
            <!--size="small"-->
            <!--:type="item.type"-->
            <!--:key="item.value"-->
            <!--@click="item.action(thisVm, item.method)">{{item.name}}</el-button>-->
            <!--<el-button @click="handleOperationFn(5)" type="text" size="small">-->
                <!--<i class="icon-shenhe icon-warn"></i>审核</el-button>-->
            <!--<el-button @click="handleOperationFn(4)" type="text" size="small">-->
                <!--<i class="icon-bohui icon-danger"></i>驳回</el-button>-->
        </div>
        <ComTableInfo
                ref="myTable"
                isCheck
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableData="tableData"
                :tableWidth="tableWidth"
                :showSearch="showSearch"
                @checked="handleSaveCheckInfo">
            <template slot="spu" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.spu[0]}}</p>
                    <p>{{scope.row.spu[1]}}</p>
                    <div class="ui-table--supplier_code--flag" :class="`ui-table_flag${scope.row.devp_code}`" v-show="scope.row.devp_type">
                        <span>{{scope.row.devp_type}}</span>
                    </div>
                    <div class="ui-table--icon_btn">
                        <i class="icon-bianji icon-main" @click.stop="handlePreview(scope.row)"></i>
                        <i class="icon-shenhe icon-warn" title="审核" @click="handleOperationFn(scope.row, 1)"></i>
                        <i class="icon-bohui icon-danger" title="驳回" @click="handleOperationFn(scope.row, 2)"></i>
                    </div>
                </div>
            </template>
            <template slot="img" slot-scope="scope">
                <div class="ui-table__img">
                    <img :src="scope.row.photo_path" alt="" @click.stop="handlePreviewImage(scope.row.img)">
                </div>
            </template>
            <template slot="good_name" slot-scope="scope">
                <div>
                    <p><span>{{scope.row.good_name[0]}}</span></p>
                    <p><span>{{scope.row.good_name[1]}}</span></p>
                </div>
            </template>
            <template slot="category_path" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.category_path"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="sample_type" slot-scope="scope">
                <div>
                    <!--<p><span>购买类型：</span><span>{{scope.row.type}}</span></p>-->
                    <!--<p><span>是否买样：</span><span>{{scope.row.is_buys}}</span></p>-->
                    <!--<p><span>是否定制：</span><span>{{scope.row.is_diy}}</span></p>-->
                    <p v-for="item of scope.row.sample_type"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="good_info" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.good_info"><span>{{item}}</span></p>
                    <!--<p><span>采样价(￥)：</span><span>{{scope.row.datum_sample_money}}</span></p>-->
                    <!--<p><span>供应商编码：</span><span>{{scope.row.supplier_code}}</span></p>-->
                    <!--<p><span>供应商名称：</span><span>{{scope.row.supplier_name}}</span></p>-->
                    <!--<p><span>采样链接：</span><span>{{scope.row.sample_link}}</span></p>-->
                    <!--<p><span>参考链接：</span><span>{{scope.row.look_link}}</span></p>-->
                    <el-popover
                            placement="bottom"
                            title="采样链接"
                            width="200"
                            trigger="click">
                        <li v-for="item in scope.row.link_reference">
                            <a :href="item" target="_blank" class="ui-link">{{item}}</a>
                        </li>
                        <el-button slot="reference" type="text"><i class="icon-caiyanglianjie"></i></el-button>
                    </el-popover>
                    <el-popover
                            placement="bottom"
                            title="参考链接"
                            width="200"
                            trigger="click">
                        <li v-for="item in scope.row.link_reference">
                            <a :href="item" target="_blank" class="ui-link">{{item}}</a>
                        </li>
                        <el-button slot="reference" type="text"><i class="icon-cankaolianjie"></i></el-button>
                    </el-popover>
                </div>
            </template>
            <template slot="audit_state" slot-scope="scope">
                <div>
                    <!--<p><span>创建：</span><span>{{scope.row.create_state}}</span></p>-->
                    <!--<p><span>开发：</span><span>{{scope.row.dev_state}}</span></p>-->
                    <p v-for="item of scope.row.product_staus"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="ope_record" slot-scope="scope">
                <div>
                    <!--<p><span>创建：</span><span>{{scope.row.create_record_user}}</span> <span>{{scope.row.create_record_time}}</span>-->
                    <!--</p>-->
                    <!--<p><span>开发：</span><span>{{scope.row.dev_record_user}}</span>-->
                    <!--<span>{{scope.row.dev_record_time}}</span></p>-->
                    <!--<p><span>品控：</span><span>{{scope.row.pk_record_user}}</span>-->
                    <!--<span>{{scope.row.pk_record_time}}</span></p>-->
                    <p v-for="item of scope.row.opera_log"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="use_time" slot-scope="scope">
                <div>
                    <!--<p><span>开发：</span><span>{{scope.row.dev_use_time}}</span></p>-->
                    <!--<p><span>品控：</span><span>{{scope.row.pk_use_time}}</span></p>-->
                    <p v-for="item of scope.row.opera_time"><span>{{item}}</span></p>
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <EditPage :show.sync="isPreviewShow" :row="saveEditRow" type="preview"></EditPage>
        <ComDialogRemarks showRemarks formLabel="选择部门"
                          title="审核" type="select"
                          :visible.sync="isShowAudit"
                          @confirm="handleConfirmOperate" :option="optionList.depList"></ComDialogRemarks>
        <ComDialogRemarks showRemarks title="驳回" :visible.sync="isShowReject"
                          @confirm="handleConfirmOperate"></ComDialogRemarks>
    </div>
</template>

<script>
    import EditPage from './ProductDatumCreate/EditPage';
    import config from '@/assets/js/config';
    import {
        autoAddTableInx,
        getDefaultTime,
        changeTableInfoProps,
        goToNewPage,
        getNumByTime
    } from '@/services/index'
    import {
        mapActions,
        mapGetters
    } from "vuex";
    import {
        getCityInfo
    } from '@/api/login';
    import {
        getSupplierCreateListInfo,
        changeSupplierState,
        getEditInfo
    } from '@/api/dev_department/supplier_development/supplier_create_list';

    import {
        SPUOperationInfo
    } from '@/api/dev_department/product_development/product_datum_create';

    export default {
        components: {
            EditPage
        },
        data() {
            return {

                //高级筛选
                FilterShow: false,

                // 时间搜索框宽度设置
                searchDateWidth: config.SEARCH_DATE_WIDTH,

                thisVm: {},

                // 搜索缩进
                showSearch: true,

                queryData: {
                    productSPU: '',
                    supplierCode: '',
                    productList: [],
                    workUser: '',
                    functionInfo: '',
                    auditState: '',
                    productName: '',
                    supplierName: '',
                    operateUser: '',
                    operateTime: [],
                    operateUseTime: [1, 20],
                    operateUseType: '',
                    operateUseMax: 30,
                },

                optionList: {
                    supplierTypeList: {},
                    settlementTypeList: {},
                    devUserList: {},
                    deliverAddressList: [],
                    paymentModeList: {},
                    liaisonsList: {},
                    createUserList: {},
                    stateList: {},
                    companyTypeList: {},
                    depList: {
                        '1': '品控',
                        '2': '文案or摄影',
                        '3': '美工'
                    },
                    timeTypeList: {
                        day: '天',
                        hours: '时',
                        minutes: '分'
                    },
                    workUserList: [],
                    operationUserList: [],
                },

                tableData: [],
                tableKey: [],
                tableWidth: [],
                tableProps: [],

                // 保存勾选数据
                saveCheckInfo: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                // 预览图片
                isShowLookImage: false,
                lookImageUrl: [],

                // 预览
                saveEditRow: {},
                isPreviewShow: false,

                // 审核驳回
                isShowAudit: false,
                isShowReject: false,
                saveCheckStatus: {
                    spu: '',
                    status: ''
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'devAnomalyAuditInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetDevAnomalyAuditInfo'
            ]),
            autoAddTableInx,
            getDefaultTime,
            goToNewPage,

            // 列表信息查询
            handleQueryInfo(params = {}, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['dev_anomaly_audit']);
                }

                let obj = {
                    spu: this.queryData.productSPU,
                    supplier_code: this.queryData.supplierCode,
                    spu_category: this.queryData.productList.join(','),
                    goods_name: this.queryData.productName,
                    supplier_name: this.queryData.supplierName,
                    function: this.queryData.functionInfo,
                    user: this.queryData.workUser,
                    opera_user: this.queryData.operateUser,
                    opera_time_start: this.queryData.operateTime[0],
                    opera_time_end: this.queryData.operateTime[1],
                    status: this.queryData.auditState,
                    opera_start_time: getNumByTime(this.queryData.operateUseTime[0], this.queryData.operateUseType, 'seconds'),
                    opera_end_time: getNumByTime(this.queryData.operateUseTime[1], this.queryData.operateUseType, 'seconds'),
                    ...params
                };

                this.GetDevAnomalyAuditInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;

                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 查询重置
            handleResetQueryInfo() {
                this.queryData = {
                    productSPU: '',
                    supplierCode: '',
                    productList: [],
                    workUser: '',
                    functionInfo: '',
                    auditState: '',
                    operateUseTime: '',
                    productName: '',
                    supplierName: '',
                    operateUser: '',
                    operateTime: [],
                };
            },

            //高级筛选
            handleClickFilter() {
                this.FilterShow = !this.FilterShow;
            },

            // 操作用时
            handleTimeRange (type) {

                switch (type) {
                    case 'day':
                        this.queryData.operateUseMax = 30;
                        break;
                    case 'hours':
                        this.queryData.operateUseMax = 24;
                        break;
                    case 'minutes':
                        this.queryData.operateUseMax = 60;
                        break;
                }
            },

            animation(isHide) {
                if (isHide) {
                    console.log(document.querySelector('body'));
                    document.querySelector('body').style.overflow = 'hidden';
                    return;
                }
                this.$refs.myTable.handleRefreshTableHeight()
            },

            // 分页条数切换
            handleSizeChange(val) {
                this.handleQueryInfo({
                    limit: val,
                    offset: 1
                }, true)
            },

            // 跳转页数切换
            handleCurrentChange(val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true)
            },

            // 保存勾选数据
            handleSaveCheckInfo(val) {
                this.saveCheckInfo = val;
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 预览
            handlePreview(row) {
                this.isPreviewShow = true;
                this.saveEditRow = row;
            },

            // 预览图片
            handleLookImage(src) {
                this.isShowLookImage = true;
                this.lookImageUrl = ['http://pic10.nipic.com/20101016/2531170_233023137000_2.jpg'];
            },

            // 重构树形结构
            handleTreeCallBackChange(arr) {
                for (let i = 0; i < arr.length; i++) {
                    if (arr[i].son_id && arr[i].son_id.length) {
                        this.handleTreeCallBackChange(arr[i].son_id);
                    } else {
                        delete arr[i].son_id;
                    }
                }
            },

            // 页面操作功能
            handleOperationFn(row, status) {
                this.saveCheckStatus.spu = row.spu[0];
                this.saveCheckStatus.status = status;
                if (status === 1) {
                    this.isShowAudit = true;
                } else {
                    this.isShowReject = true;
                }
                // if (row) {
                //
                // } else {
                //     if (!this.saveCheckInfo.length) {
                //         this.$message.warning('请勾选数据！');
                //         return;
                //     }
                //
                //     let spus = [];
                //     this.saveCheckInfo.forEach(value => {
                //         spus.push(value.spu[0])
                //     });
                //
                //     SPUOperationInfo({
                //         spu: spus,
                //         status: status
                //     }).then(({ data }) => {
                //         this.handleOperationCallBack(data);
                //     })
                // }
            },

            // 审核 驳回
            handleConfirmOperate(res) {
                let obj = {
                    spu: this.saveCheckStatus.spu,
                    type: this.saveCheckStatus.status,
                    status: 4
                };

                if (obj.type === 1) {
                    obj.dep = res.check;
                }

                SPUOperationInfo(obj).then(({ data }) => {
                    this.handleOperationCallBack(data);
                })
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo({ limit: this.pageData.currentList });
                } else {
                    this.$message.error(data.errorMess || '操作失败');
                }
            },

            //查询回调
            handleQueryInfoCallBack(data){

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'dev_anomaly_audit',
                    value: JSON.stringify(this.queryData)
                });

                //数据渲染
                this.tableData = data.data.value;
                this.tableKey = data.data.key;
                this.tableWidth[2] = '90px';
                this.tableWidth[6] = '230px';
                this.tableWidth[8] = '230px';
                this.optionList.productListList = [...data.select_list.category];
                this.optionList.functionInfoList = data.select_list.function;
                this.optionList.statusList = data.select_list.status;
                this.handleTreeCallBackChange(this.optionList.productListList);
                this.optionList.operationUserList = data.select_list.opera_user;
                this.optionList.workUserList = data.select_list.opera_user;

                // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
                this.tableProps = ['index', 'spu', 'img', 'good_name', 'category_path', 'sample_type', 'good_info', 'audit_state', 'ope_record', 'use_time', 'remarks'];

                //渲染分页
                this.pageData.currentList = data.page.limit * 1;
                this.pageData.currentPage = data.page.offset * 1;
                this.pageData.currentTotal = data.page.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try{
                if (JSON.stringify(this.devAnomalyAuditInfo) === '{}') {
                    this.handleQueryInfo({is_get_base: 1});
                }else{
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['dev_anomaly_audit']);
                    this.handleQueryInfoCallBack(this.devAnomalyAuditInfo);
                }
            }catch (e) {
                console.log(e)
            }
        },
    };
</script>

<style lang="less" scoped>

    .ui-table--supplier_code {
        font-size: 12px;
        line-height: 10px;
        p:nth-child(1) {
            font-weight: bold;
        }

        p {
            margin-bottom: 8px;
        }
    }

    /deep/ .el-table td, /deep/ .el-table th {
        overflow: hidden;
        text-align: left!important;
    }

    .ui-table--supplier_code--flag {
        width: 50px;
        height: 35px;
        line-height: 54px;
        text-align: center;
        -webkit-transform: rotateZ(45deg);
        transform: rotateZ(45deg);
        background: #63c4e9;
        position: absolute;
        font-size: 12px;
        color: #FFFFFF;
        top: -12px;
        right: -20px;
        font-weight: normal;
    }

    .ui-table--icon_btn {
        i {
            cursor: pointer;
            color: #72afff;
            font-size: 12px;
        }
    }

    .ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
        li {
            width: 130px;
            /*height: 185px;*/
            margin-right: 20px;
            text-align: center;
            position: relative;

            img {
                width: 100%;
                height: 185px;
            }

            .ui-com_edit__upload--item--mask {

                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 185px;
                background: rgba(0, 0, 0, .3);
                text-align: center;
                line-height: 185px;

                & > div {
                    display: inline-block;
                }

                span {
                    margin: 0 5px;
                    cursor: pointer;
                    color: #cccccc;
                    font-size: 20px;

                    &:hover {
                        color: #FFFFFF;
                    }
                }
            }

            &:hover {
                .ui-com_edit__upload--item--mask {
                    display: block;
                }
            }
        }
    }
    .ui-table__img img{
        max-width: 70px;
        height: 80px;
    }
</style>


