/**  --->>>>   |^ 样品扫码送样^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 样品扫码送样
export function getSampleListInfo (queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    };

    return http.getAjax('/sample/sample_scan/display_codes', data);

}

//扫码关联
export function getSampleScanInfo (queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/sample/sample_scan/display_codes', data);
}

//删除包装信息
export function delSampleScan (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/sample_control/sample_quality_control/del_scan_code', data);

}

//提交送样
export function sendSampleScan (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/sample_control/sample_quality_control/submit_send_sample', data, true);

}

//提交送样
export function getDetail (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/sample_control/sample_quality_control/get_box_info', data);

}

// FIXME: 提交送样，尚有疑问
export function submitSampleCode(params = {}){
    const data = {
        ...CONFIG.ajaxData,
        ...params
    }

    return http.postAjax('/sample/Sample_scan/submit_codes', data);
}


