<template>
    <div class="ui-main-module">
        <div class="ui-fn-module">
            <el-button type="text" @click="handleExportListInfo" size="small" icon="icon-daochu icon_blue">导出
            </el-button>
            <ComUpload label="批量导入" btnType="text" :url="importUrl" @success="handleUploadSuccess"
                       :upData="upData" :multiple="true">
                <template slot="icon">
                    <i class="icon-piliangdaoru icon_purple"></i>
                </template>
            </ComUpload>
            <el-button size="small" class="load-text" type="text" @click="handleDownloadModule"
                       icon="icon-xiazai1 icon_blue">下载模板
            </el-button>
            <el-button type="text" class="danger-border" size="small" @click="addData" icon="icon-xinzeng1 icon_blue">
                新增
            </el-button>
            <el-button type="text" class="danger-border" size="small" @click="delData" icon="icon-shanchu1 icon_pink">
                删除
            </el-button>
            <!--<el-button type="primary" @click="handleExportListInfo" size="small">导出</el-button>-->
            <!--<ComUpload label="批量导入" :url="importUrl" @success="handleUploadSuccess"-->
            <!--:upData="upData" :multiple="true"></ComUpload>-->

            <!--<el-button size="small" class="load-text" type="text" @click="handleDownloadModule">下载模板</el-button>-->
            <!--<el-button type="danger" class="danger-border" size="small" @click="delData">删除</el-button>-->
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">

            <template slot="operate" slot-scope="scope">
                <el-table-column
                    width="200px"
                    fixed="right"
                    label="操作">
                    <template slot-scope="scope">
                        <el-button @click="delItem(scope.row.id)" type="text" size="small">
                            删除
                        </el-button>
                        <el-button type="text" @click="editItem(scope.row)" size="small">编辑</el-button>
                        <el-button type="text" @click="viewLog(scope.row.id)" size="small">日志</el-button>
                    </template>
                </el-table-column>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog title="编辑" class="ui-layout_edit-dialog" :visible.sync="dialogFormVisible" width="450px">
            <el-form :model="editForm" label-width="100px">
                <el-form-item :label="item" v-for="(item,index) in tableKey" :key="index">
                    <el-input v-if="!(tableProps[index]==='reject_to_dpt'|| tableProps[index]==='setting_introduce')"
                              v-model="editForm[tableProps[index]]" size="small" placeholder="请输入"
                              :disabled="tableProps[index]==='id'||tableProps[index]==='create_time'||tableProps[index]==='create_user'"></el-input>
                    <el-select v-if="tableProps[index]==='reject_to_dpt'" size="small"
                               v-model="editForm.reject_to_dpt"
                               placeholder="请选择">
                        <el-option
                            v-for="(label, value) in departmentOption"
                            :key="value"
                            :label="label"
                            :value="value"></el-option>
                    </el-select>
                    <el-input
                        resize="none"
                        style="width: 77%"
                        placeholder="请输入"
                        type="textarea"
                        :rows="5"
                        size="small"
                        v-if="tableProps[index]==='setting_introduce'"
                        v-model="editForm.setting_introduce"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
                <el-button type="primary" @click="saveEditInfo" size="small">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="新增" class="ui-layout_edit-dialog" :visible.sync="dialogAddVisible" width="500px">
            <el-form :model="addForm" label-width="80px">
                <el-form-item label="退样类型">
                    <el-input v-model="addForm.reject_type_text" size="small" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="驳回部门">
                    <el-select v-model="addForm.reject_to_dpt"
                               size="small"
                               placeholder="请选择">
                        <el-option
                            v-for="(label, value) in departmentOption"
                            :key="value"
                            :label="label"
                            :value="value"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="是否退样">
                    <el-select v-model="addForm.remark"
                               size="small"
                               placeholder="请选择">
                        <el-option value="是">
                            是
                        </el-option>
                        <el-option value="否">
                            否
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="设定说明">
                    <el-input v-model="addForm.setting_introduce" size="small" placeholder="请输入" type="textarea"
                              :rows="5" resize="none"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddVisible = false" size="small">取 消</el-button>
                <el-button type="primary" @click="saveAddInfo" size="small">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="查看日志" class="ui-layout_edit-dialog" :visible.sync="dialogTableVisible" top="5vh">
            <ComTableInfo
                :tableKey="logKey"
                :tableProps="logProps"
                :tableData="logData"
                :tableWidth="logWidth">
                <template slot="remark" slot-scope="scope">
                    <p v-for="(item,index) in scope.row.remark" :key="index">
                        {{item}}
                    </p>
                </template>
            </ComTableInfo>
        </el-dialog>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import * as downloadApi from '@/api/download_api';
import {
    mapGetters
} from 'vuex';
import {
    getRejectedListInfo,
    delRejected,
    changeRejectedInfo,
    addRejectedInfo,
    exportRejectedInfo,
    importRejectedInfo
} from '@/api/copy_department/copy_edit_module/rejected_info_maintain';
import { getLogList } from '@/api/common';

export default {
    data () {
        return {

            // 搜索缩进
            showSearch: true,

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 上传列表
            saveFileList: [],

            //编辑弹窗
            dialogFormVisible: false,
            editForm: {},

            //部门参数
            departmentOption: config.departmentInfo,

            //日志弹窗
            dialogTableVisible: false,
            logData: [],
            logKey: [],
            logProps: [],
            logWidth: ['200px', '120px', '120px'],

            //导入的接口
            importUrl: '/rejection/rejection/import_reject_config',

            upData: {},

            //新增弹窗
            dialogAddVisible: false,
            addForm: {}

        };
    },
    computed: {
        ...mapGetters([
            'loginInfo',
        ]),
        downloadUrl () {
            return downloadApi['DOWNLOAD_REJECTED_MAINTAIN'];
        }
    },
    methods: {

        // 下载导入模板
        handleDownloadModule () {
            window.open(this.downloadUrl + '?reject_from_dpt=4', '_self');
        },
        handleUploadSuccess (data) {
            if (data) {
                this.handleQueryInfo();
            }
        },

        //查看日志
        viewLog (id) {
            this.dialogTableVisible = true;
            let obj = {
                mod_type: 1,
                id
            };
            getLogList(obj).then(({data}) => {
                if (data.status === 1) {
                    this.logData = data.data.value;
                    this.logData.map((item) => {
                        return item.remark = item.remark.split('|');
                    });
                    this.logProps = Object.keys(data.data.key);
                    this.logKey = Object.values(data.data.key);
                } else {
                    this.logData = [];
                }
            });
        },
        //新增
        saveAddInfo () {
            let obj = {
                reject_from_dpt: 4,
                ...this.addForm
            };
            addRejectedInfo(obj).then(({data}) => {
                if (data.errorMess) {
                    this.$message({
                        message: data.errorMess,
                        type: 'warning'
                    });
                    return;
                }
                this.dialogAddVisible = false;

                this.$message({
                    message: '添加成功!',
                    type: 'success'
                });
                this.handleQueryInfo();
            });

        },

        //保存修改
        saveEditInfo () {

            let newData = {...this.editForm};
            delete newData.create_time;

            //转换部门
            let arr = Object.values(config.departmentInfo);
            let idx = arr.findIndex((item) => {
                return newData.reject_to_dpt === item;
            });
            if (idx >= 0) {
                newData.reject_to_dpt = idx + 1;
            }
            let obj = {
                modify_user: this.loginInfo.user_name,
                user_id: this.loginInfo.uid,
                ...newData
            };
            changeRejectedInfo(obj).then(({data}) => {
                if (data.errorMess) {
                    this.$message({
                        message: data.errorMess,
                        type: 'warning'
                    });
                    return;
                }
                this.dialogFormVisible = false;

                this.$message({
                    message: '修改成功!',
                    type: 'success'
                });
                this.handleQueryInfo();
            });

        },

        //编辑弹窗
        editItem (obj) {
            this.dialogFormVisible = true;
            this.editForm = Object.assign({}, obj);
        },

        //勾选的id
        selectId () {
            let idArr = [];
            this.saveCheckInfo.forEach((obj) => {
                idArr.push(obj.id);
            });
            return idArr;
        },

        //新增
        addData () {
            this.dialogAddVisible = true;
        },

        //删除多个
        delData () {
            let idArr = this.selectId();
            if (!idArr.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: idArr.join(','),
                    user_id: this.loginInfo.uid,
                    modify_user: this.loginInfo.user_name
                };
                delRejected(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        //删除单个
        delItem (id) {
            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: id,
                    user_id: this.loginInfo.uid,
                    modify_user: this.loginInfo.user_name
                };
                delRejected(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        // 导出明细信息
        handleExportListInfo () {
            let obj = {
                reject_from_dpt: 4,
                id: this.selectId().join(',')
            };
            exportRejectedInfo(obj);

        },

        // 列表信息查询
        handleQueryInfo (params = {}) {
            let obj = {
                reject_from_dpt: 4,
                ...params
            };

            getRejectedListInfo(obj).then(({data}) => {
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            });
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            });
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            //数据渲染
            this.tableData = data.data.value;
            this.tableProps = Object.keys(data.data.key);
            let keys = Object.values(data.data.key);

            //对 审核人进行隐藏
            keys.splice(keys.length - 1, 1);
            this.tableKey = keys;

            this.tableWidth = ['80', '150', '700', '150', '120', '140', '150', ''];

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        }
    },
    created () {
        this.handleQueryInfo();
        this.upData = {
            user_id: this.loginInfo.staff_code || '12534',
            reject_from_dpt: 4
        };
    },
};
</script>

<style lang="less" scoped>
/deep/ .el-input {
    /*width: 100%;*/
}

@primary-color: #1299d8;

</style>



