/**  =========================> *|`| % TQE审核记录 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getAuditRecordInfo,
} from '../../../../api/quality_control_department/sample_quality_control/audit_record'

const config = {
    state: {
        auditRecordInfo: {},
    },
    mutations: {
        [types.SAVE_AUDIT_RECORD_INFO] (state, saveInfo) {
            state.auditRecordInfo = saveInfo
        }
    },
    actions: {
        GetAuditRecordInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getAuditRecordInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_AUDIT_RECORD_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




