/**  =========================> *|`| % 商品开发列表 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getProductDevListInfo,
} from '../../../../api/sample_track_manage/product_manage_module/product_dev_list';

const config = {
    state: {
        productDevListInfo: {}
    },
    mutations: {
        [types.SAVE_PRODUCT_DEV_LIST_INFO](state, saveInfo) {
            state.productDevListInfo = saveInfo;
        }
    },
    actions: {
        // 获取商品开发列表信息
        GetProductDevListInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getProductDevListInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_PRODUCT_DEV_LIST_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




