<template>
    <div class="classify">
        <div class="group-box">
            <div class="left-item">{{title}}：</div>
            <div class="right-item" :class="hasBorder?'border-bottom-box':''">
                <div class="btn-list" :class="groupIsShow&&!groupToggle?'over-hide':''">
                    <ul ref="group">
                        <li
                            v-for="(item, index) in classify"
                            :key="index"
                            class="default-btn"
                            :class="{'el-button--primary':groupIndex===index}"
                            @click="groupSearch(index)">
                            {{`${item.title}(${item.number})`}}
                        </li>
                    </ul>
                </div>
                <div class="toggle-btn" v-if="groupIsShow" @click="groupToggle = !groupToggle">
                    <div v-show="!groupToggle" class="default-btn">展开</div>
                    <div v-show="groupToggle" class="default-btn">收起</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'CopyClassify',
    props: {
        title: {
            type: String,
            default: ''
        },
        classify: {
            type: Array,
            default: []
        },
        hasBorder: {
            type: Boolean,
            default: false
        },
        checkIndex: {
            type: Number,
            default: 0
        },
        titleGroup: {
            type: String,
            default: 'group'
        }
    },
    data () {
        return {
            groupIndex: this.checkIndex,
            groupIsShow: false, //收缩按钮显示隐藏
            groupToggle: false, //是否展开
            objFn: {
                [this.titleGroup]: ''
            }
        };
    },
    methods: {

        //分类点击
        groupSearch (index) {
            this.groupIndex = index;
            this.$emit('checkGroup', {'id': index});
        },

        //判断group 高度  显示展开按钮
        initHeight () {
            let baseH = 40;
            let groupBoxH = parseInt(window.getComputedStyle(this.$refs.group).height);
            if (groupBoxH > baseH) {
                this.groupIsShow = true;
            } else {
                this.groupIsShow = false;
            }
        }
    },

    mounted () {
        const vm = this;

        if (this.classify) {
            this.initHeight();
        }

        this.objFn[this.titleGroup] = () => {
            if (this.$refs.group) {
                setTimeout(() => {
                    this.initHeight();
                }, 500);
            }
        };

        window.addEventListener('resize', this.objFn[this.titleGroup]);
    }
};
</script>

<style lang="less" scoped>
.el-button--primary {
    color: #ffffff !important;
    background: #75B1FF !important;
    border-color: #75B1FF !important;
}

.over-hide {
    height: 33px;
    overflow: hidden;
}

//有hasBorder属性时
.border-bottom-box {
    border-bottom: 1px dashed #e7e7e7;
    margin-bottom: 5px;
}

.classify {
    font-size: 14px;

    .group-box {
        display: flex;


        .left-item {
            font-weight: bold;
            color: #333333;
            line-height: 33px;
            margin-right: 5px;
        }

        .right-item {
            flex: 1;
            font-size: 12px;
            display: flex;
            line-height: 20px;

            .default-btn {
                background-color: #f4f4f4;
                color: #5a5a5a;
                border: 1px solid #e6e6e6;
                border-radius: 2px;
                padding: 5px;
                margin: 0px 5px 5px 0px;
                cursor: pointer;
                min-width: 65px;
            }

            .btn-list {
                flex: 1;

                ul {
                    display: flex;
                    flex-wrap: wrap;
                }
            }

            .toggle-btn {

            }
        }
    }

    .active-btn {
        background-color: #4a93af !important;
        color: #ffffff;
    }
}


</style>



