/**  --->>>>   |^ 样品异常记录^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取样品异常记录信息
export function getSampleAbnormalRecordInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.is_csv) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/quality/product/product_exception_list' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/quality/product/product_exception_list', data);
    }
}



