/**  =========================> *|`| % 商品资料创建 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getProductDatumCreateInfo,
} from '../../../../api/dev_department/product_development/product_datum_create';

const config = {
    state: {
        productDatumCreateInfo: {},
        childrenPageData: {
            ['/batch_create_product_datum']: {
                listInfo: {}
            }
        },
    },
    mutations: {
        [types.SAVE_PRODUCT_DATUM_CREATE_INFO](state, saveInfo) {
            state.productDatumCreateInfo = saveInfo;
        },
        [types.SAVE_BATCH_CREATE_PRODUCT_INFO](state, saveInfo) {
            state.childrenPageData['/batch_create_product_datum'].listInfo = saveInfo;
        }
    },
    actions: {
        // 获取商品资料创建信息
        GetProductDatumCreateInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getProductDatumCreateInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_PRODUCT_DATUM_CREATE_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




