/**  =========================> *|`| % 创建供应商列表 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getSupplierCreateListInfo,
} from '../../../../api/dev_department/supplier_development/supplier_create_list';

const config = {
    state: {
        createSupplierInfo: {},
        childrenPageData: {
            ['/add_supplier_list']: {
                listInfo: {}
            }
        },
    },
    mutations: {
        [types.SAVE_CREATE_SUPPLIER_INFO](state, saveInfo) {
            // let options = state.createSupplierInfo
            state.createSupplierInfo = saveInfo;
        },

        [types.SAVE_BATCH_CREATE_INFO](state, saveInfo) {
            state.childrenPageData['/add_supplier_list'].listInfo = saveInfo;
        }
    },
    actions: {
        // 获取创建供应商信息
        GetSupplierCreateListInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSupplierCreateListInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_CREATE_SUPPLIER_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




