/**  =========================> *|`| % 开发属性管理 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getProductAttrManageInfo,
} from '../../../../api/dev_department/system_manage/product_attr_manage';

const config = {
    state: {
        productAttrManageInfo: {}
    },
    mutations: {
        [types.SAVE_PRODUCT_ATTR_MANAGE_INFO](state, saveInfo) {
            state.productAttrManageInfo = saveInfo;
        }
    },
    actions: {
        // 获取开发属性管理信息
        GetProductAttrManageInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getProductAttrManageInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_PRODUCT_ATTR_MANAGE_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




