import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取文案编辑信息
export function getCopyEditInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.getAjax('/copywriters/copywriters/getCopywritersDetail', data);
}

// 文案翻译
export function copyEditTranslate (params) {
    return http.fileAjax('/copywriters/copywriters/translate', params);
}

// 保存/提交
export function submitCopyInfo (params) {
    return http.fileAjax('/copywriters/copywriters/saveCopywriters', params);
}

//关键词
export function getCopyKeyWords (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/copywriters/copywriters/keywordRecommend', data);
}



