<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品台车">
                            <el-input size="small" v-model="queryData.trolley_code"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品箱号">
                            <el-input size="small" v-model="queryData.box_code" placeholder="请输入供应商代码"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品目录" template="cascader"
                                           :options="optionList.productList"
                                           :props="{ value: 'id', children: 'son_id', label: 'category_name' }"
                                           v-model="queryData.productList"></SearchRequirement>

                        <!--<SearchRequirement label="商品目录">-->
                        <!--<el-select v-model="queryData.category_id" placeholder="请选择" size="small">-->
                        <!--<el-option-->
                        <!--v-for="(label, value) in optionList.productList"-->
                        <!--:key="value"-->
                        <!--:label="label"-->
                        <!--:value="value"></el-option>-->
                        <!--</el-select>-->
                        <!--</SearchRequirement>-->
                    </li>
                    <li>
                        <SearchRequirement label="送样时间" template="daterange"
                                           v-model="queryData.createTime"></SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="优先级">
                            <el-select v-model="queryData.priority" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.priorityList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.spu" placeholder="支持模糊查询"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.product_name" placeholder="支持模糊查询"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="送样人">
                            <el-select v-model="queryData.send_user" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.sendUserList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo()" icon="icon-chaxun">查询</el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <el-button type="text" size="small" @click="handleClickTask">
                <i class="icon-lingqurenwu icon-main"></i>领取任务
            </el-button>
            <el-button type="text" size="small" @click="handleClickPriority">
                <i class="icon-youxianji icon-plain"></i>优先级
            </el-button>
            <el-button type="text" size="small" @click="handleClickAssign">
                <i class="icon-renwuzhipai icon-warn"></i>指派任务
            </el-button>
        </div>

        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :otherHeight="['ui-fn-module']"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in optionList.moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.value)"
                              :class="queryData.list_type === item.value && 'ui-active'">{{item.label}}</span>
                    </li>
                </el-badge>
            </div>
            <!--商品台车-->
            <template slot="num" slot-scope="scope">
                <span>{{scope.row.num.num}}</span>
            </template>

            <template slot="priority" slot-scope="scope">
                <span>{{scope.row.priority.priority}}</span>
            </template>

            <template slot="pro_trolley" slot-scope="scope">
                <span>{{scope.row.pro_trolley.trolley_code}}</span>
            </template>

            <template slot="pro_box" slot-scope="scope">
                <span>{{scope.row.pro_box.box_code}}</span>
            </template>

            <template slot="pro_category" slot-scope="scope">
                <span>一级目录</span>
            </template>

            <template slot="pro_amount" slot-scope="scope">
                <p style="cursor: pointer;" @click="showDetail(scope.row.pro_box.box_id)">
                    {{scope.row.pro_amount.sku_count}}</p>
            </template>

            <template slot="send_sample_time" slot-scope="scope">
                <p>开发部：{{scope.row.send_sample_time.modify_time}}</p>
                <p>品控部：{{scope.row.send_sample_time.handle_time}}</p>
            </template>

            <template slot="send_sample_user" slot-scope="scope">
                <p>开发部：{{scope.row.send_sample_user.modify_user}}</p>
                <p>品控部：{{scope.row.send_sample_user.handle_user}}</p>
            </template>
            <template slot="remark" slot-scope="scope">
                <p v-if="scope.row.remark.remark_user">用户名：{{scope.row.remark.remark_user}}
                    备注信息：{{scope.row.remark.remark}}</p>
                <p v-if="scope.row.remark.qc_remark">用户名：{{scope.row.remark.qc_remark_user}}
                    备注信息：{{scope.row.remark.qc_remark}}</p>
            </template>


            <!--商品SPU-->
            <template slot="trolley_num" slot-scope="scope">
                <span>{{scope.row.trolley_num.trolley_code}}</span>
            </template>

            <template slot="pro_spu" slot-scope="scope">
                <span>{{scope.row.pro_spu.spu}}</span>
            </template>

            <template slot="pro_name" slot-scope="scope">
                <span>{{scope.row.pro_name.category_name}}</span>
            </template>

            <template slot="bind_sku" slot-scope="scope">
                <span>{{scope.row.bind_sku.sku}}</span>
            </template>


        </ComTableInfo>

        <ComDialogRemarks :visible.sync="isShowAssign" title="指派任务" type="select" formLabel="指派任务"
                          :option="optionList.workerList"
                          @confirm="handleConfirmAssign"></ComDialogRemarks>
        <ComDialogRemarks :visible.sync="isShowPriority" title="优先级" type="select" formLabel="优先级"
                          :option="optionList.priorityList"
                          @confirm="handleConfirmPriority"></ComDialogRemarks>
    </div>
</template>

<script>

import {
    mapGetters
} from 'vuex';

import {
    getSampleInspectListInfo,
    handleTask
} from '@/api/quality_control_department/sample_quality_control/sample_inspect_list';

export default {
    data () {
        return {
            // 搜索缩进
            showSearch: true,

            queryData: {
                trolley_code: '',
                box_code: '',
                category_id: '',
                createTime: [],
                priority: '',
                paymentMode: '',
                spu: '',
                product_name: '',
                send_user: '',
                list_type: '1',
                productList: []
            },
            optionList: {
                priorityList: {},
                sendUserList: {},
                moduleTypeList: [
                    {
                        label: '台车&箱子',
                        value: '1'
                    },
                    {
                        label: '台车&SPU',
                        value: '2'
                    },
                    {
                        label: 'SPU',
                        value: '3'
                    }
                ],
                workerList: {
                    '1001': '张三',
                    '1002': '李四'
                },
                productList: []
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //指派任务弹窗
            isShowAssign: false,

            //优先级弹窗
            isShowPriority: false,

            //是否是切换tab
            isTabClick: false,

        };
    },
    computed: {
        ...mapGetters([
            'queryParamsInfo',
            'loginInfo'
        ]),
    },
    methods: {
        //选中时 同一台车的需要全部选中
        handleSelectAbout (obj) {
            this.tableData.forEach((item, index) => {
                if (parseInt(this.queryData.list_type) === 1 && obj.data.pro_trolley.trolley_code) {
                    if (obj.data.pro_trolley.trolley_code === item.pro_trolley.trolley_code && obj.data.num.num !== item.num.num) {
                        obj.refObj.toggleRowSelection(this.tableData[index]);
                    }
                } else if (obj.data.trolley_num.trolley_code) {
                    if (obj.data.trolley_num.trolley_code === item.trolley_num.trolley_code && obj.data.num.num !== item.num.num) {
                        obj.refObj.toggleRowSelection(this.tableData[index]);
                    }
                }
            });
        },

        // 列表信息查询
        handleQueryInfo () {
            let obj = {
                department: 2,
                trolley_code: this.queryData.trolley_code,
                box_code: this.queryData.box_code,
                category_id: this.queryData.productList.join('->'),
                start_time: this.queryData.createTime[0],
                end_time: this.queryData.createTime[1],
                priority: this.queryData.priority,
                spu: this.queryData.spu,
                product_name: this.queryData.product_name,
                send_user: this.queryData.send_user,
                list_type: this.queryData.list_type,
            };
            console.log(obj, 'obj11');
            getSampleInspectListInfo(obj).then(({data}) => {

                this.handleQueryInfoCallBack(data);

            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                trolley_code: '',
                box_code: '',
                category_id: '',
                createTime: [],
                priority: '',
                paymentMode: '',
                spu: '',
                product_name: '',
                send_user: '',
                list_type: this.queryData.list_type
            };
            this.handleQueryInfo();
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.list_type = val;
            this.isTabClick = true;
            this.handleQueryInfo();
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'sample_inspect_list',
                value: JSON.stringify(this.queryData)
            });

            // 渲染下拉
            console.log(data, 'data');
            this.optionList.priorityList = data.select_list.priority;
            this.optionList.sendUserList = data.select_list.send_user;
            this.optionList.workerList = data.select_list.qc_user;
            this.optionList.productList = [...data.select_list.category_list.data];

            //数据渲染

            // let type = 'trolley_box';
            //
            // //是否是切换tab时,默认优先box，根据查询出的展示box/spu
            // if (this.isTabClick) {
            //     if (parseInt(this.queryData.list_type) === 2) {
            //         type = 'trolley_spu';
            //     }
            //     this.isTabClick = false;
            // } else if (data.data['trolley_box']) {
            //     this.$set(this.queryData, 'list_type', '1');
            //
            // } else if (data.data['trolley_spu']) {
            //     this.$set(this.queryData, 'list_type', '2');
            //     type = 'trolley_spu';
            //
            // }

            if (!data.data.value) {
                this.tableData = [];
                return;
            }

            this.tableData = data.data.value;
            console.log(this.tableData, 99);
            this.tableProps = Object.keys(data.data.key);
            this.tableKey = Object.values(data.data.key);

        },

        //是否勾选
        checkedStatus (obj) {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this[obj] = true;
        },

        //获取选中的 box_id 或　spu
        getIdByListType () {
            let ids = {
                box_id: [],
                spu: []
            };
            this.saveCheckInfo.forEach((item) => {
                if (parseInt(this.queryData.list_type) === 1) {
                    ids.box_id.push(item.pro_box.box_id);
                } else {
                    ids.spu.push(item.pro_spu.spu);
                }
            });
            ids.box_id = ids.box_id.join(',');
            ids.spu = ids.spu.join(',');
            return ids;
        },

        //回调
        handleTaskCallBack (data) {

            if (data.status === 1) {
                this.$message.success('操作成功');
                this.isTabClick = true;
                this.handleQueryInfo();
            } else {
                this.$message.error(data.errorMess);
            }
        },
        //指派任务
        handleClickAssign () {
            this.checkedStatus('isShowAssign');
        },

        //确认指派
        handleConfirmAssign (val) {
            let ids = this.getIdByListType();
            let obj = {
                list_type: this.queryData.list_type,
                task_type: 2,
                receiver: val.check,   //任务接收人
                designate_user: this.loginInfo.uid,  //指派人
                ...ids
            };

            handleTask(obj).then(({data}) => {
                this.handleTaskCallBack(data);
            });
        },

        // 优先级
        handleClickPriority () {
            this.checkedStatus('isShowPriority');
        },

        // 确认优先级
        handleConfirmPriority (val) {
            let ids = this.getIdByListType();
            let obj = {
                list_type: this.queryData.list_type,
                task_type: 3,
                priority: val.check,
                ...ids
            };
            console.log(obj, 111);
            handleTask(obj).then(({data}) => {
                console.log(data, 55);
                this.handleTaskCallBack(data);
            });
        },

        // 领取任务
        handleClickTask () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            let ids = this.getIdByListType();

            this.$confirm('确认领取？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    list_type: this.queryData.list_type,
                    task_type: 1,
                    ...ids
                };
                handleTask(obj).then(({data}) => {
                    this.handleTaskCallBack(data);
                });
            });
        },

    },
    created () {
        try {
            if (this.queryParamsInfo['sample_inspect_list']) {
                this.queryData = JSON.parse(this.queryParamsInfo['sample_inspect_list']);
            }
            this.handleQueryInfo();

        } catch (e) {
            console.log(e);
        }

        this.tableWidth = ['90px', '90px'];
        this.tableWidth[7] = '220px';
        this.tableWidth[10] = '300px';
    },
};
</script>

<style lang="less" scoped>

@upload-height: 185px;
@border-color: #aaa;

.ui-table--supplier_code {
    font-size: 14px;
    font-weight: bold;
    line-height: 14px;

    p {
        margin-bottom: 10px;
    }
}

/deep/ .el-table td, /deep/ .el-table th {
    overflow: hidden;
    text-align: left !important;
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    background: #63c4e9;
    position: absolute;
    font-size: 12px;
    color: #FFFFFF;
    top: -12px;
    right: -20px;
}

.ui-table--icon_btn {
    i {
        cursor: pointer;
    }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: @upload-height;
        }

        .ui-com_edit__upload--last_btn {
            border: 1px dashed @border-color;
            width: 100%;
            height: @upload-height;

            /deep/ .el-upload {
                display: block;
                height: @upload-height;
                line-height: @upload-height;

                span {
                    color: @border-color;
                }
            }
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: @upload-height;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}
</style>



