<template>
    <TemplateDefault color="#fff">
        <div class="ui-batch_create">
            <div class="ui-batch_create--fn">
                <ComUpload label="选择文件" :url="importUrl" @success="handleUploadSuccess" :upData="{ user_id: loginInfo.staff_code || '12534' }"></ComUpload>
                <el-button size="small" type="text" @click="handleDownloadModule(1)">下载SPU模板</el-button>
                <el-button size="small" type="text" @click="handleDownloadModule(2)">下载1688采集模板</el-button>
                <el-button style="float: right" size="small" type="primary" @click="handleConfirmUpload">确认上传</el-button>
            </div>
            <ComTableInfo
                    :tableKey="tableKey"
                    :tableData="tableData"
                    :tableProps="tableProps"
                    :otherHeight="['ui-batch_create--fn']"></ComTableInfo>
        </div>
    </TemplateDefault>
</template>

<script>
    import { mapGetters } from 'vuex';
    import { removeLabel } from '@/services'
    import * as importApi from '@/api/import_api';
    import * as downloadApi from '@/api/download_api';
    import { confirmBatchUploadInfo } from '../../../../../api/dev_department/product_development/product_datum_create';
    export default {
        name: "BatchCreatePage",
        data() {
            return {
                tableKey: [],
                tableData: [],
                tableProps: [],
                saveCheckInfo: []
            }
        },
        computed: {
            ...mapGetters(['loginInfo', 'productBatchCreateInfo']),
            importUrl() {
                return importApi['IMPORT_BATCH_PRODUCT_INFO'];
            },
            downloadUrl() {
                return downloadApi['DOWNLOAD_PRODUCT_CREATE'];
            }
        },
        created() {
            if (JSON.stringify(this.productBatchCreateInfo) !== '{}') {
                this.handleUploadInfoCallBack(this.productBatchCreateInfo);
            }
        },
        methods: {

            // 保存勾选信息
            handleSaveCheckInfo(val) {
                this.saveCheckInfo = val;
            },

            // 上传成功
            handleUploadSuccess(data) {
                this.handleUploadInfoCallBack(data);
                this.$store.commit('SAVE_BATCH_CREATE_PRODUCT_INFO', data);
            },

            // 下载导入模板
            handleDownloadModule() {
                window.open(this.downloadUrl, '_self');
            },

            // 上传回调
            handleUploadInfoCallBack(data) {
                this.tableData = data.data.value.right;
                this.tableKey = data.data.key;
                this.tableProps = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L', 'M', 'N', 'O', 'P', 'Q', 'R', 'S'];
            },

            // 确认上传
            handleConfirmUpload() {

                let codes = [];
                this.tableProps.forEach(({ credit_code }) => {
                    codes.push(credit_code);
                });

                let obj = {
                    credit_codes: codes.join(','),
                };

                confirmBatchUploadInfo(obj).then(({ data }) => {
                    if (data.status) {
                        this.$message.success('操作成功！');
                        removeLabel(this, {
                            path: '/batch_create_product_datum',
                            main: '/product_datum_create',
                            refresh: true
                        })
                    } else {
                        if (data.errorMess && data.errorMess.length) {

                            let errorStr = '';
                            data.errorMess.forEach(value => {
                                errorStr += `<div>${value}</div>`
                            });

                            this.$alert(errorStr, '错误原因', {
                                customClass: 'ui-error-list',
                                dangerouslyUseHTMLString: true
                            });
                        } else {
                            this.$message.error(data.error_mess || '操作失败!');
                        }
                    }
                })
            }
        }
    }
</script>

<style scoped lang="less">
    .el-button--small {
        padding: 9px;
    }
    .ui-batch_create {
        padding: 0 20px;
        .ui-batch_create--fn {
            text-align: left;
            margin: 10px 0;
        }
        .el-button:nth-child(2),.el-button:nth-child(3) {
            color: #3388ff;
            border-bottom: 1px #3388ff solid;
            border-radius: 0;
            padding-bottom: 5px;
        }
    }

    .ui-error-list {

        /deep/ .el-message-box__message {
            max-height: 400px;

            /deep/ p {
                color: #FF3D46;
            }
        }
    }
</style>


