<script>
import { getNumByUnit, getNumByMeasure } from '@/services';
import {
    getCopyEditInfo,
    copyEditTranslate,
    submitCopyInfo,
    getCopyKeyWords
} from '@/api/copy_department/copy_edit_module/copy_edit_detail';

export default {
    name: 'CopyEditPage',
    props: {
        show: false,
        spu: ''
    },
    data () {
        return {

            //语言
            checkLanguage: 'CN',
            leftSelect: '',
            languageType: {},

            leftLanguage: 'CN',

            languageOptions: {
                'CN': {
                    spu_title: ''
                }
            },
            spuImage: [], //买点图

            isGoodsBasic: true,
            isSupplierGoods: true,
            unitData: {
                weight1: ['g', 'kg', 'mg'],
                weight2: ['lb', 'oz'],
                checkWeight1: 'g',
                checkWeight2: 'lb',
                valueWeight1: '',
                valueWeight2: ''
            },
            measureData: {
                measure1: ['cm', 'mm', 'm', 'km'],
                measure2: ['in', 'ft', 'yd'],
                checkMeasure1: 'cm',
                checkMeasure2: 'in',
                valueMeasure1: '',
                valueMeasure2: ''
            },
            hotWords: []

        };
    },
    watch: {
        spu (val) {
            console.log(val, 'watch');
            if (val) {
                getCopyEditInfo({spu: val}).then(({data}) => {
                    console.log(data, 'data');
                    if (data.status) {
                        this.languageOptions = data.result.copywriters_data;

                        console.log(this.languageOptions, 123123);
                        this.spuImage = data.result.spu_image_data;

                        console.log(this.spuImage, 'right');
                        this.handleTranslate('en');

                        let langVal = Object.values(this.languageOptions);
                        let langKey = Object.keys(this.languageOptions);

                        // let typeKey = Object.keys(this.languageType);
                        //
                        // typeKey.forEach((item) => {
                        //
                        //     let idx = langKey.findIndex((type) => {
                        //         return item == type;
                        //     });
                        //     if (idx < 0) {
                        //         delete this.languageType[item];
                        //     }
                        // });
                        // console.log(this.languageType, 33);
                    }
                });
            }
        }
    },
    methods: {

        checkWords (text, index) {
            this.$set(this.languageOptions[this.checkLanguage].key_words, index, text);
        },
        //关键词推荐
        handleKeyWords (keyword) {

            getCopyKeyWords({keyword}).then(({data}) => {
                console.log(data, 111);
                if (data.status) {
                    this.hotWords = data.result[0].hotList;
                } else {
                    this.hotWords = [];
                }
            });

        },

        // 添加input
        handleAddInput (type, key, index) {
            this.languageOptions[type][key].splice(index, 0, key === 'key_words' ? [] : '');
        },

        // 删除input
        handleDeleteInput (type, key, index, length = 1) {
            this.languageOptions[type][key].splice(index, 1);
        },

        //保存
        saveForm (type) {
            let formData = new FormData();
            formData.append('copywriters_data', JSON.stringify(this.languageOptions));
            formData.append('spu_image_data', JSON.stringify(this.spuImage));
            formData.append('spu', this.spu);
            formData.append('save_type', type);

            submitCopyInfo(formData).then(({data}) => {
                if (data.status) {
                    this.$message.success('保存成功!');
                } else {
                    this.$message.error(data.errorMess || '操作失败!');
                }
            });
        },
        //翻译相应语言
        handleTranslate (id) {

            //如果存在 不需要掉接口
            if (this.languageOptions[id]) {
                this.checkLanguage = id;
                this.leftSelect = id;
                return;
            }

            let formData = new FormData();
            formData.append('copywriters_data', JSON.stringify(this.languageOptions.CN));
            formData.append('language', id);

            if (id === 'en') {
                formData.append('spu_image_data', JSON.stringify(this.spuImage));
            }

            copyEditTranslate(formData).then(({data}) => {
                this.languageOptions[id] = data.result[id];
                if (data.result.spu_image_data) {
                    console.log('okk');
                    this.spuImage = data.result.spu_image_data;
                }

                this.checkLanguage = id;
                this.leftSelect = id;
            });
        },
        //转换工具
        searchMeasure () {
            let data = this.measureData;
            if (data.valueMeasure1 && !isNaN(data.valueMeasure1)) {
                this.measureData.valueMeasure2 = getNumByMeasure(parseFloat(data.valueMeasure1), data.checkMeasure1, data.checkMeasure2);
            } else {
                this.measureData.valueMeasure2 = '';
            }
        },
        searchWeight1 () {
            let data = this.unitData;
            if (data.valueWeight1 && !isNaN(data.valueWeight1)) {
                this.unitData.valueWeight2 = getNumByUnit(parseFloat(data.valueWeight1), data.checkWeight1, data.checkWeight2);
            } else {
                this.unitData.valueWeight2 = '';
            }
        },

        hideSupplierGoods () {
            this.isSupplierGoods = false;
            this.isGoodsBasic = true;
        },
        //隐藏商品基础资料
        hideGoodsBasic () {
            console.log('a');
            this.isGoodsBasic = false;
            this.isSupplierGoods = true;
        },
        handleClosePage () {
            this.$emit('update:show', false);
        }
    },
    mounted () {
        let _this = this;
        setTimeout(() => {
            console.log(_this.$parent.languageList, 88);
            let list = _this.$parent.languageList;
            this.languageType = list;
            delete this.languageType.CN;
        }, 1000);
    }
};
</script>

