                    .form-msg {
                        border: 1px solid @border-color;
                        border-radius: 4px;
                        padding: 10px;

                        .attr-set {
                            display: flex;

                            .space {
                                margin: 0px 5px 0px 10px;
                                display: inline-block;
                            }

                            > li:first-child {
                                .space {
                                    margin-left: 0;
                                }
                            }
                        }

                        .key-words {
                            .attr-set {
                                justify-content: space-between;

                                li {
                                    display: flex;

                                    .el-input {
                                        flex: 1;
                                    }
                                }

                            }
                        }
                    }
                }
            }

            .tools-box {
                /deep/ .el-input {
                    width: 100%;
                }

                .unit-tools {
                    display: flex;
                    margin-bottom: 10px;

                    .unit-item {
                        flex: 1;
                        display: flex;
                        align-items: center;
                        margin-right: 50px;

                        .title {
                            width: 100px;
                            line-height: 40px;
                            text-align: right;
                            padding: 0 12px 0 0;
                            font-size: 14px;
                        }

                        > .el-input-group {
                            flex: 1;
                        }

                        .exchange-icon {
                            width: 20px;
                            height: 20px;
                            border-radius: 100%;
                            background-color: #4681e6;
                            color: white;
                            line-height: 20px;
                            text-align: center;
                            margin: 0 20px;
                            transform: rotate(180deg);
                        }
                    }
                }
            }

            .bottom-box {
                text-align: center;
                border-top: 1px solid #e6e6e6;

                button {
                    margin: 25px;
                }

                .orange-btn {
                    border: 1px solid #fe9a33;
                    color: #fe9a33;
                    background: transparent;
                }
            }

        }


    }

    //左边展开
    .ui-left-unfold {
        padding-right: 0;

        display: flex;

        .toggle-box {
            width: 50px;
            font-size: 16px;
            color: @active-color;
            font-weight: bold;
            text-align: center;
            padding-right: 20px;
            background-color: #f3f3f3;
            cursor: pointer;
            position: relative;
            z-index: 9;

        }
    }


    .ui-right {
        right: 0;
        top: 45px;
        width: 420px;
        position: absolute;
        height: 100%;
        z-index: 9;
        border-left: 1px solid @border-color;

        transition: width 0.3s;

        .content-box {
            .goods-box {
                padding: 10px;

                ul {
                    display: flex;
                    flex-direction: column;

                    li:last-child {
                        margin-bottom: 0;
                    }

                    li {
                        display: flex;
                        border: 1px solid @border-color;
                        margin-bottom: 10px;

                        img {
                            background-color: #f0f0f0;
                            width: 130px;
                            height: 130px;
                        }

                        .goods-text {
                            flex: 1;

                            p {
                                padding: 5px;
                                padding-bottom: 0;
                                word-break: break-all;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                display: -webkit-box;
                                -webkit-box-orient: vertical;
                            }

                            .chinese-text {
                                -webkit-line-clamp: 3;
                            }

                            .foreign-text {
                                -webkit-line-clamp: 4;
                            }

                            .line-solid {
                                border-bottom: 1px solid @border-color;
                                display: block;
                                margin-top: 5px;
                            }
                        }

                    }
                }
            }
        }
    }

    //右边全部展开的样式
    .ui-right-unfold {
        width: 100%;
        display: flex;

        .toggle-box {
            width: 50px;
            font-size: 16px;
            color: @active-color;
            font-weight: bold;
            text-align: center;
            padding-left: 20px;
            background-color: #f3f3f3;
            cursor: pointer;
            position: relative;
            z-index: 9;

        }

        .goods-box {
            ul {
                flex-direction: row !important;
                flex-wrap: wrap;

                li {
                    margin-bottom: 10px !important;
                    width: 49%;
                }

                li:nth-child(2n-1) {
                    margin-right: 10px !important;
                }


            }
        }
    }

    .ui-right-hide {
        width: 0;
    }

    .ui-left, .ui-right {

        box-sizing: border-box;
    }

    /deep/ .el-checkbox {
        margin-right: 20px;
        margin-left: 0;
    }
}

/deep/ .el-collapse {
    border: 0 none;
    overflow: hidden;
}

/deep/ .el-collapse-item {
    .el-collapse-item__header {
        background: #f3f3f3;
        height: 30px;
        line-height: 30px;
        text-align: left;
        padding-left: 10px;
        color: #5a8dfd;

        .el-collapse-item__arrow {
            line-height: 30px;
        }
    }

    .el-collapse-item__wrap {
        border: 0 none;
    }

    .el-collapse-item__content {
        padding: 10px;
        box-sizing: border-box;
    }
}

