<template>
    <div class="ui-main-module">
        <div class="ui-search-area" v-if="showSearch">
            <ul class="ui-main-module-search clearFix">
                <li>
                    <SearchRequirement label="图片类型">
                        <el-select v-model="queryData.imgType" placeholder="请选择" size="small">
                            <el-option
                                    v-for="(label, value) in optionList.imgTypeList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                        </el-select>
                    </SearchRequirement>
                </li>
                <li>
                    <!--<el-button size="small" type="primary" @click="isUpLoadShow=true">点击上传</el-button>-->
                    <ComUpload :isDisabled="!queryData.imgType" :url="uploadUrl" label="点击上传" @success="handleQueryInfoCallBack" :upData="upLoadParams"></ComUpload>
                </li>
                <li>
                    <el-button type="primary" size="small" @click="handleConfirmUpload">确认上传</el-button>
                </li>
            </ul>
        </div>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
        </div>
        <ComTableInfo
                isCheck
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableData="tableData"
                :tableWidth="tableWidth"
                :showSearch="showSearch"
                @checked="handleSaveCheckInfo">
            <template slot="image_type" slot-scope="scope">
                <span>{{optionList.imgTypeList[scope.row.image_type]}}</span>
            </template>
            <template slot="url" slot-scope="scope">
                <div class="ui-table__img">
                    <img :src="scope.row.url" alt="" @click="handlePreview(scope.row)">
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <PreviewImage :show.sync="isPreviewShow" :row="previewInfo" :ope="true"></PreviewImage>
    </div>
</template>

<script>
    import config from '@/assets/js/config';
    import {
        autoAddTableInx,
        getDefaultTime,
        changeTableInfoProps,
        goToNewPage
    } from '@/services/index';
    import {
        mapActions,
        mapGetters
    } from 'vuex';
    import * as importApi from '@/api/import_api';
    import {
        getTypeListOption,
        confrimUploadInfo,
        previewSPUImage
    } from '@/api/design_department/artist_manage/art_picture_upload';
    import PreviewImage from './PreviewImage';

    export default {
        components: {
            PreviewImage
        },
        data() {
            return {

                // 时间搜索框宽度设置
                searchDateWidth: config.SEARCH_DATE_WIDTH,

                thisVm: {},

                // 搜索缩进
                showSearch: true,

                queryData: {
                    imgType: ''
                },

                optionList: {
                    imgTypeList: {}
                },

                tableData: [],
                tableKey: [],
                tableWidth: [],
                tableProps: [],

                // 保存勾选数据
                saveCheckInfo: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                // 编辑
                isEditShow: false,
                formLabelWidth: '110px',
                saveCheckId: '',
                editData: {
                    comCode: '',
                    supplierName: '',
                    applyAddress: '',
                    applyDate: '',
                    companyType: '',
                    supplierLink: '',
                    pAndCity: [],
                    area: '',
                    contacts: '',
                    phoneNumber: '',
                    contactsPhone: '',
                    wxNumber: '',
                    qqNumber: '',
                    email: '',
                    paymentType: '',
                    settlementType: '',
                    point: ''
                },

                // 预览图片
                isShowLookImage: false,
                lookImageUrl: [],

                // 上传列表
                saveFileList: [],

                // 预览
                isPreviewShow: false,
                previewInfo: {}
            };
        },
        computed: {
            ...mapGetters([
                'queryParamsInfo',
                'atrUploadInfo',
                'loginInfo'
            ]),
            uploadUrl() {
                return importApi['IMPORT_ART_UPLOAD_INFO']
            },
            upLoadParams() {
                let obj = {
                    user_id: this.loginInfo.staff_code,
                    image_type: this.queryData.imgType
                };

                return obj;
            }
        },
        methods: {
            autoAddTableInx,
            getDefaultTime,
            goToNewPage,
            // 列表信息查询
            handleQueryInfo(params = {}, isFn) {
                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['atr_upload']);
                }

                let obj = {
                    status: this.queryData.state,
                    supplier_code: this.queryData.supplierCode,
                    type: this.queryData.supplierType,
                    shipping_province: this.queryData.deliverAddress[0],
                    shipping_city: this.queryData.deliverAddress[1],
                    settle_method: this.queryData.settlementType,
                    create_user: this.queryData.createUser,
                    devlop_user: this.queryData.devUser,
                    supplier_name: this.queryData.supplierName,
                    contacts: this.queryData.liaisons,
                    pay_method: this.queryData.paymentMode,
                    start_time: this.queryData.createTime[0],
                    end_time: this.queryData.createTime[1],
                    ...params
                };
                this.GetPhotographyGoodsListInfo(obj);
                // this.GetphotographyGoodsListInfo(obj).then(({data}) => {
                //
                //     // 导出
                //     if (data.status === 2) return
                //
                //     this.handleQueryInfoCallBack(data)
                // }).catch(err => {
                //     console.log(err)
                // })
            },

            // 分页条数切换
            handleSizeChange(val) {
                this.handleQueryInfo({
                    limit: val,
                    offset: 1
                }, true);
            },

            // 跳转页数切换
            handleCurrentChange(val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true);
            },

            // 保存勾选数据
            handleSaveCheckInfo(val) {
                this.saveCheckInfo = val;
            },

            // 保存上传文件
            handleSaveFileList(file, fileList) {
                console.log(fileList);
                this.saveFileList = [...fileList];
            },

            // 确认上传
            handleConfirmUpload() {
                if (!this.saveCheckInfo.length) {
                    this.$message.warning('请勾选数据！');
                    return;
                }

                let ids = [];
                this.saveCheckInfo.forEach(value => {
                    ids.push(value.spu)
                });

                confrimUploadInfo({
                    spus: ids.join(',')
                }).then(({ data }) => {
                    if (data.status) {
                        this.$message.success('操作成功！');
                        this.tableData = data.list;
                    } else {
                        this.$message.error(data.errorMess || '操作失败');
                    }
                })
            },

            // 预览
            handlePreview(row) {
                previewSPUImage({
                    spu: row.spu,
                    image_type: row.image_type
                }).then(({ data }) => {
                    this.previewInfo = {
                        ...data,
                        imgTypeList: this.optionList.imgTypeList,
                        imgType: row.image_type
                    };
                    this.isPreviewShow = true;
                })
            },

            // 预览图片
            handleLookImage(src) {
                this.isShowLookImage = true;
                this.lookImageUrl = [src];
            },

            //查询回调
            handleQueryInfoCallBack(data) {

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'atr_upload',
                    value: JSON.stringify(this.queryData)
                });

                //数据渲染
                this.tableData = data.list.list;
                this.tableKey = ['序号', 'SPU', '缩略图', '图片类型', '上传时间', '修改记录', '状态', '操作日志'];
                // this.tableWidth = ['', '', '', '', '220px', '220px'];

                // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
                this.tableProps = ['index', 'spu', 'url', 'image_type', 'upload_time', 'modify_time', 'operate_dev_user', 'file_url', 'state', 'reject_remarks'];

                //渲染分页
                // this.pageData.currentList = data.page.limit * 1;
                // this.pageData.currentPage = data.page.offset * 1;
                // this.pageData.currentTotal = data.page.total * 1;

                this.$store.commit('SAVE_ATR_UPLOAD_INFO', {
                    ...data,
                    select_list: this.optionList.imgTypeList
                });
            }
        },
        created() {
            this.thisVm = this;
            console.log(this.atrUploadInfo);
            if (JSON.stringify(this.atrUploadInfo) === '{}') {
                getTypeListOption().then(({ data }) => {
                    this.optionList.imgTypeList = data.list;
                })
            } else {
                this.queryData = JSON.parse(this.queryParamsInfo['atr_upload']);
                this.optionList.imgTypeList = this.atrUploadInfo.select_list;
                this.tableData = this.atrUploadInfo.list.list;
                this.tableKey = ['序号', 'SPU', '缩略图', '图片类型', '上传时间', '修改记录', '状态', '操作日志'];
                this.tableProps = ['index', 'spu', 'url', 'image_type', 'upload_time', 'modify_time', 'operate_dev_user', 'file_url', 'state', 'reject_remarks'];
            }

            // try {
            //     if (JSON.stringify(this.photographyGoodsListInfo) === '{}') {
            //         this.handleQueryInfo({is_get_base: 1});
            //     } else {
            //         // 获取保存的查询参数
            //         this.queryData = JSON.parse(this.queryParamsInfo['photography_goods_list']);
            //         this.handleQueryInfoCallBack(this.photographyGoodsListInfo);
            //     }
            // } catch (e) {
            //     console.log(e);
            // }
        },
    };
</script>

<style lang="less" scoped>
    @upload-height: 185px;
    @border-color: #aaa;

    .ui-table--supplier_code {
        font-size: 14px;
        font-weight: bold;
        line-height: 14px;

        p {
            margin-bottom: 10px;
        }
    }
    .ui-table__img img {
        max-width: 100px;
        max-height: 120px;
    }

    /deep/ .el-table td, /deep/ .el-table th {
        overflow: hidden;
        text-align: left !important;
    }

    .ui-table--supplier_code--flag {
        width: 50px;
        height: 35px;
        line-height: 54px;
        text-align: center;
        -webkit-transform: rotateZ(45deg);
        transform: rotateZ(45deg);
        background: #63c4e9;
        position: absolute;
        font-size: 12px;
        color: #FFFFFF;
        top: -12px;
        right: -20px;
    }

    .ui-table--icon_btn {
        i {
            cursor: pointer;
        }
    }

    .ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
        li {
            width: 130px;
            /*height: 185px;*/
            margin-right: 20px;
            text-align: center;
            position: relative;

            img {
                width: 100%;
                height: @upload-height;
            }

            .ui-com_edit__upload--last_btn {
                border: 1px dashed @border-color;
                width: 100%;
                height: @upload-height;

                /deep/ .el-upload {
                    display: block;
                    height: @upload-height;
                    line-height: @upload-height;

                    span {
                        color: @border-color;
                    }
                }
            }

            .ui-com_edit__upload--item--mask {

                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: @upload-height;
                background: rgba(0, 0, 0, .3);
                text-align: center;
                line-height: 185px;

                & > div {
                    display: inline-block;
                }

                span {
                    margin: 0 5px;
                    cursor: pointer;
                    color: #cccccc;
                    font-size: 20px;

                    &:hover {
                        color: #FFFFFF;
                    }
                }
            }

            &:hover {
                .ui-com_edit__upload--item--mask {
                    display: block;
                }
            }
        }
    }
</style>



