/**  --->>>>   |^ 创建供应商列表^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取创建供应商信息
export function getSupplierCreateListInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/supplier_export' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/supplier/supplier/supplier_search', data);
    }
}

// 确认批量上传
export function confirmBatchUploadInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/supplier/supplier/sup_data_upload', data);
}

// 修改状态
export function changeSupplierState(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/supplier/supplier/handle_sup_status', data);
}

// 获取编辑信息
export function getEditInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.getAjax('/supplier/supplier/edit_supplier_list', data);
}

// 保存编辑信息
export function saveEditInfo(params) {
    params.append('request_type', 1);
    return http.fileAjax('/supplier/supplier/edit_supplier', params);
}

// 保存或提交新增信息
export function saveOrSubmitAddInfo(params) {
    params.append('request_type', 1);
    return http.fileAjax('/supplier/supplier_found/foundSupplier', params);
}

// 获取操作日志信息
export function getOperationLogInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.getAjax('/supplier/supplier/supplier_log_list', data);
}

// 确认上传附件
export function confirmUploadFile(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/supplier/supplierAttach/uploadConfim', data);
}



