<template>
    <el-dialog
            :title="title"
            top="10vh" width="400px" custom-class="ui-com_edit"
            :visible="show" @close="handleCancel">
        <el-form ref="firstEdit" :model="firstEdit" :rules="firstRule" size="small">
            <span class="ui-title">修改前</span>
            <el-form-item label="一级小组" :label-width="labelWidth">
                <el-input disabled v-model="firstEdit.oldGroupName"></el-input>
            </el-form-item>
            <el-form-item label="小组经理" :label-width="labelWidth">
                <el-select disabled v-model="firstEdit.oldManage">
                    <el-option
                            v-for="item in optionList.groupManagerList"
                            :key="item.userNumber"
                            :label="item.userName"
                            :value="item.userNumber"></el-option>
                </el-select>
            </el-form-item>
            <span class="ui-title">修改后</span>
            <el-form-item prop="groupName" label="一级小组" :label-width="labelWidth">
                <el-input v-model="firstEdit.groupName"></el-input>
            </el-form-item>
            <el-form-item prop="manage" label="小组经理" :label-width="labelWidth">
                <el-select v-model="firstEdit.manage">
                    <el-option
                            v-for="item in optionList.groupManagerList"
                            :key="item.userNumber"
                            :label="item.userName"
                            :value="item.userNumber"></el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div slot="footer" class="ui-com_edit--btn">
            <el-button size="small" @click="handleCancel">取消</el-button>
            <el-button size="small" type="primary" @click="handleConfirm">确认</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import CONFIG from '@/assets/js/config';
    export default {
        name: "FirstGroupEdit",
        props: {
            title: {
                type: String,
                default: '编辑一级小组'
            },
            show: {
                type: Boolean,
                default: false
            },
            optionList: {
                type: Object,
                default() {
                    return {}
                }
            },
            defaultVal: {
                type: Object,
                default() {
                    return {}
                }
            }
        },
        data() {
            return {
                labelWidth: '70px',
                firstEdit: {
                    oldGroupName: '',
                    oldManage: '',
                    groupName: '',
                    manage: '',
                },
                firstRule: {
                    groupName: CONFIG.RULE_CONFIG.requiredBlur,
                    oldGroupName: CONFIG.RULE_CONFIG.requiredBlur,
                    manage: CONFIG.RULE_CONFIG.requiredChange,
                    oldManage: CONFIG.RULE_CONFIG.requiredChange,
                }
            }
        },
        methods: {
            handleCancel() {
                this.$refs.firstEdit.resetFields();
                this.$emit('update:show', false);
            },
            handleConfirm() {
                this.$refs.firstEdit.validate(res => {
                    if (res) {
                        let obj = {
                            manage: this.firstEdit.manage,
                            groupName: this.firstEdit.groupName,
                            id: this.firstEdit.oldGroupName
                        };
                        this.$emit('confirm', obj);
                    }
                })
            }
        }
    };
</script>

<style scoped lang="less">
    .ui-title {

    }
</style>


