/**  =========================> *|`| % 样品跟单记录 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getSampleTrackRecordInfo,
} from '../../../../api/sample_track_manage/sample_track_module/sample_track_record';

const config = {
    state: {
        sampleTrackRecordInfo: {}
    },
    mutations: {
        [types.SAVE_SAMPLE_TRACK_RECORD_INFO](state, saveInfo) {
            state.sampleTrackRecordInfo = saveInfo;
        }
    },
    actions: {
        // 获取样品跟单记录信息
        GetSampleTrackRecordInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSampleTrackRecordInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SAMPLE_TRACK_RECORD_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




