    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品目录" template="cascader"
                                           :options="optionList.productListList"
                                           :props="{ value: 'id', children: 'son_id', label: 'category_name' }"
                                           v-model="queryData.productList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="作业组员">
                            <el-input size="small" v-model="queryData.workUser"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="作业节点">
                            <el-select size="small" v-model="queryData.functionInfo">
                                <el-option
                                    v-for="(value, label) in optionList.functionInfoList"
                                    :key="value"
                                    :label="label"
                                    :value="value">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核状态">
                            <el-select :disabled="!queryData.functionInfo" size="small" v-model="queryData.auditState">
                                <el-option
                                    v-for="(value, label) in optionList.statusList"
                                    :key="value"
                                    :label="label"
                                    :value="value">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作用时">
                            <el-select :disabled="!queryData.functionInfo" size="small" v-model="queryData.timeType"
                                       @change="handleTimeRange(queryData.timeType)">
                                <el-option v-for="(label,value) in optionList.timeTypeList"
                                           :label="label"
                                           :value="value"
                                           :key="label">
                                </el-option>
                            </el-select>
                            <el-slider
                                v-if="queryData.timeType"
                                v-model="queryData.timeRange"
                                range
                                :min="1"
                                :max="queryData.timeMax">
                            </el-slider>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员">
                            <el-input size="small" :disabled="!queryData.functionInfo"
                                      v-model="queryData.operateUser"></el-input>
                        </SearchRequirement>
                    </li>
                    <transition name="slide-fade" @after-enter="animation()" @after-leave="animation()"
                                @before-enter="animation(1)">
                        <div v-show="filterShow">
                            <li>
                                <SearchRequirement
                                    label="操作时间"
                                    template="daterange"
                                    v-model="queryData.operateTime">
                                </SearchRequirement>
                            </li>
                        </div>
                    </transition>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})"
                               icon="icon-chaxun">查询
                    </el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="!filterShow">高级筛选
                        <i class="el-icon-caret-bottom"></i>
                    </el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="filterShow">基本筛选
                        <i class="el-icon-caret-top"></i>
                    </el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>

            <el-button @click="handleClickReject" type="text" size="small">
                <i class="icon-bohui icon-danger"></i>驳回
            </el-button>
            <el-button @click="handleClickAssign" type="text" size="small">
                <i class="icon-renwuzhipai icon-warn"></i>重新指派
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            ref="myTable"
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :otherHeight="['ui-main-tab']"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">
            <template slot="spu" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.spu[0]}}</p>
                    <p>{{scope.row.spu[1]}}</p>
                    <div class="ui-table--supplier_code--flag">
                        <span>试卖</span>
                    </div>

                    <!--<div class="ui-table&#45;&#45;icon_btn">-->
                    <!--<i :class="`icon-bianji${scope.row.status_code === '1' || scope.row.status_code === '4' ? '' : ' ui-disable'}`"-->
                    <!--@click.stop="handleClickEdit(scope.row)"></i>-->
                    <!--</div>-->
                    <div class="ui-table--icon_btn">
                        <i class="icon-bianji" @click.stop="handleClickEdit(scope.row)"></i>
                    </div>
                </div>
            </template>
            <template slot="photo_path" slot-scope="scope">
                <div class="upload-img-list" v-if="scope.row.photo_path!==''">
                    <div class="ui-com_edit__upload--item">
                        <div class="ui-com_edit__upload--item__img"
                             :style="{background:'url('+scope.row.photo_path+') no-repeat center/contain'}">
                        </div>
                        <div class="ui-com_edit__upload--item--mask">
                            <span class="el-icon-zoom-in" @click="handleLookImage(scope.row.photo_path)"></span>
                        </div>
                    </div>
                </div>
            </template>
            <template slot="good_name" slot-scope="scope">
                <div>
                    <p><span>{{scope.row.good_name[0]}}</span></p>
                    <p><span>{{scope.row.good_name[1]}}</span></p>
                </div>
            </template>
            <template slot="category_path" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.category_path" :key="key"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="sample_type" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.sample_type" :key="key"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="good_info" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.good_info" :key="key"><span>{{item}}</span></p>
                </div>
            </template>

            <template slot="product_staus" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.product_staus" :key="key"><span>{{item}}</span></p>
                </div>
            </template>

            <template slot="opera_log" slot-scope="scope">
                <div v-for="(item, index) in scope.row.opera_log" v-if="scope.row.opera_log.length<3" :key="index">
                    {{item}}
                </div>
                <el-popover v-if="scope.row.opera_log.length>3"
                            style="cursor: pointer;"
                            placement="left"
                            trigger="hover">
                    <div v-for="(item, index) in scope.row.opera_log"
                         :key="index">{{item}}
                    </div>
                    <div slot="reference">
                        <div v-for="(item, index) in scope.row.opera_log" v-if="index<3" :key="index">{{item}}
                        </div>
                        <div v-if="scope.row.opera_log.length>3">...</div>
                    </div>
                </el-popover>
            </template>
            <template slot="opera_time" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.opera_time" :key="key"><span>{{item}}</span></p>
                </div>
            </template>

            <template slot="remarks" slot-scope="scope">
                <div v-for="(item,index) in scope.row.remarks.reject_remark" :key="index">
                    <p>驳回人:{{item.create_name}}</p>
                    <p>原因:{{item.reject_content}}</p>
                    <p>时间:{{item.create_time}}</p>
                </div>
                <div v-if="scope.row.remarks.image_remark!==''" style="width: 100px;height: 100px;"
                     :style="{background:'url('+scope.row.remarks.image_remark+') no-repeat 0/catain'}"></div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <EditPage :show.sync="isEditShow" :row="saveEditRow"></EditPage>
        <LookImage :src="previewImgUrl" :show.sync="isShowPreviewImg"></LookImage>

        <!-- 驳回弹窗 -->
        <el-dialog class="ui-layout_edit-dialog" title="驳回" :visible.sync="isShowReject" width="60%">
            <el-table :data="rejectData">
                <el-table-column label="退样类型" width="200">
                    <template slot-scope="scope">
                        <el-select v-model="scope.row.type" @change="rejectTypeChange(scope.row)">
                            <el-option v-for="item in scope.row.rejectList"
                                       :label="item.name"
                                       :value="item.id"
                                       :key="item.id">
                            </el-option>
                        </el-select>
                    </template>
                </el-table-column>
                <el-table-column label="产品异常描述" width="400">
                    <template slot-scope="scope">
                        <el-input
                            type="textarea"
                            :rows="2"
                            v-model="scope.row.value"></el-input>
                    </template>
                </el-table-column>

                <el-table-column label="是否退样" width="90">
                    <template slot-scope="scope">
                        {{scope.row.remark}}
                    </template>
                </el-table-column>
                <el-table-column label="不良图片">
                    <template slot-scope="scope">
                        <div class="upload-img-list">
                            <div class="ui-com_edit__upload--item" v-if="scope.row.image!==''">
                                <div class="ui-com_edit__upload--item__img"
                                     :style="{background:'url('+scope.row.image+') no-repeat 0/contain'}">
                                </div>
                                <div class="ui-com_edit__upload--item--mask">
                                    <span class="el-icon-delete"
                                          @click="scope.row.image=''"></span>
                                    <span class="el-icon-zoom-in" @click="handleLookImage(scope.row.image)"></span>
                                </div>
                            </div>
                            <div class="ui-com_edit__upload--last_btn" v-if="scope.row.image==''">
                                <el-upload
                                    action=""
                                    multiple
                                    :show-file-list="false"
                                    :auto-upload="false"
                                    :on-change="(file) =>{handleSaveFileList(file,scope.row)}">
                                    <span class="icon-tupianshangchuan"></span>
                                </el-upload>
                            </div>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="100">
                    <template slot-scope="scope">
                        <div class="ui-ope_area--btn">
                            <span class="icon-shanchu"
                                  v-show="rejectData.length>1"
                                  @click="handleDeleteInput(scope.$index)">
                            </span>
                            <span class="icon-zengjia1" @click="handleAddInput(scope.$index)"></span>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button @click="handleCancelReject">取 消</el-button>
                <el-button type="primary" @click="handleConfirmReject">确 定</el-button>
            </div>
        </el-dialog>

        <ComDialogRemarks
            :visible.sync="isShowAssign"
            title="指派任务"
            type="select"
            formLabel="指派任务"
            :option="optionList.workerList"
            @confirm="handleConfirmAssign">
        </ComDialogRemarks>

        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import EditPage from './EditPage';
import { getNumByTime, isValidData, makeLog } from '@/services';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    getSampleAuditListInfo,
    supplierSearchInfo,
    saveSingleCreateInfo,
    getSingleCreateSaveInfo,
    getAssignGroup,
    getRejectType,
    sendBatchReject,
    assignTask
} from '@/api/quality_control_department/sample_quality_control/sample_inspect_list';
import { upLoadPhoto } from '@/api/common';
import store from '@/store';

export default {
    components: {
        EditPage
    },
    data () {
        return {

            //高级筛选
            filterShow: false,

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
                timeType: '',
                timeRange: [1, 20],
                timeMax: 30
            },

            optionList: {
                attrList: {},
                classifyList: {},
                productListList: [],
                functionInfoList: {},
                statusList: {},
                devTypeList: {},
                sampleTypeList: {},
                skuEditConfirmList: {
                    '1': '是',
                    '0': '否'
                },
                currencyList: {},
                timeTypeList: {
                    day: '天',
                    hours: '时',
                    minutes: '分'
                },
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 编辑
            isEditShow: false,
            saveEditRow: {},

            skuEditObj: {
                key: ['商品SKU', '商品名称', '采样价', '最小起订单', '建议售价', '是否买样', '是否定制', '操作'],
                tempProps: ['productSku', '', '', '', '', 'isBuy', 'isDiy', 'operation'],
                value: []
            },

            // 禁用购买类型
            disBuyType: [2, 4],

            // SKU下拉禁用
            skuOptionDis: [0, 3, 4],

            // 预览图片
            previewImgUrl: '',
            isShowPreviewImg: false,

            //指派任务弹窗
            isShowAssign: false,

            //驳回
            isShowReject: false,
            rejectData: [],
            rejectList: [],
            rejectDefault: {},

            isShowLookImage: false,
            lookImageUrl: '',
        };
    },
    computed: {
        ...mapGetters([
            'queryParamsInfo',
            'sampleAuditListInfo'
        ])
    },
    methods: {
        makeLog,
        ...mapActions([
            'GetSampleAuditListInfo'
        ]),
        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },
        //操作用时
        handleTimeRange (type) {

            switch (type) {
                case 'day':
                    this.queryData.timeMax = 30;
                    break;
                case 'hours':
                    this.queryData.timeMax = 24;
                    break;
                case 'minutes':
                    this.queryData.timeMax = 60;
                    break;
            }
        },

        //是否勾选
        checkedStatus (obj) {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return false;
            }
            this[obj] = true;
            return true;
        },

        //指派任务
        handleClickAssign () {
            this.checkedStatus('isShowAssign');

        },

        //勾选的id
        selectId () {
            return this.saveCheckInfo.map(item => item.spu[0]);
        },

        //确认指派
        handleConfirmAssign (val) {
            let obj = {
                spu: this.selectId().join(','),
                create_user: store.getters.loginInfo.staff_code,
                distr_user: val.check
            };
            assignTask(obj).then(({data}) => {
                if (data.status === 1) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo();
                } else {
                    this.$message.error('操作失败');
                }
            });
        },

        // 驳回
        handleClickReject () {
            this.checkedStatus('isShowReject');
        },

        // 保存上传文件
        handleSaveFileList (file, row) {

            let formData = new FormData();
            formData.append('file', file.raw);
            upLoadPhoto(formData).then(({data}) => {
                if (data.status == 1) {
                    row.image = data.data.path;
                } else {
                    this.$message.error(data.errorMess);
                }
            });
            console.log(row.image, 8899);
        },

        //驳回操作数据处理
        rejectTypeChange (row) {
            let list = row.rejectList;
            let data = list.find((item) => {
                return row.type === item.id;
            });
            row.value = data.value;
            row.remark = data.remark;

            // 这两行好像没有
            // row.reject_to_dpt = data.reject_to_dpt;
            // row.reject_to_dpts = data.reject_to_dpts.split('/');

        },

        // 获取驳回弹窗的下拉列表的退回类型
        async handleRejectInfo () {
            const {data: {data: {exception_list}}} = await getRejectType();
            this.rejectDefault = {
                rejectList: [...exception_list],
                type: '',
                image: '',
                remark: '',
                value: '',
                // reject_to_dpt: '',
                // reject_to_dpts: []
            };
            this.rejectData.push({...this.rejectDefault});
        },

        // 确认驳回
        handleConfirmReject () {
            let arr = [];
            this.rejectData.forEach((item) => {
                arr.push({
                    record_type: item.type,
                    abnormity_description: item.value,
                    image: item.image,
                    // type: item.reject_to_dpt
                });
            });

            let obj = {
                spu: this.selectId(),
                spu_exception_record: arr
            };

            sendBatchReject(obj).then(({data}) => {
                if (data.status === 1) {
                    this.$message.success('操作成功!');
                    this.isShowReject = false;
                    this.handleQueryInfo();
                    this.rejectData.length = 0;
                    this.rejectData.push({...this.rejectDefault});
                } else {
                    this.$message.error('操作失败! 请检查填写是否正确!');
                }
            });

        },

        // 取消驳回
        handleCancelReject () {
            this.isShowReject = false;
            this.rejectData = [{...this.rejectDefault}];
        },

        // 删除驳回行
        handleDeleteInput (idx) {
            this.rejectData.splice(idx, 1);
        },

        //添加一行
        handleAddInput (idx) {
            this.rejectData.splice(idx + 1, 0, {...this.rejectDefault});
        },

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['sample_audit_list']);
            }

            //处理滑块时间
            let use_time_start = '';
            let use_time_end = '';
            if (this.queryData.timeType) {
                use_time_start = getNumByTime(this.queryData.timeRange[0], this.queryData.timeType, 'seconds');
                use_time_end = getNumByTime(this.queryData.timeRange[1], this.queryData.timeType, 'seconds');
            }

            let obj = {
                spu: this.queryData.productSPU,
                supplier_code: this.queryData.supplierCode,
                spu_category: this.queryData.productList.join('->'),
                goods_name: this.queryData.productName,
                supplier_name: this.queryData.supplierName,
                function: this.queryData.functionInfo,
                user: this.queryData.workUser,
                opera_user: this.queryData.operateUser,
                opera_time_start: this.queryData.operateTime[0],
                opera_time_end: this.queryData.operateTime[1],
                status: this.queryData.auditState,
                use_time_start,
                use_time_end,
                ...params
            };

            this.GetSampleAuditListInfo(obj).then(({data}) => {
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
                timeType: '',
                timeRange: [1, 20],
                timeMax: 30
            };
        },

        //高级筛选
        handleClickFilter () {
            this.filterShow = !this.filterShow;
        },

        animation (isHide) {
            if (isHide) {
                console.log(document.querySelector('body'));
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 导出明细信息
        handleExportListInfo (type) {
            return;
            if (type === '_all') {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            } else {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            }
        },

        // 预览图片
        handlePreviewImage (src) {
            this.previewImgUrl = src;
            this.isShowPreviewImg = true;
        },

        // 重构树形结构
        handleTreeCallBackChange (arr) {
            for (let i = 0; i < arr.length; i++) {
                if (arr[i].son_id && arr[i].son_id.length) {
                    this.handleTreeCallBackChange(arr[i].son_id);
                } else {
                    delete arr[i].son_id;
                }
            }
        },

        // 点击编辑
        handleClickEdit (row) {
            this.saveEditRow = row;
            this.isEditShow = true;
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'sample_audit_list',
                value: JSON.stringify(this.queryData)
            });

            // 渲染下拉

            //数据渲染
            this.tableData = data.data.value;

            this.tableData.map((item) => {
                return item.opera_log.reverse();
            });

            this.tableProps = Object.keys(data.data.key);
            this.tableKey = Object.values(data.data.key);
            this.tableWidth = ['60', '150', '90', '200', '150', '250', '100', '180', '150', '150', '90', '100', '90'];

            this.tableWidth[2] = '120px';
            this.tableWidth[6] = '230px';
            this.tableWidth[8] = '230px';

            this.optionList.productListList = [...data.select_list.category];
            this.optionList.functionInfoList = data.select_list.function;
            this.optionList.statusList = data.select_list.status;
            this.handleTreeCallBackChange(this.optionList.productListList);

            // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
            // this.tableProps = ['index', 'spu', 'img', 'good_name', 'category_path', 'sample_type', 'good_info', 'audit_state', 'ope_record', 'use_time', 'remarks'];

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        },

        // 请求数据获取下拉列表的数据
        getDownlistMembers () {
            getAssignGroup().then(({data: res}) => {
                if (res.status !== 1) {
                    return this.$message.error(data.errorMess || '请求失败，请刷新后重试！');
                }
                if (isValidData(res, 'data,group_user_list')) {
                    const {group_user_list} = res.data;
                    const workerList = group_user_list.reduce((pre, cur) => ({
                        ...pre,
                        [cur.user_number]: cur.user_name
                    }), {});

                    this.optionList = {
                        ...this.optionList,
                        workerList
                    };
                }
            });
        }
    },
    created () {
        this.thisVm = this;

        // 渲染驳回弹窗必要数据
        this.handleRejectInfo();
        this.getDownlistMembers();

        try {
            if (JSON.stringify(this.sampleAuditListInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['sample_audit_list']);
                this.handleQueryInfoCallBack(this.sampleAuditListInfo);
            }
        } catch (e) {
            console.log(e);
        }
    }
};
</script>

