<template>
    <div class="ui-main-module">
        <div class="ui-fn-module">
            <el-button type="text" @click="handleExportListInfo" size="small" style="margin-right:10px;">
                <i class="icon-daochu icon_blue"></i>导出
            </el-button>
            <ComUpload btnType="text" label="批量导入" :url="importUrl" @success="handleUploadSuccess"
                       :upData="upData" :multiple="true" class="ui-upload_btn">
                <template slot="icon" slot-scope="scope">
                    <i class="icon-piliangdaoru icon_purple"></i>
                </template>
            </ComUpload>
            <el-button size="small" class="load-text" type="text" @click="handleDownloadModule"
                       icon="icon-xiajiang icon_blue">下载模板
            </el-button>
            <el-button type="text" class="danger-border" size="small" @click="addData" icon="icon-xinzeng1 icon_blue">
                新增
            </el-button>
            <el-button type="text" class="danger-border" size="small" @click="delData" icon="icon-shanchu1 icon_pink">
                删除
            </el-button>
            <el-button type="text" class="danger-border" size="small" @click="goBack" icon="icon-fanhui icon_blue">返回
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :tableMinWidth="tableMinWidth"
            :showSearch="showSearch"
            :oherHeight="['ui-main-tab']"
            @checked="handleSaveCheckInfo">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item)"
                              :class="params.fid == item.value && 'ui-active'">{{item.label}}<i class="ui-tab_line"></i></span>
                    </li>
                </el-badge>
            </div>
            <template slot="remark" slot-scope="scope">
                <!--<el-tooltip placement="left">-->
                <!--<div class="el-tooltip-content" slot="content">{{scope.row.remark}}</div>-->
                <!--<div class="el-tooltip-left-more">{{scope.row.remark}}</div>-->
                <!--</el-tooltip>-->
            </template>
            <template slot="pack_img" slot-scope="scope">
                <div class="upload-img-list" v-if="scope.row.pack_img!==''">
                    <div class="ui-com_edit__upload--item">
                        <div class="ui-com_edit__upload--item__img"
                             :style="{background:'url('+scope.row.pack_img+') no-repeat center/contain'}">
                        </div>
                        <div class="ui-com_edit__upload--item--mask">
                            <span class="el-icon-zoom-in" @click="handleLookImage(scope.row.pack_img)"></span>
                        </div>
                    </div>
                </div>

            </template>
            <template slot="operate" slot-scope="scope">
                <el-table-column
                    fixed="right"
                    label="操作"
                    width="150px">
                    <template slot-scope="scope">
                        <el-button @click="delItem(scope.row.id)" type="text" size="small">
                            删除
                        </el-button>
                        <el-button type="text" @click="editItem(scope.row)" size="small">编辑</el-button>
                        <el-button type="text" @click="viewLog(scope.row.id)" size="small">日志</el-button>
                    </template>
                </el-table-column>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog
            width="500px"
            top="5vh"
            title="编辑"
            class="ui-layout_edit-dialog"
            :visible.sync="dialogFormVisible"
            :before-close="cancelEditInfo">
            <el-form :model="editForm" label-width="100px">
                <el-form-item :label="item" v-for="(item,index) in tableKey" :key="index"
                              :class="{clearBoth:tableProps[index]==='pack_img'}">
                    <el-input v-if="tableProps[index]!=='pack_img' && tableProps[index]!=='size'"
                              v-model="editForm[tableProps[index]]"
                              size="big"
                              style="width:96%"
                              :disabled="tableProps[index]==='id'||tableProps[index]==='pack_type_id'||tableProps[index]==='create_time'||tableProps[index]==='create_user'"></el-input>
                    <div v-if="tableProps[index]==='size'" class="size-item-wrap">
                        <el-input class="size-item" v-model="size[0]" size="small"></el-input>
                        <el-input class="size-item" v-model="size[1]" size="small"></el-input>
                        <el-input class="size-item" v-model="size[2]" size="small"></el-input>
                    </div>
                    <div v-if="tableProps[index]==='pack_img'" style="text-align: left;">
                        <div class="upload-img-list">
                            <div class="ui-com_edit__upload--item" v-if="editForm.pack_img!==''">
                                <div class="ui-com_edit__upload--item__img"
                                     :style="{background:'url('+editForm.pack_img+') no-repeat 0/contain'}">
                                </div>
                                <div class="ui-com_edit__upload--item--mask">
                                    <span class="el-icon-delete"
                                          @click="editForm.pack_img=''"></span>
                                    <span class="el-icon-zoom-in" @click="handleLookImage(editForm.pack_img)"></span>
                                </div>
                            </div>
                            <div class="ui-com_edit__upload--last_btn" v-if="editForm.pack_img==''">
                                <el-upload
                                    action=""
                                    multiple
                                    :show-file-list="false"
                                    :auto-upload="false"
                                    :on-change="(file) =>{handleSaveFileList(file,editForm)}">
                                    <span class="icon-tupianshangchuan"></span>
                                </el-upload>
                            </div>
                        </div>
                    </div>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
                <el-button type="primary" @click="saveEditInfo" size="small">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog
            width="800px"
            top="5vh"
            title="新增"
            class="ui-edit_layout_big"
            :visible.sync="dialogAddVisible"
            :before-close="cancelAddInfo">

            <el-form :model="addForm" label-width="100px">
                <el-form-item :label="item" v-for="(item,index) in tableKey" :key="index"
                              :class="{clearBoth:tableProps[index]==='pack_img'}"
                              v-show="!(tableProps[index]==='id'||tableProps[index]==='create_time'||tableProps[index]==='create_user')">
                    <el-input
                        v-show="tableProps[index]!=='pack_img'&&tableProps[index]!=='pack_type_id'&&tableProps[index]!=='size'"
                        v-model="addForm[tableProps[index]]"
                        size="small">
                    </el-input>
                    <div v-if="tableProps[index]==='size'" class="size-item-wrap">
                        <el-input class="size-item" v-model="size[0]" size="small"></el-input>
                        <el-input class="size-item" v-model="size[1]" size="small"></el-input>
                        <el-input class="size-item" v-model="size[2]" size="small"></el-input>
                    </div>
                    <el-select v-if="tableProps[index]==='pack_type_id'"
                               size="small"
                               v-model="addForm.pack_type_id"
                               placeholder="请选择">
                        <el-option
                            v-for="(label, value) in packType"
                            :key="value"
                            :label="label"
                            :value="value"></el-option>
                    </el-select>
                    <div v-show="tableProps[index]==='pack_img'" style="text-align: left;">

                        <div class="upload-img-list">
                            <div class="ui-com_edit__upload--item" v-if="addForm.pack_img">
                                <div class="ui-com_edit__upload--item__img"
                                     :style="{background:'url('+addForm.pack_img+') no-repeat 0/contain'}">
                                </div>
                                <div class="ui-com_edit__upload--item--mask">
                                    <span class="el-icon-delete"
                                          @click="addForm.pack_img=''"></span>
                                    <span class="el-icon-zoom-in" @click="handleLookImage(addForm.pack_img)"></span>
                                </div>
                            </div>
                            <div class="ui-com_edit__upload--last_btn" v-if="!addForm.pack_img">
                                <el-upload
                                    action=""
                                    multiple
                                    :show-file-list="false"
                                    :auto-upload="false"
                                    :on-change="(file) =>{handleSaveFileList(file,addForm)}">
                                    <span class="icon-tupianshangchuan"></span>
                                </el-upload>
                            </div>

                        </div>
                    </div>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddVisible = false" size="small">取 消</el-button>
                <el-button type="primary" @click="saveAddInfo" size="small">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="查看日志" class="ui-layout_edit-dialog" :visible.sync="dialogTableVisible" top="5vh">
            <ComTableInfo
                :tableKey="logKey"
                :tableProps="logProps"
                :tableData="logData"
                :tableWidth="logWidth">
                <template slot="remark" slot-scope="scope">
                    <p v-for="(item,index) in scope.row.remark" :key="index">
                        {{item}}
                    </p>
                </template>
            </ComTableInfo>
        </el-dialog>

        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>

    </div>
</template>

<script>

import config from '@/assets/js/config';
import * as downloadApi from '@/api/download_api';
import {
    mapGetters
} from 'vuex';
import {
    getDeliveryPackListInfo,
    delDeliveryPack,
    changeDeliveryPackInfo,
    exportDeliveryPackInfo,
    addDeliveryPack
} from '@/api/quality_control_department/sample_quality_control/data_maintain/delivery_pack';
import { getLogList, upLoadPhoto } from '@/api/common';
import { makeLog } from '@/services';

export default {
    props: {
        queryParams: {}
    },
    data () {
        return {
            // 搜索缩进
            showSearch: true,

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableMinWidth: [],
            tableProps: [],
            packType: {},

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 上传列表
            saveFileList: [],

            //编辑弹窗
            dialogFormVisible: false,
            editForm: {},

            //日志弹窗
            dialogTableVisible: false,
            logData: [],
            logKey: [],
            logProps: [],
            logWidth: ['200px', '120px', '120px'],

            //导入的接口
            importUrl: '',
            upData: {},

            //列表tab选项
            moduleTypeList: [
                {
                    label: '发货包装',
                    value: '1',
                    log: '3'
                },
                {
                    label: '缓冲包装',
                    value: '2',
                    log: '3'
                },
                {
                    label: '退样类型',
                    value: '5',
                    log: '2'
                },
                {
                    label: '来料包装',
                    value: '3',
                    log: '3'
                },
                {
                    label: '质检等级',
                    value: '6',
                    log: '5'
                },
                {
                    label: '转接头',
                    value: '4',
                    log: '3'
                }
            ],

            params: {},

            //部门参数
            departmentOption: config.departmentInfo,

            //新增弹窗
            dialogAddVisible: false,
            addForm: {},
            size: new Array(3),
            isShowLookImage: false,
            lookImageUrl: ''

        };
    },
    computed: {
        ...mapGetters([
            'loginInfo',
            'queryParamsInfo'
        ]),
        downloadUrl () {
            return downloadApi['DOWNLOAD_PACK_MAINTAIN'];
        }
    },
    methods: {
        makeLog,
        //根据id 处理不同的参数
        handleImport () {
            switch (this.params.fid) {
                case '5':
                    this.importUrl = '/sample_config/return_config/import_config';
                    break;
                case '6':
                    this.importUrl = '/sample_config/quality_config/import_config';
                    break;
                default:
                    this.importUrl = '/sample_config/sample_config/import_config';

            }
            this.upData = {
                user_id: this.loginInfo.staff_code || '12534',
                fid: this.params.fid
            };
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        // 保存上传文件
        handleSaveFileList (file, obj) {

            let formData = new FormData();
            formData.append('file', file.raw);

            upLoadPhoto(formData).then(({data}) => {
                if (data.status == 1) {
                    this.$set(obj, 'pack_img', data.data.path);
                } else {
                    this.$message.error(data.errorMess);
                }
            });
        },

        // 切换tab列表
        handleToggleTableInfo (obj) {
            this.params.fid = obj.value;
            this.params.log = obj.log;
            this.handleQueryInfo();
            this.handleImport();
            this.addForm = {};
        },

        // 下载导入模板
        handleDownloadModule () {
            window.open(`${this.downloadUrl}?fid=${this.params.fid}`, '_self');
        },

        //上传文件成功
        handleUploadSuccess (res, file) {
            if (res.errorMess) {
                this.$message.error(res.errorMess || '操作失败');
            } else {
                this.handleQueryInfo();
            }
        },

        //查看日志
        viewLog (id) {
            this.dialogTableVisible = true;
            let obj = {
                mod_type: this.params.log,
                id
            };
            getLogList(obj).then(({data}) => {
                if (data.status === 1) {
                    this.logData = data.data.value;
                    this.logData.map((item) => {
                        return item.remark = item.remark.split('|');
                    });
                    this.logProps = Object.keys(data.data.key);
                    this.logKey = Object.values(data.data.key);
                } else {
                    this.logData = [];
                }
            });
        },
        //取消保存
        cancelAddInfo () {
            this.dialogAddVisible = false;
        },
        //取消保存
        cancelEditInfo () {
            this.dialogFormVisible = false;
            this.editForm.fileList = [];
            this.size = [];
        },

        //新增
        saveAddInfo () {
            const size = this.size.join('*');
            let obj = {
                fid: this.params.fid,
                ...this.addForm,
                size
            };
            addDeliveryPack(obj).then(({data}) => {
                if (data.errorMess) {
                    this.$message({
                        message: data.errorMess,
                        type: 'warning'
                    });
                    return;
                }
                this.dialogAddVisible = false;

                this.$message({
                    message: '添加成功!',
                    type: 'success'
                });
                this.addForm = {};
                this.size = [];
                this.handleQueryInfo();
            });

        },

        //保存修改
        saveEditInfo () {

            let newData = {...this.editForm};
            delete newData.create_time;
            delete newData.fileList;
            newData.size = this.size.join('*');

            // 转换类型
            for (let i in this.packType) {
                if (newData.pack_type_id === this.packType[i]) {
                    newData.pack_type_id = i;
                    break;
                }
            }

            let obj = {
                fid: this.params.fid,
                ...newData
            };
            changeDeliveryPackInfo(obj).then(({data}) => {
                if (data.errorMess) {
                    this.$message({
                        message: data.errorMess,
                        type: 'warning'
                    });
                    return;
                }
                this.dialogFormVisible = false;

                this.$message({
                    message: '修改成功!',
                    type: 'success'
                });
                this.handleQueryInfo();
            });

        },

        //编辑弹窗
        editItem (obj) {
            this.dialogFormVisible = true;
            this.editForm = Object.assign({}, obj);
            this.size = this.editForm.size.split('*');
            if (this.editForm.pack_img !== '') {
                this.editForm.fileList = [{
                    name: '',
                    url: this.editForm.pack_img
                }];
            }

        },

        //勾选的id
        selectId () {
            let idArr = [];
            this.saveCheckInfo.forEach((obj) => {
                idArr.push(obj.id);
            });
            return idArr;
        },

        //新增
        addData () {
            this.dialogAddVisible = true;
            console.log(this.editForm, 8989);
        },

        //删除多个
        delData () {
            let idArr = this.selectId();
            if (!idArr.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: idArr.join(','),
                    modify_user: this.loginInfo.user_name,
                    fid: this.params.fid
                };
                delDeliveryPack(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        //删除单个
        delItem (id) {
            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: id,
                    modify_user: this.loginInfo.user_name,
                    fid: this.params.fid
                };
                delDeliveryPack(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        // 导出明细信息
        handleExportListInfo () {
            let obj = {
                fid: this.params.fid,
                id: this.selectId().join(',')
            };
            exportDeliveryPackInfo(obj);
        },

        // 列表信息查询
        handleQueryInfo (params = {}) {
            console.log(this.params);
            let obj = {
                fid: this.params.fid,
                ...params
            };
            console.log(obj, 88);
            getDeliveryPackListInfo(obj).then(({data}) => {
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            });
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            });
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            //数据渲染
            this.tableData = data.data.value;
            this.tableProps = Object.keys(data.data.key);
            this.tableKey = Object.values(data.data.key);
            this.tableMinWidth = this.tableProps.map(item => {
                if (item === 'id') return '40';
                if (item === 'pack_type_id' || item === 'sku' || item === 'material_type') return '70';
                if (item === 'pack_img') return '100px';
                if (item === 'create_time' || item === 'size') return '90';
                if (item === 'remark' || item === 'introduce' || item === 'define_rqstd') return '200';
            });

            this.packType = data.select_list.pack_type;

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        },
        goBack () {
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'basic_data_router',
                value: null
            });
            this.$router.go(-1);
        }
    },
    created () {
        this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
            key: 'basic_data_router',
            value: this.$route.fullPath
        });

        let arr = this.queryParams.split(',');
        this.params = {
            fid: arr[0],
            log: arr[1]
        };

        this.handleQueryInfo();
        this.handleImport();
    }

};
</script>

<style lang="less" scoped>

@primary-color: #1299d8;

.clearBoth {
    float: none !important;
    clear: both;
}

/deep/ .ui-upload_btn {
    .el-button {
        padding: 0;
    }
}

.size-item-wrap {
    display: flex;
    width: 100%;

    .size-item {
        width: 33.33%;
        margin-right: 15px;

        &:not(:last-child)::after {
            content: '*';
            position: absolute;
            top: 4px;
            right: -10px;
            font-size: 16px;
            color: #606266;
        }
    }
}
</style>



