/**  =========================> *|`| % 摄影小组管理 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getPhotographyGroupInfo,
} from '../../../../api/design_department/photography_manage/photography_group_manage'

const config = {
    state: {
        photographyGroupInfo: {},
    },
    mutations: {
        [types.SAVE_PHOTOGRAPHY_GROUP_INFO] (state, saveInfo) {
            state.photographyGroupInfo = saveInfo
        }
    },
    actions: {
        GetPhotographyGroupInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getPhotographyGroupInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_PHOTOGRAPHY_GROUP_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




