<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="小组名称">
                            <el-input v-model="queryData.groupName" size="small"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement
                                template="select"
                                v-model="queryData.groupManager" label="小组经理" :options="optionList.searchUserList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement
                                template="select"
                                v-model="queryData.groupDirector" label="小组主管" :options="optionList.searchUserList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement
                                template="cascader"
                                :props="{label: 'category_name', value: 'id'}"
                                v-model="queryData.classify" label="适用分类" :options="optionList.canClassifyList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement
                                template="select"
                                v-model="queryData.devType" label="开发类型" :options="optionList.devTypeList"></SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})" icon="icon-chaxun">查询</el-button>
                </div>
            </div>
        </transition>
        <div class="clearFix">

        </div>
        <div class="ui-shortcut">
            <div class="ui-shortcut--title">
                <span>一级小组</span>
            </div>
            <ul class="ui-shortcut--area clearFix">
                <li v-for="item in optionList.groupTopList" @click="handleToggleFirst(item)">
                    <div class="ui-title">
                        <span>{{item.group_name}}</span>
                    </div>
                    <div class="ui-label">
                        <span>经理人 {{item.number}}</span>
                    </div>
                    <span class="icon-shanchu ui-ope_icon" @click.stop="handleDeleteFirstInfo(item)"></span>
                    <span class="icon-bianji ui-ope_icon" @click.stop="handleEditFirst(item)"></span>
                </li>
                <li class="ui-shortcut--area--ope" @click="handleClickAddFirstInfo">
                    <span class="icon-tianjia"></span>
                </li>
            </ul>
        </div>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <el-button type="text" size="small" @click="handleClickAdd">
                <i class="icon-xinzeng1 icon_blue"></i>新增小组</el-button>
        </div>
        <ComTableInfo
                isCheck
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableData="tableData"
                :tableWidth="['']"
                :otherHeight="['ui-shortcut']"
                :showSearch="showSearch">
            <template slot="group_category_cn" slot-scope="scope">
                <div>
                    <p v-for="item in scope.row.group_category_cn">{{item}}</p>
                </div>
            </template>
            <template slot="region" slot-scope="scope">
                <span>{{optionList.areaList[scope.row.region * 1]}}</span>
            </template>
            <template slot="group_user_info" slot-scope="scope">
                <div>
                    <p><span>组长：</span><span>{{scope.row.user_groupman_cn}}</span></p>
                    <p><span>主管：</span><span>{{scope.row.user_director_cn}}</span></p>
                    <p><span>经理：</span><span>{{scope.row.user_manager_cn}}</span></p>
                </div>
            </template>
            <template slot="group_user_number" slot-scope="scope">
                <div>
                    <p>
                        <span>{{Object.values(scope.row.group_user_info).join(',')}}</span>
                    </p>
                    <p><span>{{scope.row.group_user_info && Object.values(scope.row.group_user_info).length}}</span></p>
                </div>
            </template>
            <template slot="operation" slot-scope="scope">
                <el-button type="text" size="small" @click="handleClickEdit(scope.row, 'readonly')">预览</el-button>
                <el-button type="text" size="small" @click="handleClickEdit(scope.row)">编辑</el-button>
                <!--<el-button type="text" size="small">操作日志</el-button>-->
                <el-button type="text" size="small" @click="handleDeleteInfo(scope.row)">删除</el-button>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog
                :title="isEditFirstShow ? '编辑一级小组' : '新增一级小组'"
                width="400px"
                class="ui-layout_edit-dialog"
                :visible.sync="isFirstAddShow" @close="handleCancelAddFirstInfo">
            <el-form ref="firstEdit" :model="firstEdit" size="small">
                <el-form-item prop="groupName" label="一级小组" :label-width="labelWidth">
                    <el-input v-model="firstEdit.groupName"></el-input>
                </el-form-item>
                <el-form-item prop="manage" label="小组经理" :label-width="labelWidth">
                    <el-select filterable v-model="firstEdit.manage">
                        <el-option
                                v-for="(label, value) in optionList.userList"
                                :key="value"
                                :label="label"
                                :value="value"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="ui-com_edit--btn">
                <el-button size="small" @click="handleCancelAddFirstInfo">取消</el-button>
                <el-button size="small" type="primary" @click="handleSaveAddFirstInfo">保存</el-button>
            </div>
        </el-dialog>
        <el-dialog
                :title="isReview ? '预览' : isEditShow ? '编辑' : '新增'"
                top="10vh"
                width="800px"
                custom-class="ui-com_edit"
                :visible.sync="isAddShow"
                @close="handleCancelEdit">
            <el-form ref="editData" :model="editData" :rules="editRule" size="small">
                <div class="clearFix">
                    <div class="ui-group">
                        <div class="ui-title">
                            <span>二级小组</span>
                        </div>
                        <div class="ui-item">
                            <el-form-item prop="groupName" label="小组名称" :label-width="labelWidth">
                                <el-input v-model="editData.groupName" :disabled="isReview"></el-input>
                            </el-form-item>
                            <el-form-item prop="area" label="所属地区" :label-width="labelWidth">
                                <el-select filterable v-model="editData.area" :disabled="isReview">
                                    <el-option v-for="(item, index) in optionList.areaList" :label="item" :key="index" :value="index"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item prop="devType" label="开发类型" :label-width="labelWidth">
                                <el-select v-model="editData.devType" :disabled="isReview">
                                    <el-option v-for="(item, index) in optionList.devTypeList" :label="item" :key="index" :value="index"></el-option>
                                </el-select>
                            </el-form-item>
                            <div class="ui-tow_layout">
                                <el-form-item prop="isDiy" label="是否定制" :label-width="labelWidth">
                                    <el-select v-model="editData.isDiy" :disabled="isReview">
                                        <el-option v-for="(item, index) in optionList.whetherList" :label="item" :key="index" :value="index"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item prop="isOss" label="是否海外" :label-width="labelWidth">
                                    <el-select v-model="editData.isOss" :disabled="isReview">
                                        <el-option v-for="(item, index) in optionList.whetherList" :label="item" :key="index" :value="index"></el-option>
                                    </el-select>
                                </el-form-item>
                            </div>
                        </div>
                    </div>
                    <div class="ui-group">
                        <div class="ui-title">
                            <span>一级小组</span>
                        </div>
                        <div class="ui-item">
                            <el-form-item prop="parentGroup" label="一级小组" :label-width="labelWidth">
                                <el-select filterable v-model="editData.parentGroup" :disabled="isReview">
                                    <el-option v-for="(item, index) in optionList.groupTopList"
                                               :label="item.group_name" :key="index" :value="item.top_level_pid"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item prop="groupManager" label="小组经理" :label-width="labelWidth">
                                <el-input disabled v-model="editData.groupManager"></el-input>
                            </el-form-item>
                            <el-form-item prop="groupDirector" label="小组主管" :label-width="labelWidth">
                                <el-select filterable v-model="editData.groupDirector" :disabled="isReview">
                                    <el-option
                                            v-for="(label, value) in optionList.searchUserList"
                                            :key="value"
                                            :label="label"
                                            :value="value"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item prop="groupHeadman" label="小组组长" :label-width="labelWidth">
                                <el-select filterable v-model="editData.groupHeadman" :disabled="isReview">
                                    <el-option
                                            v-for="(label, value) in optionList.searchUserList"
                                            :key="value"
                                            :label="label"
                                            :value="value"></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="ui-group">
                        <div class="ui-title">
                            <span>适用分类</span>
                        </div>
                        <div class="ui-item ui-btn--layout">
                            <el-form-item prop="canClassify" v-for="(item, index) in editData.canClassify" :key="index">
                                <el-cascader
                                        :disabled="isReview"
                                        change-on-select
                                        v-model="editData.canClassify[index]"
                                        placeholder="输入搜索"
                                        :options="optionList.canClassifyList"
                                        :props="{ label: 'category_name', value: 'id', children: 'children' }"
                                        filterable
                                ></el-cascader>
                            </el-form-item>
                            <div class="ui-item--area--ope">
                                <span class="icon-shanjian" @click="handleDelClassify" v-if="!isReview"></span>
                                <span class="icon-zengjia" @click="handleAddClassify" v-if="!isReview"></span>
                            </div>
                        </div>
                    </div>
                    <div class="ui-group">
                        <div class="ui-title">
                            <span>小组成员</span>
                        </div>
                        <div class="ui-item">
                            <el-form-item prop="groupUser">
                                <el-select v-model="editData.groupUser" multiple filterable :disabled="isReview">
                                    <el-option
                                            v-for="(label, value) in optionList.userList"
                                            :key="value"
                                            :label="label"
                                            :value="value"></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                </div>
            </el-form>
            <div slot="footer" class="ui-com_edit--btn">
                <el-button size="small" @click="handleCancelEdit" v-if="!isReview">取消</el-button>
                <el-button size="small" type="primary" @click="handleConfirmEdit" v-if="!isReview">保存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import CONFIG from '@/assets/js/config';
    import {
        autoAddTableInx,
        getDefaultTime,
        changeTableInfoProps,
        goToNewPage
    } from '@/services/index'
    import {
        mapActions,
        mapGetters
    } from "vuex";
    import {
        getUserOptionList,
        getGroupClassifyOptionList,
        saveGroupAddInfo,
        addFirstLevelGroup,
        getEditGroupOptionList,
        saveEditGroupInfo,
        editFirstLevelGroup,
        deleteGroupInfo,
        deleteFirstGroupInfo
    } from '@/api/dev_department/system_manage/dev_group_manage';

    export default {
        data() {
            return {

                // 时间搜索框宽度设置
                searchDateWidth: CONFIG.SEARCH_DATE_WIDTH,

                thisVm: {},

                // 搜索缩进
                showSearch: true,

                queryData: {
                    groupName: '',
                    groupManager: '',
                    groupDirector: '',
                    classify: [],
                    devType: '',
                    groupTop: '',
                    firstGroup: ''
                },

                optionList: {
                    userList: [],
                    canClassifyList: [],
                    areaList: [],
                    whetherList: [],
                    devTypeList: [],
                    groupTopList: [],
                    searchUserList: []
                },

                tableData: [],
                tableKey: [],
                tableProps: [],

                //分页
                pageSizes: CONFIG.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                // 编辑
                isFirstAddShow: false,
                isEditFirstShow: false,
                firstEdit: {
                    manage: '',
                    groupName: '',
                    id: ''
                },

                isReview: false,
                isAddShow: false,
                isEditShow: false,
                labelWidth: '70px',
                editData: {
                    groupName: '',
                    parentGroup: '',
                    canClassify: [[]],
                    groupManager: '',
                    groupManagerId: '',
                    groupDirector: '',
                    groupHeadman: '',
                    groupUser: [],
                    area: '',
                    devType: '',
                    isDiy: '',
                    isOss: '',
                },
                saveCheckId: '',
                editRule: {
                    groupName: CONFIG.RULE_CONFIG.requiredBlur,
                    groupManager: CONFIG.RULE_CONFIG.requiredChange,
                    groupHeadman: CONFIG.RULE_CONFIG.requiredChange,
                    groupUser: CONFIG.RULE_CONFIG.requiredChange,
                },

                // 校验分类选择
                isProofTrue: ''
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'qualityControlGroupInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetQualityControlGroupInfo'
            ]),
            autoAddTableInx,
            getDefaultTime,
            goToNewPage,

            // 列表信息查询
            handleQueryInfo(params = {}, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['quality_group']);
                }
                let obj = {
                    devp_type: this.queryData.devType,
                    category_id: this.queryData.classify[this.queryData.classify.length - 1],
                    group_name: this.queryData.groupName,
                    top_level_pid: this.queryData.firstGroup,
                    user_manager_id: this.queryData.groupManager,
                    user_director_id: this.queryData.groupDirector,
                    ...params
                };

                this.GetQualityControlGroupInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;

                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 切换一级小组查询
            handleToggleFirst(item) {
                this.queryData.firstGroup = item.top_level_pid;
                this.handleQueryInfo({limit: this.pageData.currentList});
            },

            // 查询重置
            handleResetQueryInfo() {
                this.queryData = {
                    groupName: '',
                    groupManager: '',
                    groupDirector: '',
                    classify: [],
                    devType: '',
                    groupTop: ''
                }
            },


            // 分页条数切换
            handleSizeChange(val) {
                this.handleQueryInfo({
                    limit: val,
                    offset: 1
                }, true)
            },

            // 跳转页数切换
            handleCurrentChange(val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true)
            },

            // 删除
            handleDeleteInfo(row) {
                this.$confirm('确认删除？', '提示', {
                    type: 'warning'
                }).then(() => {
                    let obj = {
                        group_id: row.id,
                        group_type: '2'
                    };
                    deleteGroupInfo(obj).then(({ data }) => {
                        this.handleOperationCallBack(data);
                    })
                })
            },

            handleDeleteFirstInfo(row) {
                this.$confirm('确认删除？', '提示', {
                    type: 'warning'
                }).then(() => {
                    let obj = {
                        group_id: row.top_level_pid,
                        group_type: '2'
                    };
                    deleteFirstGroupInfo(obj).then(({ data }) => {
                        this.handleOperationCallBack(data);
                    })
                });
            },

            // 点击新增一级信息
            handleClickAddFirstInfo() {
                this.isFirstAddShow = true;
            },

            // 保存新增一级信息
            handleSaveAddFirstInfo() {
                let obj = {
                    user_manager_id: this.firstEdit.manage,
                    group_name: this.firstEdit.groupName,
                    group_type: '2'
                };
                if (this.isEditFirstShow) {
                    obj.group_id = this.firstEdit.id;
                    editFirstLevelGroup(obj).then(({ data }) => {
                        this.handleOperationCallBack(data);
                    })
                } else {
                    addFirstLevelGroup(obj).then(({ data }) => {
                        this.handleOperationCallBack(data);
                    })
                }
            },

            // 编辑一级信息
            handleEditFirst(row) {
                this.firstEdit.manage = row.user_manager_id;
                this.firstEdit.groupName = row.group_name;
                this.firstEdit.id = row.top_level_pid;
                this.isEditFirstShow = true;
                this.isFirstAddShow = true;
            },

            // 取消一级小组新增
            handleCancelAddFirstInfo() {
                this.firstEdit = {
                    manage: '',
                    groupName: '',
                    id: ''
                };
                this.$refs.firstEdit.resetFields();
                this.isFirstAddShow = false;
            },

            // 点击新增
            handleClickAdd() {
                this.isAddShow = true;
            },

            // 点击编辑
            handleClickEdit(row, type) {

                if (type === 'readonly') {
                    this.isReview = true;
                }

                this.handlePlayUpEditInfo(row);
                this.isAddShow = true;
            },

            // 渲染下拉数据
            handlePlayUpOptionList(data) {

            },

            // 渲染编辑数据
            handlePlayUpEditInfo(row) {
                this.isEditShow = true;
                this.saveCheckId = row.id;

                this.editData.canClassify = row.group_category.map(value => {
                    return value.map(v => {
                        return v.toString();
                    })
                });

                this.editData.groupName = row.group_name;
                this.editData.parentGroup = row.top_level_pid;
                this.editData.groupManager = this.optionList.userList[row.user_manager_id];
                this.editData.groupManagerId = row.user_manager_id;
                this.editData.groupDirector = row.user_director_id;
                this.editData.groupHeadman = row.user_groupman_id;
                this.editData.groupUser = Object.keys(row.group_user_info);
                this.editData.area = row.region * 1;
                this.editData.devType = row.devp_type * 1;
                this.editData.isDiy = row.is_customized * 1;
                this.editData.isOss = row.is_overseas * 1;
            },

            // 取消编辑
            handleCancelEdit() {
                this.editData = {
                    groupName: '',
                    parentGroup: '',
                    canClassify: [[]],
                    groupManager: '',
                    groupManagerId: '',
                    groupDirector: '',
                    groupHeadman: '',
                    groupUser: [],
                    area: '',
                    devType: '',
                    isDiy: '',
                    isOss: '',
                };
                this.$refs.editData.resetFields();
                this.isAddShow = false;
                this.isEditShow = false;
                this.isReview = false;
            },

            // 删除分类
            handleDelClassify() {
                if (this.editData.canClassify.length === 1) return;
                this.editData.canClassify.pop();
            },

            // 添加分类
            handleAddClassify() {
                this.editData.canClassify.push([]);
            },

            // 校验分类
            handleProofClassify(val, inx) {
                const checkValue = val[val.length - 1];
                for (let i = 0; i < this.editData.canClassify.length; i++) {
                    let item = this.editData.canClassify[i];
                    if (inx === i) continue;
                    if (item.indexOf(checkValue) > -1) {
                        this.$message.warning('适用分类选择有误！');
                        return true;
                    }
                }
            },
            handleOpenProofClassify(val) {
                // if (this.isProofTrue !== '') {
                //
                //     this.editData.canClassify[this.isProofTrue] = [];
                // }
            },

            // 确认保存
            handleConfirmEdit() {
                this.$refs.editData.validate(res => {
                    if (res) {
                        for (let i = 0; i < this.editData.canClassify.length; i++) {
                            if (this.handleProofClassify(this.editData.canClassify[i], i)) {
                                return;
                            }
                        }

                        let groupId = [];
                        this.editData.groupUser.forEach(value => {
                            groupId.push({
                                user_staff_id: value
                            })
                        });

                        //
                        let obj = {
                            group_name: this.editData.groupName,
                            top_level_pid: this.editData.parentGroup,
                            user_groupman_id: this.editData.groupHeadman,
                            user_director_id: this.editData.groupDirector,
                            user_manager_id: this.editData.groupManagerId,
                            group_user_info: JSON.stringify(groupId),
                            devp_type: this.editData.devType || 0,
                            region: this.editData.area || 0,
                            group_type: '2',
                            is_customized: this.editData.isDiy || 0,
                            is_overseas: this.editData.isOss || 0,
                            group_category: JSON.stringify(this.editData.canClassify)
                        };

                        if (this.isEditShow) {
                            obj.group_id = this.saveCheckId;
                            saveEditGroupInfo(obj).then(({ data }) => {
                                this.handleOperationCallBack(data);
                            })
                        } else {
                            saveGroupAddInfo(obj).then(({ data }) => {
                                this.handleOperationCallBack(data);
                            })
                        }
                    }
                })
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo({ limit: this.pageData.currentList });

                    if (this.isAddShow) {
                        this.handleCancelEdit();
                    }

                    if (this.isFirstAddShow) {
                        this.handleCancelAddFirstInfo();
                    }
                } else {
                    this.$message.error(data.errorMess || '操作失败');
                }
            },

            //查询回调
            handleQueryInfoCallBack(data){

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'quality_group',
                    value: JSON.stringify(this.queryData)
                });

                //
                this.optionList.devTypeList = data.data_list.drop_down_data.devp_type_list;
                this.optionList.searchUserList = data.data_list.drop_down_data.user_data;
                this.optionList.canClassifyList = data.data_list.drop_down_data.category_list;
                this.optionList.groupTopList = data.data_list.top_group_list;
                this.optionList.areaList = data.data_list.drop_down_data.region_list;
                this.optionList.userList = data.data_list.drop_down_data.user_data;
                this.optionList.whetherList = data.data_list.drop_down_data.whether;

                //数据渲染
                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;

                this.tableProps = ['index', 'group_name', 'region', 'devp_type_cn', 'group_category_cn', 'group_user_info', 'group_user_number', 'operation'];

                //渲染分页
                this.pageData.currentList = data.page_data.limit * 1;
                this.pageData.currentPage = data.page_data.offset * 1;
                this.pageData.currentTotal = data.page_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try{
                if (JSON.stringify(this.qualityControlGroupInfo) === '{}') {
                    this.handleQueryInfo({is_get_base: 1});
                }else{
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['quality_group']);
                    this.handleQueryInfoCallBack(this.qualityControlGroupInfo);
                }
            }catch (e) {
                console.log(e)
            }
        },
        watch: {
            "editData.parentGroup"(val) {
                this.optionList.groupTopList.forEach(value => {
                    if (value.top_level_pid === val) {
                        this.editData.groupManager = this.optionList.userList[value.user_manager_id];
                        this.editData.groupManagerId = value.user_manager_id;
                    }
                });
            }
        }
    };
</script>

<style lang="less" scoped>

</style>


