/**  =========================> *|`| % 摄影扫码关联 % |`|*<======================== **/

import * as types from '../../../mutation-type';

const config = {
    state: {
        sampleScanListInfo: {}
    },
    mutations: {
        [types.SAVE_SAMPLE_SHOT_LIST_INFO] (state, saveInfo) {
            state.sampleScanListInfo = saveInfo;
        }
    },
    actions: {

        // 保存扫码列表
        SaveShotScanListInfo ({commit}, queryData = {}) {
            
            commit(types['SAVE_SAMPLE_SHOT_LIST_INFO'], queryData);
        }
    }
};

export default config;




