
/**  --->>>>   |^ 商品开发列表^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取商品开发列表信息
export function getProductDevListInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        list_type: 6,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/create_supplier' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/product/Product_list/product_list', data);
    }
}

// 生成二维码
export function createQRCodeInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/qrcode/sku_qrcode/generate', data);
}


