/**  =========================> *|`| % 文案编辑列表 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getCopyEditListInfo,
} from '../../../../api/copy_department/copy_edit_module/copy_edit_list'

const config = {
    state: {
        copyEditListInfo: {},
    },
    mutations: {
        [types.SAVE_COPY_EDIT_LIST_INFO] (state, saveInfo) {
            state.copyEditListInfo = saveInfo
        }
    },
    actions: {
        // 获取创建供应商信息
        GetCopyEditListInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getCopyEditListInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_COPY_EDIT_LIST_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




