/**  =========================> *|`| % 样品采购下单 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getSamplePurchaseInfo,
} from '../../../../api/sample_track_manage/sample_track_module/sample_purchase';

const config = {
    state: {
        samplePurchaseInfo: {}
    },
    mutations: {
        [types.SAVE_SAMPLE_PURCHASE_INFO](state, saveInfo) {
            state.samplePurchaseInfo = saveInfo;
        }
    },
    actions: {
        // 获取样品采购下单信息
        GetSamplePurchaseInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSamplePurchaseInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SAMPLE_PURCHASE_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




