<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.productList" label="商品目录" template="cascader" :options="optionList.productLists"
                                           :props="{label: 'category_name', value: 'id', children: 'child_id'}">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商">
                            <el-input size="small" v-model="queryData.supplier"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.buyType" label="购买类型" template="select"
                                           :options="optionList.buyTypeList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.function" label="职能" template="select" :options="optionList.functionList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员">
                            <el-input size="small" v-model="queryData.opeUser"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.opeTime" label="操作时间" template="daterange">
                        </SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})" icon="icon-chaxun">查询</el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <!--<el-button-->
            <!--v-if="item.method && item.permit(thisVm)"-->
            <!--v-for="item in buttonList"-->
            <!--size="small"-->
            <!--:type="item.type"-->
            <!--:key="item.value"-->
            <!--@click="item.action(thisVm, item.method)">{{item.name}}</el-button>-->
            <el-button type="text" size="small" @click="handleCreateOrder">
                <i class="icon-shengchengdingdan icon-plain"></i>生成订单</el-button>
            <el-button type="text" size="small" @click="handleOperationFn(1, '取消买样')">
                <i class="icon-quxiaomaiyang icon-main"></i>取消买样</el-button>
            <el-button type="text" size="small" @click="handleOperationFn(2, '刷新金额')">
                <i class="icon-quxiaomaiyang icon-main"></i>刷新金额</el-button>
            <el-button type="text" size="small" @click="handleOperationFn(3, '推送付款')">
                <i class="icon-quxiaomaiyang icon-main"></i>推送付款</el-button>
            <el-button type="text" size="small" @click="handleTransferShow">
                <i class="icon-renwuzhipai icon-warn"></i>任务指派</el-button>
        </div>
        <ComTableInfo
                isCheck
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableData="tableData"
                :tableWidth="tableWidth"
                :showSearch="showSearch"
                @checked="handleSaveCheckInfo">
            <template slot="supplier_code" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.supplier_code}}</p>
                    <div class="ui-table--supplier_code--flag">
                        <span>试卖</span>
                    </div>
                </div>
            </template>
            <template slot="sku_list" slot-scope="scope">
                <div>
                    <p v-for="item in scope.row.sku_list">{{`${item.sku} ${item.attribute_value_cn} ${item.reference_price}`}}</p>
                </div>
            </template>
            <template slot="dev_imgs" slot-scope="scope">
                <div class="ui-table__img">
                    <img :src="scope.row.dev_imgs && JSON.parse(scope.row.dev_imgs)[0]" alt="" @click="handleLookImage(JSON.parse(scope.row.dev_imgs)[0])">
                </div>
            </template>
            <template slot="sample_datum" slot-scope="scope">
                <div>
                    <p><span>购买类型：</span> <span>{{scope.row.aaa}}</span></p>
                    <p><span>供应商编码：</span> <span>{{scope.row.supplier_code}}</span></p>
                    <p><span>供应商名称：</span> <span>{{scope.row.supplier_name}}</span></p>
                    <p><span>采样链接：</span> <span>{{scope.row.aaa}}</span></p>
                    <p><span>联系人/电话：</span> <span>{{scope.row.phone}}</span></p>
                </div>
            </template>
            <template slot="log_time" slot-scope="scope">
                <div>
                    <p v-for="item in scope.row.log_time">{{item}}</p>
                </div>
            </template>
            <template slot="payment_info" slot-scope="scope">
                <div>
                    <p><i class="icon-quanxianguanli"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.receive_account}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.shop_name}}</p>
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <el-dialog
                class="ui-layout_edit-dialog"
                custom-class="ui-com_edit"
                title="生成订单"
                width="1000px"
                top="5vh"
                :visible.sync="isAddShow"
                @close="handleCancelSubmitOrder">
            <div class="ui-create_order">
                <div class="ui-top_info">
                    <ul>
                        <li>
                            <span>收货人：</span>
                            <span>{{addOrderInfo.user_info.consignee}}</span>
                        </li>
                        <li>
                            <span>收货信息：</span>
                            <span>{{addOrderInfo.user_info.receipt_info}}</span>
                        </li>
                        <li>
                            <span>联系电话：</span>
                            <span>{{addOrderInfo.user_info.phone}}</span>
                        </li>
                    </ul>
                </div>
                <div class="ui-table_title">
                    <table><thead>
                    <tr>
                        <th></th>
                        <th>单价（元）</th>
                        <th>数量</th>
                        <th>金额（元）</th>
                    </tr>
                    </thead></table>
                </div>
                <div class="ui-table_content" v-for="(supItem, supInx) in addOrderInfo.spu_info" :key="supInx">
                    <div class="ui-table_content--title"><span>供应商名称：{{supItem.supplier_name}}</span></div>
                    <table>
                        <tbody>
                            <tr v-for="(item, index) in supItem.sku_list">
                                <td>
                                    <div class="ui-table_content--item ui-media clearFix">
                                        <div class="ui-media--img">
                                            <img :src="item.link" alt="">
                                        </div>
                                        <div class="ui-media--content">
                                            <p>{{item.title_cn}}</p>
                                            <span>{{item.attribute_value_cn}}</span>
                                            <div class="ui-media--content--icon" :style="{
                                            backgroundImage: `url(${item.sku_image_url || saveActiveLink[supInx + '-' +index]})`}">
                                                <!--<img :src="item.link" alt="" @click="handleLookImage(item.link)"><i class="el-icon-d-arrow-right"></i>-->
                                                <!--<i v-if="!isCheckLink" class="icon-dianpumingcheng1" style="color: #ff6000"></i>-->
                                                <!--<i class="el-icon-d-arrow-right"></i>-->
                                                <transition mode="in-out" name="slide-fade">
                                                    <ul class="ui-media--content--down clearFix">
                                                        <li class="ui-not_copy"
                                                            :class="checkProductInfo[`${supInx}-${index}-${i}`] && 'ui-active'"
                                                            v-for="(attr, i) in item.alibaba_atr.data.sku"
                                                            @click="handleCheckProduct(`${supInx}-${index}`, i, item, attr, supItem.supplier_name,
                                                        !checkProductInfo[`${supInx}-${index}-${i}`])">
                                                            <img :src="attr.skuImageUrl_min" alt="">
                                                            <span>{{attr.sku_atr[1].attribute_value}}</span>
                                                        </li>
                                                    </ul>
                                                </transition>
                                            </div>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="ui-table_content--item">
                                        <span>{{item.unit_price}}</span>
                                    </div>
                                </td>
                                <td>
                                    <div class="ui-table_content--item">
                                        <span v-if="supItem.order_number">{{item.count}}</span>
                                        <el-input v-if="!supItem.order_number" size="small" v-model="countList[supInx + '-' +index]" @blur="handleCountPrice(supInx + '-' +index, item.unit_price)"></el-input>
                                    </div>
                                </td>
                                <td>
                                    <div class="ui-table_content--item">
                                        <span>{{priceList[supInx + '-' +index]}}</span>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div class="ui-table_content--row_count">
                        <div>
                            <span>运费共计：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].shippingFee : supItem.freight_price_total}}</span>
                            <span>优惠金额：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].discount : supItem.discounts_price}}</span>
                            <span>PO号：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].po : supItem.po}}</span>
                        </div>
                        <div>
                            <span>总额：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].totalAmount : supItem.price_total}}</span>
                            <span>商品金额：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].sumProductPayment : supItem.goods_price}}</span>
                            <span>订单号：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].order_id : supItem.order_number}}</span>
                        </div>
                    </div>
                </div>
                <div class="ui-table_footer clearFix">
                    <p>共计商品：{{addOrderInfo.user_info.sum_count}}件</p>
                    <p>合计总额：<span class="ui-total_amt">{{addOrderInfo.user_info.sum_total}}元</span></p>
                </div>
            </div>
            <div slot="footer">
                <el-button size="small" @click="handleCancelSubmitOrder">取消</el-button>
                <el-button size="small" type="primary" @click="handleSubmitCreateOrder"
                           v-if="submitCreateData === '{}' || addOrderInfo.spu_info[0] && !addOrderInfo.spu_info[0].order_number">提交</el-button>
            </div>
        </el-dialog>
        <ComDialogRemarks :visible.sync="isTransferShow" formLabel="指派给" title="任务指派" type="select"
                          :option="optionList.userList" @confirm="handleConfirmTransfer"></ComDialogRemarks>
    </div>
</template>

<script>
    import config from '@/assets/js/config';
    import {
        autoAddTableInx,
        getDefaultTime,
        changeTableInfoProps,
        goToNewPage
    } from '@/services/index'
    import {
        mapActions,
        mapGetters
    } from "vuex";
    import {
        getCityInfo
    } from '@/api/login';
    import {
        getSamplePurchaseDetailInfo,
        submitCreateOrder,
        samplePurchaseOperation,
        workTransfer
    } from '@/api/sample_track_manage/sample_track_module/sample_purchase';

    export default {
        data() {
            return {

                // 时间搜索框宽度设置
                searchDateWidth: config.SEARCH_DATE_WIDTH,

                thisVm: {},

                // 搜索缩进
                showSearch: true,

                queryData: {
                    productSPU: '',
                    productList: [],
                    supplier: '',
                    buyType: '',
                    productName: '',
                    opeUser: '',
                    opeTime: '',
                    function: '',
                },

                optionList: {
                    buyTypeList: {},
                    functionList: {},
                    productLists: [],
                    userList: []
                },

                tableData: [],
                tableKey: [],
                tableWidth: [],
                tableProps: [],

                // 保存勾选数据
                saveCheckInfo: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                // 预览图片
                isShowLookImage: false,
                lookImageUrl: [],

                // 生成订单
                isAddShow: false,
                addOrderInfo: {
                    user_info: {},
                    spu_info: []
                },

                //保存激活图片链接
                saveActiveLink: {},

                //判断是否勾选图片
                isCheckLink: false,
                checkProductInfo: {},
                countList: {},
                priceList: {},
                submitCreateData: {},

                isTransferShow: false
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'samplePurchaseInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetSamplePurchaseInfo'
            ]),
            autoAddTableInx,
            getDefaultTime,
            goToNewPage,

            // 列表信息查询
            handleQueryInfo(params = {}, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['sample_purchase']);
                }

                let obj = {
                    spu: this.queryData.productSPU,
                    category_id: this.queryData.productList,
                    supplier_code: this.queryData.supplierName,
                    sample_type: this.queryData.buyType,
                    title_cn: this.queryData.productName,
                    function: this.queryData.function,
                    opera_user: this.queryData.opeUser,
                    opera_time_start: this.queryData.opeTime[0],
                    opera_time_end: this.queryData.opeTime[1],
                    is_get_base: 1,
                    ...params
                };

                this.GetSamplePurchaseInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;

                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 查询重置
            handleResetQueryInfo() {
                this.queryData = {
                    productSPU: '',
                    productList: [],
                    supplier: '',
                    buyType: '',
                    productName: '',
                    opeUser: '',
                    opeTime: '',
                }
            },

            // 分页条数切换
            handleSizeChange(val) {
                this.handleQueryInfo({
                    limit: val,
                    offset: 1
                }, true)
            },

            // 跳转页数切换
            handleCurrentChange(val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true)
            },

            // 保存勾选数据
            handleSaveCheckInfo(val) {
                this.saveCheckInfo = val;
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 预览图片
            handleLookImage(src) {
                this.isShowLookImage = true;
                this.lookImageUrl = src;
            },

            // 操作
            handleOperationFn(type, msg) {

                if (!this.saveCheckInfo.length) {
                    this.$message.warning('请勾选数据！');
                    return;
                }

                this.$confirm(`确认${msg}`, '提示', {
                    type: 'warning'
                }).then(() => {
                    let obj = {};
                    if (type === 1) {
                        let spu_po_arr = [];
                        this.saveCheckInfo.forEach(value => {
                            spu_po_arr.push({
                                po: value.sku_list[0].po,
                                spu: value.spu,
                                order_id: value.sku_list[0].order_id
                            })
                        })

                        obj = {
                            status: type,
                            spu_po_arr: JSON.stringify(spu_po_arr)
                        }
                    } else {
                        let [spu, order] = [[], []]
                        this.saveCheckInfo.forEach(value => {
                            spu.push(value.spu);
                            order.push(value.sku_list[0].order_number)
                        })

                        obj = {
                            status: type,
                            spu: JSON.stringify(spu),
                            order_id: JSON.stringify(order)
                        }
                    }
                    samplePurchaseOperation(obj).then(({ data }) => {
                        this.handleOperationCallBack(data);
                    })
                })
            },

            // 计算金额
            handleCountPrice(key, price) {
                this.priceList[key] = this.countList[key] * price;
            },

            // 生成订单
            handleCreateOrder() {
                if (!this.saveCheckInfo.length) {
                    this.$message.warning('请勾选数据！');
                    return;
                }
                let spu = [];
                this.saveCheckInfo.forEach(value => {
                    spu.push(value.spu);
                });
                getSamplePurchaseDetailInfo({
                    spu: spu.join(',')
                }).then(({ data }) => {
                    if(data.status) {
                        this.addOrderInfo = data.data;
                        this.isAddShow = true;
                        data.data.spu_info.forEach((supItem, supInx) => {
                            supItem.sku_list.forEach((item, inx) => {
                                this.$set(this.countList, `${supInx}-${inx}`, item.count);
                                this.$set(this.priceList, `${supInx}-${inx}`, item.sum_price);
                            })
                        })
                    } else {
                        this.$message.error(data.errorMess || '操作失败')
                    }
                });
            },

            // 勾选产品
            handleCheckProduct(key, i, item, attr, supplierName, isCheck) {
                let obj = '';
                if (isCheck) {
                    obj = {
                        offerId: item.alibaba_atr.data.productID,
                        specId: attr.specId,
                        supplierName,
                        sku: item.sku,
                        spu: item.spu,
                        link: item.link
                    };
                    this.isCheckLink = true;
                }else {
                    this.isCheckLink = false;
                }
                item.alibaba_atr.data.sku.forEach((value, inx) => {
                    delete this.checkProductInfo[`${key}-${inx}`]
                });
                this.$set(this.checkProductInfo, `${key}-${i}`, obj);
                this.$set(this.saveActiveLink, key, attr.skuImageUrl_min);
            },

            // 取消提交订单
            handleCancelSubmitOrder() {
                this.saveActiveLink = {};
                this.checkProductInfo = {};
            },

            // 提交生成订单
            handleSubmitCreateOrder() {
                let spu = [];
                this.saveCheckInfo.forEach(value => {
                    spu.push(value.spu);
                });
                let skuArr = [];
                for (let [key, value] of Object.entries(this.checkProductInfo)) {
                    let inxKey = key.replace(/-[0-9]+$/, '');
                    skuArr.push({
                        ...value,
                        count: this.countList[inxKey]
                    })
                }

                let allLength = 0;
                this.addOrderInfo.spu_info.forEach(value => {
                    allLength += value.sku_list.length;
                })

                if (allLength !== skuArr.length) {
                    this.$message.warning('请选择商品规格！');
                    return;
                }

                let obj = {
                    spu: spu.join(','),
                    fullName: this.addOrderInfo.user_info.consignee,
                    address: this.addOrderInfo.user_info.receipt_info,
                    mobile: this.addOrderInfo.user_info.phone,
                    checkInfo: JSON.stringify(skuArr)
                };
                submitCreateOrder(obj).then(({ data }) => {
                    // this.handleOperationCallBack(data);
                    this.submitCreateData = data.data;
                })
            },

            // 指派
            handleTransferShow() {
                if (!this.saveCheckInfo.length) {
                    this.$message.warning('请勾选数据！');
                    return;
                }
                this.isTransferShow = true;
            },

            // 确认指派
            handleConfirmTransfer({ check }) {
                let spu = [];
                this.saveCheckInfo.forEach(value => {
                    spu.push(value.spu);
                });
                // check
                let obj = {
                    spu: spu.join(','),
                    distr_user: check
                }
                workTransfer(obj).then(({ data }) => {
                    this.handleOperationCallBack(data);
                })
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    if (this.isAddShow) {
                        this.isAddShow = false;
                    }
                    this.$message.success('操作成功');
                    this.handleQueryInfo({ limit: this.pageData.currentList });
                } else {
                    this.$message.error(data.errorMess || '操作失败');
                }
            },

            //查询回调
            handleQueryInfoCallBack(data){

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'sample_purchase',
                    value: JSON.stringify(this.queryData)
                });

                // 渲染下拉
                this.optionList.buyTypeList = data.drop_down_box.buy_list;
                this.optionList.functionList = data.drop_down_box.function;
                this.optionList.productLists = data.drop_down_box.category_list.data;

                let userObj = {};
                data.group_user.forEach(value => {
                    userObj[value.user_staff_id] = value.user_name;
                })
                this.optionList.userList = userObj;

                //数据渲染
                this.tableData = data.data.data_list.value;
                this.tableKey = [...data.data.data_list.key];
                this.tableKey.unshift('序号');

                // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
                this.tableProps = ['index', 'spu', 'sku_list', 'dev_imgs', 'title_cn', 'category_path', 'sample_datum', 'log_time', 'remarks'];
                this.tableWidth = ['60', '120', '350', '70', '', '150', '250', '220', ''];

                //渲染分页
                this.pageData.currentList = data.data.paging_data.limit * 1;
                this.pageData.currentPage = data.data.paging_data.offset * 1;
                this.pageData.currentTotal = data.data.paging_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try{
                if (JSON.stringify(this.samplePurchaseInfo) === '{}') {
                    this.handleQueryInfo({is_get_base: 1});
                } else {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['sample_purchase']);
                    this.handleQueryInfoCallBack(this.samplePurchaseInfo);
                }
            }catch (e) {
                console.log(e)
            }
        },
    };
</script>

<style lang="less" scoped>

    @border-color: #e6e6e6;
    @active-color: #3A7DEE;
    @icon-img: 30px;

    .ui-table--supplier_code {
        font-size: 14px;
        font-weight: bold;
        line-height: 14px;

        p {
            margin-bottom: 10px;
        }
    }
    .ui-table--supplier_code--flag {
        width: 50px;
        height: 35px;
        line-height: 54px;
        text-align: center;
        -webkit-transform: rotateZ(45deg);
        transform: rotateZ(45deg);
        background: #63c4e9;
        position: absolute;
        font-size: 12px;
        color: #FFFFFF;
        top: -12px;
        right: -20px;
    }

    .ui-table--icon_btn {
        i {
            cursor: pointer;
        }
    }

    .ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
        li {
            width: 130px;
            /*height: 185px;*/
            margin-right: 20px;
            text-align: center;
            position: relative;

            img {
                width: 100%;
                height: 185px;
            }

            .ui-com_edit__upload--item--mask {

                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 185px;
                background: rgba(0, 0, 0, .3);
                text-align: center;
                line-height: 185px;

                & > div {
                    display: inline-block;
                }

                span {
                    margin: 0 5px;
                    cursor: pointer;
                    color: #cccccc;
                    font-size: 20px;

                    &:hover {
                        color: #FFFFFF;
                    }
                }
            }

            &:hover {
                .ui-com_edit__upload--item--mask {
                    display: block;
                }
            }
        }
    }

    .ui-create_order {
        .ui-top_info {
            li {
                margin-bottom: 10px;
                font-size: 14px;
                color: #666;
            }
        }

        .ui-table_title {
            height: 30px;
            border: 1px solid @border-color;
            box-sizing: border-box;
            background: #fafafa;

            table {
                border-collapse:collapse;
                width: 100%;

                thead th {
                    height: 24px;
                    line-height: 24px;
                    color: #333333;
                    font-size: 14px;
                    font-weight: 100;
                    width: 15%;

                    &:first-child {
                        width: 40%;
                    }
                }
            }
        }

        .ui-table_content {
            margin-top: 15px;
            border: 1px solid @border-color;
            border-bottom: 0;
            position: relative;
            .ui-table_content--title {
                height: 30px;
                line-height: 30px;
                background: #fafafa;
                color: #333333;
                font-size: 14px;
                font-weight: 100;
                padding-left: 20px;
            }
            table {
                width: 100%;
                border-collapse:collapse;
                border-bottom: 1px solid @border-color;

                &:last-child {
                    border: 0 none;
                }

                tbody tr:last-child td {
                    border-bottom: 0 none;
                }

                tbody td {
                    color: #333333;
                    font-size: 14px;
                    font-weight: 100;
                    width: 15%;
                    border-bottom: 1px solid @border-color;
                    padding: 20px 0;

                    &:first-child {
                        width: 40%;
                        padding-left: 18px;
                        box-sizing: border-box;
                    }
                }
                .ui-table_content--item {
                    height: 70px;

                    .el-input {
                        width: 90px;
                    }
                }

                .ui-media {
                    position: relative;
                    .ui-media--img {
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                    .ui-media--img img {
                        width: 70px;
                        height: 70px;
                    }

                    .ui-media--content {
                        margin-left: 82px;
                        padding-right: 60px;
                        position: relative;

                        p {
                            /*line-height: 1.3em;*/
                            /*color: #666666;*/
                            display: -webkit-box;
                            -webkit-box-orient: vertical;
                            -webkit-line-clamp: 2;
                            overflow: hidden;
                            font-size: 12px;
                        }

                        span {
                            color: #999999;
                            font-size: 12px;
                        }

                        .ui-media--content--icon {
                            margin-top: 4px;
                            position: relative;
                            display: inline-block;
                            width: @icon-img;
                            height: @icon-img;
                            border: 1px solid @border-color;
                            background: url(https://cbu01.alicdn.com/img/ibank/2018/364/402/9335204463_327185066.32x32.jpg) no-repeat center;
                        }

                        .ui-media--content--icon > img {
                            margin-top: 5px;
                        }

                        .ui-media--content--icon:hover .ui-media--content--down {
                            opacity: 1;
                            visibility: unset;
                            /*max-height: unset;*/
                        }

                        .ui-media--content--down {

                            &::before {
                                display: block;
                                content: '';
                                position: absolute;
                                width: 10px;
                                height: 10px;
                                background: #FFFFFF;
                                transform: rotateZ(45deg);
                                top: -4px;
                                left: 5px;
                                box-shadow: 0 0 6px 1px #c9c9c9;
                            }

                            &::after {
                                content: '';
                                position: absolute;
                                width: 24px;
                                height: 16px;
                                background: #FFFFFF;
                                top: 0;
                                left: 0;
                            }

                            transition: .2s all linear;
                            box-shadow: 0 0 6px -1px #c9c9c9;
                            opacity: 0;
                            visibility: hidden;
                            width: 510px;
                            background: #FFFFFF;
                            border: 1px solid @border-color;
                            padding: 10px 0 0 10px;
                            position: absolute;
                            top: 40px;
                            z-index: 1;
                            /*max-height: 100px;*/
                            img {
                                width: @icon-img;
                                height: @icon-img;
                                vertical-align: middle;
                            }
                            li {
                                position: relative;
                                z-index: 2;
                                float: left;
                                color: #fc8246;
                                border: 1px solid @border-color;
                                margin-bottom: 5px;
                                margin-right: 10px;
                                cursor: pointer;
                                padding-right: 5px;

                                &.ui-active {
                                    border: 1px solid @active-color;
                                }
                            }
                        }
                    }
                }
            }

            .ui-table_content--row_count {
                position: absolute;
                bottom: 10px;
                right: 30px;

                span {
                    display: inline-block;
                    width: 150px;
                    color: #FC7019;
                    &:nth-child(3n) {
                        width: 230px;
                    }
                }
            }
        }
    }

    /deep/ .el-dialog__body {
        padding-top: 10px
    }

    .ui-table__img img {
        max-width: 70px;
        max-height: 90px;
    }
</style>


