                p::before {
                    content: "";
                    width: 5px;
                    height: 13px;
                    top: 3px;
                    left: 0;
                    position: absolute;
                    background-color: #72b0fe;
                }

            }

            .btn-list {
                padding: 10px 10px 20px 20px;
                display: flex;
                flex-direction: column;
            }

            &.check-list {
                flex: 2;
                min-width: 1035px;

