                            font-size: 14px;
                        }
                    }

                    .list-top {
                        display: flex;

                        .level-two {
                            flex: 1;

                            ul {
                                display: flex;
                                flex-wrap: wrap;
                                /*min-width: 800px;*/

                                li {
                                    width: 120px;
                                    height: 35px;
                                    border-radius: 10px;
                                    border: 1px solid #72b0fe;
                                    text-align: center;
                                    line-height: 35px;
                                    color: #72b0fe;
                                    margin: 0px 14px 10px 0px;
                                }

                                li:hover {
                                    background-color: #72b0fe;
                                    color: white;
                                }
                            }
                        }
                    }

                    .list-bottom {
                        display: flex;

                        .level-one {
                            margin-right: 22px;

                            &.item:nth-child(1) {
                                background: linear-gradient(to right, #7a7ff9, #a884f3, #f08ae6);
                            }

                            &.item:nth-child(2) {
                                background: linear-gradient(to right, #fc958b, #fd818d, #fa6891);
                            }

                            &.item:nth-child(3) {
                                background: linear-gradient(to right, #8cc4f6, #50a9fc);
                            }

