                }
            }

            &.member-maintain {
                flex: 1;
                min-width: 600px;

                .icon-list {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: 20px 0px;

                    .line-dashed {
                        border-right: 1px dashed #cccccc;
                        margin: 0px 60px;
                        height: 95px;
                    }
                }
            }

            &.quality-standard {
                flex: 2;
                min-width: 1035px;

                .quality-box {
                    display: flex;

                    .btn-list {
                        .option-box {
                            width: 220px;
                            border: 1px solid #e6e6e6;
                            border-radius: 8px;
                            overflow: hidden;

                            .op-title {
                                font-size: 14px;
                                font-weight: bold;
                                color: #333333;
                                height: 30px;
                                line-height: 30px;
                                text-align: center;
                                position: relative;
                                margin-top: 8px;
                                border-bottom: 1px solid #e6e6e6;
                            }

                            .op-title::before {
                                content: "";
                                width: 100%;
                                height: 8px;
                                background: #a8bffd;
                                position: absolute;
                                top: -10px;
                                left: 0px;
                            }

                            .op-item {
                                padding: 10px 7px;

                                li:last-child {
                                    margin-bottom: 0px;
                                }

                                li {
                                    height: 30px;
                                    line-height: 30px;
                                    background-color: #fafafb;
                                    border: 1px solid #e6e6e6;
                                    margin-bottom: 10px;
                                    border-radius: 3px;
                                    overflow: hidden;

                                    p {
                                        position: relative;
                                        padding-left: 20px;
                                    }

                                    p::before {
                                        content: "";
                                        width: 3px;
                                        height: 100%;
                                        background-color: #aabffb;
                                        opacity: 0.3;
                                        position: absolute;
                                        left: 0px;
                                        top: 0;
                                    }
                                }
                            }
                        }

                        .delivery-pack, .cushion-pack {
                            height: 220px;
                            margin-bottom: 20px;
                        }

                        .joint, .quality-grade {
                            height: 275px;
                        }

                        .sample-type, .material-package {
                            height: 515px;
                        }

                        .cushion-pack {
                            .op-title::before {
                                background-color: #d7acf8;
                            }

                            .op-item {
                                li {
                                    p::before {
                                        background-color: #d7acf8;
                                    }
                                }
                            }
                        }

                        .quality-grade {
                            .op-title::before {
                                background-color: #80bef7;
                            }

                            .op-item {
                                li {
                                    p::before {
                                        background-color: #80bef7;
                                    }
                                }
                            }
