                        .sample-type {
                            .op-title::before {
                                background-color: #f99ea8;
                            }

                            .op-item {
                                li {
                                    p::before {
                                        background-color: #f99ea8;
                                    }
                                }
                            }
                        }

                        .material-package {
                            .op-title::before {
                                background-color: #f9a3cc;
                            }

                            .op-item {
                                li {
                                    p::before {
                                        background-color: #f9a3cc;
                                    }
