</style>
<script>
import {
    goToNewPage
} from '@/services/index';

import { mapGetters } from 'vuex';

import {
    getBasicCheckListInfo
} from '@/api/quality_control_department/sample_quality_control/basic_data_maintain';

export default {
    name: 'BasicDataMaintain',
    data () {
        return {
            checkOption: {
                sub: [],
                type_name: ''
            },
            checkList: [
                {
                    id: '',
                    type_name: '',
                    sub: []
                }
            ]
        };
    },
    computed: {
        ...mapGetters([
            'queryParamsInfo'
        ])
    },
    methods: {
        goEditPage (id) {
            this.$router.push(`/test_list_maintain?queryParams=${id}`);
        },

        handleJumpToGroup () {
            goToNewPage(this, {
                pageUrl: '/quality_control_group',
                pageMain: '/basic_data_maintain',
                pageName: '品控小组维护'
            }, '_new');
        }
    },

    created () {
        //进入到缓存路径
        if (this.queryParamsInfo['basic_data_router']) {
            this.$router.push(this.queryParamsInfo['basic_data_router']);
        }
        getBasicCheckListInfo().then(({data}) => {
            this.checkList = data.data.list;
            this.checkOption = this.checkList.find((item) => {
                return item.sub.length > 0;
            });
        });
    }
};
</script>



