/**  =========================> *|`| % 商品扫码关联 % |`|*<======================== **/

import * as types from '../../../mutation-type';

const config = {
    state: {
        productScanListInfo: {}
    },
    mutations: {
        [types.SAVE_PRODUCT_SCAN_LIST_INFO] (state, saveInfo) {
            state.productScanListInfo = saveInfo;
        }
    },
    actions: {
        // 保存扫码列表
        SaveProductCancelListInfo ({commit}, queryData = {}) {

            commit(types['SAVE_PRODUCT_SCAN_LIST_INFO'], queryData);

        }
    }
};

export default config;




