<template>
    <transition name="bounce">
        <div class="ui-dialog_page" v-show="show" @keydown.esc="handleClosePage">
            <div class="ui-head">
                <span>SPU补录</span>
                <span class="el-icon-close" @click="handleClosePage"></span>
            </div>
            <div class="ui-left_nav">
                <ul>
                    <li
                            v-for="(item, index) in operationList"
                            :key="index"
                            :class="{ 'ui-active': activePage === index && 'ui-active' || submitSkuList.indexOf(item.label) > -1}"
                            @click="handleToggleEditArea(index, item)">
                        <span>{{ item.label }}</span>
                    </li>
                </ul>
            </div>
            <SPUPage :type="type" :row="row" v-if="activePage === 0" @submit="handleSubmitSpu"></SPUPage>
            <SKUPage :type="type" v-for="(item, index) in operationList" :key="index" :row="row" :sku="item.label" v-if="index && activePage === index" @submit="handleSaveSkuSubmitState"></SKUPage>
        </div>
    </transition>
</template>

<script>
    import { getSPURepairInfo, getRepairSKUState } from '@/api/dev_department/product_development/product_datum_create';
    import SKUPage from './SKUPage';
    import SPUPage from './SPUPage';
    export default {
        name: "EditPage",
        components: {
            SKUPage,
            SPUPage
        },
        props: {
            show: false,
            row: {
                type: Object,
                default() {
                    return {}
                }
            },
            type: '',
            page: 0
        },
        data() {
            return {
                sku: '',
                activePage: -1,
                submitSkuList: [],
                editPageData: {},
                isComplete: false
            }
        },
        computed: {
            operationList() {
                let skuList = [];
                let spu = '';
                try {
                    this.row.sku.split(',').forEach(value => {
                        skuList.push({
                            value: value,
                            label: value
                        })
                    });
                    spu = this.row.spu[0];
                } catch (e) {
                    console.log(this.row);
                }
                return [
                    {
                        label: 'SPU',
                        value: spu
                    },
                    ...skuList
                ]
            },
        },
        created() {

        },
        methods: {
            handleClosePage() {
                this.$emit('update:show', false);
                this.$emit('success', true);
            },

            // 切换编辑区域
            handleToggleEditArea(index, item) {
                if (!this.isComplete && this.type !== 'preview') {
                    this.$message.warning('请先提交SPU！');
                    return;
                };

                this.sku = !index ? '' : item.value;

                this.handleGetRepairInfo({ [!index ? 'spu' : 'sku']: item.value }, item.value, index)
            },

            // 获取补录信息
            handleGetRepairInfo(query = {}, editKey, editInx) {
                let obj = {
                    is_reject: this.type === 'preview' ? '1' : this.row.reject_type,
                    ...query
                };
                getSPURepairInfo(obj).then(({ data }) => {
                    this.$set(this.editPageData, editKey, data.data);
                    this.activePage = editInx;
                });
            },

            // 保存SKU提交状态
            handleSaveSkuSubmitState(val) {
                if (this.submitSkuList.indexOf(val) === -1) {
                    this.submitSkuList.push(val);
                    if (this.submitSkuList.length >= (this.operationList.length - 1)) {
                        this.handleClosePage();
                    }
                }
            },

            // 保存SPU提交状态
            handleSubmitSpu() {
                this.isComplete = true;
            }
        },
        watch: {
            show(val) {
                if (val) {
                    this.isComplete = this.row.sup_status == 1;
                    getRepairSKUState({
                        spu_sku: this.row.sku
                    }).then(({ data }) => {
                        this.submitSkuList = [...data.data];
                    });
                    if (this.page) {
                        this.handleGetRepairInfo({ sku: this.operationList[this.page].value }, this.operationList[this.page].value, this.page);
                    } else {
                        this.handleGetRepairInfo({ spu: this.row.spu[0] }, this.row.spu[0], 0);
                    }
                } else {
                    this.activePage = -1;
                }
            }
        }
    }
</script>

<style scoped lang="less">

    @border-color: #e6e6e6;
    @active-color: #5b8eff;
    .ui-dialog_page {
        width: 100%;
        height: 100%;
        background: #FFFFFF;
        top: 0;
        left: 0;
        position: absolute;
        z-index: 20;
        text-align: left;

        .ui-head {
            background: #f3f3f3;
            height: 45px;
            line-height: 45px;
            padding: 0 20px;
            font-size: 14px;
            position: relative;
            z-index: 2;
            color: #333333;
            font-weight: bold;

            .el-icon-close {
                float: right;
                font-size: 22px;
                cursor: pointer;
                margin-top: 10px;
            }
        }

        .ui-left_nav {
            width: 140px;
            height: 100%;
            position: absolute;
            left: 0;
            border-right: 1px solid @border-color;
            border-top: 1px solid @border-color;
            z-index: 1;

            ul > li {
                height: 40px;
                border-bottom: 1px solid @border-color;
                line-height: 40px;
                font-size: 14px;
                padding-left: 10px;
                cursor: pointer;

                &.ui-active {
                    background: @active-color;
                    color: #FFFFFF;
                }
            }
        }

        .ui-left_nav {
            top: 0;
            padding-top: 45px;
            box-sizing: border-box;
        }

        /deep/ .el-checkbox {
            margin-right: 20px;
            margin-left: 0;
        }
    }

    /deep/ .el-collapse {
        border: 0 none;
        overflow: hidden;
    }
    /deep/ .el-collapse-item {
        .el-collapse-item__header {
            background: #f3f3f3;
            height: 30px;
            line-height: 30px;
            text-align: left;
            padding-left: 10px;
            color: #3388ff;

            .el-collapse-item__arrow {
                line-height: 30px;
            }
        }

        .el-collapse-item__wrap {
            border: 0 none;
            padding: 10px;
        }

        .el-collapse-item__content {
            padding: 10px;
            margin-bottom: 0;
            box-sizing: border-box;
        }
    }

    .bounce-enter-active {
        animation: bounce-in .5s;
    }
    .bounce-leave-active {
        animation: bounce-in .5s reverse;
    }
    @keyframes bounce-in {
        0% {
            transform: scale(0) translate(-1000px, -1000px) rotateX(360deg);
        }
        100% {
            transform: scale(1) translate(0, 0) rotateX(0deg);
        }
    }
</style>


