/**  --->>>>   |^ 商品扫码关联^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

//扫码关联
export function getProductScanInfo (queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    };

    return http.getAjax('/product/scan_code/scan_codes', data);
}

//提交
export function submitProductScan (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/product/scan_code/upload_codes', data);

}

export function submitProductRemark (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/product/scan_code/mark_codes', data);
}

//确认关联
export function relevanceProductScan (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/product/scan_code/sure_submit_codes', data);

}

//删除扫码
export function delProductScan (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/product/scan_code/delete_codes', data);

}

//提交送样
export function sendSampleScan (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/sample_control/sample_quality_control/submit_send_sample', data, true);

}

//提交送样
export function getDetail (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/sample_control/sample_quality_control/get_box_info', data);

}



