
import http from '@/http';
import CONFIG from "../../assets/js/config";

// 获取用户信息
export function getUserInfo(queryParams) {
    // const data = {};
    return http.postAjax('/login/login/getuserinfo', queryParams);
}

// 获取省份信息
export function getProvinceInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/supplier/supplier/get_province', data);
}

// 获取城市信息
export function getCityInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/supplier/supplier/get_city', data);
}

// 查询供应商信息
export function getSupplierDetailInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/eyecheck/companyinfo/getcompanyinfo', data);
}


