
/**  --->>>>   |^ 禁用供应商列表^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';
import Store from '@/store';

// 获取禁用供应商列表信息
export function getSupplierDisabledListInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 4,
        status: 6,
        user_id: Store.getters.loginInfo.staff_code,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/supplier_export' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/supplier/supplier/supplier_search', data);
    }
}


