<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="属性名称">
                            <el-input size="small" v-model="queryData.attrName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="属性类型">
                            <el-select size="small" v-model="queryData.moduleType">
                                <el-option v-for="(item, value) in optionList.moduleTypeList" :label="item.label"
                                           :value="item.value" :key="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})"
                               icon="icon-chaxun">查询
                    </el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <el-button type="text" size="small" @click="handleClickAdd">
                <i class="icon-xinzeng1 icon_blue"></i>新增属性
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="['']"
            :showSearch="showSearch"
            :otherHeight="['ui-main-tab']">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in optionList.moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.value)"
                              :class="queryData.moduleType === item.value && 'ui-active'">{{item.label}}</span>
                    </li>
                </el-badge>
            </div>
            <template slot="operation" slot-scope="scope">
                <el-button type="text" @click="handleClickEdit(scope.row, true)" size="small">查看</el-button>
                <el-button type="text" @click="handleClickEdit(scope.row)" size="small">编辑</el-button>
                <el-button type="text" @click="handleClickDelete(scope.row)" size="small">删除</el-button>
            </template>
        </ComTableInfo>
        <!--<div class="ui-main-module-page">-->
        <!--<el-pagination-->
        <!--@size-change="handleSizeChange"-->
        <!--@current-change="handleCurrentChange"-->
        <!--:current-page="pageData.currentPage"-->
        <!--:page-sizes="pageSizes"-->
        <!--:page-size="pageData.currentList"-->
        <!--layout="total, sizes, prev, pager, next, jumper"-->
        <!--:total="pageData.currentTotal">-->
        <!--</el-pagination>-->
        <!--</div>-->
        <el-dialog
            :title="isEditShow ? isPreviewShow ? '预览' : '编辑' : '新增'"
            width="800px"
            class="ui-layout_edit-dialog"
            :visible.sync="isAddShow"
            @close="handleCancelEdit">
            <el-form ref="editData" :model="editData" :rules="editRule" :validate-on-rule-change="false" size="small"
                     label-width="80px">
                <el-form-item prop="attrCode" label="属性CODE">
                    <el-input :disabled="isPreviewShow" v-model="editData.attrCode" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item prop="attrName" label="属性名称">
                    <el-input :disabled="isPreviewShow" v-model="editData.attrName" placeholder="请输入"></el-input>
                </el-form-item>
                <!--<el-form-item prop="attrType" label="属性显示类型" :label-width="labelWidth">-->
                <!--<el-select v-model="editData.attrType">-->
                <!--<el-option v-for="item in optionList.moduleTypeList" :label="item.label" :value="item.value" :key="item.value"></el-option>-->
                <!--</el-select>-->
                <!--</el-form-item>-->
                <el-form-item prop="canValue" label="可选值">
                    <el-select
                        :disabled="isPreviewShow"
                        no-data-text="输入回车可添加"
                        no-match-text="输入回车可添加"
                        v-model="editData.canValue"
                        multiple
                        filterable
                        allow-create
                        default-first-option
                        placeholder="输入回车可添加">
                        <el-option
                            v-for="item in optionList.canValueList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item prop="remarks" label="备注">
                    <el-input
                        resize="none"
                        placeholder="请输入"
                        :disabled="isPreviewShow"
                        v-model="editData.remarks"
                        type="textarea"
                        :rows="5"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="ui-com_edit--btn">
                <el-button size="small" @click="handleCancelEdit">取消</el-button>
                <el-button size="small" type="primary" @click="handleConfirmEdit">保存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage
} from '@/services/index';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    addAttrInfo,
    getAttrDetailInfo,
    modifyAttrInfo,
    deleteAttrInfo
} from '@/api/dev_department/system_manage/product_attr_manage';

export default {
    data () {
        return {

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                moduleType: '2',
                attrName: '',
            },

            optionList: {
                moduleTypeList: [
                    {
                        label: '销售属性',
                        value: '2'
                    },
                    {
                        label: '物流属性',
                        value: '1'
                    }
                ],
                canValueList: []
            },

            tableData: [],
            tableKey: [],
            tableProps: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 编辑
            isAddShow: false,
            isEditShow: false,
            isPreviewShow: false,
            labelWidth: '100px',
            saveCheckId: '',
            editData: {
                attrCode: '',
                attrName: '',
                attrType: '',
                canValue: [],
                remarks: '',
            },
        };
    },
    computed: {
        ...mapGetters([
            'buttonList',
            'queryParamsInfo',
            'productAttrManageInfo'
        ]),
        editRule () {

            let rule = {
                attrType: config.RULE_CONFIG.requiredChange,
                attrName: config.RULE_CONFIG.requiredBlur,
                // remarks: config.RULE_CONFIG.requiredBlur,
                canValue: config.RULE_CONFIG.requiredChange,
            };

            if (this.queryData.moduleType === '2') {
                rule.attrCode = config.RULE_CONFIG.requiredBlur;
            }

            return rule;
        }
    },
    methods: {
        ...mapActions([
            'GetProductAttrManageInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['product_attr']);
            }

            let obj = {
                attribute_name_cn: this.queryData.attrName,
                attribute_type_id: this.queryData.moduleType,
                ...params
            };

            this.GetProductAttrManageInfo(obj).then(({data}) => {

                // 导出
                if (data.status === 2) return;

                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData.attrName = '';
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 切换表格数据
        handleToggleTableInfo (val) {
            this.queryData.moduleType = val;
            this.handleQueryInfo();
        },

        // 点击删除
        handleClickDelete ({attribute_id}) {
            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    attribute_id
                };

                deleteAttrInfo(obj).then(({data}) => {
                    this.handleOperationCallBack(data);
                });
            });
        },

        // 点击新增
        handleClickAdd () {
            this.isAddShow = true;
        },

        // 点击编辑
        handleClickEdit (row, isPreview) {
            getAttrDetailInfo({
                attribute_id: row.attribute_id
            }).then(({data}) => {
                this.editData.attrCode = data.data.attribute_code;
                this.editData.attrName = data.data.attribute_value_cn;
                this.editData.attrType = '';
                this.editData.canValue = data.data.attribute_value;
                this.editData.remarks = data.data.remark;
                this.saveCheckId = row.attribute_id;
                this.isEditShow = true;
                this.isAddShow = true;
                if (isPreview) {
                    this.isPreviewShow = true;
                }
            });
        },

        // 取消编辑
        handleCancelEdit () {
            this.editData.attrCode = '';
            this.editData.attrName = '';
            this.editData.attrType = '';
            this.editData.remarks = '';
            this.editData.canValue.splice(0);
            this.$refs.editData.resetFields();
            this.isAddShow = false;
            this.isEditShow = false;
            this.isPreviewShow = false;
        },

        // 确认保存
        handleConfirmEdit () {
            this.$refs.editData.validate(res => {
                if (res) {
                    let obj = {
                        attribute_type_id: this.queryData.moduleType,
                        display_type: this.editData.attrType,
                        attribute_code: this.editData.attrCode,
                        attribute_name_cn: this.editData.attrName,
                        remark: this.editData.remarks,
                        attribute_value_list: JSON.stringify(this.editData.canValue)
                    };

                    if (this.isEditShow) {
                        obj.attribute_id = this.saveCheckId;
                        modifyAttrInfo(obj).then(({data}) => {
                            this.handleOperationCallBack(data);
                        });
                    } else {
                        addAttrInfo(obj).then(({data}) => {
                            this.handleOperationCallBack(data);
                        });
                    }
                }
            });
        },

        // 操作回调
        handleOperationCallBack (data) {
            if (data.status) {
                if (this.isAddShow) {
                    this.handleCancelEdit();
                }
                this.$message.success('操作成功');
                this.handleQueryInfo({limit: this.pageData.currentList});
            } else {
                this.$message.error(data.errorMess || '操作失败');
            }
        },

        //查询回调
        handleQueryInfoCallBack ({data}) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'product_attr',
                value: JSON.stringify(this.queryData)
            });

            //数据渲染
            this.tableData = data.data_list.value;
            this.tableKey = data.data_list.key;

            this.tableProps = ['index', 'attribute_code', 'attribute_name_cn', 'display_type', 'count', 'attribute_type', 'operation'];

            //渲染分页
            // this.pageData.currentList = data.page_data.limit * 1;
            // this.pageData.currentPage = data.page_data.offset * 1;
            // this.pageData.currentTotal = data.page_data.total * 1;
        }
    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.productAttrManageInfo) === '{}') {
                this.handleQueryInfo();
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['product_attr']);
                this.handleQueryInfoCallBack(this.productAttrManageInfo);
            }
        } catch (e) {
            console.log(e);
        }
    },
};
</script>

<style lang="less" scoped>
/deep/ .el-input, .el-select {
    width: 100%;
}

.ui-com_edit {
    ul > li {
        /*width: 300px;*/
        /*margin: 0 auto;*/

        &:nth-child(2n) {
            float: right;
        }
    }

    .ui-com_edit--content--item--layout {
        padding-top: 40px;
        margin-top: 20px;
        border-top: 1px solid #EBEBEB;
    }
}
</style>



