<template>
    <TemplateDefault color="#fff">
        <div class="ui-edit_form--three">
            <el-form ref="editData" :model="editData" :rules="editRule" class="ui-edit_form_basic">
                <ul class="clearFix ui-edit_form--area">
                    <li class="ui-form--label_layout ui-lineHeight">
                        <el-form-item prop="comCode" label="统一社会信用代码" :label-width="formLabelWidth">
                            <el-input v-model="editData.comCode" @blur="handleSupplierInfo" size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="supplierName" label="供应商全称" :label-width="formLabelWidth">
                            <el-input v-model="editData.supplierName" readonly size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item label="注册地址" :label-width="formLabelWidth">
                            <el-input v-model="editData.applyAddress" readonly size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="applyDate" label="注册日期" :label-width="formLabelWidth">
                            <el-input v-model="editData.applyDate" readonly size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="supplierLink" label="供应商链接" :label-width="formLabelWidth">
                            <el-input v-model="editData.supplierLink" size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li class="ui-edit_form--item--db">
                        <el-form-item prop="pAndCity" label="发货地址" :label-width="formLabelWidth">
                            <el-cascader
                                    size="small"
                                    filterable
                                    change-on-select
                                    :props="{label: 'city_name', value: 'city_code'}"
                                    :options="optionList.deliverAddressList"
                                    v-model="editData.pAndCity"
                                    @change="handleSelectDeliver"
                            ></el-cascader>
                            <el-input v-model="editData.area" size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="companyType" label="公司类型" :label-width="formLabelWidth">
                            <el-select v-model="editData.companyType" size="small">
                                <el-option
                                        v-for="(label, value) in optionList.companyTypeList"
                                        :label="label"
                                        :key="value"
                                        :value="value"></el-option>
                            </el-select>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="phoneNumber" label="手机号码" :label-width="formLabelWidth">
                            <el-input v-model="editData.phoneNumber" size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="contactsPhone" label="联系电话" :label-width="formLabelWidth">
                            <el-input v-model="editData.contactsPhone" size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="wxNumber" label="" :label-width="formLabelWidth">
                            <label slot="label"><span class="ui-sel_required"></span>微信号</label>
                            <el-input v-model="editData.wxNumber" size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="qqNumber" label="Q Q 号码" :label-width="formLabelWidth">
                            <label slot="label"><span class="ui-sel_required"></span>Q Q 号码</label>
                            <el-input v-model="editData.qqNumber" size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="email" label="电子邮箱" :label-width="formLabelWidth">
                            <el-input v-model="editData.email" size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li class="ui-com_edit--content--item--layout">
                        <el-form-item prop="paymentType" label-width="35px">
                            <el-radio-group v-model="editData.paymentType">
                                <el-radio :label="1">线上支付宝</el-radio>
                                <el-radio :label="2">线下境外</el-radio>
                                <el-radio :label="3">线下境内</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="contacts" label="联系人" :label-width="formLabelWidth">
                            <el-input v-model="editData.contacts" size="small"></el-input>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="settlementType" label="结算方式" :label-width="formLabelWidth">
                            <el-select v-model="editData.settlementType" size="small">
                                <el-option
                                        v-for="(label, value) in optionList.settlementTypeList"
                                        :label="label"
                                        :key="value"
                                        :value="value"></el-option>
                            </el-select>
                        </el-form-item>
                    </li>
                    <li>
                        <el-form-item prop="point" label="开票税点" :label-width="formLabelWidth">
                            <el-select v-model="editData.point" size="small">
                                <el-option v-for="(item, index) in optionList.pointList" :label="item" :value="item" :key="index"></el-option>
                            </el-select>
                        </el-form-item>
                    </li>
                    <ComFormItem v-model="editData" :option="addFormItemOption" :label-width="formLabelWidth"></ComFormItem>
                </ul>
            </el-form>
            <div class="ui-com_edit__upload">
                <span class="ui-com_edit__leftLine"></span><h3>上传附属图片</h3>
                <ul class="clearFix">
                    <li v-for="(item, index) in saveFileList" :key="index">
                        <div class="ui-com_edit__upload--item">
                            <img :src="item.url" alt="">
                            <div class="ui-com_edit__upload--item--mask">
                                <span class="el-icon-delete" @click="handleDeleteFile(index)"></span>
                                <span class="el-icon-zoom-in" @click="handleLookImage(item.url)"></span>
                            </div>
                            <span>{{item.name.replace(/.[a-zA-Z]+$/, '')}}</span>
                        </div>
                    </li>
                    <li>
                        <div class="ui-com_edit__upload--last_btn">
                            <el-upload
                                    ref="myUpload"
                                    action=""
                                    multiple
                                    :show-file-list="false"
                                    :auto-upload="false"
                                    :on-change="handleSaveFileList">
                                <span class="icon-tupianshangchuan"></span>
                                <p>请点击上传</p>
                            </el-upload>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="ui-edit_form--ope">
                <el-button @click="handleSaveOrSubmitEditInfo('1')" size="small">创建</el-button>
                <el-button @click="handleSaveOrSubmitEditInfo('2')" type="primary" size="small">提交</el-button>
            </div>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
    </TemplateDefault>
</template>

<script>
    import { mapGetters } from 'vuex';
    import {
        getSupplierDetailInfo
    } from '@/api/login';
    import {
        saveOrSubmitAddInfo
    } from '@/api/dev_department/supplier_development/supplier_create_list';
    import CONFIG from '@/assets/js/config';
    import { getCityInfo } from '@/api/login';
    import { removeLabel } from '@/services'
    export default {
        name: "AddPage",
        props: {
            queryParams: {}
        },
        data() {
            return {
                formLabelWidth: '100px',

                editData: {
                    comCode: '',
                    supplierName: '',
                    applyAddress: '',
                    applyDate: '',
                    companyType: '',
                    supplierLink: '',
                    pAndCity: [],
                    area: '',
                    contacts: '',
                    phoneNumber: '',
                    contactsPhone: '',
                    wxNumber: '',
                    qqNumber: '',
                    email: '',
                    paymentType: '',
                    settlementType: '',
                    point: ''
                },
                editRule: {
                    comCode: CONFIG.RULE_CONFIG.requiredBlur,
                    supplierName: CONFIG.RULE_CONFIG.requiredChange,
                    applyAddress: CONFIG.RULE_CONFIG.requiredChange,
                    applyDate: CONFIG.RULE_CONFIG.requiredChange,
                    companyType: CONFIG.RULE_CONFIG.requiredChange,
                    supplierLink: CONFIG.RULE_CONFIG.requiredBlur,
                    pAndCity: CONFIG.RULE_CONFIG.requiredBlur,
                    area: CONFIG.RULE_CONFIG.requiredBlur,
                    contacts: CONFIG.RULE_CONFIG.requiredBlur,
                    phoneNumber: [
                        CONFIG.RULE_CONFIG.phone,
                        CONFIG.RULE_CONFIG.requiredBlur
                    ],
                    // contactsPhone: [
                    //     {
                    //         ...CONFIG.RULE_CONFIG.phone
                    //     }
                    // ],
                    qqNumber: CONFIG.RULE_CONFIG.number,
                    email: CONFIG.RULE_CONFIG.email,
                    paymentType: CONFIG.RULE_CONFIG.requiredBlur,
                    settlementType: CONFIG.RULE_CONFIG.requiredChange,
                    point: CONFIG.RULE_CONFIG.requiredBlur,
                    shopName: CONFIG.RULE_CONFIG.requiredBlur,
                    gatheringName: CONFIG.RULE_CONFIG.requiredBlur,
                    gatheringAccount: CONFIG.RULE_CONFIG.requiredBlur,
                    devUser: CONFIG.RULE_CONFIG.requiredBlur,
                    auditUser: CONFIG.RULE_CONFIG.requiredBlur,
                    importDate: CONFIG.RULE_CONFIG.requiredBlur,
                    gatheringPhone: [
                        CONFIG.RULE_CONFIG.phone,
                        CONFIG.RULE_CONFIG.requiredBlur
                    ],
                    gatheringId: [
                        ...CONFIG.RULE_CONFIG.requiredBlur,
                        CONFIG.RULE_CONFIG.idNumber
                    ],
                    bankName: CONFIG.RULE_CONFIG.requiredBlur,
                },

                addFormItemConfig: {
                    '1': [
                        {
                            label: '店铺名称',
                            key: 'shopName',
                            value: ''
                        },
                        {
                            label: '收款名称',
                            key: 'gatheringName'
                        },
                        {
                            label: '收款账号',
                            key: 'gatheringAccount',
                        },
                        {
                            label: '开发人',
                            key: 'devUser',
                        }
                    ],
                    '2': [
                        {
                            label: '收款人手机号',
                            key: 'gatheringPhone',
                            isLabelLayout: true
                        },
                        {
                            label: '收款名称',
                            key: 'gatheringName'
                        },
                        {
                            label: '收款账号',
                            key: 'gatheringAccount'
                        },
                        {
                            label: '收款人身份证',
                            key: 'gatheringId',
                            isLabelLayout: true
                        },
                        {
                            label: '开发人',
                            key: 'devUser'
                        }
                    ],
                    '3': [
                        {
                            label: '开户行名称',
                            key: 'bankName'
                        },
                        {
                            label: '收款名称',
                            key: 'gatheringName',
                        },
                        {
                            label: '收款账号',
                            key: 'gatheringAccount',
                        },
                        {
                            label: '开发人',
                            key: 'devUser',
                        }
                    ]
                },

                optionList: {
                    companyTypeList: {},
                    settlementTypeList: {},
                    createUserList: {},
                    stateList: {},
                    paymentModeList: {},
                    depUserList: {},

                    // 表单下拉
                    deliverAddressList: [],

                    pointList: []
                },

                // 上传列表
                saveFileList: [],
                fileNameList: ['营业执照', '收款协议', '纳税人证明', '开票记录'],

                // 预览图片
                isShowLookImage: false,
                lookImageUrl: [],

                // 选择图片
                uploadFilter: {
                    list: [],
                    time: ''
                }
            }
        },
        computed: {
            ...mapGetters(['pageEditState', 'comOptions']),
            addFormItemOption() {
                return this.addFormItemConfig[this.editData.paymentType]
            }
        },
        methods: {
            // 查询供应商信息
            handleSupplierInfo() {
                if (!this.editData.comCode) return;
                let obj = {
                    credit_code: this.editData.comCode
                };
                getSupplierDetailInfo(obj).then(({ data }) => {
                    if (data.status) {
                        this.editData.supplierName = data.result.name;
                        this.editData.applyDate = data.result.from_time;
                        this.editData.applyAddress = data.result.reg_location;
                    } else {
                        this.$message.error(data.errorMess || '获取供应商信息失败');
                    }
                })
            },

            // 选择发货地址
            handleSelectDeliver(val) {
                let obj = {
                    province_code: val[0]
                };
                for (let item of this.optionList.deliverAddressList) {
                    if (item.value === val[0] && item.children.length) return;
                }
                getCityInfo(obj).then(({ data }) => {
                    this.optionList.deliverAddressList.map(value => {
                        if (value.city_code === val[0]) {
                            value.children = [
                                ...data.data
                            ];
                        }
                    });
                })
            },

            // 保存上传文件
            handleSaveFileList(file, fileList) {
                clearTimeout(this.uploadFilter.time);
                this.uploadFilter.list.push(file);
                this.uploadFilter.time = setTimeout(() => {

                    for (let i = 0; i < this.uploadFilter.list.length; i++) {
                        const fileName = this.uploadFilter.list[i].name.replace(/.[a-zA-Z]+$/, '');
                        if (this.fileNameList.indexOf(fileName) === -1) {
                            this.$refs.myUpload.clearFiles();
                            this.uploadFilter.list.splice(0);
                            this.$message.error('请确认文件名！');
                            return;
                        }
                    }

                    let newFileList = [];
                    if (this.saveFileList.length) {
                        this.uploadFilter.list.forEach(value => {
                            const newFileName = value.name.replace(/.[a-zA-Z]+$/, '');
                            for (let i = 0; i < this.saveFileList.length; i++) {
                                const oldFileName = this.saveFileList[i].name.replace(/.[a-zA-Z]+$/, '');
                                if (oldFileName === newFileName) {
                                    this.saveFileList.splice(i, 1, value);
                                    break;
                                } else if (i === this.saveFileList.length - 1) {
                                    newFileList.push(value);
                                }
                            }
                        });
                    } else {
                        newFileList = [...this.uploadFilter.list];
                    }

                    this.saveFileList = [...this.saveFileList, ...newFileList];
                    this.$refs.myUpload.clearFiles();
                    this.uploadFilter.list.splice(0);
                }, 10);
            },

            // 删除文件
            handleDeleteFile(inx) {
                this.saveFileList.splice(inx, 1);
            },

            // 预览图片
            handleLookImage(src) {
                this.isShowLookImage = true;
                this.lookImageUrl = src;
            },

            // 保存新增信息
            handleSaveOrSubmitEditInfo(type) {
                this.$refs.editData.validate(res => {
                    if (res) {
                        if (!this.editData.qqNumber.replace(/\s/g, '') && !this.editData.wxNumber.replace(/\s/g, '')) {
                            this.$message.warning('请输入QQ号或微信号！');
                            return;
                        }
                        let obj = {
                            credit_code: this.editData.comCode,      //统一社会信用代码
                            supplier_name: this.editData.supplierName,    //供应商全称
                            type: this.editData.companyType,    //供应商类型 1贸易商 2生产商
                            web_link: this.editData.supplierLink,    //供应商链接
                            devlop_user: this.editData.devUser,      //开发人
                            create_user: '',      //创建人
                            tax_point: this.editData.point.replace('%', ''),      //开票税点
                            settle_method: this.editData.settlementType,      //结算方式
                            contacts: this.editData.contacts,      //联系人
                            mobile: this.editData.phoneNumber,      //手机号码
                            phone: this.editData.contactsPhone,      //联系电话
                            wechat: this.editData.wxNumber,      //微信号
                            qq: this.editData.qqNumber,      //qq
                            email: this.editData.email,      //电子邮箱
                            shipping_province: this.editData.pAndCity[0],      //发货地址-省
                            shipping_city: this.editData.pAndCity[1],      //发货地址-市
                            // shipping_area: this.editData.area,      //发货地址-区
                            shipping_address: this.editData.area,      //发货详细地址
                            pay_method: this.editData.paymentType || '',      //支付方式
                            shop_name: this.editData.shopName || '',      //店铺名称
                            receive_name: this.editData.gatheringName || '',      //收款名称
                            receive_account: this.editData.gatheringAccount || '',      //收款账号
                            receive_mobile: this.editData.gatheringPhone || '',      //收款人手机号码
                            receive_id_number: this.editData.gatheringId || '',      //收款人证件号码
                            receive_bank: this.editData.bankName || '',      //对公收款开户行
                            add_type: type,      //添加类型  1 保存  2 提交
                        };

                        let formData = new FormData();
                        for (let key in obj) {
                            formData.append(key, obj[key]);
                        }

                        this.saveFileList.forEach((value, inx) => {
                            formData.append(`file_image_data[${inx}]`, value.raw)
                        });

                        saveOrSubmitAddInfo(formData).then(({ data }) => {
                            this.handleOperationCallBack(data)
                        })
                    }
                })
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    this.$message.success('操作成功！');
                    removeLabel(this, {
                        refresh: true,
                        path: '/add_supplier_list',
                        main: '/supplier_create_list'
                    });
                } else {
                    this.$message.error(data.errorMess || '操作失败！');
                }
            }
        },
        created() {
            // 保存页面参数
            const pageData = this.pageEditState['/add_supplier_list'];
            if (pageData && pageData.value) {
                const { optionList, editData, saveFileList } = pageData.value;
                this.optionList = optionList;
                this.editData = editData;
                this.saveFileList = saveFileList;
            } else {
                this.optionList = {
                    ...this.optionList,
                    ...this.comOptions[this.queryParams]
                };
            }
        },
        beforeDestroy() {
            this.$store.commit('SAVE_PAGE_EDIT_STATE', {
                key: '/add_supplier_list',
                path: '',
                value: {
                    saveFileList: this.saveFileList,
                    editData: this.editData,
                    optionList: this.optionList
                }
            });
        }
    }
</script>

<style scoped lang="less">

    @upload-height: 185px;
    @border-color: #aaa;

    /deep/ .el-form-item__error {
        padding: 0;
    }

    .ui-edit_form--three {
        ul {
            border-bottom: 1px #e6e6e6 solid;
        }

        .ui-sel_required {
            position: relative;
            left: -2px;
            &::after {
                content: "*";
                color: orange;
            }
        }

        .el-select {
            width: 100%;
        }

        /deep/ .el-form-item {
            margin-bottom: 10px;
            position: relative;
        }

        .ui-edit_form--area > li, /deep/ .ui-edit_form--area > div > li {
            float: left;
            width: 25%;
            height: 50px;
            padding-right: 50px;
            box-sizing: border-box;
            margin-left: 20px;
        }
        /deep/.ui-edit_form--area .ui-lineHeight{
            .el-form-item__label {
                line-height: 20px;
            }
        }
        /deep/li.ui-com_edit--content--item--layout {
            width: 100%;
            text-align: left;
            height: 30px;
            margin: 0;
            padding-left: 30px;
            .el-form-item__content {
                line-height: 5px;
            }

        }
        .ui-edit_form--item--db {
            /*/deep/ .el-input {*/
                /*width: 47%;*/
            /*}*/
            .el-cascader, .el-input {
                width: 50%;
                float: left;
            }
            .el-cascader--small {
                line-height: 40px;
            }
        }

        .ui-form_upload {
            margin-top: 20px;
            .ui-form_upload--title {
                font-size: 14px;
                font-weight: bold;
                text-align: left;
                display: block;
            }

            li {
                float: left;
                width: 130px;
                /*height: 185px;*/
                margin-right: 20px;
                text-align: center;
                position: relative;

                img {
                    width: 100%;
                    height: 185px;
                }

                .ui-form_upload--label {
                    font-size: 14px;
                    position: absolute;
                    width: 100%;
                    bottom: 10px;
                    left: 0;
                }

                .ui-form_upload--item--mask {

                    display: none;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 185px;
                    background: rgba(0, 0, 0, .3);
                    text-align: center;
                    line-height: 185px;

                    & > div {
                        display: inline-block;
                    }

                    span {
                        margin: 0 5px;
                        cursor: pointer;
                        color: #cccccc;
                        font-size: 20px;

                        &:hover {
                            color: #FFFFFF;
                        }
                    }
                }

                &:hover {
                    .ui-form_upload--item--mask {
                        display: block;
                    }
                }
            }
        }

        .ui-edit_form--ope {
            padding: 10px;
            .el-button {
                margin: 0 28px;
            }
        }
    }
    .ui-edit_form--three:nth-child(1) {
        padding-top: 40px;
    }
    .ui-com_edit__upload {
        position: relative;
        h3 {
            font-size: 12px;
            text-align: left;
            position: relative;
            margin: 10px 0 0 20px;
            padding-left: 8px;

        }
        .ui-com_edit__leftLine {
            height: 12px;
            width: 2px;
            display: inline-block;
            background: #333333;
            position: absolute;
            left: 20px;
            top: 3px;
        }
        li {
            float: left;
            width: 130px;
            margin: 20px;
            text-align: center;
            position: relative;

            .ui-com_edit__upload--item > span {
                color: #666;
                font-size: 14px;
                position: absolute;
                bottom: 10px;
                left: 0;
                display: block;
                width: 100%;
            }

            img {
                width: 100%;
                height: @upload-height;
            }

            .ui-com_edit__upload--last_btn {
                border: 1px solid #EAEEED;
                width: 100%;
                height: @upload-height;

                /deep/ .el-upload {
                    display: block;
                    height: @upload-height;
                    line-height: @upload-height;
                    background: #F5F9F8;

                    span {
                        color: @border-color;
                        font-size: 34px;
                        margin-right: 0;
                        position: relative;
                        top: -8px
                    }
                    p {
                        position: absolute;
                        left: 33px;
                        top: 20px;
                        font-size: 12px;
                        color: #999999;
                    }
                    .ui-click_txt {
                        display: inline-block;
                        font-size: 12px;
                        width: 100px;

                    }
                }
            }

            .ui-com_edit__upload--item--mask {

                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: @upload-height;
                background: rgba(0, 0, 0, .3);
                text-align: center;
                line-height: 185px;

                & > div {
                    display: inline-block;
                }

                span {
                    margin: 0 5px;
                    cursor: pointer;
                    color: #cccccc;
                    font-size: 20px;

                    &:hover {
                        color: #FFFFFF;
                    }
                }
            }

            &:hover {
                .ui-com_edit__upload--item--mask {
                    display: block;
                }
            }
        }
    }
</style>


