
/**  --->>>>   |^ 样品跟单管理^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取样品跟单管理信息
export function getSampleTrackManageInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 4,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/create_supplier' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/sample/Sample_order/select_sample_order_list', data);
    }
}

// 确认到样
export function confirmDownSample(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/sample/Sample_order/confirm_sample_spu', data);
}

// 退回样品
export function backSampleInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/sample/Sample_order/send_back_sample_spu', data);
}

// 取消买样
export function cancelBuySample(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/sample/Sample_order/cancel_sample_spu', data);
}

// 获取物流信息
export function getLogisticsInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/sample/Sample_order/get_ali_logistics_info', data);
}




