            span {
                cursor: pointer;
            }
        }
    }

    .ui-currency--area {
        padding: 0 20px 20px;

        .el-select {
            width: unset;
        }

        .el-input-group--append {
            width: 200px;
            background-color: #fff;

            .el-select {
                width: 90px;
            }
        }

        /deep/ .el-input-group__append {
            background-color: #fff;
        }
    }

    .ui-table--form {
        padding: 0 20px 10px;
    }

    .ui-table--supplier_code {
        font-size: 12px;
        line-height: 10px;
        p:nth-child(1) {
            font-weight: bold;
        }

        p {
            margin-bottom: 8px;
            span {
                color: #3388ff;
                cursor: pointer;
            }
        }

    }

    /deep/ .el-table td, /deep/ .el-table th {
        overflow: hidden;
        text-align: left !important;
    }

    .ui-table--supplier_code--flag {
        width: 50px;
        height: 35px;
        line-height: 54px;
        text-align: center;
        -webkit-transform: rotateZ(45deg);
        transform: rotateZ(45deg);
        position: absolute;
        font-size: 12px;
        color: #FFFFFF;
        top: -12px;
        right: -20px;
        font-weight: normal;
    }

    .ui-table--icon_btn {
        i {
            cursor: pointer;
        }
    }

    .ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
        li {
            width: 130px;
            /*height: 185px;*/
            margin-right: 20px;
            text-align: center;
            position: relative;

            img {
                width: 100%;
                height: 185px;
            }

            .ui-com_edit__upload--item--mask {

                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 185px;
                background: rgba(0, 0, 0, .3);
                text-align: center;
                line-height: 185px;

                & > div {
                    display: inline-block;
                }

                span {
                    margin: 0 5px;
                    cursor: pointer;
                    color: #cccccc;
                    font-size: 20px;

                    &:hover {
                        color: #FFFFFF;
                    }
                }
            }

            &:hover {
                .ui-com_edit__upload--item--mask {
                    display: block;
                }
            }
        }
    }

    .ui-table__img img{
        max-width: 70px;
        max-height: 80px;
    }

    .ui-sku_open {
        li {
            cursor: pointer;

            &:hover {
                color: #3EA8FF;
            }
        }
    }
    .el-icon-warning {
        color: #F56C6C;
    }

    .el-icon-success {
        color: #67C23A;
    }
</style>



