/**  =========================> *|`| % 美工小组管理 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getAtrGroupInfo,
} from '../../../../api/design_department/artist_manage/atr_group_manage'

const config = {
    state: {
        atrGroupInfo: {},
    },
    mutations: {
        [types.SAVE_ATR_GROUP_INFO] (state, saveInfo) {
            state.atrGroupInfo = saveInfo
        }
    },
    actions: {
        // 获取文案小组管理信息
        GetAtrGroupInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getAtrGroupInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_ATR_GROUP_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




