/**  =========================> *|`| % 商品修图列表 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getGoodsModificationListInfo,
} from '../../../../api/design_department/artist_manage/goods_modification_list'

const config = {
    state: {
        goodsModificationListInfo: {},
    },
    mutations: {
        [types.SAVE_GOODS_MODIFICATION_LIST_INFO] (state, saveInfo) {
            state.goodsModificationListInfo = saveInfo
        }
    },
    actions: {
        GetGoodsModificationListInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getGoodsModificationListInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_GOODS_MODIFICATION_LIST_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




