/**  =========================> *|`| % 样品审核列表 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getSampleAuditListInfo,
} from '../../../../api/quality_control_department/sample_quality_control/sample_audit_list'

const config = {
    state: {
        sampleAuditListInfo: {},
    },
    mutations: {
        [types.SAVE_SAMPLE_AUDIT_LIST_INFO] (state, saveInfo) {
            state.sampleAuditListInfo = saveInfo
        }
    },
    actions: {
        GetSampleAuditListInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getSampleAuditListInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SAMPLE_AUDIT_LIST_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




