<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model.trim="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model.trim="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商类型">
                            <el-select v-model="queryData.supplierType" placeholder="请选择" size="small">
                                <el-option
                                        v-for="(label, value) in optionList.supplierTypeList"
                                        :key="value"
                                        :label="label"
                                        :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="结算方式">
                            <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                <el-option
                                        v-for="(label, value) in optionList.settlementTypeList"
                                        :key="value"
                                        :label="label"
                                        :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="开发人" template="select" :options="optionList.devUserList"
                                           v-model="queryData.devUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="发货地址">
                            <el-cascader
                                    change-on-select
                                    v-model="queryData.deliverAddress"
                                    size="small"
                                    placeholder="请选择"
                                    :options="optionList.deliverAddressList"
                                    filterable
                                    :props="{value: 'city_code', label: 'city_name'}"
                                    @change="handleSelectDeliver"
                            ></el-cascader>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="支付方式">
                            <el-select v-model="queryData.paymentMode" placeholder="请选择" size="small">
                                <el-option
                                        v-for="(label, value) in optionList.paymentModeList"
                                        :key="value"
                                        :label="label"
                                        :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="联络人">
                            <el-input size="small" v-model="queryData.liaisons"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="创建人" template="select" :options="optionList.createUserList"
                                           v-model="queryData.createUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="创建日期" template="daterange" v-model="queryData.createTime"></SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})" icon="icon-chaxun">查询</el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <el-button @click="handleClickAudit" type="text" size="small">
                <i class="icon-shenhe icon-warn"></i>审核</el-button>
            <el-button @click="handleClickReject" type="text" size="small">
                <i class="icon-bohui icon-danger"></i>驳回</el-button>
        </div>
        <ComTableInfo
                isCheck
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableData="tableData"
                :tableWidth="tableWidth"
                :showSearch="showSearch"
                @checked="handleSaveCheckInfo">
            <template slot="index" slot-scope="scope">
                <div>
                    <p>{{autoAddTableInx(scope.$index)}}</p>
                </div>
            </template>
            <template slot="supplier_code" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.supplier_code}}</p>
                    <div class="ui-table--supplier_code--flag" :class="scope.row.type === '生产商' ? 'ui-table_flag1' : 'ui-table_flag3'">
                        <span>{{scope.row.type.replace('商', '')}}</span>
                    </div>
                    <div class="ui-table--icon_btn">
                        <!--<i class="icon-chakanshuxingleixing" @click.stop="handleClickPreview(scope.row)"></i>-->
                    </div>
                </div>
            </template>
            <template slot="supplier_name" slot-scope="scope">
                <div>
                    <p>{{scope.row.supplier_name}}</p>
                    <div class="ui-table--icon_btn">
                        <el-popover
                                placement="bottom"
                                title="供应商链接"
                                width="200"
                                trigger="click">
                            <span class="ui-link">{{scope.row.web_link}}</span>
                            <el-button slot="reference" type="text"><i class="icon-gongyingshanglianjie"></i></el-button>
                        </el-popover>
                        <i class="icon-tianyancha" @click="handlePreviewDetail(scope.row)"></i>
                    </div>
                </div>
            </template>
            <template slot="shipping" slot-scope="scope">
                <div>
                    <p>{{`${scope.row.shipping_province}${scope.row.shipping_city}`}}</p>
                    <p>{{`${scope.row.shipping_area}${scope.row.shipping_address}`}}</p>
                </div>
            </template>
            <template slot="contacts_info" slot-scope="scope">
                <div>
                    <p><i class="icon-ren icon-plain"></i>{{`${scope.row.contacts}/${scope.row.mobile}`}}</p>
                    <p><i class="icon-QQ icon-main"></i><a target="_self" :href="`tencent://message/?uin=${scope.row.qq}`">{{scope.row.qq}}</a></p>
                    <p><i class="icon-weixin icon-wx"></i>{{scope.row.wechat}}</p>
                    <p><i class="icon-youxiang icon-danger"></i>{{scope.row.email}}</p>
                </div>
            </template>
            <template slot="payment_info" slot-scope="scope">
                <div v-if="scope.row.pay_method === '线上支付宝'">
                    <p><i class="icon-zhifufangshi icon-warn"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><i class="icon-zhifubaozhanghao1 icon-main"></i>{{scope.row.receive_account}}</p>
                    <p><i class="icon-zhifubaomingcheng icon-main"></i>{{scope.row.receive_name}}</p>
                    <p><i class="icon-dianpumingcheng1 icon-dp"></i>{{scope.row.shop_name}}</p>
                </div>
                <div v-else-if="scope.row.pay_method === '线下境外'">
                    <p><i class="icon-zhifufangshi icon-warn"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><i class="icon-ren icon-main"></i>{{`${scope.row.receive_name}/${scope.row.receive_mobile}`}}</p>
                    <p><i class="icon-yinhangqiazhanghao icon-main"></i>{{scope.row.receive_account}}</p>
                    <p><i class="icon-shenfenzhenghaoma icon-dp"></i>{{scope.row.receive_id_number}}</p>
                </div>
                <div v-else-if="scope.row.pay_method === '线下境内'">
                    <p><i class="icon-zhifufangshi icon-warn"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><i class="icon-gongsimingcheng icon-main"></i>{{scope.row.receive_name}}</p>
                    <p><i class="icon-yinhangqiazhanghao icon-main"></i>{{scope.row.receive_account}}</p>
                    <p><i class="icon-kaihuhang icon-dp"></i>{{scope.row.receive_bank}}</p>
                </div>
            </template>
            <template slot="operate_dev_user" slot-scope="scope">
                <div>
                    <p v-if="scope.row.devlop_user"><span>开发人：</span><span>{{scope.row.devlop_user}}</span></p>
                    <p v-if="scope.row.create_user"><span>创建人：</span><span>{{scope.row.create_user}}</span></p>
                    <p v-if="scope.row.check_user"><span>审核人：</span><span>{{scope.row.check_user}}</span></p>
                </div>
            </template>
            <template slot="file_url" slot-scope="scope">
                <ul class="ui-table_img">
                    <li v-for="(name, key) in fileTypeList" v-if="scope.row[key]">
                        <div class="ui-table_img--item" :style="`background:url(${scope.row[key]}) center no-repeat`"></div>
                        <span>{{ name }}</span>
                    </li>
                </ul>
            </template>
            <template slot="operation" slot-scope="scope">
                <el-button type="text" @click="handleOperaiontLog(scope.row)" size="small">操作日志</el-button>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>

        <el-dialog
                custom-class="ui-com_edit"
                title="预览"
                width="1200px"
                top="3vh"
                :visible.sync="isPreviewShow">
            <div class="ui-com_edit--content--item">
                <el-form ref="editData" :model="editData">
                    <ul class="clearFix ui-com_edit--content--item3">
                        <li class="ui-form--label_layout">
                            <el-form-item prop="comCode" label="统一社会信用代码" :label-width="formLabelWidth">
                                <el-input v-model="editData.comCode" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="supplierName" label="供应商全称" :label-width="formLabelWidth">
                                <el-input v-model="editData.supplierName" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="applyAddress" label="注册地址" :label-width="formLabelWidth">
                                <el-input v-model="editData.applyAddress" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="applyDate" label="注册日期" :label-width="formLabelWidth">
                                <el-input v-model="editData.applyDate" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="supplierLink" label="供应商链接" :label-width="formLabelWidth">
                                <el-input v-model="editData.supplierLink" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li class="ui-com_edit--content--item--double">
                            <el-form-item prop="pAndCity" label="发货地址" :label-width="formLabelWidth">
                                <el-cascader
                                        filterable
                                        change-on-select
                                        size="small"
                                        :options="optionList.deliverAddressList"
                                        :props="{value: 'city_code', label: 'city_name'}"
                                        v-model="editData.pAndCity"
                                        @change="handleSelectDeliver"
                                ></el-cascader>
                                <el-input v-model="editData.area" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="companyType" label="公司类型" :label-width="formLabelWidth">
                                <el-select v-model="editData.companyType" size="small">
                                    <el-option
                                            v-for="(label, value) in optionList.companyTypeList"
                                            :label="label"
                                            :key="value"
                                            :value="value"></el-option>
                                </el-select>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="phoneNumber" label="手机号码" :label-width="formLabelWidth">
                                <el-input v-model="editData.phoneNumber" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="contactsPhone" label="联系电话" :label-width="formLabelWidth">
                                <el-input v-model="editData.contactsPhone" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="contacts" label="联系人" :label-width="formLabelWidth">
                                <el-input v-model="editData.contacts" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="qqNumber" :label-width="formLabelWidth">
                                <label slot="label">Q Q 号码</label>
                                <el-input v-model="editData.qqNumber" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="email" label="电子邮箱" :label-width="formLabelWidth">
                                <el-input v-model="editData.email" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="wxNumber" :label-width="formLabelWidth">
                                <label slot="label">微信号</label>
                                <el-input v-model="editData.wxNumber" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="settlementType" label="结算方式" :label-width="formLabelWidth">
                                <el-select v-model="editData.settlementType" size="small">
                                    <el-option
                                            v-for="(label, value) in optionList.settlementTypeList"
                                            :label="label"
                                            :key="value"
                                            :value="value"></el-option>
                                </el-select>
                            </el-form-item>
                        </li>
                        <li>
                            <el-form-item prop="point" label="开票税点" :label-width="formLabelWidth">
                                <el-input v-model="editData.point" size="small"></el-input>
                            </el-form-item>
                        </li>
                        <li class="ui-com_edit--content--item--layout">
                            <el-form-item prop="paymentType" label-width="35px">
                                <el-radio-group v-model="editData.paymentType">
                                    <el-radio label="1">线上支付宝</el-radio>
                                    <el-radio label="2">线下境外</el-radio>
                                    <el-radio label="3">线下境内</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </li>
                        <ComFormItem v-model="editData" :option="addFormItemOption" :label-width="formLabelWidth" size="small"></ComFormItem>
                    </ul>
                </el-form>
                <div class="ui-com_edit__dialog--title">
                    <span>上传附属图片</span>
                </div>
                <div class="ui-com_edit__upload">
                    <ul class="clearFix">
                        <li v-for="(item, index) in saveFileList" :key="index">
                            <div class="ui-com_edit__upload--item">
                                <img :src="item.url" alt="">
                                <div class="ui-com_edit__upload--item--mask">
                                    <span class="el-icon-zoom-in" @click="handleLookImage(item.url)"></span>
                                </div>
                                <span>{{item.name.replace(/.[a-zA-Z]+$/, '')}}</span>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </el-dialog>

        <ComDialogRemarks showRemarks :visible.sync="isShowAudit" title="审核" @confirm="handleConfirmAudit"></ComDialogRemarks>
        <ComDialogRemarks showRemarks :visible.sync="isShowReject" title="驳回" @confirm="handleConfirmReject"></ComDialogRemarks>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <QueryPreview :show.sync="isQueryPreview" :detail="queryPreviewData"></QueryPreview>
        <OperationLog :show.sync="isOpeLogShow" :id="saveCheckId"></OperationLog>
    </div>
</template>

<script>
    import QueryPreview from './SupplierCreateList/QueryPreview'
    import OperationLog from './SupplierCreateList/OperationLog'
    import config from '@/assets/js/config';
    import {
        getSupplierDetailInfo,
        getCityInfo
    } from '@/api/login';
    import {
        autoAddTableInx,
        getDefaultTime,
        changeTableInfoProps,
        goToNewPage,
        comChangeSupplierState
    } from '@/services/index'
    import {
        mapActions,
        mapGetters
    } from "vuex";
    import {
        getSupplierCreateListInfo,
        changeSupplierState,
        getEditInfo
    } from '@/api/dev_department/supplier_development/supplier_create_list';

    export default {
        components: {
            QueryPreview,
            OperationLog
        },
        data() {
            return {


                // 时间搜索框宽度设置
                searchDateWidth: config.SEARCH_DATE_WIDTH,

                thisVm: {},

                // 搜索缩进
                showSearch: true,

                queryData: {
                    supplierCode: '',
                    supplierName: '',
                    supplierType: '',
                    settlementType: '',
                    devUser: '',
                    deliverAddress: [],
                    paymentMode: '',
                    liaisons: '',
                    createUser: '',
                    createTime: ''
                },

                optionList: {
                    supplierTypeList: {},
                    settlementTypeList: {},
                    devUserList: {},
                    deliverAddressList: [],
                    paymentModeList: {},
                    liaisonsList: {},
                    createUserList: {}
                },

                tableData: [],
                tableKey: [],
                tableWidth: [],
                tableProps: [],

                // 保存勾选数据
                saveCheckInfo: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                // 审核
                isShowAudit: false,

                // 驳回
                isShowReject: false,

                fileTypeList: {
                    bl_url: '营业执照',
                    tca_url: '收款协议',
                    gtc_url: '纳税人证明',
                    b_r_rul: '开票记录',
                },

                // 预览图片
                isShowLookImage: false,
                lookImageUrl: [],

                // 天眼查预览
                isQueryPreview: false,
                queryPreviewData: {},

                // 点击预览
                isPreviewShow: false,
                formLabelWidth: '110px',
                saveFileList: [],
                editData: {
                    comCode: '',
                    supplierCode: '',
                    supplierName: '',
                    applyAddress: '',
                    applyDate: '',
                    companyType: '',
                    supplierLink: '',
                    pAndCity: [],
                    area: '',
                    contacts: '',
                    phoneNumber: '',
                    contactsPhone: '',
                    wxNumber: '',
                    qqNumber: '',
                    email: '',
                    paymentType: '',
                    settlementType: '',
                    point: ''
                },
                addFormItemConfig: {
                    '1': [
                        {
                            label: '店铺名称',
                            key: 'shopName',
                            value: ''
                        },
                        {
                            label: '收款名称',
                            key: 'gatheringName'
                        },
                        {
                            label: '收款账号',
                            key: 'gatheringAccount',
                        },
                        {
                            label: '开发人',
                            key: 'devUser',
                        },
                        {
                            label: '审核人',
                            key: 'auditUser',
                        },
                        {
                            label: '导入日期',
                            key: 'importDate'
                        }
                    ],
                    '2': [
                        {
                            label: '收款人手机号',
                            key: 'gatheringPhone',
                            isLabelLayout: true
                        },
                        {
                            label: '收款名称',
                            key: 'gatheringName'
                        },
                        {
                            label: '收款账号',
                            key: 'gatheringAccount'
                        },
                        {
                            label: '收款人身份证',
                            key: 'gatheringId',
                            isLabelLayout: true
                        },
                        {
                            label: '开发人',
                            key: 'devUser'
                        },
                        {
                            label: '审核人',
                            key: 'auditUser'
                        },
                        {
                            label: '导入日期',
                            key: 'importDate'
                        },
                    ],
                    '3': [
                        {
                            label: '开户行名称',
                            key: 'bankName'
                        },
                        {
                            label: '收款名称',
                            key: 'gatheringName',
                        },
                        {
                            label: '收款账号',
                            key: 'gatheringAccount',
                        },
                        {
                            label: '开发人',
                            key: 'devUser',
                        },
                        {
                            label: '审核人',
                            key: 'auditUser',
                        },
                        {
                            label: '导入日期',
                            key: 'importDate',
                        },
                    ]
                },
                // 操作日志
                saveCheckId: '',
                isOpeLogShow: false,
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'supplierDisAuditInfo'
            ]),
            addFormItemOption() {
                return this.addFormItemConfig[this.editData.paymentType]
            }
        },
        methods: {
            ...mapActions([
                'GetSupplierDisabledAuditInfo'
            ]),
            autoAddTableInx,
            getDefaultTime,
            goToNewPage,

            // 列表信息查询
            handleQueryInfo(params = {}, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['supplier_dis_audit']);
                }

                let obj = {
                    status: this.queryData.state,
                    supplier_code: this.queryData.supplierCode.replace(/\s+/g, ','),
                    type: this.queryData.supplierType,
                    shipping_province: this.queryData.deliverAddress[0],
                    shipping_city: this.queryData.deliverAddress[1],
                    settle_method: this.queryData.settlementType,
                    create_user: this.queryData.createUser,
                    devlop_user: this.queryData.devUser,
                    supplier_name: this.queryData.supplierName,
                    contacts: this.queryData.liaisons,
                    pay_method: this.queryData.paymentMode,
                    start_time: this.queryData.createTime[0],
                    end_time: this.queryData.createTime[1],
                    ...params
                };

                this.GetSupplierDisabledAuditInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;

                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 查询重置
            handleResetQueryInfo() {
                this.queryData = {
                    supplierCode: '',
                    supplierName: '',
                    supplierType: '',
                    settlementType: '',
                    devUser: '',
                    deliverAddress: [],
                    paymentMode: '',
                    liaisons: '',
                    createUser: '',
                    createTime: '',
                    state: '',
                }
            },

            // 分页条数切换
            handleSizeChange(val) {
                this.handleQueryInfo({
                    limit: val,
                    offset: 1
                }, true)
            },

            // 跳转页数切换
            handleCurrentChange(val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true)
            },

            // 保存勾选数据
            handleSaveCheckInfo(val) {
                this.saveCheckInfo = val;
            },

            // 操作日志
            handleOperaiontLog({ supplier_id }) {
                this.saveCheckId = supplier_id;
                this.isOpeLogShow = true;
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 天眼查数据预览
            handlePreviewDetail({ credit_code }) {
                getSupplierDetailInfo({
                    credit_code: credit_code
                }).then(({ data }) => {
                    this.queryPreviewData = data.result;
                    this.isQueryPreview = true;
                });
            },

            // 预览
            handleClickPreview({ supplier_id, status_code }) {
                this.isPreviewShow = true;

                let obj = {
                    supplier_id,
                    status: status_code
                };
                getEditInfo(obj).then(({ data }) => {

                    // 加载地址
                    this.handleSelectDeliver([data.data.data.province_code]);
                    this.saveCheckId = supplier_id;
                    this.isEditShow = true;
                    this.editData.comCode = data.data.data.credit_code;
                    this.editData.supplierName = data.data.data.supplier_name;
                    this.editData.supplierCode = data.data.data.supplier_code;
                    this.editData.applyAddress = data.data.data.reg_address;
                    this.editData.applyDate = data.data.data.reg_time;
                    this.editData.companyType = data.data.data.type;
                    this.editData.supplierLink = data.data.data.web_link;
                    this.editData.pAndCity = [data.data.data.province_code, '1000'];
                    this.editData.area = data.data.data.shipping_address;
                    this.editData.contacts = data.data.data.contacts;
                    this.editData.phoneNumber = data.data.data.mobile;
                    this.editData.contactsPhone = data.data.data.phone;
                    this.editData.wxNumber = data.data.data.wechat;
                    this.editData.qqNumber = data.data.data.qq;
                    this.editData.email = data.data.data.email;
                    this.editData.paymentType = data.data.data.pay_method;
                    this.editData.settlementType = data.data.data.settle_method;
                    this.editData.point = data.data.data.tax_point;
                    this.editData.devUser = data.data.data.devlop_user;
                    this.editData.auditUser = data.data.data.check_user;
                    this.editData.importDate = data.data.data.create_time;
                    this.editData.gatheringAccount = data.data.data.receive_account;
                    this.editData.bankName = data.data.data.receive_bank;
                    this.editData.gatheringName = data.data.data.receive_name;
                    this.editData.gatheringId = data.data.data.receive_id_number;
                    this.editData.gatheringPhone = data.data.data.receive_mobile;
                    this.editData.shopName = data.data.data.shop_name;

                    for (let key in this.fileTypeList) {
                        if (data.data.data[key]) {
                            this.saveFileList.push({
                                name: this.fileTypeList[key],
                                url: data.data.data[key]
                            });
                        }
                    }
                });
            },

            // 选择发货地址
            handleSelectDeliver(val) {
                let obj = {
                    province_code: val[0]
                };
                for (let item of this.optionList.deliverAddressList) {
                    if (item.value === val[0] && item.children.length) return;
                }
                getCityInfo(obj).then(({ data }) => {
                    this.optionList.deliverAddressList.map(value => {
                        if (value.city_code === val[0]) {
                            value.children = [
                                ...data.data
                            ];
                        }
                    });
                })
            },

            // 预览图片
            handleLookImage(src) {
                this.isShowLookImage = true;
                this.lookImageUrl = src;
            },

            // 审核
            handleClickAudit() {
                if (!this.saveCheckInfo.length) {
                    this.$message.warning('请勾选数据！');
                    return;
                }
                this.isShowAudit = true;
            },

            // 确认审核
            handleConfirmAudit({ remark }) {

                let obj = comChangeSupplierState(6, remark,this.saveCheckInfo);

                changeSupplierState(obj).then(({ data }) => {
                    this.handleOperationCallBack(data);
                });
            },

            // 驳回
            handleClickReject() {
                if (!this.saveCheckInfo.length) {
                    this.$message.warning('请勾选数据！');
                    return;
                }
                this.isShowReject = true;
            },

            // 确认驳回
            handleConfirmReject({ remark }) {
                let obj = comChangeSupplierState(4, remark,this.saveCheckInfo);

                changeSupplierState(obj).then(({ data }) => {
                    this.handleOperationCallBack(data);
                });
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo({ limit: this.pageData.currentList });
                } else {
                    this.$message.error(data.errorMess || '操作失败');
                }
            },

            //查询回调
            handleQueryInfoCallBack(data){

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'supplier_dis_audit',
                    value: JSON.stringify(this.queryData)
                });

                if (!this.optionList.deliverAddressList.length) {
                    let deliverList = [];
                    data.select_list.province_info.forEach(value => {
                        let obj = {
                            city_name: value.province_name,
                            city_code: value.province_code
                        };
                        deliverList.push({
                            ...obj,
                            children: []
                        })
                    });

                    this.optionList.deliverAddressList = [...deliverList];
                    this.handleSelectDeliver(this.queryData.deliverAddress);
                }

                // 渲染下拉
                this.optionList.supplierTypeList = data.select_list.sup_type;
                this.optionList.settlementTypeList = data.select_list.sup_settle_method;
                this.optionList.devUserList = data.select_list.devlop_user;
                this.optionList.paymentModeList = data.select_list.sup_pay_method;
                // this.optionList.liaisonsList = data.select_list.sup_pay_method;
                this.optionList.createUserList = data.select_list.create_user;
                this.optionList.stateList = data.select_list.sup_status;

                //数据渲染
                this.tableData = data.data.value;
                this.tableKey = [...data.data.key];
                this.tableKey.push('操作');
                this.tableWidth = ['80px', '100px', '180px', '150px', '180px', '180px', '180px', '240px', '80px', '', '100px'];

                // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
                this.tableProps = ['index', 'supplier_code', 'supplier_name', 'shipping', 'contacts_info', 'payment_info', 'operate_dev_user', 'file_url', 'status', 'disable_reason', 'operation'];

                //渲染分页
                this.pageData.currentList = data.page.limit * 1;
                this.pageData.currentPage = data.page.offset * 1;
                this.pageData.currentTotal = data.page.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try{
                if (JSON.stringify(this.supplierDisAuditInfo) === '{}') {
                    this.handleQueryInfo();
                }else{
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['supplier_dis_audit']);
                    this.handleQueryInfoCallBack(this.supplierDisAuditInfo);
                }
            }catch (e) {
                console.log(e)
            }
        },
    };
</script>

<style lang="less" scoped>

</style>


