<template>
    <TemplateDefault color="#fff">
        <div class="ui-batch_create">
            <div class="ui-batch_create--fn">
                <ComUpload label="选择文件" :url="importUrl" @success="handleUploadSuccess" :upData="{ user_id: loginInfo.staff_code || '12534' }"></ComUpload>
                <div class="ui-batch_create--sub">
                    <span>本次导入数据共 <span style="color: #1EAEFF;">{{importInfo.sup_count}}</span> 条，其中 <span style="color: #FF281C;">{{importInfo.err_count}}</span>条数据有误，{{importInfo.err_count !== 0 ? '请修正后重新上传！' : '请确认上传。'}}</span>
                    <el-button size="small" type="primary" @click="handleConfirmUpload">确认上传</el-button>
                </div>
            </div>
            <ComTableInfo
                    isCheck
                    :tableKey="tableKey"
                    :tableData="tableData"
                    :tableProps="tableProps"
                    :otherHeight="['ui-batch_create--fn']"
                    @checked="handleSaveCheckInfo">
                <template slot="bl_url" slot-scope="scope">
                    <div class="ui-table__img">
                        <img :src="scope.row.bl_url + '?v=' + Math.random()" alt="">
                    </div>
                </template>
                <template slot="b_r_rul" slot-scope="scope">
                    <div class="ui-table__img">
                        <img :src="scope.row.b_r_rul + '?v=' + Math.random()" alt="">
                    </div>
                </template>
                <template slot="gtc_url" slot-scope="scope">
                    <div class="ui-table__img">
                        <img :src="scope.row.gtc_url + '?v=' + Math.random()" alt="">
                    </div>
                </template>
                <template slot="tca_url" slot-scope="scope">
                    <div class="ui-table__img">
                        <img :src="scope.row.tca_url + '?v=' + Math.random()" alt="">
                    </div>
                </template>
            </ComTableInfo>
        </div>
    </TemplateDefault>
</template>

<script>
    import { mapGetters } from 'vuex';
    import { removeLabel } from '@/services'
    import * as importApi from '@/api/import_api';
    import * as downloadApi from '@/api/download_api';
    import { confirmUploadFile } from '../../../../../api/dev_department/supplier_development/supplier_create_list';
    export default {
        name: "BatchCreatePage",
        data() {
            return {
                tableKey: [],
                tableData: [],
                tableProps: [],
                saveCheckInfo: [],
                importInfo: {
                    sup_count: 0,
                    err_count: 0,
                }
            }
        },
        computed: {
            ...mapGetters(['loginInfo', 'batchCreateInfo']),
            importUrl() {
                return importApi['IMPORT_SUPPLIER_UPLOAD_FILE'];
            },
            downloadUrl() {
                return downloadApi['DOWNLOAD_SUPPLIER_CREATE'];
            }
        },
        created() {
            // if (JSON.stringify(this.batchCreateInfo) !== '{}') {
            //     this.handleUploadInfoCallBack(this.batchCreateInfo);
            // }
        },
        methods: {

            // 保存勾选信息
            handleSaveCheckInfo(val) {
                this.saveCheckInfo = val;
            },

            // 上传成功
            handleUploadSuccess(data) {
                console.log(data);
                this.handleUploadInfoCallBack(data);
                // this.$store.commit('SAVE_BATCH_CREATE_INFO', data);
            },

            // 标记错误信息
            autoFlagErrorInfo(row, key) {
                return row.err_field && row.err_field.split(',').indexOf(key) > -1 ? 'ui-error_info' : '';
            },

            // 下载导入模板
            handleDownloadModule() {
                window.open(this.downloadUrl, '_self');
            },

            // 上传回调
            handleUploadInfoCallBack(data) {
                this.tableData = data.data.value;
                this.tableKey = Object.values(data.data.key);
                this.tableProps = Object.keys(data.data.key);
                this.importInfo = {
                    sup_count: data.data.total_num,
                    err_count: data.data.err_num,
                }
            },

            // 确认上传
            handleConfirmUpload() {

                if (!this.saveCheckInfo.length) {
                    this.$message.warning('请勾选数据！');
                    return;
                }

                let ids = [];
                this.saveCheckInfo.forEach(({ id }) => {
                    ids.push(id);
                });

                let obj = {
                    id: ids.join(','),
                };

                confirmUploadFile(obj).then(({ data }) => {
                    if (data.status) {
                        this.$message.success('操作成功！');
                        removeLabel(this, {
                            path: '/create_supplier_upload',
                            main: '/supplier_create_list',
                            refresh: true
                        })
                    } else {
                        if (data.errorMess && data.errorMess.length) {

                            let errorStr = '';
                            data.errorMess.forEach(value => {
                                errorStr += `<div>${value}</div>`
                            });

                            this.$alert(errorStr, '错误原因', {
                                customClass: 'ui-error-list',
                                dangerouslyUseHTMLString: true
                            });
                        } else {
                            this.$message.error(data.error_mess || '操作失败!');
                        }
                    }
                })
            }
        }
    }
</script>

<style scoped lang="less">
    @error-info: #FF3D46;
    .el-button--small {
        padding: 9px;
    }
    .ui-batch_create {
        padding: 0 20px;
        .ui-batch_create--fn {
            text-align: left;
            margin: 10px 0;
        }

        .el-button:nth-child(1) {
            color: #3388ff;
            border-bottom: 1px #3388ff solid;
            border-radius: 0;
            padding-bottom: 5px;
            padding-left: 0;
            padding-right: 0;
            margin-right: 10px;
        }

    }

    .ui-error-list {

        /deep/ .el-message-box__message {
            max-height: 400px;

            /deep/ p {
                color: @error-info;
            }
        }
    }

    .ui-error_info {
        color: @error-info!important;
    }

    /deep/ .el-table td, /deep/ .el-table th {
        overflow: hidden;
        text-align: left!important;
    }

    .ui-table--supplier_code--flag {
        width: 50px;
        height: 35px;
        line-height: 54px;
        text-align: center;
        -webkit-transform: rotateZ(45deg);
        transform: rotateZ(45deg);
        background: #63c4e9;
        position: absolute;
        font-size: 12px;
        color: #FFFFFF;
        top: -12px;
        right: -20px;
        font-weight: normal;
    }

    .ui-batch_create--sub {
        float: right;

        span {
            font-size: 14px;
        }
    }
    .ui-table__img img{
        max-width: 70px;
        max-height: 80px;
    }
</style>


