/**  --->>>>   |^ 美工历史图片^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 美工历史图片列表
export function getArtHistoryImgInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/design/History_image/image_list', data);
}

// 预览图片详情
export function previewImageDetailInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/design/History_image/spu_image_list', data);
}


