<template>
    <div class="ui-qrcode_print">
        <iframe name="myIframe" ref="myIframe" :src="`/home/qrCodeIndex?sku_list=${data}`" frameborder="0"></iframe>
    </div>
</template>

<script>
    export default {
        name: "PrintQRCode",
        props: ['data'],
        data() {
            return {
                url: ''
            }
        },
        methods: {
            handlePrint() {
                // this.$refs.myIframe.focus();
                // this.$refs.myIframe.print();
                window.frames['myIframe'].focus();
                window.frames['myIframe'].print();
                // window.frames['myIframe'].onload = () => {
                //     console.log('123123');
                // }
            }
        },
        watch: {
            data(val) {

            }
        }
    }
</script>

<style scoped lang>
    .ui-qrcode_print {
        position: absolute;
        z-index: -999;
        top: 0;
        left: 0;
        width: 66mm;
        height: 32mm;
    }
</style>


