<template>
    <div class="ui-search_require clearFix" :style="{ width: areaWidth }">
        <label class="ui-search_label" :style="{ width: width }">{{ label }}</label>
        <div class="ui-search_input" :style="{ marginLeft: width }" :class="isTwo && 'ui-search_input-two'">
            <slot>
                <el-date-picker
                        clearable
                        v-if="template === 'daterange'"
                        v-model="modelForm"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd"
                        :size="size">
                </el-date-picker>
                <el-cascader
                        clearable
                        v-if="template === 'cascader'"
                        filterable
                        change-on-select
                        v-model="modelForm"
                        :size="size"
                        placeholder="请选择"
                        :props="props"
                        :options="options"
                        @change="change"
                ></el-cascader>
                <el-select clearable v-model="modelForm" :size="size" v-if="template === 'select'">
                    <el-option v-for="(label, value) in options" :label="label" :value="value" :key="value"></el-option>
                </el-select>
            </slot>
        </div>
    </div>
</template>

<script>
    /**
     *  头部查询组件 > 简便头部书写，自适应宽度
     * */
    export default {
        name: "SearchRequirement",
        model: {
            prop: 'model',
            event: 'changes'
        },
        props: {
            template: {
                type: String,
                default: ''
            },
            model: '',
            label: {
                type: String,
                default: ''
            },
            width: {
                type: String,
                default: '100px'
            },
            fixedWidth: {
                type: String,
                default: ''
            },
            isTwo: {
                type: Boolean,
                default: false
            },
            props: {
                type: Object,
                default() {
                    return { value: 'city_code', label: 'city_name' }
                }
            },
            options: {
                type: [Array, Object],
                default() {
                    return []
                }
            }
        },
        data() {
            return {
                areaWidth: '',
                minWidth: 325,
                modelForm: '',
                size: 'small'
            }
        },
        created() {
            this.modelForm = this.model;
        },
        mounted() {
            this.$nextTick(() => {
                let areaWidth = (getEleAttr('.ui-search-area').offsetWidth - 140) / 5;
                this.areaWidth = (this.fixedWidth || areaWidth) + 'px';
            })
        },
        methods: {
            change(val) {
                this.$emit('change', val);
            }
        },
        watch: {
            modelForm(val) {
                this.$emit('changes', val);
            },
            model() {
                this.modelForm = this.model;
            }
        }
    }
</script>

<style scoped lang="less">
    .ui-search_require {
        .ui-search_label {
            display: block;
            float: left;
            line-height: 32px;
            padding-right: 12px;
            box-sizing: border-box;
            text-align: right;
            font-size: 12px;
            color: #333333;
        }
        .ui-search_input {
            line-height: 32px;
        }
        .ui-search_input-two {
            .el-input {
                width: 42% !important;
            }
        }
        .el-cascader {
            width: 100%;
        }
    }
</style>


