<template>
    <TemplateDefault color="#fff">
        <div class="ui-batch_create">
            <div class="ui-batch_create--fn">
                <el-button size="small" type="text" @click="handleDownloadModule">下载模板</el-button>
                <ComUpload label="选择文件" :url="importUrl" @success="handleUploadSuccess" :upData="{ user_id: loginInfo.staff_code || '12534' }"></ComUpload>
                <div class="ui-batch_create--sub">
                    <span>本次导入数据共 <span style="color: #1EAEFF;">{{importInfo.sup_count}}</span> 条，其中 <span style="color: #FF281C;">{{importInfo.err_count}}</span> 条数据有误，请修正后重新上传！</span>
                    <el-button size="small" type="primary" @click="handleConfirmUpload">确认上传</el-button>
                </div>
            </div>
            <ComTableInfo
                    isCheck
                    :tableKey="tableKey"
                    :tableData="tableData"
                    :tableProps="tableProps"
                    :otherHeight="['ui-batch_create--fn']"
                    @checked="handleSaveCheckInfo">
                <template slot="supplier_code" slot-scope="scope">
                    <div class="ui-table--supplier_code" :class="autoFlagErrorInfo(scope.row, 'supplier_code')">
                        <p>{{scope.row.supplier_code}}</p>
                        <div class="ui-table--supplier_code--flag" :class="scope.row.type === '生产商' ? 'ui-table_flag1' : 'ui-table_flag3'">
                            <span>{{scope.row.type.replace('商', '')}}</span>
                        </div>
                    </div>
                </template>
                <template slot="supplier_name" slot-scope="scope">
                    <div :class="autoFlagErrorInfo(scope.row, 'supplier_name')">
                        <p>{{scope.row.supplier_name}}</p>
                        <div class="ui-table--icon_btn">
                            <el-popover
                                    placement="bottom"
                                    title="供应商链接"
                                    width="200"
                                    trigger="click">
                                <a :href="scope.row.web_link" target="_blank">{{scope.row.web_link}}</a>
                                <el-button slot="reference" type="text"><i class="icon-gongyingshanglianjie"></i></el-button>
                            </el-popover>
                        </div>
                    </div>
                </template>
                <template slot="shipping_address" slot-scope="scope">
                    <div :class="autoFlagErrorInfo(scope.row, 'shipping_address')">
                        <p>{{`${scope.row.shipping_province}${scope.row.shipping_city}`}}</p>
                        <p>{{`${scope.row.shipping_address}`}}</p>
                    </div>
                </template>
                <template slot="phone" slot-scope="scope">
                    <div :class="autoFlagErrorInfo(scope.row, 'phone')">
                        <p><i class="icon-ren icon-plain"></i>{{`${scope.row.contacts}/${scope.row.mobile}`}}</p>
                        <p><i class="icon-QQ icon-main"></i><a target="_self" :href="`tencent://message/?uin=${scope.row.qq}`">{{scope.row.qq}}</a></p>
                        <p><i class="icon-weixin icon-wx"></i><a target="_self" href="weixin://message/">{{scope.row.wechat}}</a></p>
                        <p><i class="icon-youxiang icon-danger"></i>{{scope.row.email}}</p>
                    </div>
                </template>
                <template slot="pay_method" slot-scope="scope">
                    <div v-if="scope.row.pay_method === '线上支付宝'" :class="autoFlagErrorInfo(scope.row, 'pay_method')">
                        <p><i class="icon-zhifufangshi icon-warn"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                        <p><i class="icon-zhifubaozhanghao1 icon-main"></i>{{scope.row.receive_account}}</p>
                        <p><i class="icon-zhifubaomingcheng icon-main"></i>{{scope.row.receive_name}}</p>
                        <p><i class="icon-dianpumingcheng1 icon-dp"></i>{{scope.row.shop_name}}</p>
                    </div>
                    <div v-else-if="scope.row.pay_method === '线下境外'" :class="autoFlagErrorInfo(scope.row, 'pay_method')">
                        <p><i class="icon-zhifufangshi icon-warn"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                        <p><i class="icon-ren icon-main"></i>{{`${scope.row.receive_name}/${scope.row.receive_mobile}`}}</p>
                        <p><i class="icon-yinhangqiazhanghao icon-main"></i>{{scope.row.receive_account}}</p>
                        <p><i class="icon-shenfenzhenghaoma icon-dp"></i>{{scope.row.receive_id_number}}</p>
                    </div>
                    <div v-else-if="scope.row.pay_method === '线下境内'" :class="autoFlagErrorInfo(scope.row, 'pay_method')">
                        <p><i class="icon-zhifufangshi icon-warn"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                        <p><i class="icon-gongsimingcheng icon-main"></i>{{scope.row.receive_name}}</p>
                        <p><i class="icon-yinhangqiazhanghao icon-main"></i>{{scope.row.receive_account}}</p>
                        <p><i class="icon-kaihuhang icon-dp"></i>{{scope.row.receive_bank}}</p>
                    </div>
                </template>
                <template slot="tax_point" slot-scope="scope">
                    <span :class="autoFlagErrorInfo(scope.row, 'tax_point')">{{ scope.row.tax_point }}（%）</span>
                </template>
                <template slot="create_user" slot-scope="scope">
                    <div :class="autoFlagErrorInfo(scope.row, 'create_user')">
                        <p v-if="scope.row.devlop_user"><span>开发人：</span><span>{{scope.row.devlop_user}}</span></p>
                        <p v-if="scope.row.create_user"><span>创建人：</span><span>{{scope.row.create_user}}</span></p>
                        <p v-if="scope.row.check_user"><span>审核人：</span><span>{{scope.row.check_user}}</span></p>
                    </div>
                </template>
            </ComTableInfo>
        </div>
    </TemplateDefault>
</template>

<script>
    import { mapGetters } from 'vuex';
    import { removeLabel } from '@/services'
    import * as importApi from '@/api/import_api';
    import * as downloadApi from '@/api/download_api';
    import { confirmBatchUploadInfo } from '../../../../../api/dev_department/supplier_development/supplier_create_list';
    export default {
        name: "BatchCreatePage",
        data() {
            return {
                tableKey: [],
                tableData: [],
                tableProps: [],
                saveCheckInfo: [],
                importInfo: {
                    sup_count: 0,
                    err_count: 0,
                }
            }
        },
        computed: {
            ...mapGetters(['loginInfo', 'batchCreateInfo']),
            importUrl() {
                return importApi['IMPORT_BATCH_SUPPLIER_INFO'];
            },
            downloadUrl() {
                return downloadApi['DOWNLOAD_SUPPLIER_CREATE'];
            }
        },
        created() {
            console.log(this.batchCreateInfo);
            if (JSON.stringify(this.batchCreateInfo) !== '{}') {
                this.handleUploadInfoCallBack(this.batchCreateInfo);
            }
        },
        methods: {

            // 保存勾选信息
            handleSaveCheckInfo(val) {
                this.saveCheckInfo = val;
            },

            // 上传成功
            handleUploadSuccess(data) {
                this.handleUploadInfoCallBack(data);
                this.$store.commit('SAVE_BATCH_CREATE_INFO', data);
            },

            // 标记错误信息
            autoFlagErrorInfo(row, key) {
                return row.err_field && row.err_field.split(',').indexOf(key) > -1 ? 'ui-error_info' : '';
            },

            // 下载导入模板
            handleDownloadModule() {
                window.open(this.downloadUrl, '_self');
            },

            // 上传回调
            handleUploadInfoCallBack(data) {
                this.tableData = data.data.value;
                this.tableKey = data.data.key;
                this.tableProps = ['index', 'supplier_code', 'supplier_name', 'shipping_address', 'phone', 'pay_method', 'tax_point', 'create_user'];
                this.importInfo = {
                    sup_count: data.select_list.sup_count,
                    err_count: data.select_list.err_count,
                }
            },

            // 确认上传
            handleConfirmUpload() {

                if (!this.saveCheckInfo.length) {
                    this.$message.warning('请勾选数据！');
                    return;
                }

                let codes = [];
                this.saveCheckInfo.forEach(({ credit_code }) => {
                    codes.push(credit_code);
                });

                let obj = {
                    credit_codes: codes.join(','),
                };

                confirmBatchUploadInfo(obj).then(({ data }) => {
                    if (data.status) {
                        this.$message.success('操作成功！');
                        removeLabel(this, {
                            path: '/batch_create_supplier',
                            main: '/supplier_create_list',
                            refresh: true
                        })
                    } else {
                        if (data.errorMess && data.errorMess.length) {

                            let errorStr = '';
                            data.errorMess.forEach(value => {
                                errorStr += `<div>${value}</div>`
                            });

                            this.$alert(errorStr, '错误原因', {
                                customClass: 'ui-error-list',
                                dangerouslyUseHTMLString: true
                            });
                        } else {
                            this.$message.error(data.error_mess || '操作失败!');
                        }
                    }
                })
            }
        }
    }
</script>

<style scoped lang="less">
    @error-info: #FF3D46;
    .el-button--small {
        padding: 9px;
    }
    .ui-batch_create {
        padding: 0 20px;
        .ui-batch_create--fn {
            text-align: left;
            margin: 10px 0;
        }

        .el-button:nth-child(1) {
            color: #3388ff;
            border-bottom: 1px #3388ff solid;
            border-radius: 0;
            padding-bottom: 5px;
            padding-left: 0;
            padding-right: 0;
            margin-right: 10px;
        }

    }

    .ui-error-list {

        /deep/ .el-message-box__message {
            max-height: 400px;

            /deep/ p {
                color: @error-info;
            }
        }
    }
    
    .ui-error_info {
        color: @error-info!important;
    }

    /deep/ .el-table td, /deep/ .el-table th {
        overflow: hidden;
        text-align: left!important;
    }

    .ui-table--supplier_code--flag {
        width: 50px;
        height: 35px;
        line-height: 54px;
        text-align: center;
        -webkit-transform: rotateZ(45deg);
        transform: rotateZ(45deg);
        background: #63c4e9;
        position: absolute;
        font-size: 12px;
        color: #FFFFFF;
        top: -12px;
        right: -20px;
        font-weight: normal;
    }

    .ui-batch_create--sub {
        float: right;

        span {
            font-size: 14px;
        }
    }
</style>


