/**  --->>>>   |^ 商品目录管理^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取目录信息
export function getProductCatalogueManageInfo(queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    };

    return http.getAjax('/category/category/select_category', data);
}

// 新增目录
export function addProductCatalogueInfo(queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    };

    return http.postAjax('/category/category/create_category', data);
}


// 编辑目录
export function editProductCatalogueInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/category/category/edit_category', data);
}

// 删除
export function deleteProductCatalogueInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/category/category/del_category', data);
}


