/**  --->>>>   |^ 商品属性管理^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取商品属性管理信息
export function getProductAttrManageInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/create_supplier' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/system/attribute/select_attribute_list', data);
    }
}

// 新增属性
export function addAttrInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/system/attribute/add_attribute', data);
}

// 查看属性详情
export function getAttrDetailInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/system/attribute/show_attribute', data);
}

// 编辑属性
export function modifyAttrInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/system/attribute/update_attribute', data);
}

// 删除属性
export function deleteAttrInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/system/attribute/delete_attribute', data);
}


