/**  =========================> *|`| % 摄影商品列表 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getPhotographyGoodsListInfo,
} from '../../../../api/design_department/photography_manage/photography_goods_list'

const config = {
    state: {
        photographyGoodsListInfo: {},
    },
    mutations: {
        [types.SAVE_PHOTOGRAPHY_GOODS_LIST_INFO] (state, saveInfo) {
            state.photographyGoodsListInfo = saveInfo
        }
    },
    actions: {
        GetPhotographyGoodsListInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getPhotographyGoodsListInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_PHOTOGRAPHY_GOODS_LIST_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




