/**  =========================> *|`| % 样品异常记录 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getSampleAbnormalRecordInfo,
} from '../../../../api/quality_control_department/sample_quality_control/sample_abnormal_record'

const config = {
    state: {
        sampleAbnormalRecordInfo: {},
    },
    mutations: {
        [types.SAVE_SAMPLE_ABNORMAL_RECORD_INFO] (state, saveInfo) {
            state.sampleAbnormalRecordInfo = saveInfo
        }
    },
    actions: {
        GetSampleAbnormalRecordInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getSampleAbnormalRecordInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SAMPLE_ABNORMAL_RECORD_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




