/**  --->>>>   |^ 样品审核列表^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 样品审核列表
export function getSampleAuditListInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    return http.getAjax('/quality/product/product_list', data);
}

// 获取商品资料创建信息
export function getProductDatumCreateInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/create_supplier' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/product/Product_list/product_list', data);
    }
}

// 获取单个创建下拉数据
export function getOptionListInfo () {
    const data = {
        ...CONFIG.ajaxData
    };
    return http.getAjax('/product/product/select_list', data);
}

// 根据供应商查询
export function supplierSearchInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params,
    };

    return http.getAjax('/product/product/get_supplier_name', data);
}

// 保存单个创建
export function saveSingleCreateInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/product/product/single_creation', data);
}

// 提交SPU补录
export function submitSPURepairInfo (params) {
    for (let [key, value] of Object.entries(CONFIG.ajaxData)) {
        params.append(key, value);
    }
    return http.fileAjax('/product/product/spu_supplement', params);
}

// 获取补录下拉信息
export function getRepairOptionListInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/product/Product/get_select_list', data);
}

// 提交保存补录SKU信息
export function submitSaveSKURepairInfo (params) {
    for (let [key, value] of Object.entries(CONFIG.ajaxData)) {
        params.append(key, value);
    }
    return http.fileAjax('/product/product/sku_supplement', params);
}

// 查询SPU补录信息
export function getSPURepairInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/product/product/read_save_data', data);
}

// 同步SKU信息
export function syncSkuInfo (params) {
    for (let [key, value] of Object.entries(CONFIG.ajaxData)) {
        params.append(key, value);
    }
    return http.fileAjax('/product/product/relation_sku', params);
}

// 确认上传批量信息
export function confirmBatchUploadInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/product/product/batch_creation', data);
}

// 查询单个创建保存信息
export function getSingleCreateSaveInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/product/product/read_single_data', data);
}



