/**  =========================> *|`| % 开发小组管理 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getDevGroupManageInfo,
} from '../../../../api/dev_department/system_manage/dev_group_manage';

const config = {
    state: {
        devGroupManageInfo: {}
    },
    mutations: {
        [types.SAVE_DEV_GROUP_MANAGE_INFO](state, saveInfo) {
            state.devGroupManageInfo = saveInfo;
        }
    },
    actions: {
        // 获取开发小组管理信息
        GetDevGroupManageInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getDevGroupManageInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_DEV_GROUP_MANAGE_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




