<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="图片类型" template="select"
                                           v-model="queryData.imgType"
                                           :options="optionList.imgTypeList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作时间" template="daterange"
                                           v-model="queryData.operateTime"></SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})" icon="icon-chaxun">查询</el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
        </div>
        <ComTableInfo
            isCheck
            ref="myTable"
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">
            <template slot="thumb_url" slot-scope="scope">
                <LoadingImg :src="scope.row.thumb_url" @click="handlePreviewImageDetailInfo(scope.row)"></LoadingImg>
            </template>
            <template slot="operate_log" slot-scope="scope">
                <el-button type="text">日志</el-button>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <PreviewImage :show.sync="isPreviewShow" :row="previewInfo"></PreviewImage>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import PreviewImage from './PreviewImage';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage
} from '@/services/index'
import {
    mapActions,
    mapGetters
} from 'vuex'
import {
    previewImageDetailInfo,
} from '../../../../api/design_department/artist_manage/art_history_img'

export default {
    components: {
        PreviewImage
    },
    data () {
        return {

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                productSPU: '',
                imgType: '',
                operateTime: [],
            },

            optionList: {
                imgTypeList: {}
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 图片预览
            isShowLookImage: false,
            lookImageUrl: '',

            // 预览图片详情
            isPreviewShow: false,
            previewInfo: {},
        }
    },
    computed: {
        ...mapGetters([
            'queryParamsInfo',
            'atrHistoryImgInfo'
        ])
    },
    methods: {
        ...mapActions([
            'GetArtHistoryImgInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {
            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['atr_history_img'])
            }

            let obj = {
                opera_time_start: this.queryData.operateTime[0],
                opera_time_end: this.queryData.operateTime[1],
                spu: this.queryData.productSPU,
                image_type: this.queryData.imgType,
                ...params
            }
            this.GetArtHistoryImgInfo(obj).then(({data}) => {

                // 导出
                if (data.status === 2) return

                this.handleQueryInfoCallBack(data)
            }).catch(err => {
                console.log(err)
            })
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                productSPU: '',
                imgType: '',
                operateTime: [],
            }
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true)
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true)
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo({
                isExcel: 1
            }, true)
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true
            this.lookImageUrl = ['http://pic10.nipic.com/20101016/2531170_233023137000_2.jpg']
        },

        // 预览图片详情
        handlePreviewImageDetailInfo({ image_type, spu }) {
            let obj = {
                image_type,
                spu
            }
            previewImageDetailInfo(obj).then(({ data }) => {
                this.previewInfo = {
                    ...data.data.list,
                    imgTypeList: this.optionList.imgTypeList,
                    imgType: image_type
                };
                this.isPreviewShow = true;
            });
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'atr_history_img',
                value: JSON.stringify(this.queryData)
            })

            // 渲染下拉
            this.optionList.imgTypeList = data.select_list.image_type;

            //数据渲染
            this.tableData = data.data.value
            this.tableKey = Object.values(data.data.key);
            this.tableWidth = ['', '', '', '', '220px', '220px']

            this.tableProps = Object.keys(data.data.key);
            this.tableProps.splice(0, 1, 'index');
            this.tableProps.splice(0, 1, 'index');

            //渲染分页
            this.pageData.currentList = data.page.limit * 1
            this.pageData.currentPage = data.page.offset * 1
            this.pageData.currentTotal = data.page.total * 1
        }
    },
    created () {
        this.thisVm = this
        try {
            if (JSON.stringify(this.atrHistoryImgInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1})
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['atr_history_img'])
                this.handleQueryInfoCallBack(this.atrHistoryImgInfo)
            }
        } catch (e) {
            console.log(e)
        }
    },
}
</script>

<style lang="less" scoped>
.operation-box {
    text-align: left;
    font-size: 14px;

    > div {
        margin-top: 10px;

        span {
            cursor: pointer;
        }
    }
}

@upload-height: 185px;
@border-color: #aaa;

.ui-table--supplier_code {
    font-size: 14px;
    font-weight: bold;
    line-height: 14px;

    p {
        margin-bottom: 10px;
    }
}

/deep/ .el-table td, /deep/ .el-table th {
    overflow: hidden;
    text-align: left !important;
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    background: #63c4e9;
    position: absolute;
    font-size: 12px;
    color: #FFFFFF;
    top: -12px;
    right: -20px;
}

.ui-table--icon_btn {
    i {
        cursor: pointer;
    }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: @upload-height;
        }

        .ui-com_edit__upload--last_btn {
            border: 1px dashed @border-color;
            width: 100%;
            height: @upload-height;

            /deep/ .el-upload {
                display: block;
                height: @upload-height;
                line-height: @upload-height;

                span {
                    color: @border-color;
                }
            }
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: @upload-height;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}
</style>



