/**  --->>>>   |^ 文案小组管理^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取文案编辑记录信息
export function getCopyEditGroupInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        group_type: 3,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/supplier_export' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/system/group/get_group_list', data);
    }
}



