/**  =========================> *|`| % 美工历史图片 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getArtHistoryImgInfo,
} from '../../../../api/design_department/artist_manage/art_history_img'

const config = {
    state: {
        atrHistoryImgInfo: {},
    },
    mutations: {
        [types.SAVE_ATR_HISTORY_IMG_INFO] (state, saveInfo) {
            state.atrHistoryImgInfo = saveInfo
        }
    },
    actions: {
        // 获取美工历史图片信息
        GetArtHistoryImgInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getArtHistoryImgInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_ATR_HISTORY_IMG_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




