/**  =========================> *|`| % 禁用供应商列表 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getSupplierDisabledListInfo,
} from '../../../../api/dev_department/supplier_development/supplier_disabled_list';

const config = {
    state: {
        supplierDisabledListInfo: {}
    },
    mutations: {
        [types.SAVE_SUPPLIER_DISABLED_LIST_INFO](state, saveInfo) {
            state.supplierDisabledListInfo = saveInfo;
        }
    },
    actions: {
        // 获取禁用供应商列表信息
        GetSupplierDisabledListInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSupplierDisabledListInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SUPPLIER_DISABLED_LIST_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




