<script>
import { getNumByUnit, getNumByMeasure } from '@/services';
import {
    getModificationDetailInfo
} from '@/api/design_department/artist_manage/goods_modification_list';

import Clipboard from 'clipboard';

export default {
    name: 'CopyEditPage',
    props: {
        show: false,
        row: {
            type: Object,
            default () {
                return {};
            }
        }
    },
    data () {
        return {
            editShowArea: ['1'],

            //语言
            checkLanguage: 'CN',
            leftLanguage: 'CN',

            languageOptions: {
                'CN': {
                    spu_title: ''
                }
            },

            isGoodsBasic: true,
            isSupplierGoods: true,
            unitData: {
                weight1: ['g', 'kg', 'mg'],
                weight2: ['lb', 'oz'],
                checkWeight1: 'g',
                checkWeight2: 'lb',
                valueWeight1: '',
                valueWeight2: ''
            },
            measureData: {
                measure1: ['cm', 'mm', 'm', 'km'],
                measure2: ['in', 'ft', 'yd'],
                checkMeasure1: 'cm',
                checkMeasure2: 'in',
                valueMeasure1: '',
                valueMeasure2: ''
            },
            formData: {},
            optionList: {
                featureVal: {},
                forHoliday: {},
                devpType: {},
                sampleType: {},
                buySample: {},
                customized: {}
            },

            isShowLookImage: false,
            lookImageUrl: '',

            checkAttr: '', //选中复制的属性

        };
    },
    watch: {
        row: function (newData, old) {
            // console.log(old, 'old');
            // console.log(newData, 'new');
            // let spu = newData.spu[0];
            let spu = '10101800005';
            if (spu) {
                getModificationDetailInfo({spu}).then(({data}) => {
                    console.log(data, 8989);
                    this.handleQueryInfoCallBack(data);
                });
            }
        }
    },
    computed: {

        //复制的文本内容
        copyAttr () {
            if (this.languageOptions[this.checkLanguage][this.checkAttr]) {
                console.log('oookkk');
                let obj = this.languageOptions[this.checkLanguage][this.checkAttr];
                console.log(obj, 123);
                if (Array.isArray(obj)) {
                    return obj.join(',');
                } else {
                    return obj;
                }

            }
            return '';
        }
    },
    methods: {

        //复制文本
        copyText (obj) {
            console.log('into-----');

            //第二次强行点击 是为了触发复制
            if (obj === this.checkAttr) {
                return;
            }

            let _this = this;

            //更改 选中状态
            _this.$set(_this, 'checkAttr', obj);
            obj = '.' + obj;

            let copyBtn = new Clipboard(obj);

            //不设置setTimeout的话 拿不到 copyAttr
            setTimeout(() => {
                console.log(_this.$refs[_this.checkAttr], 22);

                if (Array.isArray(_this.$refs[_this.checkAttr])) {
                    _this.$refs[_this.checkAttr][0].click();
                } else {
                    _this.$refs[_this.checkAttr].click();
                }
            });

            copyBtn.on('success', function (e) {
                // 复制成功
                e.clearSelection();
                _this.$message.success('复制成功!');

            });
            copyBtn.on('error', function (e) {

                // 复制成功
                console.log(e);
                _this.$message.success('复制失败!');
            });

        },

        // 预览图片
        handleLookImage (url) {
            this.isShowLookImage = true;
            this.lookImageUrl = url;
        },
        //回调
        handleQueryInfoCallBack ({data}) {

            //下拉渲染，目前只做显示
            this.optionList.featureVal = data.select_list.feature_val;
            this.optionList.forHoliday = data.select_list.for_holiday;
            this.optionList.devpType = data.select_list.devp_type;
            this.optionList.sampleType = data.select_list.sample_type;
            this.optionList.buySample = data.select_list.is_buy_sample;
            this.optionList.customized = data.select_list.is_customized;

            this.formData = data.common_data;

            //是否买样 和  是否定制 处理
            let value1 = Object.values(this.optionList.buySample);
            let idx1 = value1.findIndex((item) => {
                return item == this.formData.is_buy_sample;
            });
            let key1 = Object.keys(this.optionList.buySample);

            this.formData.is_buy_sample = key1[idx1];

            let value2 = Object.values(this.optionList.customized);
            let idx2 = value2.findIndex((item) => {
                return item == this.formData.is_customized;
            });
            let key2 = Object.keys(this.optionList.customized);

            this.formData.is_customized = key2[idx2];

            //语言

            this.languageOptions = data.langs;
            let langs = Object.keys(data.langs);
            this.leftLanguage = langs[0];
            this.checkLanguage = langs[1];

            console.log(this.leftLanguage);

            console.log(this.languageOptions, 666);

        },
        //翻译相应语言
        handleTranslate (val) {
            this.checkLanguage = val;
        },
        //转换工具
        searchMeasure () {
            let data = this.measureData;
            if (data.valueMeasure1 && !isNaN(data.valueMeasure1)) {
                this.measureData.valueMeasure2 = getNumByMeasure(parseFloat(data.valueMeasure1), data.checkMeasure1, data.checkMeasure2);
            } else {
                this.measureData.valueMeasure2 = '';
            }
        },
        searchWeight1 () {
            let data = this.unitData;
            if (data.valueWeight1 && !isNaN(data.valueWeight1)) {
                this.unitData.valueWeight2 = getNumByUnit(parseFloat(data.valueWeight1), data.checkWeight1, data.checkWeight2);
            } else {
                this.unitData.valueWeight2 = '';
            }
        },

        hideSupplierGoods () {
            this.isSupplierGoods = false;
            this.isGoodsBasic = true;
        },

        //隐藏商品基础资料
        hideGoodsBasic () {
            console.log('a');
            this.isGoodsBasic = false;
            this.isSupplierGoods = true;
        },

        handleClosePage () {
            this.$emit('update:show', false);
        },

    },
    created () {

    }
};
</script>

<style scoped lang="less">

