                /deep/ .el-upload {
                    display: block;
                    height: @upload-height;
                    line-height: @upload-height;
                    background: #F5F9F8;

                    span {
                        color: @border-color;
                    }

                    .icon-tupianshangchuan {
                        font-size: 40px;
                        margin: 0;
                    }
                }
            }

            .ui-com_edit__upload--item--mask {

                display: none;
