                                                        :key="item.id"
                                                        :label="item.id"
                                                        v-for="item in optionList.attrCheckList['电池属性']">{{item.attribute_value_cn}}</el-checkbox>
                                            </el-checkbox-group>
                                            <div class="ui-check__input clearFix">
                                                <div style="width: 40%;"
                                                     v-if="
                                                        autoShowHideAttrKey(optionList.attrCheckList['电池属性'], 'batteryAttr', 0) ||
                                                        autoShowHideAttrKey(optionList.attrCheckList['电池属性'], 'batteryAttr', 1) ||
                                                        autoShowHideAttrKey(optionList.attrCheckList['电池属性'], 'batteryAttr', 2)">
                                                    <el-input :disabled="isPreview" size="mini" v-model="editData.batteryInfo[0]"
                                                              placeholder="请输入电池类型"></el-input>
                                                </div>
                                                <div style="width: 20%;"
                                                     v-if="
                                                        autoShowHideAttrKey(optionList.attrCheckList['电池属性'], 'batteryAttr', 0) ||
                                                        autoShowHideAttrKey(optionList.attrCheckList['电池属性'], 'batteryAttr', 1) ||
                                                        autoShowHideAttrKey(optionList.attrCheckList['电池属性'], 'batteryAttr', 2)">
                                                    <el-input :disabled="isPreview" size="mini" v-model="editData.batteryInfo[1]"
                                                              placeholder="电池型号"></el-input>
                                                </div>
                                                <div style="width: 20%"
                                                     v-if="
                                                        autoShowHideAttrKey(optionList.attrCheckList['电池属性'], 'batteryAttr', 0) ||
                                                        autoShowHideAttrKey(optionList.attrCheckList['电池属性'], 'batteryAttr', 1) ||
                                                        autoShowHideAttrKey(optionList.attrCheckList['电池属性'], 'batteryAttr', 2)">
                                                    <el-input :disabled="isPreview" size="mini" v-model="editData.batteryInfo[2]"
                                                              placeholder="标称容量(mAh)"></el-input>
                                                </div>
                                            </div>
                                            <span v-if="!optionList.attrCheckList['电池属性']">加载中...</span>
                                        </div>
                                    </li>
                                    <li v-if="optionList.attrCheckList['违禁属性']">
                                        <span class="ui-label" :class="syncLabelCheck.productAttrDatumSync[5] && 'ui-active'"
                                              slot="label" @click="handleSyncCheckLabel('productAttrDatumSync', 5, 4)">违禁属性</span>
                                        <div class="ui-value">
                                            <el-checkbox-group :disabled="isPreview" v-model="editData.isBanAttr">
                                                <el-checkbox
                                                        :key="item.id"
                                                        :label="item.id"
                                                        v-for="item in optionList.attrCheckList['违禁属性']">{{item.attribute_value_cn}}</el-checkbox>
                                            </el-checkbox-group>
                                            <div class="ui-check__input clearFix">
                                                <div style="width: 40%;"
                                                     v-if="autoShowHideAttrKey(optionList.attrCheckList['违禁属性'], 'isBanAttr', 0)">
                                                    <el-input :disabled="isPreview" size="mini" v-model="editData.banInfo"
                                                              placeholder="请输入刀刃长度"></el-input>
                                                </div>
                                            </div>
                                        </div>
                                    </li>
                                    <li v-if="optionList.attrCheckList['形态属性']">
                                        <span class="ui-label" :class="syncLabelCheck.productAttrDatumSync[6] && 'ui-active'"
                                              slot="label" @click="handleSyncCheckLabel('productAttrDatumSync', 6, 4)">形态属性</span>
                                        <div class="ui-value">
                                            <el-checkbox-group :disabled="isPreview" v-model="editData.modelAttr">
                                                <el-checkbox
                                                        :key="item.id"
                                                        :label="item.id"
                                                        v-for="item in optionList.attrCheckList['形态属性']">{{item.attribute_value_cn}}</el-checkbox>
                                            </el-checkbox-group>
                                        </div>
                                    </li>
                                    <li v-if="optionList.attrCheckList['海外属性']">
                                        <span class="ui-label" :class="syncLabelCheck.productAttrDatumSync[7] && 'ui-active'"
                                              slot="label" @click="handleSyncCheckLabel('productAttrDatumSync', 7, 4)">海外属性</span>
                                        <div class="ui-value">
                                            <el-checkbox-group :disabled="isPreview" v-model="editData.overseas">
                                                <el-checkbox
                                                        :key="item.id"
                                                        :label="item.id"
                                                        v-for="item in optionList.attrCheckList['海外属性']">{{item.attribute_value_cn}}</el-checkbox>
                                            </el-checkbox-group>
                                        </div>
                                    </li>
                                    <li v-if="optionList.attrCheckList['证书类型']">
                                        <span class="ui-label" :class="syncLabelCheck.productAttrDatumSync[8] && 'ui-active'"
                                              slot="label" @click="handleSyncCheckLabel('productAttrDatumSync', 8, 4)">证书类型</span>
                                        <div class="ui-value">
                                            <el-checkbox-group :disabled="isPreview" v-model="editData.certificateType">
                                                <el-checkbox
                                                        :key="key"
                                                        :label="item.id"
                                                        v-for="(item, key) in optionList.attrCheckList['证书类型']">{{item.attribute_value_cn}}</el-checkbox>
                                            </el-checkbox-group>
                                            <div class="ui-check__input clearFix">
                                                <div style="width: 40%;"
                                                     v-if="autoShowHideAttrKey(optionList.attrCheckList['证书类型'], 'certificateType', 0)">
                                                    <el-input :disabled="isPreview" size="mini" v-model="editData.certificateTypeInfo"
                                                              placeholder="请输入"></el-input>
                                                </div>
                                            </div>
                                        </div>
                                    </li>
                                    <li v-if="optionList.attrCheckList['说明书']">
                                        <span class="ui-label" :class="syncLabelCheck.productAttrDatumSync[9] && 'ui-active'"
                                              slot="label" @click="handleSyncCheckLabel('productAttrDatumSync', 9, 4)">说明书</span>
                                        <div class="ui-value">
                                            <el-checkbox-group :disabled="isPreview" v-model="editData.instructions">
                                                <el-checkbox
                                                        :key="key"
                                                        :label="item.id"
                                                        v-for="(item, key) in optionList.attrCheckList['说明书']">{{item.attribute_value_cn}}</el-checkbox>
                                            </el-checkbox-group>
                                            <div class="ui-check__input clearFix"
                                                 v-if="autoShowHideAttrKey(optionList.attrCheckList['说明书'], 'instructions', 0)">
                                                <div style="width: 40%;">
                                                    <el-input :disabled="isPreview" size="mini" v-model="editData.instructionsInfo"
                                                              placeholder="请输入"></el-input>
                                                </div>
                                            </div>
                                        </div>
                                    </li>
                                </ul>
                            </el-collapse-item>
                        </div>
                        <div class="ui-content_area--btn" v-if="!isPreview">
                            <el-button size="small" @click="handleSaveOrSubmitInfo('1')">保存</el-button>
                            <el-button type="primary" size="small" @click="handleSaveOrSubmitInfo('2')">提交</el-button>
                        </div>
                    </el-collapse>
                </div>
            </el-form>
            <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        </div>
        <div class="ui-right" v-if="!isPreview">
            <ul class="ui-right--item">
                <li>
                    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAllSku" @change="handleCheckAllChange">All</el-checkbox>
                    <el-button type="primary" size="mini" @click="handleSyncSkuInfo">同步</el-button>
                </li>
                <li>
                    <el-checkbox-group v-model="checkSku" @change="handleRelevanceSkuChange">
                        <el-checkbox v-for="item in skuList" :label="item" :key="item">{{item}}</el-checkbox>
                    </el-checkbox-group>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import {
        getRepairOptionListInfo,
        submitSaveSKURepairInfo,
        syncSkuInfo,
        getSPURepairInfo,
    } from '@/api/dev_department/product_development/product_datum_create';
    import {
        imageUrlChangeFile
    } from '@/services';
    import CONFIG from '@/assets/js/config';
    export default {
        name: "SKUPage",
        props: ['row', 'sku', 'type'],
        data() {
            return {
                testCheck: [],

                editShowArea: ['1', '2', '3', '4', '5', '6'],

                tableKey: ['商品SKU', '商品名称', '产品目录', '样品类型', '供应商资料'],
                tableProps: ['sku', 'good_name', 'category_path', 'sample_type', 'supplier_datum'],

                optionList: {
                    packTypeList: {},
                    brandAttrList: {},
                    attrCheckList: {},
                    countryList: {},
                    infringementType: {},
                    platformList: {}
                },

                editData: {
                    packType: '',
                    packWeight: '',
                    packLength: '',
                    sampleWeight: '',
                    sampleRemarks: '',
                    packList: '',
                    brandAttr: '',
                    productBrand: '',
                    productModel: '',
                    isHaveLogo: '',
                    materialQuality: '',
                    productUse: '',
                    workPrinciple: '',
                    isTort: '',
                    isCheckPrice: '',
                    isEmpower: '',
                    isDisSales: '',
                    checkPriceInfo: [[
                        {}, {}, {}
                    ]],
                    tortInfo: [[
                        {}, {}
                    ]],
                    disSalesInfo: [[
                        {}, {}
                    ]],
                    empowerInfo: {},
                    publishAttr: [],
                    orderAttr: [],
                    orderAttrFile: {},
                    basisAttr: [],
                    specialAttr: [],
                    batteryAttr: [],
                    batteryInfo: [],
                    isBanAttr: [],
                    banInfo: '',
                    modelAttr: [],
                    overseas: [],
                    certificateType: [],
                    certificateTypeInfo: '',
                    instructions: [],
                    instructionsInfo: ''
                },
                editRule: {},

                // 显示属性隐藏字段
                showAttrHideKey: {
                    batteryAttr: ['内置电池', '配套电池', '纯电池'],
                    isBanAttr: ['管制刀具'],
                    certificateType: ['其它'],
                    instructions: ['其它'],
                    orderAttr: []
                },

                // 保存文件列表
                saveFileData: {
                    appendixDatum: [],
                    devImage: [],
                },
                saveUrlFile: {
                    devImage: [],
                    appendixDatum: [],
                },

                // 预览图片
                lookImageUrl: '',
                isShowLookImage: false,

                // 关联SKU
                isIndeterminate: false,
                checkAllSku: false,
                checkSku: [],

                // 同步信息
                syncLabelCheck: {
                    appendixDatumSync: [],
                    devImageSync: [],
                    productComDatumSync: [],
                    tortDatumSync: [],
                    productAttrDatumSync: []
                },

                // 勾选同步区域
                syncCheck: [],
                syncIsNoAllCheck: [],
                syncCheckArea: [
                    ['packType','packWeight','packLength','sampleWeight'],
                    ['brandAttr', 'productBrand', 'productModel', 'isHaveLogo', 'materialQuality', 'productUse', 'workPrinciple', 'isTort', 'isCheckPrice', 'isEmpower', 'isDisSales', 'checkPriceInfo', 'tortInfo', 'disSalesInfo'],
                    null, null,
                    ['publishAttr', 'orderAttr', 'basisAttr', 'specialAttr', 'batteryAttr', 'isBanAttr', 'modelAttr', 'overseas', 'certificateType', 'instructions']
                ]
            }
        },
        computed: {
            isPreview() {
                return this.type === 'preview'
            },
            tableData() {
                if (JSON.stringify(this.row) === '{}') {
                    return []
                } else {
                    return [{
                        ...this.row,
                        sku: this.sku
                    }]
                }
            },
            skuList() {
                let skuList = [];
                if (this.$parent.row && this.$parent.row.sku) {
                    skuList = this.$parent.row.sku.split(',');
                }
                return skuList
            },
            firstUrl() {
                return CONFIG.PRODUCTION_PATH
            },
        },
        created() {
            getRepairOptionListInfo().then(({ data }) => {
                this.optionList.attrCheckList = data.select_list.attribute;
                this.optionList.packTypeList = data.select_list.pack_type;
                this.optionList.brandAttrList = data.select_list.brand_attribute;
                this.optionList.countryList = data.select_list.country;
                this.optionList.infringementType = data.select_list.infringement_type;
                this.optionList.platformList = data.select_list.platform;
                if (Array.isArray(this.$parent.editPageData[this.sku])) {
                    let obj = {
                        is_reject: this.row.reject_type,
                        spu: this.row.spu[0]
                    };
                    getSPURepairInfo(obj).then(({ data }) => {
                        data.data.myfile.forEach((value, index) => {
                            this.saveFileData.devImage.push({
                                url: value
                            });

                            let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                            imageUrlChangeFile(value, fileName[0].replace('.', ''), (file) => {
                                this.saveUrlFile.devImage.splice(index, 1, file);
                            });
                        });
                        this.editData.sampleRemarks = data.data.buy_sample_note;
                        let isNotPack = true;
                        data.data.otherSku && JSON.parse(data.data.otherSku).forEach(value => {
                            if (value.sku === this.sku) {
                                this.editData.packList = value.packList;
                                isNotPack = false;
                            }
                        });
                        if (isNotPack) {
                            this.editData.packList = data.data.pack_list;
                        }
                    });
                } else {
                    this.handleInitData();
                }
            });
        },
        methods: {

            // 保存文件列表
            handleSaveFileList(type, fileList) {
                this.saveFileData[type] = [...this.saveFileData[type], ...fileList];
                this.$refs[type].clearFiles();
            },

            // 预览图片
            handleLookImage(url) {
                this.isShowLookImage = true;
                this.lookImageUrl = url;
            },

            // 删除文件
            handleDeleteFile(inx, key, { type }) {
                this.saveFileData[key].splice(inx, 1);
                this.saveUrlFile[key].splice(inx, 1);
                if (type === 'preview') {
                    this.editData.empowerInfo = {}
                }
            },

            // 添加input
            handleAddInput(key, index) {
                let pushVal;
                switch (key) {
                    case 'disSalesInfo':
                        pushVal = [{}, {}];
                        break;
                    case 'tortInfo':
                        pushVal = [{}, {}];
                        break;
                    case 'checkPriceInfo':
                        pushVal = [{}, {}, {}];
                        break;
                    default:
                        pushVal = '';
                }
                this.editData[key].splice(index, 0, pushVal);
            },

            // 删除input
            handleDeleteInput(key, index) {
                if (this.editData[key].length < 2) return;
                this.editData[key].splice(index, 1);
            },

            // 添加选中样式
            autoAddActiveClass(checkArr, inx) {
                return checkArr[inx];
            },

            /**
             * 显示隐藏属性K
             * @option { array or object } 下拉列表
             * @key { string } 主要key
             * @inx { number } 隐藏索引
             * @prop { object } 下拉键值
             * */
            autoShowHideAttrKey(option, key, inx, prop = { label: 'attribute_value_cn', value: 'id' }) {
                if (!option) return false;
                if (Array.isArray(option)) {
                    for (let value of option) {
                        try {
                            if (this.editData[key].indexOf(value[prop.value]) > -1) {
                                let keyInx = this.showAttrHideKey[key].indexOf(value[prop.label]);
                                if (keyInx > -1 && keyInx === inx) {
                                    return true;
                                }
                            }
                        } catch (e) {
                            console.log(this.editData[key]);
                            console.log(key);
                            console.log(prop.value);
                        }
                    }
                    return false;
                }
            },

            /**
             * label标签勾选
             * @key { string } 选择对象
             * @inx { number } 对象索引
             * @area { number } 对象区域
             * */
            handleSyncCheckLabel(key, inx, area) {
                if (this.isPreview) return;
                this.$set(this.syncLabelCheck[key], inx, !this.syncLabelCheck[key][inx]);
                this.handleSyncCheckChange(area, key);
            },

            /**
             * 同步勾选
             * @area { number } 勾选区域
             * @index { number } 索引
             * @type { string } 对象
             * */
            handleSyncCheckChange(area, type) {
                let isAll = true;
                let isNoCheck = true;

                if (this.saveFileData[type.replace('Sync', '')]) {
                    for (let i = 0; i < this.saveFileData[type.replace('Sync', '')].length; i++) {
                        if (!this.syncLabelCheck[type][i]) {
                            isAll = false;
                        } else {
                            isNoCheck = false;
                        }
                    }
                } else {
                    for (let i = 0; i < this.syncCheckArea[area].length; i++) {
                        if (!this.syncLabelCheck[type][i]) {
                            isAll = false;
                        } else {
                            isNoCheck = false;
                        }
                    }
                }

                if (isAll) {
                    this.syncCheck[area] = true;
                    this.syncIsNoAllCheck[area] = false;
                } else if (isNoCheck) {
                    this.syncCheck[area] = false;
                    this.syncIsNoAllCheck[area] = false;
                } else {
                    this.syncIsNoAllCheck[area] = true;
                }
            },

            // 同步全选
            handleSyncCheckAllChange(inx, type) {
                if (this.saveFileData[type.replace('Sync', '')]) {
                    if (this.syncCheck[inx]) {
                        for (let i = 0; i < this.saveFileData[type.replace('Sync', '')].length; i++) {
                            this.syncLabelCheck[type][i] = true;
                        }
                        this.syncIsNoAllCheck[inx] = false;
                    } else {
                        for (let i = 0; i < this.saveFileData[type.replace('Sync', '')].length; i++) {
                            this.syncLabelCheck[type][i] = false;
                        }
                        this.syncIsNoAllCheck[inx] = false;
                    }
                } else {
                    if (this.syncCheck[inx]) {
                        for (let i = 0; i < this.syncCheckArea[inx].length; i++) {
                            this.syncLabelCheck[type][i] = true;
                        }
                        this.syncIsNoAllCheck[inx] = false;
                    } else {
                        for (let i = 0; i < this.syncCheckArea[inx].length; i++) {
                            this.syncLabelCheck[type][i] = false;
                        }
                        this.syncIsNoAllCheck[inx] = false;
                    }
                }
            },

            // 提交校验
            handlePloofData(type, editData) {
                let isComplate = false;
                if (type === '2' && editData.isEmpower && editData.isEmpower === '1' && JSON.stringify(editData.empowerInfo) === '{}') {
                    this.$message.warning('请上传授权附件！');
                    isComplate = true;
                } else if (type === '3' && !this.checkSku.length) {
                    this.$message.warning('请勾选需要同步的SKU！');
                    isComplate = true;
                };
                return isComplate;
            },

            // 保存或提交信息
            handleSaveOrSubmitInfo(type, editData = this.editData) {
                let saveFileData;
                if (type === '3') {
                    saveFileData = editData;
                } else {
                    saveFileData = this.saveFileData;
                }

                if (this.handlePloofData(type, editData)) return;

                let obj = {
                    pack_type: editData.packType,
                    pack_weight: editData.packWeight,
                    size: editData.packLength,
                    weight: editData.sampleWeight,
                    sample_remarks: editData.sampleRemarks,
                    packing_list: editData.packList,
                    brand_attribute: editData.brandAttr,
                    brand: editData.productBrand,
                    model: editData.productModel,
                    logo: editData.isHaveLogo,
                    material: editData.materialQuality,
                    product_use: editData.productUse,
                    working: editData.workPrinciple,
                    authorized: editData.isEmpower,
                    publish: editData.publishAttr,
                    order_attribute: editData.orderAttr,
                    basic: editData.basisAttr,
                    special: editData.specialAttr,
                    battery: editData.batteryAttr,
                    prohibited: editData.isBanAttr,
                    shape_attribute: editData.modelAttr,
                    overseas_attribute: editData.overseas,
                    certificate_type: editData.certificateType,
                    instructions  : editData.instructions,
                    status: type,
                    battery_type: editData.batteryInfo && editData.batteryInfo[0],
                    battery_model: editData.batteryInfo && editData.batteryInfo[1],
                    battery_ml: editData.batteryInfo && editData.batteryInfo[2],
                    certificate_other: editData.certificateTypeInfo,
                    instructions_other: editData.instructionsInfo,
                };

                const radioDataChange = (rkey, rinfo, oinfo) => {
                    if (editData[rkey] === '1') {
                        let objArr = [];
                        this.editData[rinfo].forEach(value => {
                            objArr = [...objArr, ...value];
                        });

                        let infoArr = [];
                        objArr.forEach(value => {
                            let arr = [];
                            for (let key in value) {
                                arr.push(value[key]);
                            }
                            infoArr.push(arr);
                        });
                        obj[oinfo] = JSON.stringify([...objArr]);
                    } else {
                        obj[oinfo] = '0';
                    }
                };

                radioDataChange('isCheckPrice', 'checkPriceInfo', 'limit_price');
                radioDataChange('isTort', 'tortInfo', 'infringement');
                radioDataChange('isDisSales', 'disSalesInfo', 'is_prohibited');

                let formData = new FormData();

                for (let key in obj) {
                    if (obj[key] || obj[key] === 0) {
                        formData.append(key, obj[key] || '');
                    }
                }

                let isBigSubmit = type === '2' && this.$parent.submitSkuList.indexOf(this.sku) === -1 &&
                    (this.$parent.submitSkuList.length >= this.skuList.length -1);
                if (type === '3') {
                    this.checkSku.forEach((v, i) => {
                        formData.append(`sku[${i}]`, v);
                    })
                } else if (isBigSubmit){
                    let obj = {
                        sku: this.skuList,
                        spu: this.row.spu[0]
                    };
                    formData.append('sku', this.sku);
                    formData.append('big_status', JSON.stringify(obj));
                } else {
                    formData.append('sku', this.sku);
                }

                let [fileIndex, fileLink] = [0, []];
                saveFileData.appendixDatum.forEach((value, index) => {
                    if (value.type === 'preview') return;
                    if (value.raw) {
                        formData.append(`file[${fileIndex}]`, value.raw);
                        fileIndex++;
                    } else {
                        // formData.append(`file[${index}]`, this.saveUrlFile.appendixDatum[index]);
                        fileLink.push(value);
                    }
                });
                if (fileLink.length) {
                    formData.append('file_list', JSON.stringify(fileLink));
                }

                let [myfileIndex, myfileLink] = [0, []];
                saveFileData.devImage.forEach((value, index) => {
                    if (value.raw) {
                        formData.append(`myfile[${myfileIndex}]`, value.raw);
                        myfileIndex++;
                    } else {
                        // formData.append(`myfile[${index}]`, this.saveUrlFile.devImage[index]);
                        myfileLink.push(value.url);
                    }
                });

                if (myfileLink.length) {
                    formData.append('myfile_list', JSON.stringify(myfileLink));
                }

                if (editData.isEmpower && editData.isEmpower === '1') {
                    if (editData.empowerInfo.type === 'preview') {
                        formData.append('authorized_file_list', JSON.stringify(editData.empowerInfo));
                    } else {
                        formData.append('authorized_file', editData.empowerInfo.raw);
                    }
                }

                if (editData.orderAttrFile && editData.orderAttrFile !== '{}') {
                    if (editData.orderAttrFile.raw) {
                        formData.append('research_file', editData.orderAttrFile.raw);
                    } else {
                        formData.append('research_file_list', '');
                    }
                }

                if (type === '3') {
                    syncSkuInfo(formData).then(({ data }) => {
                        this.handleOperationCallBack(data);
                    })
                } else {
                    submitSaveSKURepairInfo(formData).then(({ data }) => {
                        if (type === '2' && data.status) {
                            this.$emit('submit', this.sku)
                        }
                        this.handleOperationCallBack(data);
                    });
                }
            },

            // 关联SKU
            handleRelevanceSkuChange(value) {
                let checkedCount = value.length;
                this.checkAllSku = checkedCount === this.skuList.length;
                this.isIndeterminate = checkedCount > 0 && checkedCount < this.skuList.length;
            },

            // 全选SKU
            handleCheckAllChange(val) {
                this.checkSku = val ? this.skuList : [];
                this.isIndeterminate = false;
            },

            // 同步SKU信息
            handleSyncSkuInfo() {

                // 因为一开始使用索引后面改键值对导致这里要用数组索引查找键，数组对应的索引匹配syncLabelCheck对象的键
                const keyObj = ['productComDatumSync', 'tortDatumSync', 'appendixDatumSync', 'devImageSync', 'productAttrDatumSync'];
                let keyArr = [];
                let fileObj = {
                    appendixDatum: [],
                    devImage: []
                };
                let isHasSync = false;
                this.syncIsNoAllCheck.forEach((value, index) => {
                    if (value || this.syncCheck[index]) {
                        isHasSync = true;
                        this.syncLabelCheck[keyObj[index]].forEach((v, i) => {
                            if (v) {
                                // 对区域2跟3做文件操作处理
                                if (index === 2 || index === 3) {
                                    let fileKey = keyObj[index].replace('Sync', '');
                                    fileObj[fileKey].push(this.saveFileData[fileKey][i]);
                                } else {
                                    keyArr.push(this.syncCheckArea[index][i]);
                                }
                            }
                        });
                    }
                });

                if (!isHasSync) {
                    this.$message.warning('请勾选数据后再同步！');
                    return;
                }

                let obj = { ...fileObj };
                keyArr.forEach(key => {
                    obj[key] = this.editData[key];
                    switch (key) {
                        case 'batteryAttr':
                            obj.batteryInfo = this.editData.batteryInfo;
                            break;
                        case 'certificateType':
                            obj.certificateTypeInfo = this.editData.certificateTypeInfo;
                            break;
                        case 'instructions':
                            obj.instructionsInfo = this.editData.instructionsInfo;
                            break;
                        case 'isBanAttr':
                            obj.banInfo = this.editData.banInfo;
                            break;
                        case 'isEmpower':
                            obj.empowerInfo = this.editData.empowerInfo;
                            break;
                        // case 'orderAttr':
                        //     obj.orderAttrInfo = this.editData.orderAttrInfo
                    }
                });

                // console.log(obj);
                this.handleSaveOrSubmitInfo('3', obj);
            },

            // 初始化数据
            handleInitData(refreshData) {

                let data = refreshData || this.$parent.editPageData[this.sku];
                const attrCheckFn = (eKey, iKey) => {
                    if (data[iKey]) {
                        this.editData[eKey] = data[iKey].split(',');
                    }
                };

                const multipleAreaFn = (eKey, iKey, area) => {
                    let iArr = data[iKey] && JSON.parse(data[iKey]);
                    if (!iArr && iArr !== 0) {
                        this.editData[area] = '';
                        return;
                    } else {
                        this.editData[area] = iArr ? '1' : '2';
                    }
                    if (area === 'isCheckPrice') {
                        Array.isArray(iArr) && iArr.forEach((value, index) => {
                            let keyInx = (index + 1) / 3 - 1;
                            if (Array.isArray(this.editData[eKey][Math.ceil(keyInx)])) {
                                this.$set(this.editData[eKey][Math.ceil(keyInx)], index % 3, value);
                            } else {
                                this.$set(this.editData[eKey], Math.ceil(keyInx), []);
                                this.$set(this.editData[eKey][Math.ceil(keyInx)], index % 3, value);
                            }
                        });
                    } else {
                        Array.isArray(iArr) && iArr.forEach((value, index) => {
                            let keyInx = (index + 1) / 2 - 1;
                            if (Array.isArray(this.editData[eKey][Math.ceil(keyInx)])) {
                                this.$set(this.editData[eKey][Math.ceil(keyInx)], index % 2, value);
                            } else {
                                this.$set(this.editData[eKey], Math.ceil(keyInx), []);
                                this.$set(this.editData[eKey][Math.ceil(keyInx)], index % 2, value);
                            }
                        });
                    }
                };

                this.editData.packType = data.pack_type;
                this.editData.packWeight = data.pack_weight;
                this.editData.packLength = data.size;
                this.editData.sampleWeight = data.weight;
                this.editData.sampleRemarks = data.buy_sample_note;
                this.editData.packList = data.pack_list;
                this.editData.brandAttr = data.brand_attribute;
                this.editData.productBrand = data.brand;
                this.editData.productModel = data.model;
                this.editData.isHaveLogo = data.logo;
                this.editData.materialQuality = data.material;
                this.editData.productUse = data.product_use;
                this.editData.workPrinciple = data.working;
                this.editData.isEmpower = data.authorized;
                this.editData.empowerInfo = {};
                this.editData.orderAttrFile = {};
                this.editData.batteryInfo = [data.battery_type, data.battery_model, data.battery_ml];
                // this.editData.banInfo = data.prohibited;
                this.editData.certificateTypeInfo = data.certificate_other;
                this.editData.instructionsInfo = data.instructions_other;

                attrCheckFn('publishAttr', 'publish');
                attrCheckFn('orderAttr', 'order_attribute');
                attrCheckFn('basisAttr', 'basic');
                attrCheckFn('specialAttr', 'special');
                attrCheckFn('batteryAttr', 'battery');
                attrCheckFn('isBanAttr', 'prohibited');
                attrCheckFn('modelAttr', 'shape_attribute');
                attrCheckFn('overseas', 'overseas_attribute');
                attrCheckFn('certificateType', 'certificate_type');
                attrCheckFn('instructions', 'instructions');

                multipleAreaFn('checkPriceInfo', 'limit_price', 'isCheckPrice');
                multipleAreaFn('tortInfo', 'infringement', 'isTort');
                multipleAreaFn('disSalesInfo', 'is_prohibited', 'isDisSales');


                // 清空保存的数据
                this.saveFileData.devImage.splice(0);
                this.saveFileData.appendixDatum.splice(0);
                if (data.authorized_file && !Array.isArray(data.authorized_file)) {
                    let fileObj = {
                        ...data.authorized_file,
                        type: 'preview'
                    };
                    this.editData.empowerInfo = fileObj;
                    this.saveFileData.appendixDatum.push(fileObj);
                }

                data.myfile && data.myfile.forEach((value, index) => {
                    this.saveFileData.devImage.push({
                        url: value
                    });
                    // let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                    // imageUrlChangeFile(value, fileName[0].replace('/', ''), (file) => {
                    //     this.saveUrlFile.devImage.splice(index, 1, file);
                    //     this.saveFileData.devImage.push({
                    //         url: value
                    //     });
                    // });
                });

                data.file && data.file.forEach((value, index) => {
                    this.saveFileData.appendixDatum.push(value);
                    // let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                    // imageUrlChangeFile(value, fileName[0].replace('.', ''), (file) => {
                    //     this.$set(this.saveUrlFile.appendixDatum, index, file);
                    //     this.saveFileData.appendixDatum.push({
                    //         url: value
                    //     });
                    // });
                });
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    this.$message.success('操作成功');
                    getSPURepairInfo({
                        is_reject: this.row.reject_type,
                        sku: this.sku
                    }).then(({ data }) => {
                        this.handleInitData(data.data);
                    })
                } else {
                    this.$message.error(data.errorMess || '操作失败');
                }
            }
        },
        watch: {
            sku(val) {
                this.handleInitData();
            },
            "editData.empowerInfo"(val) {
                if (JSON.stringify(val) !== '{}') {
                    let isEm = false;
                    for (let i = 0; i < this.saveFileData.appendixDatum.length; i++) {
                        if (this.saveFileData.appendixDatum[i].type === 'preview') {
                            this.saveFileData.appendixDatum[i] = {
                                ...val,
                                type: 'preview'
                            };
                            break;
                        } else if (this.saveFileData.appendixDatum.length - 1 === i) {
                            isEm = true;
                        }
                    }

                    if (isEm) {
                        this.saveFileData.appendixDatum.push({
                            ...val,
                            type: 'preview'
                        })
                    }
                }
            }
        }
    }
</script>

<style scoped lang="less">

    @border-color: #e6e6e6;
    @active-color: #B10949;

    @upload-height: 130px;

    /deep/.el-form-item__label {
        font-size: 12px;
        color: #333333;
    }
    /deep/.el-radio__label {
        font-size: 12px;
        padding-left: 5px;
    }
    /deep/.el-checkbox__label {
        font-size: 12px;
    }

    .ui-content_area {
        height: 100%;
        padding: 45px 150px 0;
        position: absolute;
        top: 0;
        width: 100%;
        box-sizing: border-box;

        .el-form {
            height: 100%;
        }

        .el-select {
            width: 100%;
        }

        .ui-active {
            color: @active-color!important;
        }

        .ui-sync_check {
            /deep/ .el-collapse-item__content {
                border-color: @active-color!important;
            }
            /deep/.el-collapse-item__wrap {
                padding-top: 0;
                padding-bottom: 0;
            }
        }

        .ui-content_area--item {
            .ui-file__checkbox {
                position: absolute;
                top: 0;
                right: 5px;
            }

            .ui-checkbox {
                text-align: right;
                .el-checkbox {
                    margin: 0;
                }

                /*& > span {*/
                    /*position: absolute;*/
                    /*top: 0;*/
                    /*right: 0;*/
                    /*font-size: 14px;*/
                /*}*/
            }

            &:not(:first-child) {
                margin-top: 20px;
            }

            ul.ui-content_area--list > li {
                /*line-height: 40px;*/
                /*height: 40px;*/
                position: relative;
                padding-top: 10px;
                min-height: 24px;
            }

            .ui-label {
                padding-left: 5px;
                padding-top: 10px;
                /*height: 40px;*/
                /*line-height: 40px;*/
                width: 80px;
                font-size: 12px;
                color: #333333;
                position: absolute;
                top: 2px;
                left: 0;
                cursor: default;
            }

            .ui-value {
                margin-left: 110px;
                span {
                    font-size: 14px;
                    color: #666666;
                }
            }

            .ui-check__input {
                width: 100%;
                & > div {
                    float: left;
                    margin-right: 2px;
                }
            }
        }

        .ui-content_area--left {
            float: left;
            width: 55%;
            padding-top: 10px;
            border-right: 1px solid @border-color;
            box-sizing: border-box;
            padding-right: 10px;
            height: 100%;
            overflow: auto;
        }
        .ui-content_area--right {
            float: right;
            width: 45%;
            padding-top: 10px;
            padding-left: 10px;
            box-sizing: border-box;
            max-height: 100%;
            overflow: auto;
        }

        .ui-content_area--form {
            margin-top: 10px;

            li:not(.ui-layout) {
                &:nth-child(2n) {
                    float: right;
                }
            }

            li {
                float: left;
                width: 40%;

                /*&:not(.ui-layout) {*/
                    /*&*/
                /*}*/

                &.ui-layout {
                    & > span {
                        font-size: 12px;
                        color: #333333;
                        padding-left: 4px;
                    }
                    width: 100%;
                }

                &.ui-ope_area {
                    position: relative;
                    .el-input {
                        margin-right: 70px;
                        width: unset;
                        display: block;
                    }

                    .ui-ope_area--btn {
                        position: absolute;
                        right: 0;
                        top: 0;

                        span {
                            cursor: pointer;
                        }
                    }

                    .ui-ope_area--item {
                        position: relative;
                        margin-bottom: 5px;
                        &:last-child {
                            margin-bottom: 0;
                        }
                    }

                    .ui-select--layout {
                        /deep/ .el-select .el-input {
                            width: 100px;
                        }
                        /deep/ .el-input-group__prepend {
                            background-color: #fff;
                        }
                        /*.el-input {*/
                            /*width: unset;*/
                        /*}*/
                    }
                }

                &.ui-ope_area--three {
                    .el-input {
                        width: 30%;
                        display: inline-table;
                        margin-right: 2px;
                    }
                }

                &.ui-ope_area--tow {
                    .el-input, .ui-ope__input {
                        width: 45%;
                        display: inline-table;
                        margin-right: 10px;
                    }
                    .ui-ope__input {
                        float: left;
                        display: flex;
                        .el-input {
                            width: 45%;
                            float: left;
                        }
                        .el-select {
                            width: 30%;
                            float: left;
                        }
                    }
                    .ui-select--layout {
                        .ui-layout__select {
                            margin-left: 6px;
                            width: 96%;
                            /deep/ .el-input {
                                width: 96%;
                            }
                        }
                    }
                }
            }
        }

        .ui-content_area--btn {
            text-align: center;
            padding: 20px 0;
            .el-button {
                margin: 0 28px;
            }
        }
    }

    .ui-com_edit__upload {
        .ui-com_edit__upload--title {
            font-size: 14px;
            font-weight: bold;
            text-align: left;
            display: block;
        }
        li {
            float: left;
            width: 130px;
            margin-bottom: 10px;
            /*height: 185px;*/
            margin-right: 20px;
            text-align: center;
            position: relative;

            .ui-com_edit__upload--item > span {
                color: #666;
                font-size: 14px;
                position: absolute;
                bottom: 10px;
                left: 0;
                display: block;
                width: 100%;
            }

            img {
                width: 100%;
                height: @upload-height;
            }

            .ui-com_edit__upload--last_btn {
                border: 1px solid #EAEEED;
                width: 100%;
                height: @upload-height;

                /deep/ .el-upload {
                    display: block;
                    height: @upload-height;
                    line-height: @upload-height;
                    background: #F5F9F8;

                    span {
                        color: @border-color;
                    }

                    .icon-tupianshangchuan {
                        font-size: 40px;
                        margin: 0;
                    }
                }
            }

            .ui-com_edit__upload--item--mask {

                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: @upload-height;
                background: rgba(0, 0, 0, .3);
                text-align: center;
                line-height: @upload-height;

                & > div {
                    display: inline-block;
                }

                span {
                    margin: 0 5px;
                    cursor: pointer;
                    color: #cccccc;
                    font-size: 20px;

                    &:hover {
                        color: #FFFFFF;
                    }
                }
            }

            &:hover {
                .ui-com_edit__upload--item--mask {
                    display: block;
                }
            }
        }
    }

    .ui-right {
        right: 0;
        top: 0;
        width: 140px;
        position: absolute;
        height: 100%;
        border-left: 1px solid @border-color;
        padding-top: 45px;
        box-sizing: border-box;
        overflow: hidden;
        .ui-right--item {
            padding: 10px;
            box-sizing: border-box;

            .el-button {
                /*margin-left: 20px;*/
                height: 20px;
                line-height: 20px;
                width: 50px;
                text-align: center;
                padding: 0;
            }
        }

        .el-checkbox {
            margin-bottom: 5px;
        }
    }
</style>


