
/**  --->>>>   |^ 取消开发列表^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取取消开发列表信息
export function getCancelDevListInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        list_type: 4,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/create_supplier' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/product/Product_list/product_list', data);
    }
}


