import http from '@/http'
import CONFIG from '@/assets/js/config'

// 检查清单列表
export function getBasicCheckListInfo (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    }

    return http.getAjax('/sample_config/qc_config/parent_type', data)

}



