/**  --->>>>   |^ 基础资料维护-质检标准^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取发货包装维护
export function getDeliveryPackListInfo (queryData = {}) {
    let data = {
        limit: 20,
        offset: 1,
        ...queryData
    };

    switch (queryData.fid) {
        case '5':
            return http.getAjax('/sample_config/return_config/config_list', data);
        case '6':
            return http.getAjax('/sample_config/quality_config/config_list', data);
        default:
            return http.getAjax('/sample_config/sample_config/config_list', data);
    }
}

//删除包装信息
export function delDeliveryPack (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    switch (params.fid) {
        case '5':
            return http.postAjax('/sample_config/return_config/del_config', data);
        case '6':
            return http.postAjax('/sample_config/quality_config/del_config', data);
        default:
            return http.postAjax('/sample_config/sample_config/del_config', data);

    }
}

//新增包装信息
export function addDeliveryPack (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    switch (params.fid) {
        case '5':
            return http.postAjax('/sample_config/return_config/add_config', data);
        case '6':
            return http.postAjax('/sample_config/quality_config/add_config', data);
        default:
            return http.postAjax('/sample_config/sample_config/add_config', data);

    }
}

// 修改包装信息
export function changeDeliveryPackInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    switch (params.fid) {
        case '5':
            return http.postAjax('/sample_config/return_config/edit_config', data);
        case '6':
            return http.postAjax('/sample_config/quality_config/edit_config', data);
        default:
            return http.postAjax('/sample_config/sample_config/edit_config', data);
    }
}

// 导出excel
export function exportDeliveryPackInfo (params = {}) {
    const data = {
        ...params
    };
    let str = '?';
    for (let key in data) {
        str = `${str}&${key}=${data[key] || ''}`;
    }
    switch (params.fid) {
        case '5':
            window.open('/sample_config/return_config/export_config' + str, '_self');
            break;
        case '6':
            window.open('/sample_config/quality_config/export_config' + str, '_self');
            break;
        default:
            window.open('/sample_config/sample_config/export_config' + str, '_self');
    }
}




