<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model.trim="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model.trim="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品目录" template="cascader"
                                           :options="optionList.productList"
                                           :props="{ value: 'id', children: 'son_id', label: 'category_name' }"
                                           v-model="queryData.productList"></SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核结果">
                            <el-select v-model="queryData.auditState" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.statusList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核时间" template="daterange"
                                           v-model="queryData.auditTime"></SearchRequirement>
                    </li>

                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model.trim="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>

                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model.trim="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>

                    <li>
                        <SearchRequirement label="作业节点">
                            <el-select size="small" v-model="queryData.functionInfo">
                                <el-option
                                    v-for="(value, label) in optionList.functionInfoList"
                                    :key="label"
                                    :label="value"
                                    :value="label">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>


                    <li>
                        <SearchRequirement label="操作人员">
                            <el-select v-model="queryData.operateUser" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.operateUserList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>

                    <li>
                        <SearchRequirement label="样品类型">
                            <el-select v-model="queryData.sampleType" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.sampleTypeList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>


                    <transition name="slide-fade" @after-enter="animation()" @after-leave="animation()"
                                @before-enter="animation(1)">
                        <div v-show="filterShow">
                            <li>
                                <SearchRequirement label="是否买样">
                                    <el-select v-model="queryData.isBuySample" placeholder="请选择" size="small">
                                        <el-option
                                            v-for="(label, value) in optionList.buySampleList"
                                            :key="value"
                                            :label="label"
                                            :value="value"></el-option>
                                    </el-select>
                                </SearchRequirement>

                            </li>
                            <li>
                                <SearchRequirement label="是否定制">
                                    <el-select v-model="queryData.isCustom" placeholder="请选择" size="small">
                                        <el-option
                                            v-for="(label, value) in optionList.customList"
                                            :key="value"
                                            :label="label"
                                            :value="value"></el-option>
                                    </el-select>
                                </SearchRequirement>

                            </li>

                            <li>
                                <SearchRequirement label="采样价">
                                    <el-input size="small" style="width: 46.5%;"
                                              v-model.trim="queryData.priceMin"></el-input>
                                    <span>-</span>
                                    <el-input size="small" style="width: 46.5%;"
                                              v-model.trim="queryData.priceMax"></el-input>
                                </SearchRequirement>
                            </li>

                            <li>
                                <SearchRequirement label="质检等级">
                                    <el-select v-model="queryData.gradeType" placeholder="请选择" size="small">
                                        <el-option
                                            v-for="(label, value) in optionList.gradeList"
                                            :key="value"
                                            :label="label"
                                            :value="value"></el-option>
                                    </el-select>
                                </SearchRequirement>
                            </li>
                            <li>
                                <SearchRequirement label="操作用时">
                                    <el-select v-model="queryData.settlementType"
                                               @change="handleTimeRange(queryData.settlementType)" placeholder="请选择"
                                               size="small">
                                        <el-option
                                            v-for="(label, value) in optionList.settlementTypeList"
                                            :key="value"
                                            :label="label"
                                            :value="value"></el-option>
                                    </el-select>

                                    <el-slider
                                        v-show="queryData.settlementType"
                                        v-model="queryData.timeRange"
                                        range
                                        :max="queryData.timeMax">
                                    </el-slider>

                                </SearchRequirement>
                            </li>
                        </div>
                    </transition>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button size="small" @click="handleResetQueryInfo" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" @click="handleQueryInfo({limit: pageData.currentList})" icon="icon-chaxun"
                               type="primary">查询
                    </el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="!filterShow">高级筛选<i class="el-icon-caret-bottom"></i></el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="filterShow">基本筛选<i class="el-icon-caret-top"></i></el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <el-button type="text" size="small" @click="handleQueryInfo({excelType:1})">
                <i class="icon-daochu icon-main"></i>导出当前页
            </el-button>
            <el-button type="text" size="small" @click="handleQueryInfo({excelType:2})">
                <i class="icon-daochu icon-main"></i>导出所有
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            ref="myTable"
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">

            <template slot="spu" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.spu[0]}}</p>
                    <p>{{scope.row.spu[1]}}</p>

                    <div class="ui-table--supplier_code--flag" v-show="scope.row.devp_type">
                        <span>{{scope.row.devp_type}}</span>
                    </div>
                    <div class="ui-table--icon_btn">
                        <i :class="`icon-bianji${scope.row.status_code === '1' || scope.row.status_code === '4' ? '' : ' ui-disable'}`"
                           @click.stop="handleClickEdit(scope.row)"></i>
                    </div>
                </div>
            </template>
            <template slot="photo_path" slot-scope="scope">
                <div class="upload-img-list" v-if="scope.row.photo_path!==''">
                    <div class="ui-com_edit__upload--item">
                        <div class="ui-com_edit__upload--item__img"
                             :style="{background:'url('+scope.row.photo_path+') no-repeat center/contain'}">
                        </div>
                        <div class="ui-com_edit__upload--item--mask">
                            <span class="el-icon-zoom-in" @click="handleLookImage(scope.row.photo_path)"></span>
                        </div>
                    </div>
                </div>
            </template>
            <template slot="good_name" slot-scope="scope">
                <p v-for="(item,index) in scope.row.good_name" :key="index">{{item}}</p>
            </template>
            <template slot="category_path" slot-scope="scope">
                <p v-for="(item,index) in scope.row.category_path" :key="index">{{item}}</p>
            </template>

            <template slot="sample_type" slot-scope="scope">
                <p v-for="(item,index) in scope.row.sample_type" :key="index">{{item}}</p>
            </template>

            <template slot="good_info" slot-scope="scope">
                <p v-for="(item,index) in scope.row.good_info" :key="index">{{item}}</p>
            </template>


            <template slot="spu_check_status" slot-scope="scope">
                <p v-for="(item,index) in scope.row.spu_check_status" :key="index">{{item}}</p>
            </template>

            <template slot="operation_record" slot-scope="scope">
                <div v-for="(item, index) in scope.row.operation_record" v-if="scope.row.operation_record.length<3"
                     :key="index">{{item}}
                </div>
                <el-popover
                    v-if="scope.row.operation_record.length>3"
                    style="cursor: pointer;"
                    placement="left"
                    trigger="hover">
                    <div v-for="(item, index) in scope.row.operation_record"
                         :key="index">{{item}}
                    </div>
                    <div slot="reference">
                        <div v-for="(item, index) in scope.row.operation_record" v-if="index<3" :key="index">{{item}}
                        </div>
                        <div v-if="scope.row.operation_record.length>3">...</div>
                    </div>
                </el-popover>
            </template>

            <template slot="operation_used_time" slot-scope="scope">
                <p v-for="(item,index) in scope.row.operation_used_time" :key="index">{{item}}</p>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>

        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <EditPage :show.sync="isEditShow" :row="saveEditRow"></EditPage>
    </div>
</template>

<script>
import config from '@/assets/js/config';

import {
    mapActions,
    mapGetters
} from 'vuex';

import {
    getAuditRecordInfo
} from '@/api/quality_control_department/sample_quality_control/audit_record';
import EditPage from './EditPage';

export default {
    components: {
        EditPage
    },
    data () {
        return {

            //高级筛选
            filterShow: false,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                productSPU: '',
                productName: '',
                supplierCode: '',
                supplierName: '',
                productList: [],
                sampleType: '',
                isBuySample: '',
                isCustom: '',
                auditState: '',
                functionInfo: '',
                settlementType: '',
                timeRange: ['', ''],
                timeMax: 10,
                operateUser: '',
                priceMin: '',
                priceMax: '',
                auditTime: [],
                gradeType: '',

            },

            optionList: {
                productList: [],
                sampleTypeList: [],
                buySampleList: [],
                customList: [],
                statusList: {},
                functionInfoList: {},
                settlementTypeList: {},
                operateUserList: {},
                gradeList: {},
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            //查看图片
            lookImageUrl: '',
            isShowLookImage: false,

            //编辑
            saveEditRow: {},
            isEditShow: false,

        };
    },
    computed: {
        ...mapGetters([
            'queryParamsInfo',
            'auditRecordInfo'
        ]),
    },
    methods: {
        ...mapActions([
            'GetAuditRecordInfo'
        ]),

        //操作用时
        handleTimeRange (type) {
            switch (type) {
                case '1':
                    this.queryData.timeMax = 60;
                    break;
                case '2':
                    this.queryData.timeMax = 24;
                    break;
                case '3':
                    this.queryData.timeMax = 30;
                    break;
            }
            this.queryData.timeRange = [1, 24];
        },

        // 点击编辑
        handleClickEdit (row) {
            this.saveEditRow = row;
            this.isEditShow = true;
        },

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['audit_record']);
            }

            let obj = {

                spu: this.queryData.productSPU,
                supplier_code: this.queryData.supplierCode,
                spu_category: this.queryData.productList.join('->'),
                sample_type: this.queryData.sampleType,
                is_buy_sample: this.queryData.isBuySample,
                is_customized: this.queryData.isCustom,
                status: this.queryData.auditState ? 13 : 4,
                function_time: this.queryData.functionInfo,
                used_time: this.queryData.settlementType,
                used_time_min: this.queryData.timeRange[0],
                used_time_max: this.queryData.timeRange[1],
                sample_price_min: this.queryData.priceMin,
                sample_price_max: this.queryData.priceMax,
                goods_name: this.queryData.productName,
                supplier_name: this.queryData.supplierName,
                function_operate: this.queryData.functionInfo,
                user_operate: this.queryData.operateUser,
                function_check: this.queryData.functionInfo,
                check_begin: this.queryData.auditTime[0],
                check_end: this.queryData.auditTime[1],
                grade_qc: this.queryData.gradeType,
                ...params
            };

            this.GetAuditRecordInfo(obj).then(({data}) => {
                // 导出
                if (data.status === 2) return;
                console.log(data, 'data');
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                productSPU: '',
                productName: '',
                supplierCode: '',
                supplierName: '',
                productList: [],
                sampleType: '',
                isBuySample: '',
                isCustom: '',
                auditState: '',
                functionInfo: '',
                settlementType: '',
                timeRange: ['', ''],
                timeMax: 10,
                operateUser: '',
                priceMin: '',
                priceMax: '',
                auditTime: [],
                gradeType: ''
            };
        },

        //高级筛选
        handleClickFilter () {
            this.filterShow = !this.filterShow;
        },

        animation (isHide) {
            if (isHide) {
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.moduleType = val;
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'audit_record',
                value: JSON.stringify(this.queryData)
            });

            this.optionList.productList = [...data.select_list.catagory_tree];
            this.optionList.sampleTypeList = data.select_list.sample_type;
            this.optionList.buySampleList = data.select_list.is_buy_sample;
            this.optionList.customList = data.select_list.is_customized;
            this.optionList.statusList = data.select_list.status;
            this.optionList.functionInfoList = data.select_list.function_time;
            this.optionList.settlementTypeList = data.select_list.time_type;
            this.optionList.operateUserList = data.select_list.devlop_user;
            this.optionList.gradeList = data.select_list.grade_qc;

            //数据渲染
            this.tableData = data.data.value;
            this.tableData.map((item) => {
                return item.operation_record.reverse();
            });
            this.tableWidth = ['', '', '', '70px', '220px', '220px'];

            this.tableProps = Object.keys(data.data.key);
            this.tableKey = Object.values(data.data.key);

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        },

        //是否勾选
        checkedStatus (obj) {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this[obj] = true;
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.auditRecordInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['audit_record']);
                this.handleQueryInfoCallBack(this.auditRecordInfo);
            }
        } catch (e) {
            console.log(e);
        }
    },
};
</script>

<style lang="less" scoped>


.ui-table--supplier_code {
    font-size: 14px;
    font-weight: bold;
    line-height: 14px;

    p {
        margin-bottom: 10px;
    }
}

/deep/ .el-table td, /deep/ .el-table th {
    overflow: hidden;
    text-align: left !important;
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    background: #63c4e9;
    position: absolute;
    font-size: 12px;
    color: #FFFFFF;
    top: -12px;
    right: -20px;
}

.ui-table--icon_btn {
    i {
        cursor: pointer;
    }
}

</style>



