<template>
    <div class="ui-scan_page ui-main-module">
        <div class="ui-head">
            <ul class="ui-head-ope_item">
                <!--<li>-->
                <!--<i class="icon-yonghu icon_blue"></i>-->
                <!--<el-input size="small" placeholder="请输入工号" @blur="handleUserInfo"-->
                <!--v-model="queryData.user_id"></el-input>-->
                <!--<el-input size="small" placeholder="带出部门" v-model="queryData.department"></el-input>-->
                <!--<el-input size="small" placeholder="带出姓名" v-model="queryData.handle_type">-->
                <!--<el-button slot="append">退出</el-button>-->
                <!--</el-input>-->
                <!--</li>-->
                <li class="ui-head-ope_item--layout">
                    <el-button type="text" size="small" @click="isRelevance = !isRelevance">
                        <i v-show="!isRelevance" class="icon-erweimahong icon-danger"></i>
                        <i v-show="isRelevance" class="icon-erweimalan icon-main"></i>
                    </el-button>
                    <el-input size="small" @keyup.enter.native="handleScanInfo" placeholder="请扫描二维码"
                              v-model="queryData.code">
                        <el-button slot="append" @click="submitScan">提交</el-button>
                    </el-input>
                </li>
            </ul>
        </div>
        <div class="ui-fn-module">
            <!--<span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>-->
            <el-button type="text" size="small" @click="deleteScan"><i class="icon-shanchusaoma icon-danger"></i>删除扫码
            </el-button>
            <el-button type="text" size="small" v-show="isRelevance" @click="relevanceScan"><i
                class="icon-querenguanlian icon-main"></i>确认关联
            </el-button>
            <el-button type="text" size="small" v-show="!isRelevance"><i class="icon-jiechubangding icon-warn"></i>解除绑定
            </el-button>
        </div>
        <div class="ui-main-module-table">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.value)"
                              :class="queryData.list_type === item.value && 'ui-active'">{{item.label}}<i
                            class="ui-tab_line"></i></span>
                    </li>
                </el-badge>
            </div>
        </div>

        <div v-show="queryData.list_type==1">
            <ComTableInfo
                isCellCancel
                ref="boxSPURef"
                isCheck
                :tableKey="tableData.boxSPUKey"
                :tableProps="tableData.boxSPUProps"
                :tableData="tableData.boxSPUData"
                :tableWidth="tableData.tableWidth"
                :otherHeight="['ui-main-tab', 'ui-head']"
                :tableRowClassName="rowClassName"
                @checked="handleSaveCheckInfo">
                <!--@selectd="handleSelectAbout"-->
                <template slot="remark" slot-scope="scope">
                    <el-input type="small" v-model="scope.row.remark" @blur="submitRemark(scope.row)"></el-input>
                </template>
            </ComTableInfo>
        </div>

        <div v-show="queryData.list_type==2">
            <ComTableInfo
                isCellCancel
                ref="trolleyBoxRef"
                isCheck
                :otherHeight="['ui-main-tab', 'ui-head']"
                :tableKey="tableData.trolleyBoxKey"
                :tableProps="tableData.trolleyBoxProps"
                :tableData="tableData.trolleyBoxData"
                :tableWidth="tableData.tableWidth"
                :tableRowClassName="rowClassName"
                @checked="handleSaveCheckInfo">

                <template slot="remark" slot-scope="scope">
                    <el-input type="small" v-model="scope.row.remark" @blur="submitRemark(scope.row)"></el-input>
                </template>
            </ComTableInfo>
        </div>

        <div v-show="queryData.list_type==3">
            <ComTableInfo
                isCellCancel
                ref="trolleySPURef"
                isCheck
                :otherHeight="['ui-main-tab', 'ui-head']"
                :tableKey="tableData.trolleySPUKey"
                :tableProps="tableData.trolleySPUProps"
                :tableData="tableData.trolleySPUData"
                :tableWidth="tableData.tableWidth"
                :tableRowClassName="rowClassName"
                @checked="handleSaveCheckInfo">

                <template slot="remark" slot-scope="scope">
                    <el-input type="small" v-model="scope.row.remark" @blur="submitRemark(scope.row)"></el-input>
                </template>
            </ComTableInfo>
        </div>

        <div v-show="queryData.list_type==4">
            <ComTableInfo
                isCellCancel
                ref="trolleySPURef"
                isCheck
                :otherHeight="['ui-main-tab', 'ui-head']"
                :tableKey="tableData.SPUKey"
                :tableProps="tableData.SPUProps"
                :tableData="tableData.SPUData"
                :tableWidth="tableData.tableWidth"
                :tableRowClassName="rowClassName"
                @checked="handleSaveCheckInfo">

                <template slot="remark" slot-scope="scope">
                    <el-input type="small" v-model="scope.row.remark" @blur="submitRemark(scope.row)"></el-input>
                </template>
            </ComTableInfo>
        </div>

        <el-dialog title="查看台车明细" :visible.sync="dialogTableVisible">
            <ComTableInfo
                :tableKey="detailKey"
                :tableProps="detailProps"
                :tableData="detailData"
                :tableWidth="detailWidth">
                <template slot="num" slot-scope="scope">
                    {{scope.row.num.num}}
                </template>
                <template slot="pro_spu" slot-scope="scope">
                    {{scope.row.pro_spu.spu}}
                </template>
                <template slot="pro_name" slot-scope="scope">
                    {{scope.row.pro_name.product_name}}
                </template>

                <template slot="pro_category" slot-scope="scope">
                    {{scope.row.pro_category.one_category_name}}
                </template>

                <template slot="pro_amount" slot-scope="scope">
                    {{scope.row.pro_amount.sku_count}}
                </template>

                <template slot="bind_sku" slot-scope="scope">
                    {{scope.row.bind_sku.sku}}
                </template>
            </ComTableInfo>
        </el-dialog>
    </div>
</template>

<script>
import {
    mapGetters,
    mapActions
} from 'vuex';
import {
    getProductScanInfo,
    submitProductScan,
    relevanceProductScan,
    delProductScan,
    submitProductRemark,

    getDetail
} from '@/api/dev_department/product_development/product_scan_relevance';
import {
    isValidData,
} from '@/services';

export default {
    name: 'ProductScanRelevance',
    data () {
        return {
            thisVm: {},
            dialogTableVisible: false,
            isRelevance: true, //是否关联
            queryData: {
                user_id: '',
                department: 2,
                handle_type: 1,
                list_type: '1'
            },

            moduleTypeList: [
                {
                    label: '箱子&SPU',
                    value: '1'
                },
                {
                    label: '台车&箱子',
                    value: '2'
                },
                {
                    label: '台车&SPU',
                    value: '3'
                },
                {
                    label: 'SPU',
                    value: '4'
                }
            ],

            tableData: {
                //箱子&SPU
                boxSPUData: [],
                boxSPUKey: [],
                boxSPUProps: [],

                trolleyBoxData: [],
                trolleyBoxKey: [],
                trolleyBoxProps: [],

                trolleySPUData: [],
                trolleySPUKey: [],
                trolleySPUProps: [],

                SPUData: [],
                SPUKey: [],
                SPUProps: [],

                tableWidth: [],
            },
            cacheList: {
                'boxSPUData': [],
                'trolleyBoxData': [],
                'trolleySPUData': [],
                'SPUData': []
            },

            // 保存勾选数据
            saveCheckInfo: [],

            //查看台车明细
            detailData: [],
            detailKey: [],
            detailWidth: [],
            detailProps: [],

            activeRow: [], //当前状态行
            activeStatus: false, //点击是否 是确认关联
            arrStatus: [false, false, false, false]

        };
    },
    computed: {
        ...mapGetters(['queryParamsInfo', 'productScanListInfo'])
    },
    methods: {
        ...mapActions(['SaveProductCancelListInfo']),

        //提交备注
        submitRemark (row) {
            let markData = {};
            switch (this.queryData.list_type) {
                case '2':
                    markData = {
                        trollery_id: row.trollery_id,
                        box_id: row.box_id
                    };
                    break;
                case '4':
                    markData = {
                        spu: row.spu
                    };
                    break;
                default:
                    markData = {
                        box_id: row.box_id,
                        spu: row.spu
                    };

            }

            //校验
            const title = Object.keys(markData);
            const val = Object.values(markData);
            for (let i in val) {
                if (val[i] === undefined || !val[i]) {
                    this.$message.warning(title[i] + '不能为空');
                    return;
                }
            }
            markData.remark = row.remark;

            let markArr = [markData];
            let obj = {
                handle_type: this.queryData.list_type,
                scan_type: 1,
                mark_data: markArr
            };
            submitProductRemark(obj).then(({data}) => {
                if (data.status) {
                    this.$message.success('备注成功!');
                }
            });
        },

        //表格行样式
        rowClassName ({row, rowIndex}) {

            let idx = this.activeRow.find((item) => {
                return item === rowIndex;
            });

            if (rowIndex === idx) return 'active-row';

            return '';
        },
        //删除关联
        deleteScan () {
            let delBox = [];
            switch (this.queryData.list_type) {
                case '1':
                    this.saveCheckInfo.forEach((item) => {
                        delBox.push({
                            box_id: item.box_id,
                            spu: item.spu
                        });
                    });
                    break;
                case '2':
                    this.saveCheckInfo.forEach((item) => {
                        delBox.push({
                            trollery_id: item.trollery_id,
                            box_id: item.box_id
                        });
                    });
                    break;
                case '3':
                    this.saveCheckInfo.forEach((item) => {
                        delBox.push({
                            box_id: item.box_id,
                            spu: item.spu
                        });
                    });
                    break;
                case '4':
                    this.saveCheckInfo.forEach((item) => {
                        delBox.push({
                            spu: item.spu
                        });
                    });
                    break;
            }
            let obj = {
                handle_type: this.queryData.list_type,
                scan_type: 1,
                delete_data: delBox
            };
            delProductScan(obj).then(({data}) => {
                if (data.errMsg) {
                    this.$message.error(data.errMsg);
                } else {
                    this.$message.success('删除成功!');
                    this.handleQueryInfo();
                }
            });
        },
        //确认关联
        relevanceScan () {
            let obj = {
                handle_type: this.queryData.list_type,
                scan_type: 1
            };
            relevanceProductScan(obj).then(({data}) => {
                if (data.errMsg) {
                    this.$message.error(data.errMsg);
                } else {
                    this.activeStatus = true;

                    this.arrStatus = [true, true, true, true];
                    this.arrStatus[this.queryData.list_type] = false;

                    this.handleQueryInfo();
                }
            });
        },

        //提交
        submitScan () {
            let obj = {
                handle_type: this.queryData.list_type,
                scan_type: 1
            };
            submitProductScan(obj).then(({data}) => {
                console.log(data, 'submit');

            });
        },

        //显示明细
        showDetail (id) {
            if (!id) {
                return;
            }

            this.dialogTableVisible = true;
            let obj = {
                box_id: id,
                ...this.queryData
            };
            getDetail(obj).then(({data}) => {
                this.detailData = data.data.value;
                this.detailProps = Object.keys(data.data.key);
                this.detailKey = Object.values(data.data.key);
            });
        },

        //通过工号查询用户信息
        handleUserInfo () {
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'sample_scan',
                value: JSON.stringify(this.queryData)
            });

        },

        //选中时 同一台车的需要全部选中
        // handleSelectAbout (obj) {
        //     this.$refs.trolleyBoxRef.$refs.myTable.toggleRowSelection(this.tableData.trolleyBoxData[1]);
        // },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;

        },

        //勾选的对象
        selectId () {
            let idArr = [];
            this.saveCheckInfo.forEach((obj) => {
                idArr.push(obj);
            });
            return idArr;
        },

        //扫码送样
        handleScanInfo () {
            let data = {
                code: this.queryData.code,
            };
            this.handleQueryInfo(data);

        },

        // 列表信息查询
        handleQueryInfo (params = {}) {
            let _this = this;
            let obj = {
                handle_type: this.queryData.list_type,
                scan_type: 1,
                ...params
            };

            this.$set(this.queryData, 'code', '');

            function handleActiveRow (obj) {
                for (let item of obj) {
                    if (item.is_complete) {
                        _this.activeRow.push(parseInt(item.num) - 1);
                    }
                }
            }

            //确认关联
            if (this.activeStatus) {
                switch (this.queryData.list_type) {
                    case '1':
                        handleActiveRow(this.tableData.boxSPUData);
                        break;

                    case '2' :
                        handleActiveRow(this.tableData.trolleyBoxData);
                        break;

                    case '3' :
                        handleActiveRow(this.tableData.trolleySPUData);
                        break;

                    case '4' :
                        handleActiveRow(this.tableData.SPUData);
                }

                setTimeout(() => {
                    this.activeRow = [];
                    getProduct();
                }, 500);
            } else {
                getProduct();
            }

            function getProduct () {

                getProductScanInfo(obj).then(({data}) => {
                    if (data.errorMess) {
                        _this.$message({
                            message: data.errorMess,
                            type: 'warning'
                        });
                        return;
                    }
                    if (data.status) {
                        data.list.reverse();

                        //扫码后 相关行提到首行，并能闪现当前状态行样式
                        let newList = Object.assign([], data.list);
                        if (obj.code) {
                            let num = 0;
                            for (let item in data.list) {

                                if (!data.list[item].is_complete) {
                                    newList.splice(item, 1);
                                    newList.unshift(data.list[item]);

                                    _this.activeRow.push(num);
                                    num++;
                                }
                            }

                            setTimeout(() => {
                                _this.activeRow = [];
                            }, 500);
                        }

                        //加一个编号
                        for (let i in newList) {
                            let n = eval(parseInt(i) + 1);
                            newList[i] = Object.assign({num: n}, newList[i]);
                        }

                        _this.handleQueryInfoCallBack(newList);
                        _this.activeStatus = false;

                    } else {
                        _this.$message.warning(data.errMsg ? data.errMsg : '操作失败');
                    }
                });
            }
        },

        //查询回调
        handleQueryInfoCallBack (data) {
            if (data.length <= 0) {
                switch (this.queryData.list_type) {
                    case '1' :
                        this.tableData.boxSPUData = [];
                        this.cacheList.boxSPUData = [];
                        break;
                    case '2' :
                        this.tableData.trolleyBoxData = [];
                        this.cacheList.trolleyBoxData = [];
                        break;
                    case '3' :
                        this.tableData.trolleySPUData = [];
                        this.cacheList.trolleySPUData = [];
                    case '4' :
                        this.tableData.SPUData = [];
                        this.cacheList.SPUData = [];
                }
                return;
            }

            //箱子&spu
            switch (this.queryData.list_type) {
                case '1' :
                    this.tableData.boxSPUData = [...data];
                    this.tableData.boxSPUKey = ['编号', '箱号', '商品SPU', '商品名称', '商品目录', '样品数量', '送样时间', '送样人', '关联SKU', '备注说明'];
                    this.tableData.boxSPUProps = Object.keys(data[0]);
                    this.cacheList.boxSPUData = [...data];
                    break;

                case '2' :
                    //台车&箱子

                    this.tableData.trolleyBoxData = [...data];
                    this.tableData.trolleyBoxKey = ['编号', '商品台车', '商品箱号', '商品目录', '商品数量', '送样时间', '送样人', '备注说明'];
                    this.tableData.trolleyBoxProps = Object.keys(data[0]);
                    this.cacheList.trolleyBoxData = [...data];
                    break;

                case '3' :

                    //台车&SPU
                    this.tableData.trolleySPUData = [...data];
                    this.tableData.trolleySPUKey = ['编号', '商品台车', '商品SPU', '商品名称', '商品目录', '样品数量', '送样时间', '送样人', '关联SKU', '备注说明'];
                    this.tableData.trolleySPUProps = Object.keys(data[0]);
                    this.cacheList.trolleySPUData = [...data];
                    break;

                case '4' :
                    //SPU
                    this.tableData.SPUData = [...data];
                    this.tableData.SPUKey = ['编号', '商品SPU', '商品名称', '商品目录', '样品数量', '送样时间', '送样人', '关联SKU', '备注说明'];
                    this.tableData.SPUProps = Object.keys(data[0]);
                    this.cacheList.SPUData = [...data];
                    break;
            }

            // 保存扫码列表
            this.SaveProductCancelListInfo(this.cacheList);

            // this.tableData.tableWidth[0] = '90px';

            // if (parseInt(this.queryData.list_type) === 1) {
            //
            //     this.tableData.tableWidth[4] = '90px';
            //     this.tableData.tableWidth[5] = '200px';
            //     this.tableData.tableWidth[7] = '300px';
            //
            // } else {
            //     this.tableData.tableWidth[5] = '90px';
            //     this.tableData.tableWidth[6] = '200px';
            //     this.tableData.tableWidth[9] = '300px';
            // }

        },
        // 列表信息查询
        handleQueryListInfo () {
            let handleData = (obj) => {
                if (isValidData(obj) && !this.arrStatus[this.queryData.list_type]) {
                    this.handleQueryInfoCallBack(obj);
                } else {
                    this.handleQueryInfo();
                    this.arrStatus[this.queryData.list_type] = false;
                }
            };

            switch (this.queryData.list_type) {
                case '1' :
                    handleData(this.cacheList.boxSPUData);
                    break;
                case '2' :
                    handleData(this.cacheList.trolleyBoxData);
                    break;
                case '3' :
                    handleData(this.cacheList.trolleySPUData);
                    break;
                case '4' :
                    handleData(this.cacheList.SPUData);
            }

        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.list_type = val;
            this.handleQueryListInfo();
        },
    },
    created () {

        if (isValidData(this.productScanListInfo)) {
            this.cacheList = this.productScanListInfo;
            this.handleQueryListInfo();
        } else {
            this.handleQueryInfo();
        }

        this.detailWidth[0] = '90px';
        this.detailWidth[4] = '90px';

        if (this.queryParamsInfo['sample_scan']) {
            this.queryData = JSON.parse(this.queryParamsInfo['sample_scan']);
        }

    }
};
</script>

<style scoped lang="less">
@border-color: #e6e6e6;


.ui-scan_page {
    text-align: left;

    .ui-head {
        padding: 10px 20px;
        background: #FFFFFF;
        border: 1px solid @border-color;
        border-top: 0;
    }

    .ui-head-ope_item {
        /deep/ .el-input {
            width: 200px;
        }

        li i {
            font-size: 22px;
            position: relative;
            top: 4px;
        }

        .ui-head-ope_item--layout {
            .el-input {
                width: 609px;
            }
        }
    }
}
</style>



