<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品目录" template="cascader"
                                           :options="optionList.productList"
                                           :props="{ value: 'id', children: 'son_id', label: 'category_name' }"
                                           v-model="queryData.productList"></SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="退样类型">
                            <el-select v-model="queryData.rejectType" multiple placeholder="请选择">
                                <el-option
                                    v-for="item in optionList.rejectTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="采样价">
                            <el-input size="small" style="width: 46.5%;" v-model="queryData.priceMin"></el-input>
                            <span>-</span>
                            <el-input size="small" style="width: 46.5%;" v-model="queryData.priceMax"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="处理时效">
                            <el-input size="small" style="width: 46.5%;" v-model="queryData.timeMin"></el-input>
                            <span>-</span>
                            <el-input size="small" style="width: 46.5%;" v-model="queryData.timeMax"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="职能">
                            <el-select size="small" v-model="queryData.functionInfo">
                                <el-option
                                    v-for="(value, label) in optionList.functionInfoList"
                                    :key="value"
                                    :label="label"
                                    :value="value">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员">
                            <el-input size="small" v-model="queryData.operateUser"></el-input>
                        </SearchRequirement>
                    </li>
                    <transition name="slide-fade" @after-enter="animation()" @after-leave="animation()"
                                @before-enter="animation(1)">
                        <div v-show="filterShow">
                            <li>
                                <SearchRequirement label="审核时间" template="daterange"
                                                   v-model="queryData.auditTime"></SearchRequirement>
                            </li>
                            <li>
                                <SearchRequirement label="单据状态">
                                    <el-select size="small" v-model="queryData.billStatus">
                                        <el-option
                                            v-for="(value, label) in optionList.statusList"
                                            :key="value"
                                            :label="label"
                                            :value="value">
                                        </el-option>
                                    </el-select>
                                </SearchRequirement>
                            </li>
                        </div>
                    </transition>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})"
                               icon="icon-chaxun">查询
                    </el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="!filterShow">高级筛选<i class="el-icon-caret-bottom"></i></el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="filterShow">基本筛选<i class="el-icon-caret-top"></i></el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <el-button type="text" size="small" @click="handleExportListInfo('_this')">
                <i class="icon-daochu icon-main"></i>导出当前页
            </el-button>
            <el-button type="text" size="small" @click="handleExportListInfo('_all')">
                <i class="icon-daochu icon-main"></i>导出全部
            </el-button>
        </div>

        <ComTableInfo
            ref="myTable"
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo"
        >
            <template slot="spu" slot-scope="scope">
                <div v-for="(item, key) in scope.row.spu" :key="key">{{item}}</div>
            </template>
            <template slot="photo_path" slot-scope="scope">
                <div class="upload-img-list" v-if="scope.row.photo_path!==''">
                    <div class="ui-com_edit__upload--item">
                        <div class="ui-com_edit__upload--item__img"
                             :style="{background:'url('+scope.row.photo_path+') no-repeat center/contain'}">
                        </div>
                        <div class="ui-com_edit__upload--item--mask">
                            <span class="el-icon-zoom-in" @click="handleLookImage(scope.row.photo_path)"></span>
                        </div>
                    </div>
                </div>
            </template>
            <template slot="category_path" slot-scope="scope">
                <div v-for="(item, key) in scope.row.category_path" :key="key">{{item}}</div>
            </template>
            <template slot="good_name" slot-scope="scope">
                <div>{{scope.row.good_name[0]}}</div>
            </template>
            <template slot="good_info" slot-scope="scope">
                <div v-for="(item, key) in scope.row.good_info" :key="key">{{item}}</div>
            </template>
            <template slot="bad_pic" slot-scope="scope">
                <div class="upload-img-list" v-if="scope.row.bad_pic!==''">
                    <div class="ui-com_edit__upload--item">
                        <div class="ui-com_edit__upload--item__img"
                             :style="{background:'url('+scope.row.bad_pic+') no-repeat center/contain'}">
                        </div>
                        <div class="ui-com_edit__upload--item--mask">
                            <span class="el-icon-zoom-in" @click="handleLookImage(scope.row.bad_pic)"></span>
                        </div>
                    </div>
                </div>
            </template>
            <template slot="operation_record" slot-scope="scope">
                <div v-for="(item, index) in scope.row.operation_record" v-if="scope.row.operation_record.length<3"
                     :key="index">{{item}}
                </div>
                <el-popover
                    v-if="scope.row.operation_record.length>3"
                    style="cursor: pointer;"
                    placement="left"
                    trigger="hover">
                    <div v-for="(item, index) in scope.row.operation_record"
                         :key="index">{{item}}
                    </div>
                    <div slot="reference">
                        <div v-for="(item, index) in scope.row.operation_record" v-if="index<3" :key="index">{{item}}
                        </div>
                        <div v-if="scope.row.operation_record.length>3">...</div>
                    </div>
                </el-popover>

            </template>

        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>

        <!-- 预览图片 -->
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>

    </div>
</template>

<script>
import config from '@/assets/js/config';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage,
    makeLog
} from '@/services';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    getCityInfo
} from '@/api/login';
import {
    getSampleAbnormalRecordInfo
} from '@/api/quality_control_department/sample_quality_control/sample_abnormal_record';

export default {
    data () {
        return {
            //图片预览
            isShowLookImage: false,
            lookImageUrl: '',

            //高级筛选
            filterShow: false,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                productSPU: '',
                supplierCode: '',
                productList: [],
                rejectType: '',
                priceMin: '',
                priceMax: '',
                timeMin: '',
                timeMax: '',
                productName: '',
                supplierName: '',
                functionInfo: '',
                operateUser: '',
                auditTime: '',
                billStatus: '',
            },

            optionList: {
                productList: [],
                functionInfoList: {},
                statusList: {},
                rejectTypeList: {}
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },
        };
    },
    computed: {
        ...mapGetters([
            'queryParamsInfo',
            'sampleAbnormalRecordInfo'
        ]),
    },
    methods: {
        makeLog,
        ...mapActions([
            'GetSampleAbnormalRecordInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,
        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = [src];
        },
        // 导出明细信息
        handleExportListInfo (type) {
            if (type === '_all') {
                this.handleQueryInfo({
                    is_csv: true,
                    limit: 0
                }, true);
            } else {
                this.handleQueryInfo({
                    is_csv: true
                }, true);
            }
        },

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['sample_abnormal_record']);
            }

            let obj = {
                spu: this.queryData.productSPU,
                supplier_code: this.queryData.supplierCode,
                spu_category: this.queryData.productList.join(','),
                reject_cfg_type: this.queryData.rejectType,
                purchase_price_min: this.queryData.priceMin,
                purchase_price_max: this.queryData.priceMax,
                handle_time_min: this.queryData.timeMin,
                handle_time_max: this.queryData.timeMax,
                goods_name: this.queryData.productName,
                supplier_name: this.queryData.supplierName,
                function: this.queryData.functionInfo,
                opera_user: this.queryData.operateUser,
                opera_time_start: this.queryData.auditTime[0],
                opera_time_end: this.queryData.auditTime[1],
                bill_status: this.queryData.billStatus,
                ...params
            };

            this.GetSampleAbnormalRecordInfo(obj).then(({data}) => {

                // 导出
                if (data.status === 2) return;

                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                supplierCode: '',
                supplierName: '',
                supplierType: '',
                settlementType: '',
                devUser: '',
                deliverAddress: [],
                paymentMode: '',
                liaisons: '',
                createUser: '',
                createTime: [],
                state: '',
            };
        },

        //高级筛选
        handleClickFilter () {
            this.filterShow = !this.filterShow;
        },

        animation (isHide) {
            if (isHide) {
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //查询回调
        handleQueryInfoCallBack (data) {
            if (data.status !== 1) {
                return this.$message.error('网络错误，请刷新重试！');
            }
            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'sample_abnormal_record',
                value: JSON.stringify(this.queryData)
            });

            //数据渲染
            this.tableData = data.data.value;

            this.tableData.map((item) => {
                return item.operation_record.reverse();
            });

            this.tableProps = Object.keys(data.data.key);
            this.tableKey = Object.values(data.data.key);
            this.tableWidth = ['60', '220', '70', '250', '250', '250', '100', '200', '200', '200', '70', '100', '100'];

            // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        },

    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.sampleAbnormalRecordInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['sample_abnormal_record']);
                this.handleQueryInfoCallBack(this.sampleAbnormalRecordInfo);
            }
        } catch (e) {
            console.log(e);
        }
    },
};
</script>

<style lang="less" scoped>
@upload-height: 185px;
@border-color: #aaa;

.ui-table--supplier_code {
    font-size: 14px;
    font-weight: bold;
    line-height: 14px;

    p {
        margin-bottom: 10px;
    }
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    background: #63c4e9;
    position: absolute;
    font-size: 12px;
    color: #FFFFFF;
    top: -12px;
    right: -20px;
}

.ui-table--icon_btn {
    i {
        cursor: pointer;
    }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: @upload-height;
        }

        .ui-com_edit__upload--last_btn {
            border: 1px dashed @border-color;
            width: 100%;
            height: @upload-height;

            /deep/ .el-upload {
                display: block;
                height: @upload-height;
                line-height: @upload-height;

                span {
                    color: @border-color;
                }
            }
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: @upload-height;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}
</style>



