/**  =========================> *|`| % 供应商创建记录 % |`|*<======================== **/

import * as types from '../../../mutation-type';

import {
    getSupplierCreateLogInfo,
} from '../../../../api/dev_department/supplier_development/supplier_create_log';

const config = {
    state: {
        supplierCreateLogInfo: {}
    },
    mutations: {
        [types.SAVE_SUPPLIER_CREATE_LOG_INFO](state, saveInfo) {
            state.supplierCreateLogInfo = saveInfo;
        }
    },
    actions: {
        // 获取创建供应商信息
        GetSupplierCreateLogInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSupplierCreateLogInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SUPPLIER_CREATE_LOG_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




