/**  --->>>>   |^ 开发小组管理^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取开发小组管理信息
export function getDevGroupManageInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        group_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/create_supplier' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/system/group/get_group_list', data);
    }
}

// 获取用户下拉列表
export function getUserOptionList() {
    const data = {
        ...CONFIG.ajaxData
    };
    return http.getAjax('/system/Group/get_developer_down_box', data);
}

