/**  --->>>>   |^ 商品修图列表^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 摄影商品列表
export function getGoodsModificationListInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/supplier/supplier/supplier_search', data);

}

// 获取下拉数据
export function getTypeListOption(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/art_design/image/getImgType', data)
}

// 确认上传
export function confrimUploadInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/art_design/image/submitUpload', data);
}

// 预览
export function previewSPUImage(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/art_design/image/getImgList', data);
}

// 预览保存
export function previewSaveInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/art_design/image/previewUpload', data);
}

// 预览提交
export function previewSubmitInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/art_design/image/previewSubmitUpload', data);
}


